kUI.directive('aspectKeep', function () {

    return {
        restrict: 'A',

        link: function (scope, elem, attrs) {

            var s = elem.height();


            var scaleFunc = null;

            elem = $(elem);

            var scale = null;

            if(attrs.hfactor){

                scale = parseFloat(attrs.hfactor);

                scaleFunc = function(elem){

                    elem.css({
                        'height':elem.width() * scale
                    });

                }
            }
            else if(attrs.wfactor){

                scale = parseFloat(attrs.wfactor);

                scaleFunc = function(elem){

                    var currentHeight = elem.height();

                    elem.css({
                        'width':currentHeight * scale
                    });
                }

            }else{
                alert("Illegal usage!!");
            }


            setInterval(function() {
                scaleFunc(elem);
            },500);

        }


    }

});
