'use strict';

kQna.directive('kwpQnaBucketMenu',['gettextCatalog','qnaService','liveAnalyticsService', function (gettextCatalog,qnaService,liveAnalyticsService) {

    return {
        restrict : 'E',
        transclude : true,
        replace :false,
        link: function (scope, elem, attrs) {
            scope._liveAnalyticsService = liveAnalyticsService;
            scope.isSafari = function() {
                return (/Version\/[\d\.]+.*Safari/).test(window.navigator.userAgent);
            };
        },
        templateUrl: "../common/kQna/views/kwpQnaBucketMenu.html"
    };
}]);

