


kUI.directive('setCommand', function ($compile) {

    return {
        restrict: 'A',
        replace: false,
        terminal: true,
        scope : {
            setCommand : '='
        },
        priority : 1200,

        link: function(scope,element, attrs) {

            if (scope.setCommand.tooltip && scope.setCommand.tooltip !== '') {

                attrs.$set('uib-tooltip', scope.setCommand.tooltip);

                if (attrs.tooltipPopupDelay === undefined) {

                    attrs.$set('tooltip-popup-delay', "1200");
                }
                if(attrs.tooltipPlacement === undefined) {
                    attrs.$set('tooltip-placement', 'bottom');
                }

            }
            if(scope.setCommand.icon && scope.setCommand.icon !== '' ) {

                if (attrs.class !== undefined)

                    attrs.$set('class', attrs.class + ' ' + scope.setCommand.icon);
                else
                    attrs.$set('class', scope.setCommand.icon);

            }


            element.bind('click',function () {
                scope.setCommand.do();
                scope.$apply();
            });
            element.removeAttr("set-command");

            $compile(element)(scope);

        }
    }
});