(function () {
    "use strict";

    function Directive() {
        function Controller($scope, $rootScope, slidesService, onlineStatusService, shortcutService,
                            kAssetsTimelineService, ASSET_TYPE, kPollsService)
        {
            var presentationLoadedUnregister= null;

            $scope.timelineAssets = [];
            $scope.selectedAssetIndex = {val : null};

            $scope.allreadyClicked = false;
            $scope.onlineAPI = onlineStatusService.onlineAPI();

            $scope.detachedSlideUrl = null;
            $scope.hasActiveAsset = false;

            $scope.currentAssetType = ASSET_TYPE.SLIDE;

            $scope.$watch(
                function()
                {
                    return kAssetsTimelineService.currentTimelineAsset;
                },
                function()
                {
                    syncCurrentAsset();
                }
            );

            function syncCurrentAsset()
            {
                $scope.hasActiveAsset = false;

                var currentAsset = kAssetsTimelineService.currentTimelineAsset;

                if(currentAsset) {

                    if(currentAsset.type === ASSET_TYPE.SLIDE) {
                        $scope.currentAssetType = ASSET_TYPE.SLIDE;
                        syncCurrentSlide();
                    } else if(currentAsset.type === ASSET_TYPE.POLL) {
                        $scope.currentAssetType = ASSET_TYPE.POLL;
                        $scope.hasActiveAsset = true;
                    }

                }else {
                    // no asset is currently presented
                    $scope.currentAssetType = null;
                }

                $scope.setSelectedAssetIndex();

            }
            
            function syncCurrentSlide()
            {
                $scope.detachedSlideUrl = '';

                var currentDocEntryInfo = slidesService.getActiveDocumentInfo();
                if(currentDocEntryInfo &&
                    (kAssetsTimelineService.currentTimelineAsset.documentEntryId === currentDocEntryInfo.id)) {
                    //asset is part of current timeline assets
                    $scope.hasActiveAsset = true;
                } else {
                    //asset is detached from current timeline assets
                    $scope.hasActiveAsset = true;
                    kAssetsTimelineService.getCurrentAssetUrl().then(function(result)
                    {
                        $scope.detachedSlideUrl = result.url;
                    },function()
                    {
                        // the is only here for safety since we know that we have current slide key so we should be able to extract url
                        $scope.detachedSlideUrl = 'http://missing-image-url';
                    });
                }

            }

            var timelineAssetsLoaded = function () {

                $scope.timelineAssets=kAssetsTimelineService.timelineAssets;

                $scope.$evalAsync(function()
                {
                    syncCurrentAsset();
                });

                $rootScope.$broadcast('mainSlideLoaded');
            };

            $scope.prevCommand = CreateCommand("","kicon-arrow",function ()
            {
                if(!kAssetsTimelineService.isTimelineLocked()){
                    // move to prev slide only if such a slide exists
                    if (kAssetsTimelineService.currentTimelineAsset) {
                        var prevSlide = kAssetsTimelineService.getPrevAsset(kAssetsTimelineService.currentTimelineAsset, true);

                        if (prevSlide) {
                            kAssetsTimelineService.currentTimelineAsset = prevSlide;
                        }
                    }

                    $scope.setSelectedAssetIndex();
                }
            });

            $scope.nextCommand = CreateCommand("","kicon-arrow",function ()
            {
                if(!kAssetsTimelineService.isTimelineLocked()) {
                    kAssetsTimelineService.currentTimelineAsset =
                        kAssetsTimelineService.getNextAsset(kAssetsTimelineService.currentTimelineAsset, false, true);
                    $scope.setSelectedAssetIndex();
                }
            });


            $scope.setSelectedAssetIndex = function() {

                $scope.selectedAssetIndex.val = kAssetsTimelineService.currentTimelineAsset ?  ($scope.getAssetIndex(kAssetsTimelineService.currentTimelineAsset) + 1) : '';
            };

            $scope.setPos = function(assetIndex)
            {
                if (isNaN(assetIndex) ||  assetIndex < 1 || assetIndex > $scope.numberOfSlides()){
                    $scope.setSelectedAssetIndex();
                    return false;
                }
                //In case someone tries a float
                assetIndex = parseInt(assetIndex);
                //getting requested asset
                var requestedAsset = $scope.timelineAssets[assetIndex-1];
                //jump to asset only if it is valid
                if(requestedAsset.type === ASSET_TYPE.SLIDE ||
                    requestedAsset.type === ASSET_TYPE.POLL && kPollsService.isPollComplete(requestedAsset)) {
                    kAssetsTimelineService.currentTimelineAsset = requestedAsset;
                }
                $scope.setSelectedAssetIndex();
            };

            $scope.getAssetIndex = function()
            {
                var currentAsset = kAssetsTimelineService.currentTimelineAsset;
                if (currentAsset) {
                    return kAssetsTimelineService.getAssetIndexInTimelineArray(currentAsset);
                }
                else {
                    return -1;
                }

            };

            $scope.numberOfSlides = function() {
                if(kAssetsTimelineService.timelineAssets){
                    return kAssetsTimelineService.timelineAssets.length;
                }
                else{
                    return -1;
                }

            };

            $scope.isVisible = function(asset)
            {
                var currentAsset = kAssetsTimelineService.currentTimelineAsset;
                if(currentAsset && asset) {
                    return kAssetsTimelineService.getAssetIndexInTimelineArray(currentAsset) ===
                                                kAssetsTimelineService.getAssetIndexInTimelineArray(asset);
                }
                else{
                    return false;
                }
            };

            kAssetsTimelineService.isReady().then(function() {
                timelineAssetsLoaded();
                presentationLoadedUnregister = $scope.$on("AssetsTimelineChanged",timelineAssetsLoaded);
            });

            $scope.$on('$destroy',function()
            {
                if (presentationLoadedUnregister)
                {
                    presentationLoadedUnregister();
                }
            });

            $scope.showNavigationToggle = function ()
            {
                return !kAssetsTimelineService.isTimelineLocked() && $scope.timelineAssets.length > 0;
            };

            // NextCommand
            shortcutService.bindCommand($scope,$scope.nextCommand,shortcutService.keyCodes.WEBCAST_KEYPRESS_SLIDE_FORWARD_ENTER);
            shortcutService.bindCommand($scope,$scope.nextCommand,shortcutService.keyCodes.WEBCAST_KEYPRESS_SLIDE_FORWARD_RIGHT);
            shortcutService.bindCommand($scope,$scope.nextCommand,shortcutService.keyCodes.WEBCAST_KEYPRESS_SLIDE_FORWARD_DOWN);
            shortcutService.bindCommand($scope,$scope.nextCommand,shortcutService.keyCodes.WEBCAST_KEYPRESS_SLIDE_FORWARD_PAGE_DOWN);
            shortcutService.bindCommand($scope,$scope.nextCommand,shortcutService.keyCodes.WEBCAST_KEYPRESS_SLIDE_FORWARD_SPACE);

            // PrevCommand
            shortcutService.bindCommand($scope,$scope.prevCommand,shortcutService.keyCodes.WEBCAST_KEYPRESS_SLIDE_BACKWARDS_LEFT);
            shortcutService.bindCommand($scope,$scope.prevCommand,shortcutService.keyCodes.WEBCAST_KEYPRESS_SLIDE_BACKWARDS_UP);
            shortcutService.bindCommand($scope,$scope.prevCommand,shortcutService.keyCodes.WEBCAST_KEYPRESS_SLIDE_BACKWARDS_PAGE_UP);
            shortcutService.bindCommand($scope,$scope.prevCommand,shortcutService.keyCodes.WEBCAST_KEYPRESS_SLIDE_BACKWARDS_SPACE);
        }

        function Link(scope, element, attrs, ctrl) {
        }

        return {
            restrict: 'A',
            scope: {},
            templateUrl: '../common/kMainAsset/directives/k-main-asset.html',
            controller: Controller,
            link: Link
        };
    }

    angular.module('k-main-asset').directive('kMainAsset', Directive);

})();



