
(function() {
    'use strict';

    function registerAdapter(kProducerActionsProvider, cuePointsServiceProvider) {

        /**
         * will create a timeline assets state object: {docId : "'slideName-1', 'pollId-1', 'slideName-2' ... "}
         */
        kProducerActionsProvider.registerAction('timeline-assets-state','codeCuePoint.Code', {transmitMode : 'always'});
        cuePointsServiceProvider.addTagToArchiveExcludeList('timeline-assets-state');

    }

    angular.module('k-assets-timeline').config(registerAdapter);

})();