kwpApp.controller('videoPreviewController',['$scope','captureService','gettextCatalog', function ($scope, captureService, gettextCatalog) {

    $scope.template = "./partials/videoPreviewView.html";
    //Data bounded to the html.
    $scope.captureControl = {videoPreview : false};

    ////This should be loaded in the future from a file containing HTML element IDs.
    var htmlVideoElementPreviewSurfaceID = "videoPreview"

    //This variable will hold the last requested promise by checking/unchecking.we will wait for it to complete each time before we make a new request.
    var lastPromise = null;

    //Commands for on air button.
    var offCommand = CreateCommand(gettextCatalog.getString("Off Air"), "kicon-onAir", function () {
        
        $scope.onoffcommand = onCommand;
        lastPromise = startPreviewPromise();
    });

    var onCommand = CreateCommand(gettextCatalog.getString("On Air"), "kicon-onAir", function () {
        $scope.onoffcommand = offCommand;
        lastPromise = stopPreviewPromise();
    });
    //This command defines the box as marked.
    onCommand.checked = true;

    /* Turn on/off the check box sign.currently the on air. */
    function updateBox(isOn) {
        //Jquery direct call for check/uncheck.This line should be removed once angular will fix behaviour.
        //This is the implementation for the default checkbox state.
        //$("#previewCheckBox").prop("checked", isOn);
        //$scope.captureControl.videoPreview = isOn;
        //$scope.$apply();
        //Current implementation uses the on air icon.
        //klog.info("Currently setting on air state="+isOn);
        //$scope.$apply();
        //$scope.onoffcommand = (isOn) ? onCommand : offCommand;

    }



    /* returns a promise for video preview starting */
    function startPreviewPromise() {

        return captureService.startPreviewVideo(htmlVideoElementPreviewSurfaceID).then(function (value) {

            updateBox(true);

        },function (error_obj) {

            updateBox(false);
            //TODO: Move this error to an error page.
            //clientErrorReporter.reportError("Preview video failed",error_obj.error_code,error_obj.message,"Please try to turn off other application which are using video/audio input.");
        });
    }

    /* returns a promise for video preview stopping */
    function stopPreviewPromise() {

        return captureService.stopPreviewVideo(htmlVideoElementPreviewSurfaceID).then(function (value) {

            updateBox(false);

        }, function (error_obj) {

            updateBox(true);

            clientErrorReporter.reportError("Stop Preview video failed",error_obj.error_code,error_obj.message);

        });
    }

    //We initiate the boot promise of this controller.We start by trying to preview video.
    klog.info("Starting Video previewing from capture controller.");

    lastPromise = startPreviewPromise();

    //Temporarily not in use until checkbox decision is complete.
    $scope.change = function (control) {
        //control.videoPreview contains the information.if(control.videoPreview==true) then the user requested to turn video on,otherwise off.
        lastPromise = lastPromise.then(function (val) {
            (control.videoPreview) ? startPreviewPromise() : stopPreviewPromise();
        });
    };
}]);