"use strict";


kwpApp.directive('kImage', ['$parse', function ($parse) {
    return {
        restrict: 'A',
        scope : {
            kShow : '='
        },
        require: ['^kImageContainer'],
        link: function (scope, parentElement, attrs, ctrls) {
            var imageAPI = {
                onResize :  function()
                {
                    if(autoResize) {
                        if (parentElement.is(':visible')) {
                            resetLayout();
                        }
                    }
                }
            };
            var imageElement = attrs.kImage ? parentElement.find('img[name="' + attrs.kImage + '"]') : null;
            var errorElement = attrs.error ? parentElement.find('[name="' + attrs.error + '"]') : null;
            var loaderElement = attrs.loader ? parentElement.find('[name="' + attrs.loader + '"]') : null;
            var autoResize = attrs.resize ? ( attrs.resize === 'false' ? false : true) : true;

            function onShow(value)
            {
                if (value)
                {
                    if(autoResize) {
                        resetLayout();
                    }
                    parentElement.show();
                }else
                {
                    parentElement.hide();
                }
            }

            function reposition(element)
            {
                var $element = $(element);
                element = $element.get(0);

                var parentHeight = parentElement.height();
                var parentWidth = parentElement.width();

                var sizes = calculateSizes(element.naturalWidth, element.naturalHeight,parentWidth,parentHeight,true);

                $element.css({
                    'position': 'absolute',
                    'top': sizes.targetTop + 'px',
                    'left': sizes.targetLeft + 'px',
                     'width': sizes.width + 'px',
                     'height': sizes.height + 'px'
                });
            }

            function calculateSizes(srcwidth, srcheight, targetwidth, targetheight, fLetterBox) {

                var result = { width: 0, height: 0, fScaleToTargetWidth: true };

                if ((srcwidth <= 0) || (srcheight <= 0) || (targetwidth <= 0) || (targetheight <= 0)) {
                    return result;
                }

                // scale to the target width
                var scaleX1 = targetwidth;
                var scaleY1 = (srcheight * targetwidth) / srcwidth;

                // scale to the target height
                var scaleX2 = (srcwidth * targetheight) / srcheight;
                var scaleY2 = targetheight;

                // now figure out which one we should use
                var fScaleOnWidth = (scaleX2 > targetwidth);
                if (fScaleOnWidth) {
                    fScaleOnWidth = fLetterBox;
                }
                else {
                    fScaleOnWidth = !fLetterBox;
                }

                if (fScaleOnWidth) {
                    result.width = Math.floor(scaleX1);
                    result.height = Math.floor(scaleY1);
                    result.fScaleToTargetWidth = true;
                }
                else {
                    result.width = Math.floor(scaleX2);
                    result.height = Math.floor(scaleY2);
                    result.fScaleToTargetWidth = false;
                }
                result.targetLeft = Math.floor((targetwidth - result.width) / 2);
                result.targetTop = Math.floor((targetheight - result.height) / 2);

                return result;
            }

            function resetLayout()
            {
                $(parentElement).css('position','absolute');

                reposition(loaderElement);

                parentElement.find('img').each(function(index,element)
                {
                    var $element = $(element);

                    if ($element.data('loaded'))
                    {
                        reposition(element);
                    }
                });
            }

            function init() {
                if (imageElement && imageElement.length > 0) {

                    // manage the show status of the element
                    var hasShowExpression =  attrs.kShow;
                    if (hasShowExpression)
                    {
                        scope.$watch('kShow',onShow);
                    }else
                    {
                        onShow(true);
                    }

                    // register to container api
                    ctrls[0].register(imageAPI);

                    scope.$on('$destroy', function () {
                        ctrls[0].unregister(imageAPI);
                    });

                    // hide image and error elements, show loading
                    imageElement.hide();
                    errorElement.hide();
                    loaderElement.show();

                    parentElement.find('img').on('load', function (e) {
                        $(e.target).data('loaded',true);
                        if(autoResize) {
                            reposition(e.target);
                        }
                        if (e.target === imageElement[0]) {
                            loaderElement.fadeOut(400,function()
                            {
                                imageElement.fadeIn();
                            });

                        }
                    }).on('error', function (e) {
                        if (e.target === imageElement[0]) {
                            loaderElement.hide();
                            errorElement.show();
                        }
                    });
                } else {
                    parentElement.children().remove();
                    parentElement.html('<!-- element children were removed by "k-image" directive since image selector was invalid -->');
                }
            }

            init();
        }
    };
}]);