kUI.directive('fontFitter', function () {
    return {
        restrict: 'A',
        scope: {
            maxFont: "@"
        },

        link: function (scope, elem, attrs) {

            if (!scope.maxFont)
                scope.maxFont="9999";
            // Establish default settings/variables
// ====================================
            var settings = $.extend({
                    maximum   : "9999",
                    minimum   : "1",
                    maxFont   : scope.maxFont,
                    minFont   : "1",
                    fontRatio : "12"
                }, attrs),

// Do the magic math
// =================
                changes = function(el) {
                    var $el = $(el),
                        elw = $el.width(),
                        width = elw > parseInt(settings.maximum) ? parseInt(settings.maximum) : elw < parseInt(settings.minimum) ? parseInt(settings.minimum) : elw,
                        fontBase = width / parseInt(settings.fontRatio),
                        fontSize = fontBase > parseInt(settings.maxFont) ? parseInt(settings.maxFont) : fontBase < parseInt(settings.minFont) ? parseInt(settings.minFont) : fontBase;
                    $el.css('font-size', fontSize + 'px');
                };
                function resizeElement()
                {
                    changes(elem);
                }
                elementWatcher.onresize(elem,resizeElement);

                scope.$on('$destroy',function()
                {
                    elementWatcher.remove(elem,resizeElement);
                });


        }


    };

});
