'use strict';
var klog = function() {
    var logger=function(level)
    {
        return function() {

            if (window.configuration===undefined ||
                configuration.logThreshold.value >= level.value) {

                var args = Array.prototype.slice.call(arguments).map(function (obj) {
                    if (obj instanceof Error) {
                        obj = obj.message + " " + obj.stack;
                    }

                    return obj.toString();
                });


	            var message = String.format.apply(this, args);


                if (level.name === 'warn') {
                    console.warn(message);
                   }
                else if (level.name === 'error') {
                    console.error(message);
                } else {
                    console.log(level.name + ": " + message);
                }

                if (ipcRenderer) {

                    ipcRenderer.send('__ELECTRON_LOG__', [level.name,message]);
                }
            }
        };
    };

    var logLevel = {
        error : {value: 0, name: "error"},
        warn : {value: 1, name: "warn"},
        info : {value: 2, name: "info"},
        verbose : {value: 3, name: "verbose"},
        debug : {value: 4, name: "debug"}
    };

    return {
        logLevel : logLevel,
        debug: logger(logLevel.debug),
        verbose: logger(logLevel.verbose),
        warn: logger(logLevel.warn),
        info: logger(logLevel.info),
        log: logger(logLevel.info),
        error: logger(logLevel.error)
    };
}();

window.onerror = function (msg, url, line) {
    klog.error("Caught[via window.onerror]: '" + msg + "' from " + url + ":" + line);
    return true; // same as preventDefault
};

