kwpApp.controller('miscController', ['$scope', 'gettextCatalog', 'kdialog', 'sessionService','captureService','kCef', function ($scope, gettextCatalog, kdialog, sessionService,captureService,kCef) {
    $scope.template = "./partials/miscView.html";

    var maxCommand = CreateCommand("", "kicon-fullscreen", function () {
        $scope.winSizeCommand = restoreCommand;
        electron.remote.BrowserWindow.getFocusedWindow().maximize();
    },gettextCatalog.getString("Maximize"));

    var restoreCommand = CreateCommand("", "kicon-fullscreen", function () {
        $scope.winSizeCommand = maxCommand;
        electron.remote.BrowserWindow.getFocusedWindow().unmaximize();
    },gettextCatalog.getString("Restore"));

    $scope.minimizeCommand = CreateCommand("", "kicon-minimize", function () {
        electron.remote.BrowserWindow.getFocusedWindow().minimize();
    },gettextCatalog.getString("Minimize"));

    $scope.debuggingMode=function() {
        return configuration.debuggingMode;
    };

    $scope.closeAppCommand = CreateCommand("", "kicon-close2", function () {

        if (captureService.isBroadcasting()) {
            kdialog.yesNoDialog({
                yesMessage:gettextCatalog.getString("Yes, Close"),
                noMessage:gettextCatalog.getString("No, Don't Close"),
                title: "Close Application",
                message: "You have chosen to close the "+whiteLabelingConfiguration.appName+"</br><b>while still presenting</b>, Are you sure? "
            }).then(function () {
                window.close();
            }, function () {
              //  alert("NO!");
            });
        } else {
            window.close();
        }


    },gettextCatalog.getString("Close"));

    $scope.aboutCommand = CreateCommand("", "kicon-info", function () {

        kdialog.infoDialog({
            title:gettextCatalog.getString("System Information"),
            templateUrl: './partials/about.html'

        });
    },gettextCatalog.getString("About"));


    $scope.debugCommand = CreateCommand(gettextCatalog.getString("debug"), "kicon-notes", function () {

        $scope.$parent.debugExpanded = !$scope.$parent.debugExpanded;
    });

    $scope.winSizeCommand = maxCommand;

    $scope.logoUrl=whiteLabelingConfiguration.logoUrl;

    $scope.showTitleBarButtons = function(){
        return configuration.showTitleBarButtons;
    };


}]);


