/* globals _ /
 / globals klog /
 / globals PromisesQueue /
 / globals Promise */

(function (){
    "use strict";

    function Directive(kAssetsTimelineService, kProducerActions, ASSET_TYPE, kPollContainer, POLL_STATE)
    {

        function Controller($scope)
        {
            
        }

        function Link(scope, element, attrs, ctrls)
        {

            var pollContainer = {
                configuration : {
                    currentView: 'producerOnly'
                },
                userVote : {metadataId: null, answer: null, inProgress: false, canUserVote: false, isReady: false},
                pollData : {
                    pollId: null,
                    errorContent: null,
                    content: null,
                    showResults: false,
                    showTotals: false,
                    pollResults: null,
                    showAnswers : false
                },
                getViewConfig : function () {
                    return pollContainer.configuration.currentView;
                },
                canUserVote : function () {
                    return pollContainer.userVote.canUserVote;
                }
            };

            var $webcastPoll = $(element).children().first();

            var pollView =  new window.webcast.PollView(element, pollContainer, $webcastPoll);
            
            //watch for container height changes
            $(element).parent().resize(
                function()
                {
                    scope.layout = pollView.getLayoutName();
                }
            );

            //watch for assets changes
            scope.$watch(
                function()
                {
                    return kAssetsTimelineService.currentTimelineAsset;
                },
                function()
                {
                    scope.layout = pollView.getLayoutName();
                    //make sure new poll asset will have its' pollContainer view state in "producerOnly"
                    _.extend(pollContainer.configuration,
                        {
                            currentView: 'producerOnly'
                        }
                    );
                    updateCurrentPollState();
                }
            );

            //listen to possible poll data change
            kProducerActions.subscribeInstanceChange(function ()
            {
                updatePollData();
            });

            // listen to poll state changes from server and act accordingly
            kProducerActions.subscribeStateChange(function()
                {
                    updateCurrentPollState();
                }
            );

            scope.$on("poll-results-updated",
                function (event, pollResults)
                {
                    if(kAssetsTimelineService.currentTimelineAsset &&
                        kAssetsTimelineService.currentTimelineAsset.type === ASSET_TYPE.POLL &&
                        kAssetsTimelineService.currentTimelineAsset.id === pollResults.pollId) {

                        var updatedPollContainer = kPollContainer.updatePollContainerResults(pollContainer, pollResults);
                        extendPollContainer(updatedPollContainer);
                        pollView.syncDOMPollResults();
                    }
                }
            );

            function updateCurrentPollState()
            {
                if(kAssetsTimelineService.currentTimelineAsset &&
                    kAssetsTimelineService.currentTimelineAsset.type === ASSET_TYPE.POLL) {
                    var updatedPollContainer = kPollContainer.getUpdatedPollContainer(pollContainer);
                    extendPollContainer(updatedPollContainer);
                    //update poll main asset view
                    pollView.syncPollDOM();
                }
            }

            function updatePollData()
            {
                if(kAssetsTimelineService.currentTimelineAsset &&
                    kAssetsTimelineService.currentTimelineAsset.type === ASSET_TYPE.POLL) {
                    var updatedPollContainer = kPollContainer.updatePollContainerData(pollContainer);
                    extendPollContainer(updatedPollContainer);
                    //update poll main asset view
                    pollView.syncPollDOM();
                }

            }

            function extendPollContainer(updatedPollContainer)
            {
                _.extend(pollContainer,updatedPollContainer);
                _.extend(pollContainer.data, {showResults: POLL_STATE.PERCENTAGES});
            }

            //init current poll state
            updateCurrentPollState();
        }

        return {
            restrict: 'E',
            templateUrl: '../common/kMainAsset/directives/k-main-poll-asset.html',
            scope: {},
            link: Link,
            controller: Controller,
            controllerAs: 'vm'
        };

    }

    angular.module('k-main-asset').directive('kMainPollAsset', Directive);


})();