
kwpApp.controller('contentPanelController', function($scope,$rootScope,slidesService,kAssetsTimelineService) {

    var self = this;
    var showVideo=false;


    var unregisterStreamUpdated = $rootScope.$on('rtcStreamUpdated', function(event, status)  {
        showVideo = status;
    });
    $scope.$on('$destroy', unregisterStreamUpdated);

    $rootScope.$on('changeActiveContentPanel', function(event,name)  {
        self.activeContentWidget = name;
    });

    function showStageContent()
    {
        self.activeContentWidget = 'stage';
    }
    function showDetailsContent()
    {
        self.activeContentWidget = 'details';
    }
    function showMonitoringContent()
    {
        self.activeContentWidget = 'monitoring';
    }
    function presentationLoaded() {

        if (showVideo || kAssetsTimelineService.currentTimelineAsset || (kAssetsTimelineService.timelineAssets && kAssetsTimelineService.timelineAssets.length > 0))
        {
            showStageContent();
        }else {

            showDetailsContent();
        }
    }

    self.showStageContent =showStageContent;
    self.showDetailsContent = showDetailsContent;
    self.showMonitoringContent = showMonitoringContent;

    self.showLiveDashboard = configuration.showLiveDashboard;
    self.showContent = false;
    self.activeContentWidget = '';


    slidesService.load().then(function()
    {
        self.showContent = true;
        presentationLoaded();

        $scope.$on("AssetsTimelineChanged",presentationLoaded);
    });


});


