'use strict';


/*
 * Incoming screen directive.Represent the live feed screen where producer receives all incoming data from users.
 * Also this module holds connection to the model of the qnaService.
 */
kQna.directive('kwpQnaScreenIncoming', function ($rootScope) {

    var controller = ['$scope','qnaService', function ($scope,qnaService) {

        $scope.qnaThreadsData = {
            model :   qnaService.getLiveData(),
            displayEntries : qnaService.liveData.displayList
        };
        $scope.isModerationModeState = function(){
            return $scope.qnaModeratorMode && (    $scope.currentQnaState ==='SETTINGS'
                                                || $scope.currentQnaState ==='ANSWER'
                                                || $scope.currentQnaState ==='ANSWER_ON_AIR_SELECT'
                                                || $scope.currentQnaState==='ANSWER_ON_AIR_RECORD');
        };
        $scope.displayEntries = qnaService.liveData.displayList;

        $scope.liveData = qnaService.liveData;

        $scope.setIndicator=function(val) {

            $scope.$parent.indicator=val;
        };

        $scope.THREAD_STATE = qnaService.THREAD_STATE;


        $scope.setQnaThreadAsDone = function(qnaThread){

            qnaService.setQnaThreadAsDone(qnaThread);

        };

        $scope.setQnaThreadAsFollow = function(qnaThread){

            qnaService.setQnaThreadAsFollow(qnaThread);

        };

        $scope.setQnaThreadAsCleared = function(qnaThread){

            qnaService.setQnaThreadAsCleared(qnaThread);

        };

        $scope.exportQnaData = function(){

            qnaService.exportQnaDataToFile();

        };
        
        qnaService.registerOnIncomingCallback(function(newVal){
            $scope.setIndicator(newVal > 0);
        });
    }];

    return {
        restrict : 'E',
        //transclude : true,
        replace : true,

        controller: controller,

        link: function (scope, $elem, attrs) {
            scope.counters = scope.qnaThreadsData.model.counters;
            scope.$incomingMessageListContainer = $elem.find('.incoming-messages');
            scope.$lastIncomingMessage = $elem.find('.bottom-of-incoming-messages');
            scope.scrollDebouncingTimeout;
            scope.unregisterNewMessageEventFunction;
            scope.showNewMessageIndicationButton = false;

            scope.NUMBER_PIXELS_FROM_THE_BOTTOM = 37;
            scope.SUBSETS = {

                INCOMING: {
                    title: "Incoming",//(pending)
                    filterFunction: function(qnaEntry){
                        return qnaEntry.getThread().isPending();
                    },
                    capacity: function(){
                        return scope.liveData.counters[scope.liveData.states.PENDING].total;
                    }
                },
                COMPLETED: {
                    title: "Completed",
                    filterFunction: function (qnaEntry) {
                        return qnaEntry.getThread().isAnswered() || qnaEntry.getThread().isDone();
                    },
                    capacity : function(){
                        return scope.liveData.counters[scope.liveData.states.ANSWERED].total +
                            scope.liveData.counters[scope.liveData.states.DONE].total;
                    }
                },
                FOLLOW_UP: {
                    title: "Followed up",
                    filterFunction: function (qnaEntry) {
                        return qnaEntry.getThread().isFollowUp();
                    },
                    capacity: function(){
                        return scope.liveData.counters[scope.liveData.states.FOLLOWUP].total;
                    }
                },
                FORWARDED: {
                    title: "Forwarded",
                    filterFunction: function (qnaEntry) {
                        return qnaEntry.getThread().isForwarded();
                    },
                    capacity: function(){
                        return scope.liveData.counters[scope.liveData.states.FORWARDED].total;
                    }
                },
                ANNOUNCED: {
                    title: "Announced",
                    filterFunction: function (qnaEntry) {
                        return qnaEntry.getThread().isAnnouncement();
                    },
                    capacity: function(){
                        return scope.liveData.counters[scope.liveData.states.ANNOUNCED].total;
                    }
                }
            };

            scope.selectedSubset = scope.SUBSETS.INCOMING;

            scope.selectSubset = function(filterBucket){

                scope.selectedSubset = filterBucket;

                scope.state.menuOpen = false;

                scope.state.settingsOpen = false;

                scope.setQnaState(scope.QNA_STATES.INCOMING);

            };

            scope.onEntryClick = function(qnaEntry){

                var thread = qnaEntry.getThread();

                if (!scope.qnaModuleState.qnaEnabled || scope.qnaModuleState.announcementOnly){
                    return;
                }

                if(thread.isAnnouncement()){

                    return;
                }

                scope.setCurrentReplyThread(thread);

                scope.setQnaState(scope.QNA_STATES.ANSWER);

            };

            scope.onNewMessagesClick = function($button) {
                // Scroll to bottom
                scope.$lastIncomingMessage[0].scrollIntoView({ behavior: 'smooth', block: 'start' });
                scope.clearAndHideNewMessageIndication();
            };

            // Release new message button indication on scroll bottom
            scope.$incomingMessageListContainer.on('scroll', function (e) {
                if (scope.liveData.incomingCounters.newMessages === 0){
                    return;
                }

                // Debouncing the event If there's a timer, cancel it
                if (scope.scrollDebouncingTimeout) {
                    window.cancelAnimationFrame(scope.scrollDebouncingTimeout);
                }

                // Setup the new requestAnimationFrame()
                scope.scrollDebouncingTimeout = window.requestAnimationFrame(function () {

                    // Run our scroll functions (BL): decide when to release the message indication button
                    var elem = $(e.currentTarget);
                    if (elem[0].scrollHeight - elem.scrollTop() <= elem.outerHeight() + scope.NUMBER_PIXELS_FROM_THE_BOTTOM) {
                        scope.clearAndHideNewMessageIndication();
                    }
                });
            });

            // show new message button indication
            scope.unregisterNewMessageEventFunction = $rootScope.$on('NewIncomingMessage', function () {
                var repeatItem = scope.$incomingMessageListContainer.find('.repeatedItem');
                var repeatedItemSize = repeatItem.length ? repeatItem.first().outerHeight() : null;

                // check if button should show depend if the container need scroll or empty... (prevent show on first messages)
                if (repeatedItemSize && (scope.$incomingMessageListContainer.height() <= scope.liveData.incomingCounters.total * repeatedItemSize)
                    && scope.liveData.incomingCounters.newMessages > 0) {

                    scope.showNewMessageIndicationButton = true;
                }
                else {
                    scope.clearAndHideNewMessageIndication();
                }
            });

            scope.clearAndHideNewMessageIndication = function() {
                // Clear Messages counter
                scope.liveData.clearNewMessagesCounter();
                // Hide the button counter indicator
                scope.showNewMessageIndicationButton = false;
            };

            scope.isShowingIncomingLiveFeed = function() {
                return scope.selectedSubset === scope.SUBSETS.INCOMING
                    && scope.currentQnaState !='SETTINGS'
                    && !(scope.currentQnaState ==='ANSWER' && scope.qnaModeratorMode)
                    && !(scope.currentQnaState ==='ANSWER_ON_AIR_RECORD' && scope.qnaModeratorMode)
                    && !(scope.currentQnaState ==='ANSWER_ON_AIR_SELECT' && scope.qnaModeratorMode);
            };

            scope.$on('$destroy', scope.unregisterNewMessageEventFunction);
        },
        templateUrl: "../common/kQna/views/kwpQnaScreenIncoming.html"
    };
});


