(function(){
    "use strict";

    function service($rootScope, $q,$timeout,slidesService,kalturaAPI)
    {
        var processUploadRequestCounter = 0;
        var self = this;


        /**
         * Performs a repeated check against the server to check when the presentation is ready to be used.
         * @param entryId
         * @param attemptsLeft
         * @returns {*}
         */
        function monitorFileUploadStatus(entryId, requestIdentifier, attemptsLeft) {

            var deferred = $q.defer();

            if (!angular.isNumber(attemptsLeft)) {
                attemptsLeft = configuration.presentationProcessingMaxAttempts;
            }

            if (attemptsLeft <= 0) {
                klog.warn('kSlidesUploader.monitorFileUploadStatus(): checking document status maximum attempts reached.');
                return $q.reject({});
            }
            klog.info('kSlidesUploader.monitorFileUploadStatus(): checking document status. Attempt #{0}', attemptsLeft);

            $timeout(function () {
                kalturaAPI.documentService.getDocumentEntryById(entryId).then(function(result)
                {
                    // this function verify that the request was not aborted.
                    if (requestIdentifier !== processUploadRequestCounter)
                    {
                        klog.info('kSlidesUploader.processUpload(): this request with identifier {0} was aborted', requestIdentifier);
                        return $q.reject({error: {token: 'abort'}});
                    }

                    return result;
                }).then(function (result) {
                    klog.info('kSlidesUploader.monitorFileUploadStatus(): checking document status returned with status {0} ', result.status);

                    if (result.status === 2) {
                        deferred.resolve({});
                    }else {
                        // handle result as failure
                        return $q.reject({error: { token : 'processing' },message : 'entry status not equals ready status'});
                    }
                }).catch(function (reason) {

                    if (reason && reason.error && reason.error.token === 'abort') {
                        deferred.reject(reason);
                    }else if (reason && reason.error && reason.error.token === 'processing') {
                        klog.info('kSlidesUploader.monitorFileUploadStatus(): document slides are still in creation process');
                        monitorFileUploadStatus(entryId, requestIdentifier, attemptsLeft - 1).then(function (result) {
                            deferred.resolve(result);
                        }, function (reason) {
                            deferred.reject(reason);
                        });
                    }else {
                        deferred.reject(reason);

                    }
                });
            }, configuration.presentationProcessingMaxDelay);

            return deferred.promise;
        }

        function abortProcessUpload()
        {
            klog.info('kSlidesUploader.processUpload(): aborting request with identifier {0} was aborted', processUploadRequestCounter);
            processUploadRequestCounter++;
        }

        function processUpload(name, token)
        {
            var deferred = $q.defer();
            processUploadRequestCounter++;

            // we use a wrapped function to be able to abort a request (using a local identifier)
            (function(name, token, requestIdentifier) {

                klog.info('kSlidesUploader.processUpload(): handling upload with name ' + name + ', upload token ' + token + ", request identifier " + requestIdentifier);

                if (requestIdentifier !== processUploadRequestCounter)
                {
                    deferred.reject({error: {token: 'abort'}});
                }
                kalturaAPI.documentService.addFromUploadedFile({
                    entryId: configuration.MediaEntryId,
                    'uploadTokenId': token,
                    fileName: name
                }).then(function(result)
                {
                    // this function verify that the request was not aborted.
                    if (requestIdentifier !== processUploadRequestCounter)
                    {
                        klog.info('kSlidesUploader.processUpload(): this request with identifier {0} was aborted', requestIdentifier);
                        return $q.reject({error: {token: 'abort'}});
                    }

                    return result;
                }).then(function (result) {
                    var entryId = result.id;

                    monitorFileUploadStatus(entryId,requestIdentifier).then(function(result)
                    {
                        // this function verify that the request was not aborted.
                        if (requestIdentifier !== processUploadRequestCounter)
                        {
                            klog.info('kSlidesUploader.processUpload(): this request with identifier {0} was aborted', requestIdentifier);
                            return $q.reject({error: {token: 'abort'}});
                        }

                        return result;
                    }).then(function () {
                        klog.info('kSlidesUploader.processUpload(): document with id {0} is ready (request identifier {1})', entryId, requestIdentifier);
                        deferred.resolve({entryId: entryId});
                    },function(reason)
                    {
                        klog.error("kSlidesUploader.processUpload(): error while processing document entry from upload file with error {0}", JSON.stringify(reason));
                        deferred.reject(reason);
                    });

                }).catch(function (reason) {
                    klog.error("kSlidesUploader.processUpload(): failed to create document entry from upload file with error {0}", JSON.stringify(reason));
                    deferred.reject(reason);
                });
            })(name,token, processUploadRequestCounter);

            return deferred.promise;
        }

        var convertSize = function(size){
            var val = '';

            //  display gigbytes
            if(Math.floor(size / 1024 / 1024 / 1024) >= 1) {
                val += Math.round(size / 1024 / 1024 / 1024 * 100)/100 + 'Gb';
            }
            // display megabytes if file size more than 5mb
            // otherwise display kilobytes
            else if(Math.floor(size / 1024 / 1024) >= 5) {
                val += Math.round(size / 1024 / 1024 * 100)/100 + 'Mb';
            }
            else {
                val += Math.round(size / 1024, 2) + 'Kb';
            }
            return val;
        };

        function removeSlides()
        {
            return slidesService.removeSlides();
        }

        function replaceSlides(slidesEntryId)
        {
            return slidesService.replaceSlides(slidesEntryId);
        }



        this.removeSlides = removeSlides;
        this.abortProcessUpload = abortProcessUpload;
        this.replaceSlides = replaceSlides;
        this.convertSize = convertSize;
        this.processUpload = processUpload;

    }


    angular.module('k-deck-slides').service('kSlidesUploader',service);


})();

