'use strict';

kQna.factory('qnaService',
    function (kalturaService, kalturaAPI,$http,$timeout,metaDataGenerator,QnaEntry,QnaThreadsManager,THREAD_STATE,
              QandA_cuePointEnums,QandA_ResponseProfile,QandA_ResponseProfileSystemName,QandA_MetadataProfileSystemName,
              qnaExport,qnaSuspendFetching,kProducerActions,kEntryManagementService,qnaEventNotificationTemplates) {

        var qnaThreadManager = new QnaThreadsManager();

        var isLoading = true;

        //Public API.
        var _liveData = {

            states : THREAD_STATE,

            incomingCounters: qnaThreadManager._counters[THREAD_STATE.PENDING],

            qnaThreadsList : qnaThreadManager.getThreadList(),

            displayList : qnaThreadManager.displayEntries,

            counters: qnaThreadManager.getCounters(),

            clearNewMessagesCounter: qnaThreadManager.clearNewMessagesCounter,

            liveDataIsReady : false,

            //TODO: apply changes to these values if needed or remove.
            qnaEnabled : configuration.qnaEnabled,

            announcementsOnly : false,

            answerOnAirEntry : null
        };


        var metadataProfileId = null;

        var onIncomingCallbacks = [];
        
        var _qnaModuleSettings = { qnaEnabled: true, announcementOnly:false, time: new Date()};
        
        var bootPromise = null;

        var customMetadataProfile = null;


        //Represent cue point creation time.This variable holds the last known time stamp where we received cue points for.
        var nextRequestStartTime = null;

        var lastUpdateTimeInComputerClock=new Date();

        var pollingGracePeriod = 0;//how much time to query backward from last CP
        var maxPollingGracePeriod = 20;

        kEntryManagementService.addPushServerNotificationsToClearOnArchive(qnaEventNotificationTemplates);

        /**
         *  register to QnA producer actions
         */
        function init() {

            kProducerActions.isReady().then(function() {

                // listen to changes from server and act accordingly
                kProducerActions.subscribeStateChange(function () {

                    onProducerStateChanged(false);
                });

                // run sync from server once to sync producer state (if server doesn't contain any relevant cuepoint)
                onProducerStateChanged(true);
            });
        }

        /**
         * check if producer app is updated with the most relevant QnA settings
         */
        function onProducerStateChanged(forceRefreshState) {

            var currentTime = new Date();
            var qnaSettings = kProducerActions.getState('actions.player-qna-settings-update.qnaSettings');

            ///todo: this is temp fix until push server will preserve state messages for longer than 24h
            var refreshStateThesholdInHours = 12;

            if (qnaSettings) {

                //since push server saves notification for only a day we might need to re-add cuepoint
                forceRefreshState = !qnaSettings.time  ||
                    (forceRefreshState && (new Date()-new Date(qnaSettings.time) )>refreshStateThesholdInHours*60*60*1000);
                if (qnaSettings !== _qnaModuleSettings) {
                    klog.info('syncing state from server. "qnaSettings" = "{0}" (previous value "{1}")',
                        qnaSettings, _qnaModuleSettings);

                    //update current qna settings object
                    _qnaModuleSettings.qnaEnabled = qnaSettings.qnaEnabled;
                    _qnaModuleSettings.announcementOnly = qnaSettings.announcementOnly;
                }
            }
            if (forceRefreshState || !qnaSettings)
            {
                //initial state - just started broadcasting and no settings cue points were found - set app defaults
                kProducerActions.setStateOrientedAction('player-qna-settings-update',
                    {
                        qnaSettings: {
                            qnaEnabled: _qnaModuleSettings.qnaEnabled,
                            announcementOnly: _qnaModuleSettings.announcementOnly,
                            time: currentTime
                        }
                    });
            }

        }

        function updateListeners(){

            for(var i=0;i<onIncomingCallbacks.length;i++){

                var onIncomingCallBack = onIncomingCallbacks[i];

                onIncomingCallBack(_liveData.counters[THREAD_STATE.PENDING].total);

            }
        }

        function cuePointsDebugPrint(cuePointsList){

            var cuePointCreationTime = null;

            var curPointUpdateTime = null;

            var cuePointxml = null;

            var cuePonitId = null;

            var cuePointMessage = null;

            var cuePoint = null;

            for(var i=0;i<cuePointsList.length;i++){

                cuePoint = cuePointsList[i];

                if(cuePoint.relatedObjects !== undefined){

                    klog.warn("cuePointid:"+cuePoint.id+",text:"+cuePoint.text+",createdAt:"+cuePoint.createdAt+",updatedAt:"+cuePoint.updatedAt+",xml:"+cuePoint.relatedObjects[QandA_ResponseProfile].objects[0].xml);

                }
                else if(cuePoint.metadata !== undefined){

                    klog.warn("cuePointid:"+cuePoint.id+",text:"+cuePoint.text+",createdAt:"+cuePoint.createdAt+",updatedAt:"+cuePoint.updatedAt+",metadata:"+cuePoint.metadata);

                }
                else{
                    klog.warn("Problem with cuepoint.missing metadata and related object.cuepoint with id:"+cuePoint.id);
                }

            }
        }

        function getItems(startTime,endTime) {

            return kalturaAPI.login().then(function () {

                return kalturaAPI.qnaService.list(configuration.MediaEntryId, QandA_ResponseProfileSystemName, startTime, endTime).then(function (res) {

                    if (qnaSuspendFetching.isSuspended()) {
                        klog.info("Q&A: Suspending fetching new questions/updated (pending requests) for next {0} ms",configuration.qnaPollingInterval);
                        return Promise.reject("Suspended");
                    }

                    var retVal = [];

                    var latestUpdateTime = 0;

                    var cuePoint = null;

                    res.objects.sort(function(a,b){

                        return a.updatedAt - b.updatedAt;

                    });

                    for(var i = 0;i < res.objects.length;i++){

                        cuePoint = res.objects[i];

                        if (!(cuePoint.relatedObjects && cuePoint.relatedObjects[QandA_ResponseProfile] && cuePoint.relatedObjects[QandA_ResponseProfile].objects &&
                            cuePoint.relatedObjects[QandA_ResponseProfile].objects.length>0 && cuePoint.relatedObjects[QandA_ResponseProfile].objects[0].xml !== undefined)){

                            klog.warn("Skipping the following cuePoint in this request due to invalid values:");

                            cuePointsDebugPrint([cuePoint]);

                            continue;

                        }

                        var qnaEntry = new QnaEntry(cuePoint);

                        if (qnaEntry.getUpdateTime() > latestUpdateTime){

                            latestUpdateTime = qnaEntry.getUpdateTime();
                        }


                        retVal.push(qnaEntry);

                    }

                    isLoading = false;

                    return Promise.resolve({data:retVal,lastUpdateTime:latestUpdateTime});
                });
            });
        }



        //Call the KALTURA service to get new Qna values.
        function updateValues(isFirstFetch) {


            function requestAgain(){

                $timeout(function(){

                    updateValues();

                },configuration.qnaPollingInterval);
            }

            if (qnaSuspendFetching.isSuspended()) {
                klog.info("Q&A: Suspending fetching new questions/updated (pending requests) for next {0} ms",configuration.qnaPollingInterval);
                requestAgain();
                return;
            }

            try {

                getItems(Math.max(0,nextRequestStartTime-pollingGracePeriod)).then(function(result){

                    var newItems = result.data;

                    var latestUpdateTime = result.lastUpdateTime;

                    try{
                        //new items is a list of QnaEntry objects.
                        if (newItems && newItems.length>0) {

                            qnaThreadManager.appendQnaData(newItems, isFirstFetch);

                            if (_liveData.answerOnAirEntry) {
                                newItems.forEach(function(answer) {
                                    if (answer.getId()===_liveData.answerOnAirEntry.getId() &&
                                       answer.getEndTime()>0) {
                                        _liveData.answerOnAirEntry = null;
                                    }
                                });
                            }

                            updateListeners();
                        }

                        if(latestUpdateTime !== 0){

                            if (nextRequestStartTime!==latestUpdateTime) {
                                lastUpdateTimeInComputerClock = new Date();
                            }
                            nextRequestStartTime = latestUpdateTime;
                        }

                        pollingGracePeriod = Math.min(maxPollingGracePeriod,Math.max(0, maxPollingGracePeriod  + ( lastUpdateTimeInComputerClock - new Date()   ) / 1000  ));
                        _liveData.liveDataIsReady = true;

                        requestAgain();
                    }
                    catch(err){
                        klog.error("Error while trying to append qna updates. "+err);
                        requestAgain();
                    }
                },function(updateErr){
                    klog.error("Failed updating Qna Messages, retrying. {0}", updateErr);
                    requestAgain();
                });
            }

            catch(err) {

                klog.error("Error while trying to request qna updates.");
                requestAgain();

            }
        }

        var _exportQnaData = function(){

            qnaExport.exportQnaDataToFile(_liveData.qnaThreadsList);

        };


        var _getLiveData = function(){

            return _liveData.qnaThreadsList;

        };


        var bootAPI = function() {
            if (bootPromise) {
                return bootPromise;
            }
            bootPromise= kalturaAPI.login().then(function() {
                kalturaAPI.startMultirequest();

                kalturaAPI.metadataProfileService.list(QandA_MetadataProfileSystemName);

                kalturaAPI.responseProfileService.list(QandA_ResponseProfileSystemName);

                kalturaAPI.execMultirequest().then( function(results) {

                    customMetadataProfile = results[0].objects[0];

                    metadataProfileId = customMetadataProfile.id;
                    if (results[1].objects.length===0) {
                        kalturaAPI.responseProfileService.add(QandA_ResponseProfileSystemName, QandA_ResponseProfile).then(function (r) {
                            klog.warn("response profile  was created " + JSON.stringify(r));

                        });
                    }
                });
            });

            return bootPromise;
        };

        bootAPI().then(function(data){

            init();

            //Start a "thread" which polls for qna data.
            updateValues(true);

        },function(err){

            //Boot failed we probably have an internal server error, need to handle this case.
            klog.error("qna service failed to boot.");

        });

        function createItem(msgContent,type,parent,state,timeStamp,searchableOnEntry, tagsToAdd){

            klog.info("Q&A: Creating item content='{0}' type='{1}' state='{2}' ",msgContent,type,state);
            var xml;

            var metaDataObj = null;

            if(parent) {

                metaDataObj = {State:state, ThreadId:parent.getThreadId(), Type:type, ThreadCreatorId:parent.getThreadCreatorId()};

            } else{

                metaDataObj = {State:state, Type:type};

            }

            xml = metaDataGenerator.createXmlFromObj(metaDataObj);

            return bootAPI().then(function (){

                qnaSuspendFetching.suspend();
                kalturaAPI.startMultirequest();

                kalturaAPI.qnaService.addAnnotationCuePoint(configuration.MediaEntryId, msgContent,(parent !== null)?parent.id:undefined,timeStamp,searchableOnEntry);
                //take the id from the above response and add the annotation metadata
                kalturaAPI.metadataService.add("{1:result:id}",customMetadataProfile.id,"annotationMetadata.Annotation",xml);

                kalturaAPI.qnaService.updateAnnotationCuePointTag("{1:result:id}", tagsToAdd);

                return kalturaAPI.execMultirequest().then(function(res){

                    qnaSuspendFetching.resume();
                    klog.info("Successful item creation.");

                    return res;

                },function(err){
                    qnaSuspendFetching.resume();
                    klog.error("Error metadata.");

                });
            });
        }

        function updateItem(qnaEntry,time){

            var srcMetadata = qnaEntry.getMetaData();

            var targetMetaData = metaDataGenerator.merge(srcMetadata,{State:THREAD_STATE.DONE});

            var xml = metaDataGenerator.createXmlFromObj(targetMetaData);

            return bootAPI().then(function (){

                qnaSuspendFetching.suspend();

                kalturaAPI.startMultirequest();

                kalturaAPI.metadataService.update(qnaEntry.getMetaDataId(),xml);

                kalturaAPI.qnaService.updateAnnotationCuePoint(qnaEntry.getId(),time);

                return kalturaAPI.execMultirequest().then(function(res){

                    qnaSuspendFetching.resume();
                    klog.info("Successful item creation.");
                    return res;

                },function(err){

                    qnaSuspendFetching.resume();
                    klog.error("Error metadata.");

                });
            });

        }



        function _enableQna(){
            kProducerActions.setStateOrientedAction('player-qna-settings-update',
                {qnaSettings : { qnaEnabled: true, announcementOnly:false}});
        }

        function _disableQna(){
            kProducerActions.setStateOrientedAction('player-qna-settings-update',
                {qnaSettings : { qnaEnabled: false, announcementOnly:false}});
        }

        function _enableAnnouncementsOnly(){
            kProducerActions.setStateOrientedAction('player-qna-settings-update',
                {qnaSettings : { qnaEnabled: true, announcementOnly:true}});
        }

        function _disableAnnouncementsOnly(){
            _enableQna();
        }

        function _answerOnAir(msgContent,time){

            return new Promise(function(sucsess, failure){

                function itemPromise(){
                    createItem(msgContent,"AnswerOnAir",null,THREAD_STATE.PENDING,time, true).then(function(res){


                        _liveData.answerOnAirEntry = new QnaEntry(res[0],res[1]);

                        sucsess(res);

                    },function(err){

                        failure(err);

                    });
                }

                if(_liveData.answerOnAirEntry   ){

                    _endAnswerOnAir(time - 1).then(function(res){

                        itemPromise();

                    },function(err) {

                        failure(err);

                    });

                    failure("Answer On Air is already running.");

                }

                itemPromise();

            });
        }
        
        function _endAnswerOnAir(time){

            return new Promise(function(sucsess, failure){

                if(!_liveData.answerOnAirEntry){

                    failure("Answer On Air is not running.");

                }

                updateItem(_liveData.answerOnAirEntry,time).then(function(res){

                    _liveData.answerOnAirEntry = null;

                    sucsess(res);

                },function(err){

                    failure(err);

                });

            });

        }

            
        return {

            disableQna : function(){
                return _disableQna();
            },
    
            enableQna : function(){
                return _enableQna();
            },
    
            enableAnnouncementsOnly: function(){
                return _enableAnnouncementsOnly();
            },
            disableAnnouncementsOnly: function(){
                return _disableAnnouncementsOnly();
            },
            
            makeAnnouncement: function (msgContent) {
                //msgContent,type,parent,state
                return createItem(msgContent,"Announcement",null,THREAD_STATE.PENDING);
    
            },

            startAnswerOnAir :function(msgContent,time) {

                return _answerOnAir(msgContent,time);

            },

            endAnswerOnAir : function(time){

                return _endAnswerOnAir(time);

            },
    
            askQuestion: function (msgContent,assignees) {
    
                return createItem(msgContent,"Question",null,THREAD_STATE.PENDING);
    
            },
    
            setQnaThreadAsDone : function(qnaThread){
    
                qnaThreadManager.setThreadAsDone(qnaThread);
    
                updateListeners();
    
            },
    
            setQnaThreadAsFollow : function(qnaThread){
    
                qnaThreadManager.setThreadAsFollowed(qnaThread);
    
                updateListeners();
    
            },
    
            setQnaThreadAsCleared : function(qnaThread){
    
                qnaThreadManager.setThreadAsCleared(qnaThread);
    
                updateListeners();
    
            },

            deleteAnnouncementThread : function(qnaThread){
                qnaThreadManager.deleteAnnouncementThread(qnaThread);
                updateListeners();
            },
    
            registerOnIncomingCallback : function(callbackFunc){
    
                onIncomingCallbacks.push(callbackFunc);
    
            },
    
            makeReply: function(entryBeingReplied,msgContent, tagsToAdd) {
                tagsToAdd = (tagsToAdd !== undefined) ? tagsToAdd : null;
    
                return createItem(msgContent,"Answer",entryBeingReplied,THREAD_STATE.ANSWERED, null, null, tagsToAdd);
    
            },

            isAnsweringOnAir : function(){
                return _liveData.answerOnAirEntry!==null;
            },

            isAnotherEntryBeingAnswered : function() {
                return qnaThreadManager.activeOnAirAnswer().filter(function(item) {
                    if (_liveData.answerOnAirEntry === null ||
                        item.getId() !== _liveData.answerOnAirEntry.getId()) {
                        return true;
                    }
                    return false;
                }).length>0;
            },

            endAllServerAnswerOnAir:function(time) {
                qnaThreadManager.activeOnAirAnswer().forEach(function(item) {
                    updateItem(item,time);
                });
            },

            exportQnaDataToFile : _exportQnaData,
    
            liveData : _liveData,
    
            getLiveData : _getLiveData,
    
            getQnaModuleState: _qnaModuleSettings,
    
            isLoading: function() { return isLoading; },
    
            getRawCuePoints: function () {
    
                return kalturaAPI.qnaService.list(configuration.MediaEntryId,this.QandA_ResponseProfileSystemName);
    
            },
    
            QandA_ResponseProfileSystemName: QandA_ResponseProfileSystemName,
    
            QandA_ResponseProfile: QandA_ResponseProfile,

            QandA_MetadataProfileSystemName: QandA_MetadataProfileSystemName
        };
});

