/* globals _ */
/* globals klog */
/* globals PromisesQueue */
/* globals Promise */

(function() {
    'use strict';

    function Provider() {

        var ArchiveTagsExcludeList = [];
        var KwpUsedTags = [];


        /**************************
         Service definition
         ***************************/
        function CuePointsService($timeout, kalturaAPI) {

            var producerFetchListTag = 'webcast:fetch-list';

            var self = this;
            var cuePointsList = [];
            var unHandledCuePoints = [];
            var lastFetchedCuePointUpdatedAt = 0;
            var ready = null;
            var onReady = null;
            var notifiers = [];
            var promisesQueue = new PromisesQueue(function (actionNameAndIdMap, actionName) {
                if (actionName === producerFetchListTag && Object.keys(actionNameAndIdMap).length !== 0) {
                    klog.info("cuepoint list request was cancelled, since there are other add/update command");
                    return true;
                }
                return false;
            });

            function suspendCuePointFetching(operationFunction, operationType) {
                return promisesQueue.add(operationFunction, 'external-' + operationType);
            }

            function init() {
                ready = new Promise(function (success, reject) {
                    onReady = success;
                });

                fetchCuePoints();
            }

            function handleArchiveCuePoints(cuePoint) {
                var tags = cuePoint.tags ?  cuePoint.tags.split(/[\s,]+/) : [];
                if (_.intersection(ArchiveTagsExcludeList,tags).length==0) {
                    var unHandledCuePointFound = false;
                    for (var j = 0; j < unHandledCuePoints.length; j++) {
                        if (unHandledCuePoints[j].id === cuePoint.id) {
                            if (cuePoint.status === kalturaAPI.cuePointService.CUE_POINT_STATUS.HANDLED) {
                                unHandledCuePoints.splice(j, 1);
                            }
                            unHandledCuePointFound = true;
                            break;
                        }
                    }
                    if (!unHandledCuePointFound && cuePoint.status !== kalturaAPI.cuePointService.CUE_POINT_STATUS.HANDLED) {
                        unHandledCuePoints.push(cuePoint);
                    }
                }

            }

            /**
             * enriching the current cuepoint with its' action state - 'added' or 'updated'
             * @param cuePoint
             */
            function enrichCuePointWithActionState(cuePoint) {

                var existingCuePointIndex = _.findIndex(cuePointsList, {id : cuePoint.id});
                var existingCuePoint = existingCuePointIndex === -1 ? null : cuePointsList[existingCuePointIndex];

                var handled = false;

                if (!existingCuePoint) {
                    klog.debug("CuePointsService.enrichCuePointWithActionState(): adding cuepoint with id '{0}'", cuePoint.id);
                    cuePointsList.push(cuePoint);
                    notify(cuePoint, 'cuepoint-added');
                    cuePoint.actionState = 'added';
                    handled = true;
                }else if (existingCuePoint.updatedAt !== cuePoint.updatedAt)
                {
                    klog.debug("CuePointsService.enrichCuePointWithActionState(): updating cuepoint with id '{0}'", cuePoint.id);
                    cuePointsList.splice(existingCuePointIndex,1);
                    cuePointsList.push(cuePoint);
                    notify(cuePoint, 'cuepoint-updated');
                    cuePoint.actionState = 'updated';
                    handled = true;
                }

                return handled;
            }

            function fixCuePointDates(cuePoint) {
                cuePoint.originalCreatedAt = cuePoint.createdAt;
                cuePoint.originalUpdatedAt = cuePoint.updatedAt;
                cuePoint.createdAt = cuePoint.createdAt * 1000;
                cuePoint.updatedAt = cuePoint.updatedAt * 1000;
            }

            function fetchCuePoints() {

                var scheduleNext = function () {
                    $timeout(function () {
                        fetchCuePoints();
                    }, 1000);
                };

                try {
                    promisesQueue.add(function () {
                        var tags = KwpUsedTags.join();
                        return kalturaAPI.cuePointService.list(configuration.MediaEntryId, "codeCuePoint.Code,thumbCuePoint.Thumb,annotation.Annotation", lastFetchedCuePointUpdatedAt, tags).then(function (res) {
                            try {

                                if (promisesQueue.getNumberOfPendingActions() - 1) // reduce 1 which is current iteration
                                {
                                    klog.info("CuePointsService.fetchCuePoints(): has other pending requests, ignoring list request results");

                                    return Promise.resolve();
                                }

                                if (res.objects.length > 0) {

                                    var updatedCuePoints = [];
                                    res.objects.forEach(function (cuePoint) {

                                        fixCuePointDates(cuePoint);

                                        if(enrichCuePointWithActionState(cuePoint)) {
                                            handleArchiveCuePoints(cuePoint);
                                            updatedCuePoints.push(cuePoint);
                                            lastFetchedCuePointUpdatedAt = cuePoint.originalUpdatedAt;
                                        }
                                    });

                                    if (updatedCuePoints.length) {
                                        notifyNewCuePointsFetched(updatedCuePoints);
                                    }
                                }

                                if (onReady !== null) {
                                    onReady();
                                    onReady = null;
                                }

                            }
                            catch (e) {
                                klog.error(e);

                            }
                            return Promise.resolve(res);
                        });
                    }, producerFetchListTag).then(function () {
                        scheduleNext();
                    }, function () {
                        scheduleNext();
                    });

                }
                catch (e) {
                    klog.error(e);
                    scheduleNext();


                }
            }

            function notifyNewCuePointsFetched(cuePoints) {

                if (!_.isArray(cuePoints)) {
                    cuePoints = [cuePoints];
                }

                notifiers.forEach(function (notifier) {
                    try {
                        if (notifier.event === 'new-cuepoints-fetched') {
                            notifier.fn(cuePoints);
                        }
                    } catch (e) {
                        klog.error(e);
                    }
                });
            }

            function notify(cuePoint, event) {
                notifiers.forEach(function (notifier) {
                    try {
                        if (notifier.event === event) {
                            notifier.fn(cuePoint);
                        }
                    } catch (e) {
                        klog.error(e);
                    }
                });
            }


            function on(fn, event) {
                notifiers.push({fn: fn, event : event});
            }

            function hasUnHandledCuePoints() {
                return (unHandledCuePoints.length > 0);
            }

            // delete all cue points from adapter type(s)
            function handleResetEntryDeleteCuePoint(mediaEntryId) {
                var cuePointTypes = [];

                return kalturaAPI.cuePointService.list(mediaEntryId, cuePointTypes.join()).then(function (result) {
                    var cuePoints=result.objects;

                    // put all the cue point IDs in cuePointHash
                    var cuePointHash = {};
                    _.each(cuePoints, function(cp){
                        cuePointHash[cp.id] = 1;
                    });

                    cuePoints = _.filter(cuePoints, function(cp){
                        var excludeTagIndex =
                            _.findIndex(ArchiveTagsExcludeList,
                                function (tagToExclude)
                                {
                                    //tags to exclude strings are a subset of the cue points tags
                                    //f.e: 'poll-data' is a tag to exclude, in the cue point the tags field will look
                                    //'poll-data,pollId:<xxxxxxx>'
                                    return (cp.tags).indexOf(tagToExclude) > -1;
                                }
                            );
                        if (excludeTagIndex < 0){
                            // if this cue point has a parent and we want to delete it too, don't delete this cue point since it will be auto deleted by the server.
                            if (cp.parentId && cuePointHash[cp.parentId] === 1) {
                                return false;
                            }
                            return true;
                        }
                        return false;
                    });

                    var promises = [];

                    klog.info("found " + cuePoints.length + " cue points to delete ");
                    var chunks = _.chunk(cuePoints, 20);
                    _.each(chunks,function(chunk){
                        kalturaAPI.startMultirequest();
                        _.each(chunk, function(cp){
                            kalturaAPI.cuePointService.delete(cp.id);
                        });
                        promises.push(kalturaAPI.execMultirequest());
                    });

                    return Promise.all(promises);

                }, function(err){
                    klog.error("failed to delete cue points " + err);
                });
            }

            function getAllCuePoints()
            {
                return cuePointsList;
            }

            function isReady()
            {
                return ready;
            }

            self.hasUnHandledCuePoints = hasUnHandledCuePoints;
            self.handleResetEntryDeleteCuePoint = handleResetEntryDeleteCuePoint;
            self.getAllCuepoints = getAllCuePoints;
            self.isReady = isReady;
            self.suspendCuePointFetching = suspendCuePointFetching;
            self.on = on;

            init();

        }

        /*************************
         Provider functions
         ************************/

        function addTagToArchiveExcludeList(tag) {

            ArchiveTagsExcludeList.push(tag);
        }

        function addKwpUsedTags(tag) {

            KwpUsedTags.push(tag);
        }

        this.addTagToArchiveExcludeList = addTagToArchiveExcludeList;
        this.addKwpUsedTags = addKwpUsedTags;

        this.$get = function ($injector) {

            return $injector.instantiate(CuePointsService);
        };

    }

    angular.module('kAPI').provider('cuePointsService', Provider);

})();