'use strict';
monitoringModule.directive('monitoring', function (kalturaAPI,$timeout,asyncLiveEntryInfoService,kCef) {

	return {
		restrict:'E',
		templateUrl: "../common/monitoring/directives/monitoring.html",
        scope: {
            visible: '='
        },
		link:function (scope,element,attrs) {

		    scope.applicationReady = false;

            window.webcast = window.webcast || {};
            window.webcast.vars = window.webcast.vars || {};
            window.webcast.vars.liveDashboard = {
                lang: 'en-US',
                mode: 'webcast',
                service_url:kserviceInfo.serverAddress,
                entryId: configuration.MediaEntryId,
                player: {}
            };

            scope.$watch(function() {
                return scope.visible;
            },function() {
                if (scope.visible) {
                    postMessageToDashboard({ type: 'playerAction', content: 'play' }, '*');
                } else {
                    postMessageToDashboard({ type: 'playerAction', content: 'pause' }, '*');
                }
            });

            kalturaAPI.login()
                .then(function(result) {
                    window.webcast.vars.liveDashboard['ks'] = result;
                    window.webcast.vars.liveDashboard.player['autoPlay'] = false;
                    scope.iFrameSource = './liveDashboard/v' + window.packageJSONFile.liveDashboardVersion + '/index.html';
                    scope.applicationReady = true;
                });

            if (window.addEventListener) {
                window.addEventListener('message', function(message) {
                    if (message.data.type) {
                        switch (message.data.type) {
                            case 'onUrlOpen':
                                kCef.openExternal(message.data.content);
                                break;
                        }
                    }
                }, false);
            }

            function postMessageToDashboard(message) {
                var receiver = document.getElementById('liveDashboardIframe');
                if (receiver && receiver.contentWindow) {
                    receiver.contentWindow.postMessage(message, '*');
                }
            }

            function onLiveEntryChange(res) {
                postMessageToDashboard({ type: 'onLiveEntryChange', content: res });
            }

            asyncLiveEntryInfoService.registerOnEntryInfoChange(onLiveEntryChange);
        }
	};

});