kUI.filter('kdate', function() {
    return function(kdate) {
        return new Date(kdate);
    };
});

kUI.filter('kdateMs', function() {
    return function(kdate) {
        return new Date(kdate);
    };
});

kUI.filter('msToDate', function() {
    return function(relTimeInMs,fullDate,withMs,withOriginalValue) {

        var negetive = false;

        if(relTimeInMs < 0){
            relTimeInMs = -relTimeInMs;
            negetive = true;
        }

        var value=relTimeInMs;

        var hours = Math.floor(relTimeInMs / (1000 * 60 * 60));
        relTimeInMs -= hours * (1000 * 60 * 60);

        var mins = Math.floor(relTimeInMs / (1000 * 60));
        relTimeInMs -= mins * (1000 * 60);

        var seconds = Math.floor(relTimeInMs / (1000));
        relTimeInMs -= seconds * (1000);

        if (fullDate==undefined ||
            fullDate==false) {
            fullDate = (hours != 0);
        }

        var retVal;
        if (fullDate)
            retVal=hours.pad(2) + ":" + mins.pad(2) + ":" + seconds.pad(2);
        else
            retVal=mins.pad(2) + ":" + seconds.pad(2);

        if (withMs!=undefined &&
            withMs){
            retVal+="."+relTimeInMs.pad(3);
        }
        if (withOriginalValue!=undefined &&
            withOriginalValue){
            retVal+=" ("+value+")";
        }
        if(negetive){
            return "-"+retVal;
        }
        return retVal;
    };
});
