/* globals _ /
 / globals klog /
 / globals PromisesQueue /
 / globals Promise */

(function (){
    "use strict";

    function Directive(kPollsService, $rootScope)
    {

        function Controller($scope)
        {
            /**
             * poll is editable if it is in a preliminary state
             * @param pollId
             * @returns {boolean}
             */
            $scope.isPollEditable = function ()
            {
                var pollState = kPollsService.getPollState($scope.pollAsset.id);
                return pollState.status === 'preliminary';
            };

            /**
             * a complete poll is a poll with a Question and the first two answers.
             * also, a complete and valid poll have only consecutive answers
             * (if answers num. 1,2,4 are set - the poll is invalid)
             * @return boolean if the poll is complete
             */
            $scope.isPollComplete = function () {
                return kPollsService.isPollComplete($scope.pollAsset);
            };

            $scope.getCurrentAddedPoll = function ()
            {
                return kPollsService.getLocallyCreatedPollId();
            };

            $scope.openPollWizard = function ()
            {
                $('.open-poll-wizard').blur();
                $rootScope.$broadcast("openPollWizard", {pollId: $scope.pollAsset.id});
            };

        }

        function Link(scope, element, attrs, ctrls)
        {

        }

        return {
            restrict: 'E',
            templateUrl: '../common/kAssetsTimeline/directives/k-poll-asset.html',
            scope: {'pollAsset':'=', 'isCentered':'='},
            link: Link,
            controller: Controller,
            controllerAs: 'vm'
        };

    }


    angular.module('k-assets-timeline').directive('kPollAsset', Directive);


})();
