function parseConfigurationDate() {
    try {
        if (configuration.fromdate) {
            configuration.fromdate = new Date(configuration.fromdate);
        }
        if (configuration.todate) {
            configuration.todate = new Date(configuration.todate);
        }
        if (kserviceInfo.ks_expiry) {
            kserviceInfo.ks_expiry = new Date(kserviceInfo.ks_expiry);
        }



    } catch(e) {
        klog.warn("Cannot convert date object! "+e);
    }
}

var loadApplicationParams = function (params) {
    try {
        //params="ks=djJ8MTA3fLmuFhemNueq6tOy4Ph_94gr8JlyrW8qN7J_hQQcPNKbFN1ASusFly3NnRwCpX894pHKFtjzriioSUjfyPg0CjXnR9o33aadvkbYtBk6ktczk4T3FuhGMlC4qBa7ZWVvta1MT45GhQqd05-aBOdrLoc5yTXAbLxLwRz2StBQg7OBIDNIooHlqsRxwPJIyPpBSJ546z1v0fAE7AO5jGh6dG8%3D&MediaEntryId=0_d2jdjhuq&uiConfID=23448317&serverAddress=http%3A%2F%2Fil-webc-server.dev.kaltura.com&eventsMetadataProfileId=662&kwebcastMetadataProfileId=663&appName=&logoUrl=&documentEntryId=0_5iq05u19&doVideoCapturing=false&app_version=1.0.6.0&app_data_path=C:/Users/asafrobinovich/AppData/Local/Kaltura&app_path=C:/dev/Capture/Release";
        //  params="&app_version=1.0.6.0&app_data_path=C:/Users/guyjacubovski/AppData/Local/Kaltura&app_path=C:/dev/capture/Release&debuggingMode=true";

        klog.info("command line params={0}", params);
        var appparams = $.deparam(params);

        if (appparams.encodedData) {
            //convret from base64 to string
            var decodedData=window.atob(appparams.encodedData);

            //de-param and add to the appaparams object
            appparams = $.extend(appparams,$.deparam(decodedData));

            delete appparams.encodedData;
        }

        //make all properties lowercase
        for(var key in appparams ) {

            // fix "true" / "false" not being true/false
            if (typeof appparams[key] === 'string' )
            {
                if (appparams[key].toLowerCase() === "true"){
                    appparams[key] = true;
                }
                else if (appparams[key].toLowerCase() === "false"){
                    appparams[key] = false;
                }
            }

            if (key.toLowerCase()===key) {
                continue;
            }

            appparams[key.toLowerCase()] = appparams[key];
            delete appparams[key];
        }
        klog.info("Running version={0} appparams={1}",kUpgradeServiceInfo.app_version, JSON.stringify(appparams, null, "\t"));

        if (!window.electron) {
            configuration.debuggingMode = true;
            configuration.checkForUpdates = false;
        }

        var appparamsused={};

        var applyConfig = function (configName, skipEmptyValues)  {
            var config=eval(configName);
            for(var key in config)
            {
                var newKey=key.toLowerCase();
                if (appparams.hasOwnProperty(newKey)) {
                    appparamsused[newKey]=true;
                    if (typeof config[key] ==='function') {
                        continue;
                    }

                    if (skipEmptyValues && !appparams[newKey]) {
                        klog.info("Skipped {0} since it's empty", key);
                        continue;
                    }

                    klog.info("Applied new {0}.{1}={2}", configName,key, appparams[newKey]);
                    config[key] = appparams[newKey];
                }
            }
        }

        applyConfig("kserviceInfo");
        applyConfig("configuration");
        applyConfig("whiteLabelingConfiguration",true);
        applyConfig("kUpgradeServiceInfo");

        var appparamsnotused=[];
        for (var key in appparams)
        {
            if (!appparamsused.hasOwnProperty(key)) {
                appparamsnotused.push(key);
            }
        }

        parseConfigurationDate();

        appparamsnotused.forEach( function(key) {
            klog.warn("Got config key not in used {0}='{1}'", key,appparams[key]);
        });

        if (appparams.logThreshold) {

            for (var prop in klog.logLevel) {
                if (appparams.logThreshold === klog.logLevel[prop].value){
                    klog.info("Setting logLevel to {0}", configuration.logThreshold.name);
                    configuration.logThreshold = klog.logLevel[prop];
                }
            }
        }


        if(kserviceInfo.serverAddress){
            while(kserviceInfo.serverAddress.length > 0 && kserviceInfo.serverAddress.substring(kserviceInfo.serverAddress.length - 1,kserviceInfo.serverAddress.length) === "/"){
                kserviceInfo.serverAddress = kserviceInfo.serverAddress.substring(0,kserviceInfo.serverAddress.length - 1);
            }
        }

        if (configuration.qnaModeratorMode) {
            configuration.showSlides = false;
            configuration.enabledAnalytics = false;
            configuration.showKwpPanelHeader = false;
            configuration.showEntryManagment = false;
            configuration.sendOnAirOnlyProducerActions = false;
            configuration.participantsPanel = false;

        }else if(configuration.liveModerator){
            configuration.showSlides = false;
            configuration.enabledAnalytics = false;
            configuration.showKwpPanelHeader = false;
            configuration.showEntryManagment = false;
            configuration.qnaModeratorMode = false;
            configuration.QnAEnabled = false;
            configuration.sendOnAirOnlyProducerActions = false;
        }
        else {
            configuration.showSlides = true;
            configuration.showKwpPanelHeader = true;
            configuration.enabledAnalytics = true;
            configuration.showEntryManagment = true;

        }

        document.title  = whiteLabelingConfiguration.appName;

    }
    catch(e) {
        alert(e);
    }
};
function loadLocalConfiguration() {
    if (window.localConfigurationOverride) {
        var params=$.param(window.localConfigurationOverride);
        klog.warn("Loading configuration from localConfiguration File",params)
        loadApplicationParams(params);
    }
}

loadLocalConfiguration();
loadApplicationParams(window.location.search.substring(1));
