/* globals _ /
 / globals klog /
 / globals PromisesQueue /
 / globals Promise */

(function (){
    "use strict";

    function Directive(kProducerActions, kAssetsTimelineService, gettextCatalog, POLL_STATE_STATUS,
                       ASSET_TYPE, POLL_STATE, captureService, kPollsService, kalturaAPI)
    {

        function Controller($scope)
        {
            $scope.setNextPollStatus = function (confirmed)
            {
                //get current poll state
                var pollState = kProducerActions.getState('actions.select-poll-state');

                switch ($scope.pollStatus) {
                    case POLL_STATE_STATUS.PRELIMINARY:
                        startPoll(pollState);
                        break;
                    case POLL_STATE_STATUS.IN_PROGRESS:
                        if(confirmed) {
                            stopPoll(pollState);
                        } else {
                            $scope.showConfirm = true;
                        }
                        break;
                    case POLL_STATE_STATUS.FINISHED:
                        showResults(pollState);
                        break;
                    default:
                        break;
                }
            };

            $scope.confirmStateChange = function ()
            {
                $scope.showConfirm = false;
                $scope.setNextPollStatus(true);
            };

            $scope.denyStateChange = function ()
            {
                $scope.showConfirm = false;
            };

            function startPoll(pollState)
            {
                //update only if poll is in PRELIMINARY state
                //(other producer could have changed its' status already)
                if(pollState && pollState.status === POLL_STATE_STATUS.PRELIMINARY) {
                    kPollsService.updatePollState(
                        getUpdatedPollState(pollState,
                            {
                                'status' : POLL_STATE_STATUS.IN_PROGRESS,
                                'showAnswers' : true,
                                'showTotals' : POLL_STATE.NUMBERS
                            })
                    );
                }
            }

            function stopPoll(pollState)
            {
                //update only if poll is in IN_PROGRESS state
                //(other producer could have changed its' status already)
                if(pollState && pollState.status === POLL_STATE_STATUS.IN_PROGRESS) {
                    //update pollState with a unique identifier of the producer
                    kalturaAPI.login().then(
                        function (ks)
                        {
                            kPollsService.updatePollState(
                                getUpdatedPollState(pollState,
                                    {'status' : POLL_STATE_STATUS.FINISHED, 'ksHash' : getKSHash(ks)})
                            );
                        }
                    );
                }
            }

            function showResults(pollState)
            {
                //update only if poll is in FINISHED state
                //(other producer could have changed its' status already)
                if(pollState && pollState.status === POLL_STATE_STATUS.FINISHED) {
                    kPollsService.updatePollState(
                        getUpdatedPollState(pollState, {'showResults' : POLL_STATE.PERCENTAGES})
                    );
                }
            }

            function getUpdatedPollState(pollState, updatedValues)
            {
                return _.extend(
                    {
                        'pollId': pollState.pollId,
                        'status': pollState.status,
                        'showAnswers': pollState.showAnswers,
                        'showResults': pollState.showResults,
                        'showTotals' : pollState.showTotals
                    },
                    updatedValues);
            }


        }

        function Link(scope, element, attrs, ctrls)
        {
            scope.showResults = false;
            scope.pollStatus = '';
            scope.pollStatusText = '';
            scope.showConfirm = false;
            scope.confirmText = gettextCatalog.getString('Are you sure?');

            $('.toggle-confirm-container').resize(
                function ()
                {
                    var height = $(this).height() + 'px';
                    $('.approve-icon').css({'line-height' : height});
                    $('.deny-icon').css({'line-height' : height});
                    $('.confirm-text').css({'line-height' : height});
                }
            );
            
            scope.isBroadcasting = function ()
            {
                return captureService.isBroadcasting();
            };
            
            scope.$watch(
                function()
                {
                    return kAssetsTimelineService.currentTimelineAsset;
                },
                function()
                {
                    var currentAsset = kAssetsTimelineService.currentTimelineAsset;
                    if(currentAsset && currentAsset.type === ASSET_TYPE.POLL) {
                        updatePollState();
                    }
                }
            );

            // listen to poll state changes from server and act accordingly
            kProducerActions.subscribeStateChange(function()
                {
                    updatePollState();
                }
            );

            function updatePollState()
            {
                var pollState = kProducerActions.getState('actions.select-poll-state');

                if(pollState) {
                    scope.pollStatus = pollState.status;
                    scope.showResults = pollState.showResults;
                    pollStatusTextMapping(scope.pollStatus);
                }
            }

            function pollStatusTextMapping(pollStatus)
            {
                switch (pollStatus) {
                    case POLL_STATE_STATUS.PRELIMINARY:
                        scope.pollStatusText = gettextCatalog.getString('Start Poll');
                        break;
                    case POLL_STATE_STATUS.IN_PROGRESS:
                        scope.pollStatusText = gettextCatalog.getString('Stop Poll');
                        break;
                    case POLL_STATE_STATUS.FINISHED:
                        scope.pollStatusText = gettextCatalog.getString('Share Final Results');
                        break;
                    default:
                        break;
                }
            }


            //initial state
            updatePollState();

        }

        return {
            restrict: 'E',
            templateUrl: '../common/kMainAsset/directives/k-main-poll-toggle.html',
            scope: {},
            link: Link,
            controller: Controller,
            controllerAs: 'vm'
        };

    }

    angular.module('k-main-asset').directive('kMainPollToggle', Directive);


})();