'use strict';

function Settings() {
    var saveToFileInsteadStreaming = false;

    var that = this;

    this.addProperty = function (key, defaultvalue) {
        Object.defineProperty(that, key, {
            get: function () {
                var value = localStorage.getItem(key);
                if (!value) {
                    return defaultvalue;
                }

                return value;
            },
            set: function (value) {
                localStorage.setItem(key, value);
            }
        });
    };


    this.addProperty("saveToFile", false);


    var parseEntrySettings=function(key) {
        var a=key.split('__');

        return { key: key,  entryId: a[2] ,  creationTime: new Date(parseInt(a[3])), settings: null };
    };

    var entriesSettings = [];

    Settings.prototype.getEntrySettings = function (entryId) {

        var existedBefore = false;


        var entryArr=entriesSettings.filter( function (f) {
            return f.entryId===entryId;
        } );

        var entry=null;

        if (entryArr.length>0) {
            entry = entryArr[0];
        }

        var itemKey=null;

        if (!entry) {
            var creationTime=new Date();
            itemKey="__entry__"+entryId+"__"+creationTime.getTime();

            settings.creationTime=creationTime;

            entry={ key: itemKey, entryId: entryId, creationTime: creationTime, settings: settings  };
            entriesSettings.push( entry);
        }
        else {
            existedBefore = true;
            itemKey="__entry__"+entryId+"__"+entry.creationTime.getTime();

            entry.settings = JSON.parse(localStorage.getItem(itemKey));
        }

        entry.settings.save = function () {
            localStorage.setItem(itemKey, JSON.stringify(this));
        };

        if (!existedBefore) {
            entry.settings.save();
        }

        return entry.settings;
    };


    Settings.prototype.setEntrySettings = function (entryId,settings) {
        var entry = this.getEntrySettings(entryId);
        $.extend(entry,settings);
        entry.save();
    };


    var openEntriesSettings=function() {
        var entriesPurgeTimeWindow = 7 * 24 * 60 * 60 * 1000; //7 days
        var entriesPurgeCountWindow = 50;

        for (var index = 0; index < localStorage.length; index++) {
            var key = localStorage.key(index);

            if (key.startsWith("__entry__")) {
                var entry=parseEntrySettings(key);
                entriesSettings.push(entry);
            }
        }

        //sort by time
        entriesSettings.sort(function(a, b){
            return b.creationTime.getTime()-a.creationTime.getTime();
        });


        for (index = 0; index < entriesSettings.length; index++) {
            var entry = entriesSettings[index];
            //7 days old get deleted
            if (index>=entriesPurgeCountWindow ||
                new Date().getTime() - entry.creationTime.getTime() > entriesPurgeTimeWindow) {
                localStorage.removeItem(entry.key);
            }
        }
    };

   openEntriesSettings();


}

var settings = new Settings();

