kUI.directive("focusOutOnEnter",function(){
    return {
        restrict : 'A',
        link : function(scope,element){
            $(element).keyup(function (e){
                if (e.keyCode === 13 && $(element).is(':focus')) {
                    $(element).blur();
                }
            });
        }
    };
});