'use strict';


kQna.directive('kwpQnaAnswerOnAirMenu',['gettextCatalog', function (gettextCatalog) {

    var controller = ['$scope','qnaService','kalturaService', function ($scope,qnaService,kalturaService) {

        $scope.announceMessageToAll = function(msgText){

            return qnaService.makeAnnouncement (msgText);

        };

    }];

    return {
        restrict : 'E',
        transclude : true,
        replace :false,
        controller: controller,
        link: function (scope, elem, attrs) {



            scope.closeAnswerOnAir = CreateCommand("", "kicon-close2", function () {

                scope.stopRecording();
                scope.setQnaState(scope.QNA_STATES.ANSWER);

            },gettextCatalog.getString("Close"));




        },
        templateUrl: "../common/kQna/views/kwpQnaMenuAnswerOnAirRecording.html"
    };
}]);

