(function () {
    "use strict";

    /**
     *
     * Use on a parent element, when you want its' children's font to be rescaled according to its' size.
     * Each child element should have the parent's 'scalableClass' attribute value as a class so this directive will
     * handle it.
     * In addition, each 'scalable' child should hold a max and min font size attributes
     *
     * e.g
     *
     * <div class="container" k-timeline-font-scale scalableClass="scaleMe" initHeight="220" >
     *
     *      <div class="scaleMe text1" initFontSize=13>{{some text1}}</div>
     *      <div class="scaleMe text2" initFontSize=45>{{some text2}}</div>
     *
     * </div>
     *
     *
     */
    function Directive($timeout) {

        function link(scope, element, attrs, ctrls)
        {
            var $element = $(element);

            var $parent = $element.parent();

            var scalableClass = element.attr('scalableClass') || 'scalableClass';

            var initHeight = element.attr('initHeight') || 130;

            //waiting for DOM rendering to end
            $timeout (
                function () {

                    $element.resize(function ()
                    {
                        //go over each child element with a class defined in 'scalableClass' attribute
                        $element.find('.' + scalableClass).each(function ()
                        {
                            var initFontSize = $(this).attr('initFontSize') || 13;

                            var scale = $element.height() / initHeight;

                            var newFontSize = initFontSize * scale;

                            $(this).attr('style', 'font-size:' + newFontSize + 'px !important');

                        });
                    });
                }
            );
        }

        return {
            restrict: 'A',
            link: link
        };

    }

    angular.module('k-assets-timeline').directive('kTimelineFontScale', Directive);

}());
