(function () {
    "use strict";

    function Directive($timeout) {

        function Controller()
        {
        }

        function link(scope, element, attrs, ctrls)
        {
            var ctrl = ctrls[0];
            var assetsTimeLine = ctrls[1];
            var $element = $(element);
            var $parent = $element.parent();

            attrs.$observe('kDraggableAsset', function(allowDrag) {
                if (allowDrag === 'true' && !$element.data('ui-draggable')){
                    $element.draggable({
                        axis: "x",
                        scroll: true,
                        opacity: 0.85,
                        cursorAt: { left: 10 },
                        connectToSortable: "#eventsdock-wrapper-for-sortable",
                        zIndex: 99999,
                        delay: 200,
                        refreshPositions: true,
                        beforeStart: function () {
                            assetsTimeLine.recalcLayout({'elementSizes': 'same', 'renderAllSlides': true});

                            // set containment so wont be able to drag past end
                            var containmentX1 = $parent.offset().left;
                            var containmentY1 = $parent.offset().top;
                            var containmentX2 = ($parent.outerWidth() +  $parent.offset().left - $element.outerWidth());
                            var containmentY2 = ($parent.outerHeight() +  $parent.offset().top - $element.outerHeight());
                            $element.draggable( "option", "containment", [containmentX1, containmentY1, containmentX2, containmentY2] );
                        },
                        start: function (event, ui) {
                            assetsTimeLine.setDragInProgress(true);
                            assetsTimeLine.scrollToDraggedElement($(event.target).index());
                        },
                        stop: function (event, ui) {
                            var targetPos = $(event.target).index();
                            // No need to update positions, just to make the service update the server
                            assetsTimeLine.updateAssetsState({ updateServer:true });
                            assetsTimeLine.setDragInProgress(false);
                            assetsTimeLine.scrollToDroppedElement(targetPos);
                        }
                    });
                }
                else if ($element.data('ui-draggable')){

                    $element.draggable( 'destroy' );
                }
            });
        }

        return {
            restrict: 'A',
            controller: Controller,
            require: ['kDraggableAsset','^assetsTimeline'],
            link: link
        };
    }

    angular.module('k-assets-timeline').directive('kDraggableAsset', Directive);
}());