'use strict';

kQna.directive('kwpQnaRecordBox',['gettextCatalog','$anchorScroll','$location','$timeout', function (gettextCatalog,$anchorScroll,$location,$timeout) {

    var controller = ['$scope','qnaService','kalturaService', function ($scope,qnaService,kalturaService) {



    }];

    return {
        restrict : 'E',
        replace:true,
        controller: controller,


        link: function (scope, elem, attrs) {


            scope.startRecording = function(){

            };

            scope.stopStartRecording = function(){

                scope.toggleRecording();

            };

            scope.getPlaceHolderText = function(){
                return gettextCatalog.getString('Use this space to write prep notes.');
            };

            scope.textAreaBlur = function(){
                elem.find(".answerOnAirRecordNoteArea")[0].placeholder = scope.getPlaceHolderText();
            };

            scope.textAreaKeyDown = function(){
                elem.find(".answerOnAirRecordNoteArea")[0].placeholder = '';
            };
        },
        templateUrl: "../common/kQna/views/kwpQnaRecordBox.html"
    };
}]);


