/* globals _ */
/* globals PromisesQueue */
/* globals Promise */

(function() {
    'use strict';

    function registerActionHandlerFactory(kProducerActionsProvider) {

        function ActionHandlerFactory()
        {
            function validateData(actionData)
            {
                var errorMessage = '';

                if(actionData)
                {
                    if (actionData.transmitMode)
                    {
                        if (actionData.transmitMode !== 'always' && actionData.transmitMode !== 'on-air-only')
                        {
                            errorMessage += "property 'transmitMode' value '" + actionData.transmitMode + "' is invalid (acceptable value is 'always' | 'on-air-only')'\n";
                        }

                        if(actionData.dataOrientation) {
                            if(actionData.dataOrientation !== 'instance' && actionData.dataOrientation !== 'state')
                            {
                                errorMessage += "Supported data orientation is 'instance' or 'state'";
                            }
                        }
                    }
                }

                if (errorMessage)
                {
                    klog.error(errorMessage);
                    return false;
                }else
                {
                    return true;
                }
            }

            function create(actionData)
            {
                if (!validateData(actionData))
                {
                    throw new Error("action data is invalid. please review logs and modify");
                }

                return _.extend({
                    transmitMode : 'always',
                    dataOrientation: 'instance',
                    extractContentFromCuePoint: function (cuePoint) {
                        var content = {};
                        if(cuePoint) {
                            content.data = cuePoint.partnerData ?
                                (_.isString(cuePoint.partnerData) ?
                                    JSON.parse(cuePoint.partnerData) : cuePoint.partnerData) : null;
                            content.text = cuePoint.text ?
                                (_.isString(cuePoint.text) ?
                                    JSON.parse(cuePoint.text) : cuePoint.text) : null;
                            content.id = cuePoint.id ? cuePoint.id : null;
                            content.parentId = cuePoint.parentId ? cuePoint.parentId : null;
                        }

                        return content;
                    },
                    transmitInstance: function (cuePointsService, actionContent, actionId, kalturaAPI) {
                        klog.info("codeCuePointAdapter.transmitInstance(): transmitting action with id '" + actionId + "'");
                        klog.debug(actionContent);

                        var apiRequest = {
                            actionId: actionId,
                            actionName: this.name,
                            actionContent: actionContent,
                            mediaEntryId: configuration.MediaEntryId
                        };

                        return kalturaAPI.producerActionsService.createCodeCuepointAction(apiRequest);
                    }
                }, actionData,{
                    cuePointType: 'annotation.Annotation'
                });
            }

            this.create = create;

        }

        kProducerActionsProvider.registerActionHandlerFactory('KalturaAnnotation',new ActionHandlerFactory());
    }

    angular.module('kProducerActions').config(registerActionHandlerFactory);

})();