/* globals _ */
/* globals PromisesQueue */
/* globals Promise */

(function() {
    'use strict';

    function registerAdapter(kProducerActionsProvider, cuePointsServiceProvider) {

        // NOTICE: Several module actions are handled separately in folder 'producer-actions'

        kProducerActionsProvider.registerAction('poll-data','codeCuePoint.Code',{transmitMode : 'always', dataOrientation: 'instance'});
        kProducerActionsProvider.registerAction('poll-results','codeCuePoint.Code',{history : true, dataOrientation : 'state', transmitMode : 'on-air-only'});

        cuePointsServiceProvider.addTagToArchiveExcludeList('poll-data');

    }

    angular.module('kPolls').config(registerAdapter);

})();