(function () {
    "use strict";

    function Directive() {
        function Controller($scope, $rootScope,kProducerActions,kaPlayerViewControlsService) {

            var self = this;
            var dropdownId = 'player-view-mode';

            function toggleDropdown()
            {
                $rootScope.$broadcast('ka-dropdown-toggle-request',{id : dropdownId});
            }


            function syncCurrentViewIcon()
            {
                // update the current state svg name (empty string will be provided is no current view found)
                self.currentStateSVGName = kaPlayerViewControlsService.getCurrentViewSVGName();
            }

            self.toggleDropdown = toggleDropdown;

            $scope.$on('ka-dropdown-is-open-mode',function(e,args) {
                // handle only the relevant detached dropdown
                if (dropdownId === args.id)
                {
                    self.isOpen = args.isOpen;
                }
            });

            $scope.$on('kaPlayerViewControlsService.onPlayerViewModeIdChanged', function (event, args) {
                syncCurrentViewIcon();
            });

            kProducerActions.isReady().then(function()
            {
               syncCurrentViewIcon();
            });


        }

        function Link(scope, element, attrs, ctrls) {
            var ctrl = ctrls[0];




        }

        return {
            restrict: 'A',
            scope: {},
            require: ['kaPlayerViewToggle'],
            templateUrl: '../common/kaPlayerViewControl/directives/ka-player-view-toggle.html',
            controller: Controller,
            link: Link,
            controllerAs: 'vm',
            bindToController: {}
        };
    }

    angular.module('kaPlayerViewControl').directive('kaPlayerViewToggle', Directive);

})();