kQna.directive('qnaModeratorAnalytics', function () {

	var controller = ['$scope','qnaService','liveAnalyticsService', function ($scope,qnaService,liveAnalyticsService) {
		$scope._liveAnalyticsService = liveAnalyticsService;
	}];

	return {
		restrict : 'E',
		transclude : true,
		replace : true,
		controller: controller,
		templateUrl: "../common/kQna/views/qnaModeratorAnalytics.html"
	};
});


