'use strict';
/*
 * Main qna directive.keeps the global shared state of the module which is accessible by sub screens.
 * Also this module holds connection to the model of the qnaService.
 */
kQna.directive('kwpQnaDebug', function () {

    var controller = ['$scope','qnaService','kalturaAPI','$http', function ($scope,qnaService,kalturaAPI,$http) {



        $scope.getAnnotationCuePoints=function() {
            return qnaService.getRawCuePoints().then(function (res) {
                /*
                var data = "<pre style='word-wrap: break-word;white-space: pre-wrap;'> "+res+"</pre>";
                window.open("data:text/html," + encodeURIComponent(data),
                    "_blank", "width=500,height=400");
*/
                alert(JSON.stringify(res));
            });
        };

        $scope.deleteAllResponseProfiles=function() {
            return kalturaAPI.responseProfileService.list().then(function (res) {
                var promies = [];
                res.objects.forEach(function (item) {
                    if (item.systemName.indexOf(qnaService.QandA_ResponseProfileSystemName) > -1 ) {
                        promies.push(kalturaAPI.responseProfileService.delete(item.id));
                    }
                });
                return Promise.all(promies);
            });
        };

        $scope.installResponseProfile=function() {

            $scope.deleteAllResponseProfiles().then(function () {
                kalturaAPI.responseProfileService.add(qnaService.QandA_ResponseProfileSystemName, qnaService.QandA_ResponseProfile).then(function (r) {
                    alert("response profile  was created " + JSON.stringify(r));

                });
            });
        };


        $scope.installMetadataProfile=function() {

            $http.get('../common/kQna/customeMetadata.xsd').then(function (xsd) {
                kalturaAPI.metadataProfileService.list(qnaService.QandA_MetadataProfileSystemName).then (function (res) {

                    if (res.objects.length===0) {

                        return kalturaAPI.metadataProfileService.add(qnaService.QandA_MetadataProfileSystemName, xsd.data, "annotationMetadata.Annotation").then(function (res) {
                            alert(JSON.stringify(res));
                        });
                    } else {
                        var customMetadataProfile = res.objects[0];
                            customMetadataProfile.xsd = xsd.data;

                        return kalturaAPI.metadataProfileService.update(customMetadataProfile.id, customMetadataProfile.xsd).then(function (res) {
                                alert(JSON.stringify(res));
                        });
                    }
                });
            });

        };


        $scope.deleteAllAnnotations=function() {
            qnaService.getRawCuePoints().then(function(res){
                var promises = [];
                res.objects.forEach(function (item) {
                    promises.push(kalturaAPI.cuePointService.delete(item.id));
                });
                return Promise.all(promises);
            });
        };

        $scope.createMockAnnotations=function() {

            return qnaService.makeAnnouncement("Announcment #1").then(function() {
                return qnaService.makeAnnouncement("Announcment #2");
            }).then(function() {
                return qnaService.askQuestion("Question #1.1", null).then(function (question) {
                    return question.reply("Answer #1.2").then(function (question) {
                        return question.reply("Question #1.3");
                    });
                });
            }).then(function() {
                return qnaService.askQuestion("Question #2", null);
            });
        };

    }];

    return {
        restrict : 'E',
        transclude : true,
        replace : true,
        controller: controller,
        link: function (scope, elem, attrs) {


            //Initialized state of this module is the


        },
        templateUrl: "../common/kQna/views/kwpQnaDebug.html"
    };
});


