kngLayout.directive('kFlyoutPanel', function ($timeout) {
    return {
        restrict: 'E',
        transclude: true,

        scope: {
            ktitle: '@ktitle',
            id: '@id',
            expanded:"=",
            width:'='
        },
        link: function (scope, elem, attrs) {

            scope.elem = elem;

            scope.$watch('expanded',function() {


                if (scope.expanded) {
                    elem.css({"right": "0px", "width": scope.width+"px"});
                } else {
                    elem.css({"right": -scope.width+"px", "width": scope.width+"px"});
                }

                scope.wasEverExpanded= scope.wasEverExpanded || scope.expanded;
            });
        },
        templateUrl:'../common/kngLayout/directives/partials/kFlyoutPanelTemplate.html'
    };
});
