'use strict';

kQna.directive('xngFocus', function($timeout) {
    return {
        restrict : 'A', // only antribute
        link : function (scope, element, attrs) {
            scope.$watch(attrs.xngFocus, function (newVal) {
                if (!!newVal) {
                    //we need timeout here since otherwise the DOM will do focus on the button after this call
                    $timeout(function() {
                        element[0].focus();
                    }, 500);

                }
            });
        }
    };
});

kQna.directive('kwpQnaAnnounceBox',['gettextCatalog','$timeout', function (gettextCatalog, $timeout) {

    var controller = ['$scope','qnaService','kalturaService', function ($scope,qnaService,kalturaService) {

        $scope.announceMessageToAll = function(msgText){

            return qnaService.makeAnnouncement (msgText);

        };

    }];

    return {
        restrict : 'E',
        transclude : true,
        replace :false,
        controller: controller,
        link: function (scope, elem, attrs) {

            // This is the text that the text area starts with.

            scope.announceText="";
            scope.enteringAnnouncement = false;

            scope.showFooter=function(){
                if (scope.currentQnaState !='SETTINGS'
                    && !(scope.currentQnaState ==='ANSWER' && scope.qnaModeratorMode)
	                && !(scope.currentQnaState ==='ANSWER_ON_AIR_RECORD' && scope.qnaModeratorMode)
                    && !(scope.currentQnaState ==='ANSWER_ON_AIR_SELECT' && scope.qnaModeratorMode) ) {
                    return true
                }
                return false;
            };
            function closeTextarea() {
                var qnaFooter = angular.element( document.querySelector('#qnaFooter' ) );
                if(!$(qnaFooter).is(':focus')){
                    scope.enteringAnnouncement = false;
                }
            }
            scope.onBlur = function () {
                $timeout(closeTextarea,0);
            };

            scope.keydown=function($event) {
                if ($event.keyCode == 27) {
                    scope.enteringAnnouncement = false;
                }
            };

            scope.announceButtonOnClick = function() {

                if (!(scope.qnaModuleState.qnaEnabled || scope.qnaModuleState.announcementOnly)){
                    return;
                }

                //Valid for announcement.
                if(scope.enteringAnnouncement){

                    scope.enteringAnnouncement = false;

                    if(scope.announceText.length>0) {


                        scope.announceMessageToAll(scope.announceText).then(function () {

                            klog.debug("Announcement sent to server.");
                            scope.announceText="";

                        }, function (err) {

                            klog.error("Failed to Announce text:" + scope.announceText);
                            scope.announceText="";
                        });
                    }
                }
                else {
                    scope.enteringAnnouncement = true;

                }
            };

            scope.closeTextBoxCommand = CreateCommand("", "kicon-close2", function () {

                if(scope.enteringAnnouncement){
                    scope.enteringAnnouncement = false;
                    scope.announceText="";
                }

            },gettextCatalog.getString("Close"));

        },
        templateUrl: "../common/kQna/views/kwpQnaAnnounceBox.html"
    };
}]);

