'use strict';

kQna.directive('kwpQnaAnswerTextBox',['gettextCatalog','qnaService', function (gettextCatalog,qnaService) {

    return {
        restrict : 'E',
        transclude : true,
        replace :false,
        link: function (scope, elem, attrs) {

            scope.replyText = "";

            scope.reply = function(currentSelectedThread){

                var replyText = scope.replyText;

                scope.replyText = "";

                scope.replyToMessage(currentSelectedThread.getFirstQuestion(),replyText);

            };
            scope.keydown = function($event,currentSelectedThread){
                if( $event.altKey && $event.keyCode === 13 && !isIE()){
                    var textArea = $event.currentTarget;
                    textArea.value += '\n';
                    textArea.scrollTop = textArea.scrollHeight;
                    textArea.selectionEnd= textArea.selectionEnd + 7;
                    
                }else if(!$event.shiftKey && !$event.altKey && $event.keyCode === 13){
                    $event.preventDefault();
                    if(scope.replyText.length >0){
                        scope.reply(currentSelectedThread);
                    }
                }
            };

        },
        templateUrl: "../common/kQna/views/kwpQnaAnswerTextBox.html"
    };
}]);

