(function () {
    "use strict";

    angular.module('kUpgradeModule').controller('upgradeController', ['$scope', 'upgradeService', 'gettextCatalog', '$modalInstance','kCef', function ($scope, upgradeService, gettextCatalog, $modalInstance,kCef) {

        $scope.srv = upgradeService;
        $scope.downloadProgress=0;
        $scope.isMandatory=upgradeService.upgradeInfo().isMandatory;

        $scope.upgradeCommand = CreateCommand( gettextCatalog.getString("Yes, Upgrade now"), "", function () {
            upgradeService.upgradeCommand();
        });

        if (!upgradeService.upgradeInfo().isMandatory) {
            $scope.declineUpgradeCommand = CreateCommand(gettextCatalog.getString("No, remind me later"), "", function () {
                upgradeService.declineUpgrade();
                $modalInstance.dismiss('cancel');
            });
        }
        else {
            $scope.declineUpgradeCommand = CreateCommand(gettextCatalog.getString("No, close application"), "", function () {
                $modalInstance.dismiss('cancel');
                window.close();
            });

        }
        if (upgradeService.upgradeInfo().isMandatory) {
            $scope.cancelCommand = CreateCommand(gettextCatalog.getString("Cancel and close"), "", function () {
                $modalInstance.dismiss('cancel');
            });
        }
        else {
            $scope.cancelCommand = CreateCommand(gettextCatalog.getString("Cancel"), "", function () {
                $modalInstance.dismiss('cancel');
            });

        }

        kCef.on("downloadProgress", function(status) {
            klog.warn("updateProgress " + JSON.stringify(status));
            $scope.downloadProgress = status.progress;
            if (status.isCompleted) {
                klog.warn("Completed downloading file " + status.fullPath + " running it");
                kCef.openShell(status.fullPath);
                window.close();
            }
        });
    }]);
})();
