kwpApp.factory('asyncLiveEntryInfoService', ['$injector','$rootScope','kalturaService','$interval', function ($injector,$rootScope,kalturaService,$interval) {

    var REFRESH_RATE_MS = 1000;

    var registrationFunctions = [];

    var currentEntryInfo = null;

    var updatingLiveEntryInfo=false;

    var timerHandler = null;

    var _notifyTimeChangeToRegisteredUsers = function(result){

        for(var i=0;i<registrationFunctions.length;i++){

            var functionToExecute = registrationFunctions[i];

            functionToExecute(result);

        }
    };

    function processNewEntryInfo(entryInfo) {
        var prevInfo = currentEntryInfo;
        if ((angular.equals(prevInfo,entryInfo) === false) && !updatingLiveEntryInfo) {
            currentEntryInfo = entryInfo;

            _notifyTimeChangeToRegisteredUsers(entryInfo);

        }
    }

    var _refreshLiveEntryInfoFunc = function(){

        kalturaService.getLiveEntryInfo(configuration.MediaEntryId).then(function(res){

            processNewEntryInfo(res);
        });
    };

    var _stopPolling = function(){

        if(timerHandler !== null) {
            $interval.cancel(timerHandler);
            timerHandler = null;
        }

    };

    var _startPolling = function(){

        if(timerHandler === null) {
            timerHandler = $interval(_refreshLiveEntryInfoFunc, REFRESH_RATE_MS);
        }
    }
    _startPolling();


    var _getCurrentEntryInfo = function(){

        return currentEntryInfo;

    };

    var _onEntryInfoChange = function(functionToCall){

        if(typeof(functionToCall) === "function") {

            registrationFunctions.push(functionToCall);
        }
        else{
            klog.error("Registering illegal function");
        }

    };

    var _unRegisterOnEntryInfoChange = function(func){

        var index = registrationFunctions.indexOf(func);

        if (index > -1) {
            registrationFunctions.splice(index, 1);
        }

    };

    function updateLiveEntryInfo(args,sendUpdateToRegistredUsers) {

        if (sendUpdateToRegistredUsers && currentEntryInfo) {
            var newEntryInfo = _.extend(currentEntryInfo, args);
            processNewEntryInfo(newEntryInfo);
        }

        updatingLiveEntryInfo=true;
        try {
            return kalturaService.updateLiveEntryInfo(configuration.MediaEntryId, args).then( function(res) {
                updatingLiveEntryInfo = false;
                processNewEntryInfo(res);
                return res;
            }).catch(function (err) {
                updatingLiveEntryInfo = false;
                return err;
            });
        }catch(e) {
            klog.error("Exception calling updateLiveEntryInfo "+e)
            updatingLiveEntryInfo = false;
            return Promise.reject(e);
        }
    }


    return {
        getCurrentEntryInfo : _getCurrentEntryInfo,
        registerOnEntryInfoChange : _onEntryInfoChange,
        updateLiveEntryInfo: updateLiveEntryInfo,
        isUpdatingLiveEntry: function() {
            return updatingLiveEntryInfo;
        }
    };
}]);

