'use strict';
/*
 * Incoming screen directive.Represent the live feed screen where producer receives all incoming data from users.
 * Also this module holds connection to the model of the qnaService.
 */
kQna.directive('kwpQnaScreenAnswerOnAirRecording', function (gettextCatalog,QnaEntry,qnaService, $timeout) {

    var controller = ['$scope','qnaService','captureService','gettextCatalog', function ($scope,qnaService,captureService,gettextCatalog) {

        $scope.transmitAnswerOnAir = function(parentEntry,msgText){

            return captureService.getPosition().then(function (time) {

                return qnaService.startAnswerOnAir(msgText,time);

            });

        };

        $scope.transmitEndOfAnswerOnAir = function(){

            return captureService.getPosition().then(function (time) {

                return qnaService.endAnswerOnAir(time);

            });

        };

        $scope.endAllServerAnswerOnAir = function(){

            return captureService.getPosition().then(function (time) {

                return qnaService.endAllServerAnswerOnAir(time);

            });

        };
        $scope.qnaService = qnaService;//.isAnsweringOnAir();

    }];

    return {
        restrict : 'E',

        controller: controller,

        link: function (scope, elem, attrs) {

            scope.readonly = true;

            var userOnAirConfirmationText = gettextCatalog.getString("Your question will be answered on-air.");

            var textElement = elem.find(".editOnAirText")[0];

            scope.onAirText = scope.currentSelectedEntry.getContent();



            scope.stopRecording = function() {

                if (qnaService.isAnotherEntryBeingAnswered()) {
                    return;
                }
                if (!scope.qnaService.isAnsweringOnAir()) {

                    return;
                }

                scope.loading = true;

                scope.transmitEndOfAnswerOnAir().then(function(res){

                    scope.loading = false;

                },function(err){

                    scope.loading = false;

                });
            }


            scope.startRecording = function() {

                if (qnaService.isAnotherEntryBeingAnswered()) {
                    return;
                }
                if (scope.qnaService.isAnsweringOnAir() || !scope.captureService.isBroadcasting()) {

                    return;
                }

                scope.loading = true;

                scope.transmitAnswerOnAir(scope.currentSelectedEntry,scope.onAirText).then(function(entry){

                    scope.qnaService.makeReply(scope.currentSelectedEntry,userOnAirConfirmationText, 'qna, aoa_auto_reply');

                    scope.loading = false;

                },function(err){

                    klog.error("Error sending answer on-air.");

                    scope.loading = false;

                });
            }


            scope.toggleRecording = function(){

                if(scope.loading){
                    return;
                }

                if(!scope.qnaService.isAnsweringOnAir() && scope.captureService.isBroadcasting()){

                    scope.startRecording();
                }
                else if(scope.qnaService.isAnsweringOnAir()){

                    scope.stopRecording();

                }
                else{
                    klog.info("On Air is disabled no video stream.");
                }
            };

            scope.onEditButtonClick = function(){

                scope.readonly = !scope.readonly;

                if(!scope.readonly){

                    $timeout(function(){
                        textElement.focus();
                    },0);
                }

            };

        },
        templateUrl: "../common/kQna/views/kwpQnaScreenAnswerOnAirRecording.html"
    };
});


