/* globals _ */
/* globals PromisesQueue */
/* globals Promise */

(function() {
    'use strict';



    function registerActionHandlerFactory(kProducerActionsProvider) {

        function ActionHandlerFactory()
        {
            function validateData(actionData)
            {
                var errorMessage = '';

                if(actionData)
                {
                    if (actionData.transmitMode)
                    {
                        if (['always','on-air-only'].indexOf(actionData.transmitMode) === -1)
                        {
                            errorMessage += "property 'transmitMode' value '" + actionData.transmitMode + "' is invalid (acceptable values are '{empty}', 'always','on-air-only'\n";
                        }

                        if(actionData.dataOrientation) {
                            if(actionData.transmitMode !== 'always' && (actionData.dataOrientation === 'instance'))
                            {
                                errorMessage += "Instance data oriented action is valid only for 'transmitMode = always'";
                            }
                        }
                    }
                }


                if (errorMessage)
                {
                    klog.error(errorMessage);
                    return false;
                }else
                {
                    return true;
                }
            }
            function create(actionData)
            {
                if (!validateData(actionData))
                {
                    throw new Error("action data is invalid. please review logs and modify");
                }

                return _.extend({
                    transmitMode : 'on-air-only',
                    emptyContentMode : 'fix-automatically',
                    checkContentRelevancy : function(state,actionContent,api) {
                        var actionName = api.getCurrentActionName();
                        return api.isNewContent(actionName, actionContent);
                    },
                    extractContentFromCuePoint: function (cuePoint) {
                        return cuePoint && cuePoint.partnerData ? (_.isString(cuePoint.partnerData) ? JSON.parse(cuePoint.partnerData) : cuePoint.partnerData) : null;
                    },
                    transmitState: function (cuePointsService, content, api, kalturaAPI, captureService) {
                        // add create code cue point request to queue
                        klog.info('codeCuePointAdapter.transmit(): transmitting add cue point request');
                        var tags=this.name;

                        var previewMode = captureService.isPreviewMode();
                        if (previewMode) {
                            //todo: fix player to support __PREVIEW_CUEPOINT_TAG__ in polls and view modes
                            //tags+=", "+kProducerActionsProvider.__PREVIEW_CUEPOINT_TAG__;
                        }

                        var apiRequest = {
                            partnerData: content,
                            tags: tags,
                            mediaEntryId: configuration.MediaEntryId
                        };

                        return kalturaAPI.cuePointService.createCodeCuePoint(apiRequest).then( function (cuePoint) {
                            //mark as handled so it won't be copied to VOD
                            if (previewMode) {
                                klog.info("code-cue-point_code.js: updateStatus as handled for  " + cuePoint.id);
                                return kalturaAPI.cuePointService.updateStatus(cuePoint.id,3).then( function() {
                                    return cuePoint;
                                });
                            }
                            return cuePoint;
                        });
                    },
                    transmitInstance: function (cuePointsService, actionContent, actionId, kalturaAPI) {
                        klog.info("codeCuePointAdapter.transmitInstance(): transmitting action with id '" + actionId + "'");
                        klog.debug(actionContent);

                        var apiRequest = {
                            actionId: actionId,
                            actionName: this.name,
                            actionContent: actionContent,
                            mediaEntryId: configuration.MediaEntryId
                        };

                        return kalturaAPI.producerActionsService.createCodeCuepointAction(apiRequest);
                    }
                }, actionData,{
                    cuePointType: 'codeCuePoint.Code'
                });
            }

            this.create = create;

        }

        kProducerActionsProvider.registerActionHandlerFactory('codeCuePoint.Code',new ActionHandlerFactory());
    }

    angular.module('kProducerActions').config(registerActionHandlerFactory);

})();