'use strict';

kQna.directive('kwpQnaForwardOnAirNav',['gettextCatalog', function (gettextCatalog) {

    var controller = ['$scope','qnaService','kalturaService', function ($scope,qnaService,kalturaService) {

        $scope.announceMessageToAll = function(msgText){

            return qnaService.makeAnnouncement (msgText);

        };

    }];

    return {
        restrict : 'E',
        transclude : true,
        replace :true,
        controller: controller,
        link: function (scope, elem, attrs) {

            var answerOnAirMessageText = "Question will be answered on-air";

            scope.forwardMessage = function() {

                //TODO:Implement.
                //scope.setQnaState(scope.QNA_STATES.FORWARD);

            };

            scope.answerOnAir = function() {

                var entries = scope.currentSelectedThread.getEntries();

                if(entries.length > 1){
                    scope.setQnaState(scope.QNA_STATES.ANSWER_ON_AIR_SELECT);
                }
                else{
                    //Assumption:1 entry Thread which is a question.
                    scope.setCurrentSelectedEntry(entries[0]);

                    scope.setQnaState(scope.QNA_STATES.ANSWER_ON_AIR_RECORD);

                }

            };

        },
        templateUrl: "../common/kQna/views/kwpQnaForwardOnAirNav.html"
    };
}]);

