(function () {

    /**
     * set a watcher on the rootScope and log to console
     * each time the digestCycle is running
     *
     * @param $rootScope
     * @param $log
     * @constructor
     */
    function DigestCounter($rootScope) {

        var self = this;
        var watcher = null;

        self.setCounter = function () {
            watcher = $rootScope.$watch(function () {
                klog.debug("digest cycles running!");
            });
        };

        self.resetCounter = watcher;
    }



    /**
     * returns the number of watchers currently
     * registered on the module
     *
     * @constructor
     */
    function WatcherCounter ($rootElement) {

        var self = this;

        self.watchers = [];
        self.watchersWithoutDuplicates = [];

        self.count = function (element) {
            countWatchers($rootElement);
            removeDuplications();
            return self.watchersWithoutDuplicates.length;
        };

        /**
         * loop over the scopes and populate the watchers array
         *
         * @param element
         */
        function countWatchers (element) {
            angular.forEach(['$scope', '$isolateScope'], function (scopeProperty) {
                if (element.data() && element.data().hasOwnProperty(scopeProperty)) {
                    angular.forEach(element.data()[scopeProperty].$$watchers, function (watcher) {
                        self.watchers.push(watcher);
                    });
                }
            });

            angular.forEach(element.children(), function (childElement) {
                countWatchers(angular.element(childElement));
            });
        }

        /**
         * build an duplications free watcher array
         */
        function removeDuplications () {
            angular.forEach(self.watchers, function(item) {
                if(self.watchersWithoutDuplicates.indexOf(item) < 0) {
                    self.watchersWithoutDuplicates.push(item);
                }
            });
        }

    }

    kUI.service('DigestCounter',['$rootScope',DigestCounter]);
    kUI.service('WatcherCounter',['$rootElement',WatcherCounter]);
}());
