/* globals kserviceInfo */
/* globals JSON */
/* globals Promise */

(function () {
    "use strict";

    angular.module('kUpgradeModule').factory('upgradeService', function (kalturaAPI, kUpgradeServiceInfo, kdialog, gettextCatalog, kCef, WebcastingVersionInfoUIConfName) {

        var declinedAvailableUpdate = false;
        var isUpgrading = false;
        var upgradeInfo;
        var systemLatestVersion = "1.0.0.0";

        function checkForUpdates() {
            if (configuration.checkForUpdates) {
                kalturaAPI.login().then(function () {

                    var getOSVersionPromise = kCef.available ? kCef.sendIPC("getOsMake") : Promise.resolve('osx');

                    getOSVersionPromise.then(function(result) {
                        var osMake = result;

                        if (configuration.GlobalUIConf && configuration.PartnerUiConf)
                        {
                            var partnerVersionInfo = parsePartnerUIConf(configuration.PartnerUiConf);
                            var serverVersionInfo = parseGlobalUIConf({objects:[configuration.GlobalUIConf],totalCount:1}, osMake);
                            validateAppVersion(partnerVersionInfo, serverVersionInfo);
                            return;
                        }

                        kalturaAPI.startMultirequest();

                        kalturaAPI.UIConfService.get(kUpgradeServiceInfo.uiConfID);
                        kalturaAPI.UIConfService.listTemplates({ "partnerIdEqual": 0, "objTypeEqual": kalturaAPI.UIConfService.UI_CONF_TYPES.KALTURA_WEBCASTING_STUDIO, "nameLike": WebcastingVersionInfoUIConfName });
                        if (kserviceInfo.playerUIConf) {
                            kalturaAPI.UIConfService.get(kserviceInfo.playerUIConf);
                        }

                        kalturaAPI.execMultirequest().then(function(results){
                            if (_.isObject(results)) {
                                delete results[2].confFile;
                                delete results[2].config;
                            }
                            klog.info("results from upgrade service multi request: " + JSON.stringify(results));
                            var partnerVersionInfo = parsePartnerUIConf(results[0]);
                            var serverVersionInfo = parseGlobalUIConf(results[1], osMake);
                            validateAppVersion(partnerVersionInfo, serverVersionInfo);
                            verifyMinimalPlayerVersion(results[2]);

                        }, function(errors){
                            klog.error("got errors while running the upgrade service multi request " + JSON.stringify(errors));
                        });
                    });
                });
            }
        }

        function validateAppVersion(partnerVersionInfo, serverVersionInfo)
        {
            systemLatestVersion = serverVersionInfo.recommendedVersion;
            kUpgradeServiceInfo.systemLatestVersion = serverVersionInfo.recommendedVersion;
            if (serverVersionInfo.playerMinimalVersion) {
                kUpgradeServiceInfo.playerMinimalVersion = serverVersionInfo.playerMinimalVersion;
            }
            upgradeInfo = getUpgradeInfo(partnerVersionInfo, serverVersionInfo, kUpgradeServiceInfo.app_version);
            if (upgradeInfo.upgradeAvailable){
                showUpgradeDialog(upgradeInfo);
            }
        }

        function parsePartnerUIConf(result) {
            var config = JSON.parse(result.config);
            return {
                url: result.swfUrl,
                minimalVersion: config.minimalVersion,
                ignoreOptionalUpdates : config.ignoreOptionalUpdates
            };
        }

        function parseGlobalUIConf(listResults, osMake) {
            if (listResults.objects.length === 0){
                klog.error("failed getting uiconf with name [" + WebcastingVersionInfoUIConfName + "]");
            }
            else{
                if (listResults.objects.length > 1) {
                    klog.warn("there are [" + listResults.objects.length + "] object of type " + WebcastingVersionInfoUIConfName + " using the first");
                }
                return JSON.parse(listResults.objects[0].config)[osMake];
            }
        }

        // get the decision regarding upgrade
        // returns: {
        //  upgradeAvailable: true / false
        //  isMandatory: true / false
        //  versionName: 1.2.3.4
        //  url: http://www.kaltura.com/install.exe
        // }
        function getUpgradeInfo(partnerVersionInfo, serverVersionInfo, currentAppVersion) {

            function getVersionName() {
                if (useRecommendedVersion){
                    return serverVersionInfo.recommendedVersion;
                }
                if (useSystemMinimalVersion){
                    return serverVersionInfo.minimalVersion;
                }
                return partnerVersionInfo.minimalVersion;
            }

            function getVersionUrl() {
                if (useRecommendedVersion){
                    return serverVersionInfo.recommendedVersionUrl;
                }
                if (useSystemMinimalVersion){
                    return serverVersionInfo.minimalVersionUrl;
                }
                return partnerVersionInfo.url;
            }

            var upgradeAvailable = false;
            var isMandatory = false;
            var useSystemMinimalVersion = false;
            var minimalVersion = partnerVersionInfo.minimalVersion;
            var useRecommendedVersion = false;

            // system min <= producer min
            if (cmpVersion(serverVersionInfo.minimalVersion, partnerVersionInfo.minimalVersion) >= 0) {
                useSystemMinimalVersion = true;
                minimalVersion = serverVersionInfo.minimalVersion;
            }

            // app version < minVersion
            if (cmpVersion(currentAppVersion, minimalVersion) < 0) {
                upgradeAvailable = true;
                isMandatory = true;
            }

            // if we allow optional updates and recommendedVersion > currentAppVersion and recommendedVersion > minimalVersion
            if (!partnerVersionInfo.ignoreOptionalUpdates &&
                cmpVersion(currentAppVersion, serverVersionInfo.recommendedVersion) < 0 &&
                cmpVersion(minimalVersion, serverVersionInfo.recommendedVersion) < 0 ) {
                upgradeAvailable = true;
                useRecommendedVersion = true;
            }

            return {
                upgradeAvailable : upgradeAvailable,
                isMandatory: isMandatory,
                versionName: getVersionName(),
                url: getVersionUrl()
            };
        }

        function showUpgradeDialog(upgradeInfo) {
            kdialog.templatedDialog({

                templateUrl: '../common/upgrade/upgrade.html',
                controller: 'upgradeController',
                keyboard: false,
                backdrop: 'static'
            }).then(
                function () {
                },
                function () {
                    if (upgradeInfo.isMandatory) {
                        window.close();
                    }
                }
            );
        }

        function verifyMinimalPlayerVersion(UIConf) {
            if (UIConf) {
                var ver = UIConf.html5Url.match(/\/html5\/html5lib\/v([^\/]*)\/mwEmbedLoader.php/);
                if (_.isArray(ver) && ver.length === 2) {
                    var playerVersion = ver[1];
                    if (cmpVersion(playerVersion, kUpgradeServiceInfo.playerMinimalVersion) < 0) {
                        klog.warn(playerVersion);
                        kdialog.attention(gettextCatalog.getString("Player outdated!"),gettextCatalog.getString("Player is outdated - at least v"+kUpgradeServiceInfo.playerMinimalVersion+" is required,<br>Please upgrade your player<br>(player id: " + kserviceInfo.playerUIConf + ", version: " + playerVersion + ")"),"OK");
                    }
                }
            }
        }

        function cmpVersion(v1, v2) {
            if(v1===v2) {
                return 0;
            }
            var a1 = v1.split(".");
            var a2 = v2.split(".");
            for( var i = 0; i < a1.length && i < a2.length; i++ ) {
                var intdiff = parseInt(a1[i],10) - parseInt(a2[i],10);
                if( intdiff>0 ) {
                    return 1;
                }
                else if( intdiff<0 ) {
                    return -1;
                }
            }
            var lengthdiff = a1.length - a2.length;
            return (lengthdiff>0) ? 1 : (lengthdiff<0) ? -1 : 0;
        }

        function downloadURI(uri, name) {
            var link = document.createElement("a");
            link.download = name;
            link.href = uri;
            link.click();
        }

        function upgradeCommand() {
	        var fileURL=upgradeInfo.url;

            if (navigator.platform.startsWith("Mac")) {
                fileURL=fileURL.replace('.exe', '.dmg');
            }

            klog.warn("Upgrading to version " + fileURL);

            if (!upgradeInfo.url.startsWith("http")){
                //klog.error("url for downloading the new version seams to be invalid {0}", latestVersionInfo.url);
                if(!upgradeInfo.url.startsWith("/")){
                    klog.error("url for downloading the new version seams to be invalid {0}", upgradeInfo.url);
                    return;
                }
                else {
                    upgradeInfo.url = kserviceInfo.serverAddress + upgradeInfo.url;
                }
            }

            klog.warn("Downloading "+fileURL);


            var filename = fileURL.replace(/^.*[\\\/]/, '');
            var localFile = upgradeInfo.versionName +"_"+filename;

            isUpgrading=true;

            klog.warn("Saving to "+localFile);

            downloadURI(fileURL,localFile);
        }

        function isCurrentSystemLatest() {
            return (cmpVersion(kUpgradeServiceInfo.app_version, systemLatestVersion) >= 0);
        }

        function getVersionStringForInfoDialog() {
            var ret = gettextCatalog.getString("Version: " ) + kUpgradeServiceInfo.app_version + " ";
            if (isCurrentSystemLatest()){
                ret += gettextCatalog.getString("(up-to-date)");
            }
            else{
                ret += gettextCatalog.getString("(update available)");
            }
            return ret;
        }

    return {
            getUpgradeInfo : getUpgradeInfo, // public for unit testing
            checkForUpdates : checkForUpdates,
            upgradeCommand : upgradeCommand,
            appName : kUpgradeServiceInfo.appName,
            getVersionStringForInfoDialog : getVersionStringForInfoDialog,
            isUpgrading : function() { return isUpgrading; },
            upgradeInfo : function() { return upgradeInfo; },
            currentVersion : function() { return kUpgradeServiceInfo.app_version; },
            declineUpgrade : function() { declinedAvailableUpdate = true; }
        };
    });
})();
