kQna.directive('kwpQnaEntry', function (qnaService, $timeout, kCef, THREAD_STATE, kdialog, gettextCatalog) {

    return {
        restrict : 'E',
        replace : false,
        scope : {
            actionableEntry:'@',
            clearableEntry:'@',
            announcement:'@',
            entryData:'=',
            onEntryClick:'&',
            showCopyAction:'@'
        },
        link: function (scope, elem, attrs) {
            scope.THREAD_STATE = THREAD_STATE;

            scope.contentElement = elem.find('.qnaContent');

            scope.threadData = scope.entryData.getThread();

            scope.threadNotPending = (scope.threadData.isDone() || scope.threadData.isFollowUp() || scope.threadData.isAnswered());

            scope.threadPending = !scope.threadNotPending;

            scope.threadCompleted = (scope.threadData.isDone() || scope.threadData.isAnswered());

            scope.mouseEnterOnEntry = function(){

                if(scope.clearableEntry){

                    elem.find('.announcementButtonsContainer').show();

                }
            };

            scope.mouseLeaveOnEntry = function(){

                if(scope.clearableEntry){
                    elem.find('.announcementButtonsContainer').hide();
                }
            };

            scope.entryClick = function(){

                scope.onEntryClick(scope.entryData);

            };

            scope.openUrl = function (url) {
                if(kCef.available) {
                    kCef.sendIPC('open-external-link', url);
                } else {
                    window.open(url,'_blank');
                }
            };

            scope.onRevealActionButtonsHover = function($event){
                $event.stopImmediatePropagation();
                if(isMobile() && $event.type === 'mouseover'){
                    return;
                }

                elem.find('.qnaEntryFooterTriggerIcon').hide();

                var currentHeight = scope.contentElement.height();

                if (scope.threadData.isFollowUp() || scope.threadData.isDone() || scope.threadData.isAnswered()){

                    scope.contentElement.addClass('mediumSizeContent');
                }
                else{
                    scope.contentElement.addClass('smallSizeContent');
                }

                scope.contentElement.height(currentHeight);

                elem.find('.entryActionButtons').show();
                elem.find('.entryActionButtons button').first().focus();
            };

            scope.onBlur = function(){
                if(isMobile()){
                    $timeout(function (){
                        var buttonInFocus = $(':focus').parents('.entryActionButtons').hasClass('entryActionButtons');
                        if(!buttonInFocus){
                            scope.actionButtonsOverlayOff();
                        }
                    },0);
                }
            };
            scope.actionButtonsOverlayOff = function(){

                elem.find('.entryActionButtons').hide();

                if (scope.threadData.isFollowUp() || scope.threadData.isDone() || scope.threadData.isAnswered()){

                    scope.contentElement.removeClass('mediumSizeContent');

                }
                else{

                    scope.contentElement.removeClass('smallSizeContent');

                }

                elem.find('.qnaEntryFooterTriggerIcon').show();

            };


            scope.setThreadAsDone = function($event){
                $event.stopImmediatePropagation();
                elem.find('.entryActionButtons').hide();

                qnaService.setQnaThreadAsDone(scope.threadData);
            };

            scope.setThreadAsFollowed = function($event){
                $event.stopImmediatePropagation();

                elem.find('.entryActionButtons').hide();

                qnaService.setQnaThreadAsFollow(scope.threadData);
            };

            scope.clear = function(){

                qnaService.setQnaThreadAsCleared(scope.threadData);

            };

            scope.delete = function() {

                kdialog.yesNoDialog({
                    yesMessage:gettextCatalog.getString("Yes, Remove"),
                    noMessage:gettextCatalog.getString("No"),
                    title: 'Remove an announcement',
                    message: 'Are you sure you want to remove the announcement?'
                }).then(function () {
                    qnaService.deleteAnnouncementThread(scope.threadData);
                });
            };
        },
        templateUrl: "../common/kQna/views/kwpQnaEntry.html"
    };
});