/* globals _ */
/* globals PromisesQueue */
/* globals Promise */

(function() {
    'use strict';

    function registerAdapter(kProducerActionsProvider) {
        var handler = {

            history : true,

            checkContentRelevancy : function(state,actionContent,api)
            {
                var result = false;

                if (_.get(state,'podcasting.type') === 'poll')
                {
                    var lastSelectPollState = api.getLastTransmittedAction('select-poll-state');

                    if (lastSelectPollState)
                    {
                        var lastSelectAThumbActionCuePoint = api.getLastTransmittedAction('select-a-thumb');
                        var lastRemoveSelectedThumbCuePoint = api.getLastTransmittedAction('remove-selected-thumb');
                        
                        if ((lastRemoveSelectedThumbCuePoint &&
                            lastRemoveSelectedThumbCuePoint.createdAt > lastSelectPollState.createdAt) ||
                            (lastSelectAThumbActionCuePoint && lastSelectAThumbActionCuePoint.createdAt > lastSelectPollState.createdAt))
                        {
                           result = true;
                        } else {
                            result = api.isNewContent('select-poll-state', actionContent);
                        }
                    }else
                    {
                        result = true;
                    }
                }
                return result;
            },
            updateState : function (state,content)
            {
                var modified = false;

                if (_.get(state,'podcasting.type') !== 'poll') {
                    _.set(state,'podcasting.type','poll');
                    modified = true;
                }

                return modified;
            }
        };

        kProducerActionsProvider.registerAction('select-poll-state','codeCuePoint.Code', handler);
    };

    angular.module('kPolls').config(registerAdapter);

})();