'use strict'
/**
 * Created by asafrobinovich on 12/2/14.
 */
kwpApp.factory('liveAnalyticsService', ['kalturaService', 'asyncLiveEntryInfoService', function (kalturaService, asyncLiveEntryInfoService){

    //watching,average bitrate,avg buffering
    var _liveData = {
        numberOfConnectedPeople : "0",
        averageBitrate : "0",
        averageBufferingTimePerMinute : "0",
        peakAudience : "0",
        plays : null,
        secondsViewed : null,
        startEvent : null,
        timestamp : "0",
        graphData : [],
        pointEvery : 10, // in seconds
        totalGraphPoints: 180
    };
    _liveData.minGraphTime = _liveData.pointEvery*_liveData.totalGraphPoints;
    var graceTime = 120;
    var lastRealGraphValue = 0;
    var maxTimeToQueryBack = 129600;
    var avgStatsTimeToQueryBack = 60;
    var avgStatsGraceTime = 2;
    var intervalTime = 15000;
    var MAX_ANALYTICS_TIME_BACKWARDS = configuration.maxAnalyticsBackwardQueryTime;

    // call the kaltura Service to get new values.
    var updateValues=function () {


        var p1=kalturaService.getAverageRates(configuration.MediaEntryId, -1 * avgStatsTimeToQueryBack, -1 * avgStatsGraceTime).then(function(result){

            if (result && result.objects && result.objects.length > 0) {

                var liveData = result.objects[0];

                if (liveData.bufferTime!==undefined) {
                    _liveData.averageBufferingTimePerMinute = parseFloat(liveData.bufferTime);
                    klog.debug("Updating bufferTime" + _liveData.averageBufferingTimePerMinute);
                }
                if (liveData.avgBitrate!==undefined) {
                    _liveData.averageBitrate   = parseInt(liveData.avgBitrate);
                    klog.debug("Updating averageBitrate" + _liveData.averageBitrate);
                }

            }else{

                klog.debug("getAverageRates returned with no results");

            }

        },function(err){

            klog.error("Live analytics call to getAverageRates failed");

        });

        var serverTime = Math.floor((kalturaService.getServerTime() /10000) * 10);

        var timeToQueryBack = (lastRealGraphValue <= 0) ? -1 * maxTimeToQueryBack : lastRealGraphValue - serverTime - graceTime;

        var p2=kalturaService.getLiveEntryTimeline(configuration.MediaEntryId, timeToQueryBack, -1 * graceTime).then(function(data) {

            if(data && data.length > 0) {
                klog.debug("getLiveEntryTimeline data was - " + data[0].data);
                var parsedData = parseData(data[0].data);
                if (parsedData.length > 0) {
                    lastRealGraphValue = parsedData[parsedData.length - 1].x;
                }

                var now = Math.floor((kalturaService.getServerTime() / 10000) * 10);

                var startTime = 0;
                if (kserviceInfo.mockAnalyticsAPI) {
                    startTime = now;
                } else {

                    var currentEntryInfo = asyncLiveEntryInfoService.getCurrentEntryInfo();
                    if(currentEntryInfo && currentEntryInfo.firstBroadcast){

                        startTime = Math.max(currentEntryInfo.firstBroadcast,now - MAX_ANALYTICS_TIME_BACKWARDS);

                    }else{
                        if (currentEntryInfo && currentEntryInfo.firstBroadcast) {
                            klog.warn("failed to get asyncLiveEntryInfoService.getCurrentEntryInfo().firstBroadcast. setting lastRealGraphValue to 0");
                        }
                        lastRealGraphValue = 0;
                    }
                    // means that firstBroadcast is null
                    if (startTime === null || startTime === 0) {
                        startTime = now;
                    }
                }

                var tmpData = mergeData(_liveData.graphData, parsedData);

                var graphStartTime = Math.min((startTime / 10) * 10, (tmpData.length > 0) ? tmpData[0].x : (startTime / 10) * 10);
                var graphEndTime = now - graceTime;

                if (graphEndTime - graphStartTime < _liveData.minGraphTime) {
                    graphStartTime = graphEndTime - _liveData.minGraphTime;
                }

                tmpData = fillDataGaps(tmpData, graphStartTime, graphEndTime, _liveData.totalGraphPoints);
                _liveData.graphData = trimStart(tmpData, _liveData.totalGraphPoints);
                _liveData.numberOfConnectedPeople = Math.max(_liveData.graphData[_liveData.graphData.length-1].y, 0);

                klog.debug("Updating number of connected people tp be "+_liveData.numberOfConnectedPeople);


            }else{
                klog.warn("getLiveEntryTimeline returned with bad result.cannot parse result.");
            }
        }, function(err){
            klog.log("Live analytics call to getLiveEntryTimeline failed");
            lastRealGraphValue = 0;
            _liveData.graphData = [];
        });

        Promise.all([p1,p2]).finally(function() {
            setTimeout( function() {
                updateValues();
            },intervalTime);
        })
    };

    // Get 2 data arrays,
    // delete from the first one all the data that is newer then the oldest point in the 2nd one.
    // append the data from the 2nd one to the first one.
    var mergeData = function(inOldData, inNewData) {

        // Select minimal time. We use it to know which slides to delete from inOldData.
        var newDataOldestPointTime = (inNewData.length > 0) ? inNewData[0].x : ((inOldData.length > 0) ? inOldData[inOldData.length-1] : 0);

        for (var i = inOldData.length -1; i >= 0; i--) {
            if (inOldData[i].x >= newDataOldestPointTime) {
                inOldData.pop();
            }
        }

        for (var j = 0; j < inNewData.length; j++) {
            inOldData[inOldData.length] = inNewData[j];
        }

        return inOldData;
    };

    /* gapsStart/gapsEnd can be Smaller/Bigger than start/end values of data and will be filled respectively
     *  with zeroes.mid points data will also be filled with zeroes in case missing.
     * */
    var fillDataGaps = function(data, fillGapsStart, fillGapsEnd, maxNullPointsToAdd){

        if (data === undefined) {
            return undefined;
        }

        if (data.length === 0){
            data.push({x:fillGapsEnd, y:-1});
        }

        var firstPoint = data[0].x;
        var lastPoint = data[data.length - 1].x;
        var curx, nextx, i = 1;
        // add points between lastPoint and firstPoint
        curx = firstPoint + 10;
        while (curx < lastPoint) {
            nextx = data[i].x;
            if (curx < nextx) {
                // need to add point here
                data.splice(i, 0, {
                    'x': curx,
                    'y': data[i-1].y
                });
            }
            curx += 10;
            i++;
        }
        // add points between fillGapsStart and firstPoint
        curx = firstPoint - 10;
        i = 0;
        while (fillGapsStart <= curx && i < maxNullPointsToAdd) {
            data.unshift({
                'x': curx,
                'y': -1
            });
            curx -= 10;
            i++;
        }
        // add points after lastPoint till fillGapsEnd
        curx = lastPoint + 10;
        while (curx <= fillGapsEnd) {
            data.push({
                'x': curx,
                'y': data[data.length - 1].y
            });
            curx += 10;
        }

        return data;
    };

    var parseData = function parseData(str) {
        var os = str.split(';');
        var objects = [];
        os.forEach(function (sLine) {
            if (sLine) {
                var parts = sLine.split(',');
                var time = parseInt(parts[0], 10);
                var liveViewers = parseInt(parts[1],10);
                var dvrViewers = parseInt(parts[2],10);
                var totalViewers= liveViewers+dvrViewers;
                objects.push({'x': time, 'y': totalViewers});
            }
        });

        //if (objects.length === 0){
        //    klog.warn("data for graph returned zero results");
        //    var now = new Date();
        //    objects.push({'x': now.getTime()/10000 * 10, 'y': 0});
        //}

        return objects;
    };

    // remove simulated points from the data if we have real points to replace them with
    var trimStart = function(data, MinGraphPoints) {
        if (!data || data.length === 0) {
            return data;
        }
        var pointsToTrim=0;
        while(data.length-pointsToTrim > MinGraphPoints && data[pointsToTrim].y === -1) {
            pointsToTrim++;
        }
        if (pointsToTrim>0) {
            data.splice(0, pointsToTrim);
        }
        return data;
    };

    updateValues();

    return {
        liveData : _liveData
    };
}]);
