/* globals _ */

(function () {

    "use strict";

    angular.module('k-assets-timeline').directive('assetsTimeline', function () {

        return {
            restrict: 'E',

            transclude: true,

            replace: true,

            templateUrl: '../common/kAssetsTimeline/directives/assetsTimelineView.html',

            controller: function ($scope, $rootScope, slidesService, gettextCatalog, onlineStatusService,
                                  shortcutService, $timeout, kAssetsTimelineService, kPollsService, ASSET_TYPE, POLL_STATE_STATUS) {

                var self = this;

                $scope.events = [];
                $scope.onlineAPI = onlineStatusService.onlineAPI();
                $scope.isReady = false;
                $scope.locked = {state: true};
                $scope.visibleSlides = 0;
                $scope.currentText = gettextCatalog.getString("Current");
                $scope.nextText = gettextCatalog.getString("Next");
                $scope.visibleSlidesCount = 1;
                $scope.ready = false;
                $scope.showPollWizard = false;
                $scope.centeredAssetIndex = -1;
                $scope.dragInProgress = false;
                
                var centeredAsset = null;
                var pivotX = 0;
                var element = null;
                var clientWidth = null;
                var iconNodes = null;
                var tileHeight = null;
                var eventWidth = null;
                var aspectRatio = null;
                var maximumSize = null;
                var minimumSize = null;

                $scope.currentEventIndex = function () {
                    return kAssetsTimelineService.getAssetIndexInTimelineArray(kAssetsTimelineService.currentTimelineAsset);
                };

                $scope.oneSlidePrev = CreateCommand("", "kicon-arrow", function () {
                    klog.info("Relative prev");
                    var prevAsset = kAssetsTimelineService.getPrevAsset(centeredAsset, false);
                    $scope.locked.state = false;
                    if (prevAsset) {
                        $scope.centerevent(prevAsset);
                    }
                });

                $scope.oneSlideNext = CreateCommand("", "kicon-arrow flat-button-rotated", function () {
                    klog.info("Relative next");
                    $scope.locked.state = false;
                    $scope.centerNextAsset();
                });

                $scope.centerNextAsset = function () {
                    var nextAsset = kAssetsTimelineService.getNextAsset(centeredAsset, false, false);
                    if (nextAsset) {
                        $scope.centerevent(nextAsset);
                    } else {
                        $scope.centerevent(centeredAsset, true);
                    }
                };

                $scope.centerAddedAsset = function ()
                {
                    var nextAsset = kAssetsTimelineService.getNextAsset(centeredAsset, true, false);
                    if (nextAsset) {
                        $scope.centerevent(nextAsset);
                    } else {
                        $scope.centerevent(centeredAsset, true);
                    }
                };

                $scope.uploadFocused = CreateCommand(gettextCatalog.getString(""), "", function () {
                    klog.info('event quick upload');
                    $scope.eventdblclick(centeredAsset);
                });

                $scope.syncTimelineCommand = CreateCommand(gettextCatalog.getString("Sync back"), "kicon-target", function () {
                    $scope.locked.state = true;
                    if (kAssetsTimelineService.currentTimelineAsset) {
                        setCenteredAsset(kAssetsTimelineService.currentTimelineAsset);
                        $scope.centerNextAsset();
                    }
                });

                var timelineAssetsLoaded = function (recalcLayoutOptions) {
                    var ev = [];

                    kAssetsTimelineService.timelineAssets.forEach(function (slide) {
                        ev.push(slide);
                    });

                    klog.info('{0} slide loaded!', ev.length);
                    $scope.events = ev;

                    // if the centered asset is deleted we want to make the one before it the centered one
                    if (kAssetsTimelineService.getAssetIndexInTimelineArray(centeredAsset) < 0){
                        var asset = kAssetsTimelineService.getAssetByIndex($scope.centeredAssetIndex < ev.length ? $scope.centeredAssetIndex : ev.length - 1);
                        $scope.centerevent(asset);
                    }

                    if (ev.length > 0) {
                        $scope.locked.state = true;
                        //refresh centered event only if doesn't exist OR if poll was added
                        if (kPollsService.getLocallyCreatedPollId()) {
                            $scope.centerAddedAsset();
                        } else if (!centeredAsset) {
                            $scope.centerNextAsset();
                        } else {
                            $scope.centerevent(centeredAsset, true, true); //no change in center event is needed
                        }
                    }

                    $scope.recalcLayoutOptions = recalcLayoutOptions;
                    $scope.safeApply(function () {
                        $timeout(function () {
                            // recalculate layout after dom was updated (since the recalculation rely on dom elements
                            recalcLayout($scope.recalcLayoutOptions);
                            shouldOpenAnAddPollWizard();
                            $scope.locked.state = false;
                        }, 200);
                    });
                    $rootScope.$broadcast('timelineloaded');
                };

                $scope.eventdblclick = function (event)
                {
                    if(!kAssetsTimelineService.isTimelineLocked() && kAssetsTimelineService.canBroadcastAsset(event)) {
                        if (kAssetsTimelineService.currentTimelineAsset !== event) {
                            klog.info(' eventdblclick! ' + event.title);
                            kAssetsTimelineService.currentTimelineAsset = event;
                            $scope.locked.state = true;
                        }
                    }
                };

                Number.prototype.clamp = function (min, max) {
                    return Math.min(Math.max(this, min), max);
                };

                function gauss(a, b, c, x) {
                    var gaussVal = a * Math.exp(-(x - b) * (x - b) / (2 * c * c));
                    return gaussVal.clamp(0, 1);
                }

                function calcEventMeasurements() {
                    element = document.getElementById('eventsdock-events');

                    //not loaded yet
                    if (!element) {
                        return;
                    }
                    clientWidth = element.clientWidth;
                    iconNodes = $(element).find('.eventsTimeline-event');

                    aspectRatio = slidesService.getSlidesAspectRatio();


                    maximumSize = element.clientHeight;
                    minimumSize = maximumSize / 2;
                    tileHeight = Math.min(clientWidth / 5, minimumSize);
                    eventWidth = (aspectRatio * tileHeight);
                }

                // options is an object.
                // supported options are:
                // {
                //  'elementSizes': 'same', // all elements are of the same size, any other value will use the co sinus form
                //  'renderAllSlides': true // should all slides be rendered, or just the visible ones
                // }
                function recalcLayout(options) {

                    calcEventMeasurements();
                    var renderAllSlides = options && options.renderAllSlides;
                    var sameSizeElements = options && options.elementSizes && options.elementSizes === 'same';
                    var halfClientWidth = clientWidth / 2;
                    var maxfallout = 0.1;
                    var x = renderAllSlides ? 0 : halfClientWidth - pivotX - eventWidth / 2;
                    $scope.visibleSlidesCount = Math.max(1, Math.floor(clientWidth / eventWidth + 0.5));

                    var centerX = clientWidth / 2;
                    try {

                        for (var i = 0; i < iconNodes.length; i++) {

                            var distance = ((x + eventWidth / 2) - centerX) / clientWidth;
                            var distanceInTiles = (((x + eventWidth / 2) - centerX)) / eventWidth;

                            //var fallout=scale1*Math.cos(distance * Math.PI*2);
                            var fallout = gauss(1, 0, maxfallout, distance);
                            var actualHeight = sameSizeElements ? minimumSize : tileHeight + fallout * (maximumSize - minimumSize);
                            var actualWidth = aspectRatio * actualHeight;

                            var xshift = (actualWidth - eventWidth) * distanceInTiles;
                            //      klog.debug("icon="+i+" distance="+distance+ " fallout="+fallout+" xshift="+xshift)
                            var left = (x + eventWidth / 2) - (actualWidth / 2) + xshift;

                            if (renderAllSlides || (left + actualWidth >= 0 && left < clientWidth)) {

                                // set the top margin of the image for the icon
                                // iconNodes[index].style.marginLeft='-'+aspectRatio*size*1/4+'10px';
                                var newCss = {
                                    'left': left + 'px',
                                    'width': actualWidth + 'px',
                                    'height': actualHeight + 'px',
                                    'top': (maximumSize - actualHeight) / 2 + 'px',
                                    'zIndex': sameSizeElements ? 100 : Math.round(100 * actualHeight + distanceInTiles),
                                    'display' : 'block'
                                };
                                if (renderAllSlides){
                                    newCss.transition = 'left 0.1s ease';
                                }

                                $(iconNodes[i]).css(newCss);

                                if (!$(iconNodes[i]).is(":visible")){
                                    $(iconNodes[i]).show({duration:0});
                                }
                            }
                            else {

                                $(iconNodes[i]).hide();
                            }

                            x += (eventWidth - 2 );
                        }
                    }
                    catch (e) {
                        alert(e);
                    }
                }

                // move event in location from to be at position to
                function moveElement(from, to) {
                    kAssetsTimelineService.changeAssetToPosition(from, to);
                }

                // options {updateServer: true / false}
                function updateAssetsState(options){
                    kAssetsTimelineService.updateTimeLineAssetsState(options);
                }

                function scrollToDraggedElement(index){

                    var widthToNode = -1*clientWidth/2;
                    for (var i = 0; i < index; i++) {
                        widthToNode += $(iconNodes[i]).width();
                    }

                    widthToNode = Math.max(widthToNode,0);

                    $("#eventsdock-events").scrollLeft(widthToNode);
                }

                function setCenteredAsset(asset){
                    centeredAsset = asset;
                    $scope.centeredAssetIndex = asset ? kAssetsTimelineService.getAssetIndexInTimelineArray(asset) : -1;
                }

                function scrollToDroppedElement(index){
                    $scope.locked.state = false;
                    var asset = kAssetsTimelineService.getAssetByIndex(index);
                    $scope.centerevent(asset, true);
                }

                function setDragInProgress(value){
                    $scope.dragInProgress = value;
                }

                kAssetsTimelineService.isReady().then(function () {
                    $scope.$watch(function () {
                        return kAssetsTimelineService.currentTimelineAsset;
                    }, function () {
                        $timeout(function () {
                            setCenteredAsset(kAssetsTimelineService.currentTimelineAsset);
                            if ($scope.locked.state) {
                                $scope.centerNextAsset();
                            }
                        }, 100);

                    });

                    var element = document.getElementById('eventsdock-events');
                    elementWatcher.onresize(element, function () {
                        $scope.centerevent(centeredAsset, true);
                    });

                    setCenteredAsset(kAssetsTimelineService.currentTimelineAsset);
                    timelineAssetsLoaded();
                    $scope.$on("AssetsTimelineChanged", function(event, args) {
                        timelineAssetsLoaded(args? args.recalcLayoutOptions : undefined);
                    });
                    $scope.$on("openPollWizard", function (event, pollData) {
                       if(pollData && pollData.pollId) {
                            openPollWizard(pollData.pollId);
                       }
                    });
                    $scope.$on("pollWizardClosed", function () {
                        $scope.showPollWizard = false;
                    });

                    $scope.isReady = true;

                });

                $scope.eventclicked = function (event, element) {
                    if (!$scope.dragInProgress) {
                        $scope.locked.state = false;
                        $scope.centerevent(event);
                    }
                };

                var animate = new Animate();

                /**
                 *
                 * @param event
                 * @param noanimation
                 * @param suspendLayoutUpdate suspend layout recalc, set true only if the UI is not yet synced with the events array. (f.e - when adding asset by other producer)
                 */

                $scope.centerevent = function (event, noanimation, suspendLayoutUpdate) {

                    calcEventMeasurements();
                    if ($scope.events.length === 0) {
                        return;
                    }

                    var value = kAssetsTimelineService.getAssetIndexInTimelineArray(event);
                    if (!noanimation &&
                        value === kAssetsTimelineService.getAssetIndexInTimelineArray(centeredAsset)) {
                        return;
                    }

                    var newScrollOffset = kAssetsTimelineService.getAssetIndexInTimelineArray(event) * (eventWidth - 2);

                    if (noanimation) {
                        pivotX = newScrollOffset;

                        if (!suspendLayoutUpdate) {
                            recalcLayout();
                        }
                    }
                    else {
                        animate.transition(pivotX, newScrollOffset,
                            function (val) {
                                pivotX = val;

                                recalcLayout();
                            }, 500, 15);

                    }
                    if (event) {
                        setCenteredAsset(event);
                    }
                };

                $scope.eventmouseleave = function (e) {


                };

                $scope.isCenteredEvent = function (event) {
                    if (centeredAsset) {
                        if (!_.isEmpty(centeredAsset.slideName)) {
                            return centeredAsset.slideName === event.slideName;
                        } else {
                            return centeredAsset.id === event.id;
                        }
                    } else {
                        //centered asset was removed
                        return false;
                    }

                };

                $scope.isElementDraggable = function(event) {
                    return (event.type === ASSET_TYPE.POLL && kPollsService.getPollState(event.id).status === POLL_STATE_STATUS.PRELIMINARY);
                };

                //auto display 'add poll' wizard if the current producer added a poll
                function shouldOpenAnAddPollWizard() {
                    //only open current poll wizard if we are not displaying the wizard now
                    if (kPollsService.getLocallyCreatedPollId() && !$scope.showPollWizard && $scope.events.length > 0) {
                        $scope.pollObject =
                        {
                            id: kPollsService.getLocallyCreatedPollId(),
                            state: 'active',
                            question: '',
                            answers: {}
                        };
                        kPollsService.setLocallyCreatedPollId(null);
                        $timeout(function () {
                            $scope.showPollWizard = true;
                        }, 1000);
                    }
                }

                function openPollWizard(pollId)
                {
                    var pollAsset = kAssetsTimelineService.getAssetById(pollId);
                    var pollText = pollAsset.text;
                    $scope.pollObject =
                    {
                        id: pollAsset.id,
                        state: pollText.state,
                        question: pollText.question,
                        answers: pollText.answers
                    };
                    $scope.showPollWizard = true;
                }

                shortcutService.bindCommand($scope, $scope.oneSlideNext, shortcutService.keyCodes.WEBCAST_KEYPRESS_TIMELINE_FORWARD);

                shortcutService.bindCommand($scope, $scope.oneSlidePrev, shortcutService.keyCodes.WEBCAST_KEYPRESS_TIMELINE_BACKWARDS);

                shortcutService.bindCommand($scope, $scope.syncTimelineCommand, shortcutService.keyCodes.WEBCAST_KEYPRESS_TIMELINE_GO_TO_CURRENT);

                shortcutService.bindCommand($scope, $scope.uploadFocused, shortcutService.keyCodes.WEBCAST_KEYPRESS_TIMELINE_SET_FOCUSED_AS_CURRENT);

                self.recalcLayout = recalcLayout;
                self.moveElement = moveElement;
                self.updateAssetsState = updateAssetsState;
                self.scrollToDraggedElement = scrollToDraggedElement;
                self.scrollToDroppedElement = scrollToDroppedElement;
                self.setDragInProgress = setDragInProgress;
            },
            link: function () {
            }
        };
    });

}());