'use strict';

kQna.factory('qnaExport', ['kCef' ,'sessionService', function (kCef,sessionService) {

    var translateEntry=function(entry) {

        var content = entry.getContent();

        content = content.replace(/"/g, '""');

        return ",\"" + content + "\"";

    };

    var translateThread=function(thread) {

        var threadEntries = thread.getEntries();

        if (threadEntries.length === 0) {

            return "";

        }

        var firstEntry = threadEntries[0];

        var csv = String.format("{0},{1},{2}",new Date(firstEntry.getTime()*1000), firstEntry.getOwner(), firstEntry.isAnnouncement() ? "Yes" : "No" );

        threadEntries.forEach(function(entry){

            csv+=translateEntry(entry);

        });


        return csv;

    };


    var exportCSVText=function(qnaThreadsList) {
        //var csv = "\uFEFFTime, User, Announcement?, Thread";
        var csv = "Time, User, Announcement?, Thread";

        qnaThreadsList.forEach(function (thread) {
            csv += "\n";
            csv += translateThread(thread);
        });

        return csv;
    };

    var exportToCSV=function(qnaThreadList) {

        var downloadFileName = sessionService.getTitle()+"-qna.csv";

        qnaThreadList.sort(function(a,b) {
            return a.getCreationTime()- b.getCreationTime();
        });
        var csv = exportCSVText(qnaThreadList);

        if (kCef.available) {
            kCef.sendIPC('saveAsDialog',downloadFileName,  "Comma-separated values",["csv"],"Select file for export").then(function (fileName) {
                if (fileName) {
                    kCef.sendIPC("saveFile",fileName, csv).then(function () {
                        klog.info("Exporting file completed.");
                    },function(err){
                        klog.error("Export to file failed."+err);
                    });
                }
            }).catch( function(f) {
                klog.error("Saving the filed caused an exception.");
            });
        }
        else{
	        download(downloadFileName, csv);
        }
    };


    return {
        exportQnaDataToFile : function(qnaThreadsList) {
            exportToCSV(qnaThreadsList);
        }
    };
}]);
