(function () {
    "use strict";

    function Directive() {
        function Controller($scope, kaPlayerViewControlsService) {

            var self = this;

            function switchView(buttonsViewId) {
                var viewMode = self.buttonsModes[buttonsViewId];
                viewMode.mainType = viewMode.mainType === 'presentation' ? 'video' : 'presentation';

                if (self.buttonsModes[buttonsViewId].isActive)
                {
                    updateServerOnChange();
                }
            }

            function unSelectAllViews()
            {
                self.buttonsModes.sbs.isActive = false;
                self.buttonsModes.pip.isActive = false;
                self.buttonsModes.single.isActive = false;
            }

            function selectView(buttonsViewId)
            {
                unSelectAllViews();

                self.buttonsModes[buttonsViewId].isActive = true;

                updateServerOnChange();

            }

            function updateServerOnChange()
            {
                var activeButtonServerId = getServerModeIdOfActive();

                if (activeButtonServerId)
                {
                    kaPlayerViewControlsService.changeViewById(activeButtonServerId);
                }
            }


            function getServerModeIdOfActive()
            {
                var serverModeId;

                var activeButtonId = _.findKey(self.buttonsModes,{isActive : true});

                if (activeButtonId)
                {
                    switch(activeButtonId)
                    {
                        case 'pip':
                            serverModeId = self.buttonsModes['pip'].mainType === 'video' ? 'pip-parent-in-large' : 'pip-parent-in-small';
                            break;
                        case 'sbs':
                            serverModeId = self.buttonsModes['sbs'].mainType === 'video' ? 'sbs-parent-in-left' : 'sbs-parent-in-right';
                            break;
                        case 'single':
                            serverModeId = self.buttonsModes['single'].mainType === 'video' ? 'parent-only' : 'no-parent';
                            break;
                    }
                }

                return serverModeId;
            }
            function updateModesFromServer(serverViewId)
            {
                unSelectAllViews();

                switch (serverViewId)
                {
                    case 'sbs-parent-in-right':
                        self.buttonsModes.sbs.isActive = true;
                        self.buttonsModes.sbs.mainType = 'presentation';
                        break;
                    case 'sbs-parent-in-left':
                        self.buttonsModes.sbs.isActive = true;
                        self.buttonsModes.sbs.mainType = 'video';
                        break;
                    case 'pip-parent-in-small':
                        self.buttonsModes.pip.isActive = true;
                        self.buttonsModes.pip.mainType = 'presentation';
                        break;
                    case 'pip-parent-in-large':
                        self.buttonsModes.pip.isActive = true;
                        self.buttonsModes.pip.mainType = 'video';
                        break;
                    case 'parent-only':
                        self.buttonsModes.single.isActive = true;
                        self.buttonsModes.single.mainType = 'video';
                        break;
                    case 'no-parent':
                        self.buttonsModes.single.isActive = true;
                        self.buttonsModes.single.mainType = 'presentation';
                        break;
                    default:
                        break;
                }
            }

            function init()
            {
                updateModesFromServer(kaPlayerViewControlsService.getCurrentViewId());
            }

            self.selectView = selectView;
            self.switchView = switchView;
            self.buttonsModes = { pip : {isActive :false, mainType : 'video'},'sbs' : {isActive :false, mainType : 'presentation'}, 'single' : {isActive :false, mainType : 'video'}};

            $scope.$on('kaPlayerViewControlsService.onPlayerViewModeIdChanged', function (event, args) {
                updateModesFromServer(args.playerViewModeId);
            });

            init();
        }

        function Link(scope, element, attrs, ctrls) {
            var ctrl = ctrls[0];

        }

        return {
            restrict: 'A',
            scope: {},
            require: ['kaPlayerViewWidget'],
            templateUrl: '../common/kaPlayerViewControl/directives/ka-player-view-widget.html',
            controller: Controller,
            link: Link,
            controllerAs: 'vm',
            bindToController: {}
        };
    }

    angular.module('kaPlayerViewControl').directive('kaPlayerViewWidget', Directive);

})();