/* globals _ */
/* globals PromisesQueue */
/* globals Promise */

(function() {
    'use strict';

    function registerAdapter(kProducerActionsProvider) {
        var handler = {
            checkContentRelevancy : function(state,actionContent,api)
            {
                var result = false;

                if (_.get(state,'podcasting.type') === 'thumb')
                {
                    var lastSelectAThumbActionCuePoint = api.getLastTransmittedAction('select-a-thumb');
                    var lastRemoveSelectedThumbCuePoint = api.getLastTransmittedAction('remove-selected-thumb');
                    var lastSelectPollState = api.getLastTransmittedAction('select-poll-state');

                    if (lastSelectAThumbActionCuePoint)
                    {
                        if ((lastRemoveSelectedThumbCuePoint &&
                            lastRemoveSelectedThumbCuePoint.createdAt > lastSelectAThumbActionCuePoint.createdAt) ||
                            (lastSelectPollState && lastSelectPollState.createdAt > lastSelectAThumbActionCuePoint.createdAt))
                        {
                           result = true;
                        } else {
                            result = api.isNewContent('select-a-thumb', actionContent);
                        }
                    }else
                    {
                        result = true;
                    }
                }
                return result;
            },
            updateState : function (state,content)
            {
                var modified = false;

                if (_.get(state,'podcasting.type') !== 'thumb') {
                    _.set(state,'podcasting.type','thumb');
                    modified = true;
                }

                return modified;
            }

        };

        kProducerActionsProvider.registerAction('select-a-thumb', 'codeCuePoint.Thumb', handler);
    };


    angular.module('k-deck-slides').config(registerAdapter);

})();