'use strict';
kQna.directive('kwpQnaLoadProgress',['gettextCatalog', function (gettextCatalog) {

    var controller = ['$scope','qnaService','kalturaService', function ($scope,qnaService,kalturaService) {

        $scope.isLoading=function() {
            return qnaService.isLoading();
        };

        $scope.NoMessages=function() {
            return qnaService.liveData.counters[qnaService.liveData.states.PENDING].total === 0;
        };
    }];

    return {
        restrict : 'E',
        replace:true,
        controller: controller,

        link: function (scope, elem, attrs) {


        },
        templateUrl: "../common/kQna/views/kwpQnaLoadProgress.html"
    };
}]);


