/* globals _ */

/**
 * Provides a service for getting polls state and data as a poll container object for
 * the main poll view and external poll screen displays
 */
(function () {
    "use strict";

    function Service(kAssetsTimelineService, kProducerActions, kPollsService ,
                     POLL_STATE_STATUS, POLL_STATE, localHardwareService)
    {
        var self = this;

        var lastPollContainer = {
            configuration : {
                currentView: 'sharedView'
            },
            userVote : {metadataId: null, answer: null, inProgress: false, canUserVote: false, isReady: false},
            pollData : {
                pollId: null,
                errorContent: null,
                content: null,
                showResults: false,
                showTotals: false,
                pollResults: null,
                showAnswers : false
            },
            canUserVote : function () {
                return lastPollContainer.userVote.canUserVote;
            }
        };

        function getUpdatedPollContainer(currentPollContainer)
        {
            lastPollContainer = currentPollContainer ? currentPollContainer : lastPollContainer;

            var currentPollAsset = kAssetsTimelineService.currentTimelineAsset;
            var currentPollState = kProducerActions.getState('actions.select-poll-state');
            var currentPollResults = kProducerActions.getState('actions.poll-results');

            if(currentPollState) {
                //update poll state
                _.extend(lastPollContainer.pollData,
                    {
                        pollId : currentPollAsset.id,
                        content : currentPollAsset.text,
                        showTotals: true, //producer should always see the totals and results is any exists
                        showAnswers : currentPollState.showAnswers,
                        showResults: currentPollState.showResults
                    }
                );
                _.extend(lastPollContainer.userVote,
                    {
                        canUserVote : currentPollState.status !== POLL_STATE_STATUS.FINISHED
                    }
                );
                if(currentPollState.showResults !== 'disabled') {
                    //make sure view configuration is set to 'shared' when showing results to users
                    _.extend(lastPollContainer.configuration,
                        {
                            currentView: 'sharedView'
                        }
                    );
                }
                //if initial state - make sure the reaults UI is being initialized
                if (currentPollState.status === POLL_STATE_STATUS.PRELIMINARY) {
                    _.extend(lastPollContainer.pollData,
                        {
                            pollResults: null
                        }
                    );
                }
                //if final results need to be taken
                else if (currentPollState.status === POLL_STATE_STATUS.FINISHED) {
                    //if current poll results state is not for this current poll, get results from history
                    if(!currentPollResults || currentPollResults.pollId !== currentPollState.pollId) {
                        currentPollResults = kPollsService.getPollResults(currentPollAsset.id);
                    }
                    //update poll results in needed
                    _.extend(lastPollContainer.pollData,
                        {
                            pollResults: currentPollResults
                        }
                    );
                }
            }

            postPollContainerChanged(lastPollContainer);
            return lastPollContainer;
        }
        
        function updatePollContainerResults(currentPollContainer, pollResults)
        {
            lastPollContainer = currentPollContainer ? currentPollContainer : lastPollContainer;

            _.extend(lastPollContainer.pollData,
                {
                    pollResults: pollResults
                }
            );

            postPollContainerChanged(lastPollContainer);
            return lastPollContainer;
        }

        function updatePollContainerData(currentPollContainer)
        {
            lastPollContainer = currentPollContainer ? currentPollContainer : lastPollContainer;

            var currentPoll = _.find(kPollsService.getActivePollsDataAsArray(), function (poll) {
                return poll.id === kAssetsTimelineService.currentTimelineAsset.id;
            });
            if(currentPoll) {
                _.extend(lastPollContainer.pollData,
                    {
                        content : currentPoll.text
                    }
                );
            }

            postPollContainerChanged(lastPollContainer);
            return lastPollContainer;
        }

        function postPollContainerChanged(lastPollContainer)
        {
            if(localHardwareService.getFullScreenWindow()) {
                var newPollState = JSON.stringify(lastPollContainer);
                ipcRenderer.send('pollUpdate', {type: 'poll', data: newPollState});
            }
        }

        self.getUpdatedPollContainer = getUpdatedPollContainer;
        self.updatePollContainerResults = updatePollContainerResults;
        self.updatePollContainerData = updatePollContainerData;

    }

    angular.module('kPolls').service('kPollContainer', Service);


})();