"use strict";

kQna.factory('QnaEntry',['metaDataGenerator','kalturaAPI','QandA_ResponseProfile','QandA_cuePointEnums', 'qnaSuspendFetching', 'THREAD_STATE','QandA_ResponseProfile_user',function(metaDataGenerator,kalturaAPI,QandA_ResponseProfile,QandA_cuePointEnums,qnaSuspendFetching, THREAD_STATE, QandA_ResponseProfile_user) {

    //TODO: remove this dependency.
    var myUserId = kserviceInfo.userId;

    var anonymousUsersHash = (function(){
        var _hash = [];
        var _counter = 0;
        return {
            getIDFromHash: function(hash){
                if(!_hash[hash]){
                    _counter = _counter+1;
                    _hash[hash] = _counter;
                }
                return _hash[hash];
            }
        };
    })();

    //Updates the metadata both locally and remotely on the server.
    function fillMissingMetaData(metaObj,metadataId,cuePoint){

        var missingData = {};

        if(metaObj.ThreadId === undefined){

            missingData.ThreadId = cuePoint.id;

        }
        if(metaObj.State === undefined){

            missingData.State = "Pending";

        }

        metaObj = metaDataGenerator.merge(metaObj,missingData);

        qnaSuspendFetching.suspend();
        kalturaAPI.metadataService.update(metadataId,metaDataGenerator.createXmlFromObj(metaObj)).then(function() {
            qnaSuspendFetching.resume();
        },function() {
            qnaSuspendFetching.resume();
        })

        return metaObj;

    }

    function QnaEntry(cuePoint,metaData) {

        var metadata = (metaData)?metaData:cuePoint.relatedObjects[QandA_ResponseProfile].objects[0];

        var metadataXml = metadata.xml;

        var metaDataId = metadata.id;

        this.thread = null;

        var metaObj = metaDataGenerator.createObjFromXml(metadataXml);

        if(metaObj.ThreadId === undefined || metaObj.State === undefined){

            metaObj = fillMissingMetaData(metaObj,metaDataId,cuePoint);

        }

        $.extend(cuePoint,{ metadata: metaObj, metadataId: metaDataId});

        this.userData = null;
        if (cuePoint.relatedObjects && cuePoint.relatedObjects[QandA_ResponseProfile_user])
        {
            this.userData = cuePoint.relatedObjects[QandA_ResponseProfile_user].objects[0];
        }

        delete cuePoint.relatedObjects;

        this.cuePoint = cuePoint;

        this.type = cuePoint.metadata.Type ? QandA_cuePointEnums[cuePoint.metadata.Type] : undefined;
    }

    QnaEntry.prototype.getId = function() {

        return this.cuePoint.id;

    };

    QnaEntry.prototype.setState = function(stateObj) {

        //klog.info("Q&A setState "+this.cuePoint.id+" " +JSON.stringify(stateObj));

        var _this=this;
        this.cuePoint.metadata = metaDataGenerator.merge(this.cuePoint.metadata,stateObj);

        var xml=metaDataGenerator.createXmlFromObj(this.cuePoint.metadata);

        qnaSuspendFetching.suspend();
        return kalturaAPI.metadataService.update(this.cuePoint.metadataId, xml)
            .then(function(res) {

                klog.info("Q&A: Change metadata {0} (id={1})for cuePoint {2} successful (UpdatedAt={3})",xml,res.id,_this.getId(),res.updatedAt);

                if (stateObj.State === THREAD_STATE.DELETED && _this.cuePoint.metadata.Type === 'Announcement') {
                    return _this._addingTagsToAnnotation()
                        .then(function () {
                            qnaSuspendFetching.resume();
                        });
                }
                else {
                    qnaSuspendFetching.resume();
                }

            }, function(err){
                qnaSuspendFetching.resume();
                klog.error("Q&A:  Change metadata FAILED!!! {0}",err);
            });
    };

    QnaEntry.prototype._addingTagsToAnnotation = function () {
        var _this = this;

        return kalturaAPI.qnaService.updateAnnotationCuePointTag(_this.cuePoint.id, _this.cuePoint.tags + ', ' + 'Annotation_Deleted')
            .then(function (res) {
                klog.info('Q&A update Annotation CuePoint Tag succeeded ' + JSON.stringify(res));
            })
            .catch(function (err) {
                klog.error('Q&A: Change CuePoint Tag FAILED! ' + JSON.stringify(err));
            });
    };

    QnaEntry.prototype.getContent = function() {

        return this.cuePoint.text;

    };

    QnaEntry.prototype.wasEntryCreatedByMe = function() {

        return this.cuePoint.userId === myUserId;

    };

    QnaEntry.prototype.getTime = function() {

        return this.cuePoint.createdAt;

    };

    QnaEntry.prototype.getUpdateTime = function() {

        return this.cuePoint.updatedAt;

    };


    QnaEntry.prototype.getOwner = function() {

        if (this.type === QandA_cuePointEnums.Question){

            // If this is an anonymous user, the ThreadCreatorId will be ##<anonymousUserName>HashSeparator<HashCode(can be negative)>##
            // To identify it it should
            // * start and end with ##
            // * has the string "HashSeparator"
            // * after the "HashSeparator" have only a number (or a negative number)

            var HashSeparator = "HashSeparator";
            var userId = this.cuePoint.metadata.ThreadCreatorId;
            var startEndToken = "##";

            if (userId.startsWith(startEndToken) && userId.endsWith(startEndToken)){
                var lastHashSeparatorIndex = userId.lastIndexOf(HashSeparator);
                if (lastHashSeparatorIndex >= 0){
                    var hashCode = userId.substring(lastHashSeparatorIndex + HashSeparator.length, userId.length - startEndToken.length);
                    if (!isNaN(hashCode) && isFinite(hashCode)){
                        return userId.substring(startEndToken.length, lastHashSeparatorIndex) + anonymousUsersHash.getIDFromHash(hashCode);
                    }
                }
            }

            let firstLast = this.getFirstLast();
            if (firstLast)
                return firstLast;
            return this.cuePoint.metadata.ThreadCreatorId;
        }
        if (this.cuePoint.userId && this.userData)
        {
            let firstLast = this.getFirstLast();
            if (firstLast)
                return firstLast;
        }
        return this.cuePoint.userId;

    };

    QnaEntry.prototype.getThreadId = function() {

        return this.cuePoint.metadata.ThreadId;

    };

    QnaEntry.prototype.isAnnouncement = function() {

        return this.type === QandA_cuePointEnums.Announcement;

    };

    QnaEntry.prototype.getState = function(){

        return this.cuePoint.metadata.State;

    };

    QnaEntry.prototype.getThreadCreatorId = function(){

        return this.cuePoint.metadata.ThreadCreatorId;

    };

    QnaEntry.prototype.getMetaDataId = function(){

        return this.cuePoint.metadataId;

    };

    QnaEntry.prototype.getMetaData = function(){

        return this.cuePoint.metadata;

    };

    QnaEntry.prototype.getType = function(){

        return this.cuePoint.metadata.Type;

    };

    QnaEntry.prototype.getStartTime = function(){

        return this.cuePoint.startTime;

    };

    QnaEntry.prototype.getEndTime = function(){

        return this.cuePoint.endTime;

    };

    QnaEntry.prototype.isAnswerOnAir = function(){

        return this.type === QandA_cuePointEnums.AnswerOnAir;

    };

    QnaEntry.prototype.isQuestion = function(){

        return this.type === QandA_cuePointEnums.Question;

    };

    QnaEntry.prototype.setThread = function(thread){

        this.thread = thread;
    };

    QnaEntry.prototype.getThread = function(thread){

        return this.thread;
    };

    QnaEntry.prototype.toString = function() {

        return JSON.stringify(this);

    };

    QnaEntry.prototype.getFirstLast = function() {
        if (this.userData)
        {

            let firstLast = '';
            if (this.userData.firstName)
            {
                firstLast += this.userData.firstName;
            }
            if (this.userData.lastName)
            {
                firstLast += ' ' + this.userData.lastName;
            }
            return firstLast;
        }
        return null;
    }

    return QnaEntry;

}]);
