    angular.module('kAPI').service('kAPIliveQnaService', function (kalturaAPICaller) {
        var tag='qna';
        return {

            list: function (mediaEntryId,responseProfileSystemName, from,upTo) {
                var params = {
                    service: "cuepoint_cuepoint",
                    action: "list",
                    'filter:objectType': 'KalturaAnnotationFilter',
                    'filter:cuePointTypeEqual': 'annotation.Annotation',
                    'filter:tagsLike':tag,
                    "filter:entryIdEqual": mediaEntryId,
                    'filter:orderBy': "+updatedAt",
                    'filter:isPublicEqual': 1
                };

                if (responseProfileSystemName) {
                    params['responseProfile:objectType']="KalturaResponseProfileHolder";
                    params["responseProfile:systemName"]=responseProfileSystemName;
                }

                if (from) {
                    params["filter:updatedAtGreaterThanOrEqual"] = from;
                }
                if(upTo){
                    params["filter:updatedAtLessThanOrEqual"] = upTo;
                }

                return kalturaAPICaller.call(params);
            },
            addAnnotationCuePoint: function(mediaEntryId,textAnnotation,parentId,videoTimeStamp,searchableOnEntry){
                klog.info("add annotation CuePoint entry=" + mediaEntryId);

                var params = {
                    service: "cuePoint_cuePoint",
                    action: "add",
                    "cuePoint:objectType": "KalturaAnnotation",
                    "cuePoint:entryId": mediaEntryId,
                    "cuePoint:text":textAnnotation,
                    "cuePoint:isPublic": 1
                };

                if (parentId) {
                    params["cuePoint:parentId"]=parentId;
                }

                if (videoTimeStamp){
                    params["cuePoint:startTime"] = videoTimeStamp;
                }

                if(searchableOnEntry && searchableOnEntry === true){
                    params["cuePoint:searchableOnEntry"] = 1;
                }
                else{
                    params["cuePoint:searchableOnEntry"] = 0;
                }


                return kalturaAPICaller.call(params);
            },
            updateAnnotationCuePoint: function(cuePointID,time){
                klog.info("updating annotation CuePoint entry=" + cuePointID);

                var params = {
                    service: "cuePoint_cuePoint",
                    action: "update",
                    id:cuePointID,
                    "cuePoint:objectType": "KalturaAnnotation",
                    "cuePoint:endTime": time
                };

                return kalturaAPICaller.call(params);

            },
            updateAnnotationCuePointTag:function(cuePointID, tagsToAdd){
               // klog.info("updating annotation CuePoint entry=" + cuePointID+" adding annotation cue point qna tag.");

                var params = {
                    service: "cuePoint_cuePoint",
                    action: "update",
                    id:cuePointID,
                    "cuePoint:objectType": "KalturaAnnotation",
                    "cuePoint:tags": tagsToAdd ? tagsToAdd : tag

                };

                return kalturaAPICaller.call(params);
            }


        };
    });

    angular.module('kAPI').service('kAPIliveQnaMockService', function (kalturaAPICaller) {
        return {
            list : function(id,from,to){

                var listPromise = new Promise(function(resolve, reject) {

                    var qnaData = [];

                    function randomString(){

                        var strings = [
                            "very short content!,,?",
                            "very long content very long content very long content very long content " +
                            "very long content very long content very long content very long content very " +
                            "long content very long content very long content very long content very long " +
                            "content very long content very long content very long content very long content very " +
                            "long content very long content ",
                            "standard content.Hello Can u please help me the ux is amazing but i am too stupid to understand",
                            "very weird chars ^@#$ 2$3242123)(*&^%$#@!)$*!(@#!)$@#"
                        ];


                        var selected = Math.floor((Math.random() * 4));

                        return strings[selected];

                    }
                    var size = 0;
                    if(from === undefined){

                        size = configuration.numberOfMockQnaThreads;
                    }
                    else{
                        if(kserviceInfo.mockQnaUpdates){
                            //Between 0 to 3 updates;
                            size = Math.floor((Math.random() * 4));
                        }
                    }
                    for(var i=0; i < size;i++){

                        var creationTime = 0;

                        if(from === undefined) {
                            creationTime = new Date(1).getTime() / 1000;
                        }
                        else{
                            creationTime = new Date().getTime() / 1000;
                        }
                        qnaData[i] = { userId:"david david david david david david david david" + i,text : randomString() , createdAt:creationTime,threadID:"123"};
                    }

                    resolve({objects:qnaData});

                    //We might want to simulate a random fail.
                    //reject(Error("It broke"));

                });

                return listPromise;
            }
        };
    });

