    angular.module('kAPI').service('kAPIProducerActionsService', function (kalturaAPICaller){
        return {
            createCodeCuepointAction : function(actionData) {
                if (!actionData || !_.every(['actionId', 'mediaEntryId', 'actionContent', 'actionName'], _.partial(_.has, actionData))) {
                    throw new Error('missing required properties');
                }

                klog.debug('new code cue point data {0}', actionData);


                klog.info("adding code cue point for action '" + actionData.actionName + "' (id '" + actionData.actionId + "')");

                var tags = actionData.actionName + ',id:' + actionData.actionId;

                var request = {
                    service: "cuePoint_cuePoint",
                    action: "add",
                    "cuePoint:objectType": "KalturaCodeCuePoint",
                    "cuePoint:code": '{"message" : "content inside partner data"}',
                    "cuePoint:partnerData": JSON.stringify(actionData.actionContent),
                    "cuePoint:entryId": actionData.mediaEntryId,
                    "cuePoint:tags": tags
                };

                return kalturaAPICaller.call(request);
            }
        };
    });


    angular.module('kAPI').service('kAPIcuePointService', function (kalturaAPICaller) {
        return {
            CUE_POINT_STATUS: {READY:1, DELETED:2, HANDLED:3, PENDING:4},
            get:function(id) {
                var params = {
                    service: "cuepoint_cuepoint",
                    action: "get",
                    "id": id
                };
                return kalturaAPICaller.call(params);
            },
            update: function (id,type,code) {
                var params = {
                    service: "cuepoint_cuepoint",
                    action: "update",
                    "id": id,
                    "cuePoint:objectType": type
                };
                if (code) {
                    params["cuePoint:code"] = code;
                }
                return kalturaAPICaller.call(params);
            },

            updateStatus: function (id,status) {
                var request = {
                    service: "cuepoint_cuepoint",
                    action: "updateStatus",
                    "id": id,
                    "status": status
                };
                return kalturaAPICaller.call(request);

            },
            list : function()
            {
                var requestData = null;
                if (arguments.length > 1)
                {
                    requestData = {
                        mediaEntryId : arguments[0],
                        types : arguments[1],
                        minimumUpdatedAt : arguments.length > 2 ? arguments[2] : null,
                        minimumCreatedAt : null,
                        tags : arguments.length > 3 ? arguments[3] : null
                    };
                }else
                {
                    requestData = arguments[0];
                }

                if (!_.every(['mediaEntryId','types'], _.partial(_.has,requestData)))
                {
                    throw new Error('missing required properties');
                }

                klog.debug("service: 'cuepoint_cuepoint', action : 'list',  for media id '{0}' data {1}", requestData.mediaEntryId,requestData);

                var request = {
                    service: "cuepoint_cuepoint",
                    action: "list",
                    'filter:objectType': 'KalturaCuePointFilter',
                    "filter:entryIdEqual": requestData.mediaEntryId,
                    'filter:orderBy': '+updatedAt'
                };

                if (requestData.types) {
                    request["filter:cuePointTypeIn"]=requestData.types;
                }

                if (requestData.minimumUpdatedAt) {
                    request["filter:updatedAtGreaterThanOrEqual"] = requestData.minimumUpdatedAt;
                }

                if (requestData.minimumCreatedAt) {
                    request["filter:createdAtGreaterThanOrEqual"] = requestData.minimumCreatedAt;
                }

                if (requestData.tags) {
                    request["filter:tagsMultiLikeOr"] = requestData.tags;
                }

                return kalturaAPICaller.call(request, true);
            },

            add: function (mediaEntryId, title, description,partnerData,tags) {
                klog.info("addCuePoint entry=" + mediaEntryId + " title=" + title);

                var params = {
                    service: "cuePoint_cuePoint",
                    action: "add",
                    "cuePoint:objectType": "KalturaThumbCuePoint",
                    "cuePoint:entryId": mediaEntryId,
                    "cuePoint:subType": 1   //slide and not chapter
                };

                if (title) {
                    params["cuePoint:title"] = title;
                }
                if (description) {
                    params["cuePoint:description"] = description;
                }
                if (partnerData) {
                    params["cuePoint:partnerData"] = _.isObject(partnerData) ? JSON.stringify(partnerData) : partnerData;
                }

                if (tags) {
                    params["cuePoint:tags"] = tags;
                }
                return kalturaAPICaller.call(params);
            },

            createCodeCuePoint : function(cuePointData)
            {
                if (!_.every(['mediaEntryId','code','tags'], _.partial(_.has,cuePointData)) && !_.every(['mediaEntryId','partnerData','tags'], _.partial(_.has,cuePointData)))
                {
                    throw new Error('missing required properties');
                }

                var requestCodeValue = null;
                if (!cuePointData.code && cuePointData.partnerData)
                {
                    requestCodeValue =  {message : 'content inside partner data'};
                }else
                {
                    requestCodeValue = cuePointData.code;
                }

                klog.info("add code cue point entry for media id " + cuePointData.mediaEntryId);
                klog.debug('new code cue point data {0}',cuePointData);

                var request = {
                    service: "cuePoint_cuePoint",
                    action: "add",
                    "cuePoint:objectType": "KalturaCodeCuePoint",
                    "cuePoint:code": _.isObject(requestCodeValue) ? JSON.stringify(requestCodeValue) : requestCodeValue,
                    "cuePoint:entryId": cuePointData.mediaEntryId,
                    "cuePoint:tags" : cuePointData.tags
                };

                if (cuePointData.partnerData) {
                    request["cuePoint:partnerData"] = _.isObject(cuePointData.partnerData) ? JSON.stringify(cuePointData.partnerData) : cuePointData.partnerData;
                }

                if (cuePointData.startTime) {
                    request["cuePoint:startTime"] = cuePointData.startTime;
                }

                if (cuePointData.systemName) {
                    request["cuePoint:systemName"] = cuePointData.systemName;
                }

                if (cuePointData.description) {
                    request["cuePoint:description"] = cuePointData.description;
                }
                return kalturaAPICaller.call(request);
            },
            updateCodeCuePoint : function(cuePointId, cuePointData) {

                var request = {
                    service: "cuepoint_cuepoint",
                    action: "update",
                    "id": cuePointId,
                    "cuePoint:objectType": "KalturaCodeCuePoint"
                };

                if (cuePointData.partnerData) {
                    request["cuePoint:partnerData"] = _.isObject(cuePointData.partnerData) ? JSON.stringify(cuePointData.partnerData) : cuePointData.partnerData;
                }
                if (cuePointData.code) {
                    request["cuePoint:code"] = _.isObject(cuePointData.code) ? JSON.stringify(cuePointData.code) : cuePointData.code;
                }

                return kalturaAPICaller.call(request);
            },
            createAnnotationCuePoint : function (cuePointData)
            {
                if (!_.every(['mediaEntryId','text','partnerData','tags'], _.partial(_.has,cuePointData))) {
                    throw new Error('missing required properties');
                }

                klog.info("add code annotation cue point for media id " + cuePointData.mediaEntryId);
                klog.debug('new annotation cue point data {0}',cuePointData);

                var request = {
                    service: "cuePoint_cuePoint",
                    action: "add",
                    "cuePoint:objectType": "KalturaAnnotation",
                    "cuePoint:entryId": cuePointData.mediaEntryId,
                    "cuePoint:isPublic": 1,
                    "cuePoint:searchableOnEntry" : 1,
                    "cuePoint:tags" : cuePointData.tags,
                    "cuePoint:partnerData" :
                        _.isObject(cuePointData.partnerData) ?
                            JSON.stringify(cuePointData.partnerData) : cuePointData.partnerData,
                    "cuePoint:text" :
                        _.isObject(cuePointData.text) ? JSON.stringify(cuePointData.text) : cuePointData.text
                };

                if (cuePointData.parentId)
                {
                    request[ "cuePoint:parentId"] = cuePointData.parentId;
                }

                return kalturaAPICaller.call(request);
            },
            updateAnnotationCuePoint : function (cuePointId, cuePointData)
            {
                if(!_.isObject(cuePointData.text) && !_.isObject(cuePointData.partnerData)) {
                    throw new Error('missing required properties');
                }

                klog.info("updating annotation CuePoint: " + cuePointId);

                var request = {
                    service: "cuePoint_cuePoint",
                    action: "update",
                    id:cuePointId,
                    "cuePoint:objectType": "KalturaAnnotation",
                };
                if(cuePointData.data) {
                    request["cuePoint:partnerData"] = _.isObject(cuePointData.partnerData) ?
                                                    JSON.stringify(cuePointData.partnerData) : cuePointData.partnerData;
                }
                if(cuePointData.text) {
                    request["cuePoint:text"] = _.isObject(cuePointData.text) ?
                                                JSON.stringify(cuePointData.text) : cuePointData.text;
                }

                if (cuePointData.parentId)
                {
                    request[ "cuePoint:parentId"] = cuePointData.parentId;
                }

                return kalturaAPICaller.call(request);
            },
            addCodeCuePoint: function (mediaEntryId,systemName, startTime, code, description, tags) {
                //** NOTE - this function is obslete, use function 'createCodeCuePoint' instead **//
                return this.createCodeCuePoint({
                    mediaEntryId : mediaEntryId,
                    systemName : systemName,
                    code:code,
                    description:description,
                    tags : tags,
                    startTime:startTime
                });
            },
            delete: function (id) {
                return kalturaAPICaller.call({
                    service: "cuePoint_cuePoint",
                    action: "delete",
                    "id": id
                }).then(function (result) {
                    klog.info("deleted cuepoint '" + id + "' ");
                }, function(err){
                    klog.error("failed to delete cue point #" + id + " " + err);
                });
            }
        };
    });

    angular.module('kAPI').service('kAPIPollsService', function (kalturaAPICaller){
       return {
           addPollObject: function () {
               klog.info("creating poll object");

               var params = {
                   service: "poll_poll",
                   action: "add",
                   "pollType": "SINGLE_ANONYMOUS"
               };

               return kalturaAPICaller.call(params);
           },

           resetVotes:function(pollId,answerIds) {

               klog.info("resetVotes for pollId [" + pollId + "] answerIds ["+answerIds+"]");

               var params = {
                   service: "poll_poll",
                   action: "resetVotes",
                   "pollId": pollId,
                   "answerIds": answerIds.join(',')
               };
               return kalturaAPICaller.call(params);
           },

           getPollVotes: function (pollId, answerIds) {
               klog.info("getPollVotes for pollId [" + pollId + "] ");

               var params = {
                   service: "poll_poll",
                   action: "getVotes",
                   "pollId": pollId,
                   "answerIds": answerIds
               };
               return kalturaAPICaller.call(params);
           },

           getVotes: function (pollId, answers) {
               var _this = this;
               var answerAsString = answers.join(",");
               return _this.getPollVotes(pollId, answerAsString).then(function (item) {
                       if (angular.isObject(item)) {
                           Promise.resolve(item);
                       } else
                       if (angular.isString(item)) {
                           return Promise.resolve(JSON.parse(item));
                       }
                   }, function (err) {
                       klog.error("Failed to get votes for pollId: " + pollId + " :" + err);
                       return Promise.reject(err);
                   })
                   .catch(function (err) {
                       klog.error("Error while getting votes for pollId: " + pollId + " :" + err);
                       return Promise.reject(err);
                   });
           },

           voteAsPlayer : function(mediaEntryId, pollId, metadataProfileId, answerId, userId){
               var params = {
                   "service": "cuePoint_cuePoint",
                   "action": "add",
                   "cuePoint:objectType": "KalturaAnnotation",
                   "cuePoint:entryId": mediaEntryId,
                   "cuePoint:isPublic": 0,
                   "cuePoint:searchableOnEntry": 0,
                   "cuePoint:tags": 'id:' + pollId
               };

               var params2 = {
                   service: "metadata_metadata",
                   action: "add",
                   metadataProfileId: metadataProfileId,
                   objectId: "{1:result:id}",
                   xmlData: '<metadata><Answer>' + answerId + '</Answer><UserId>' + userId + '</UserId></metadata>',
                   objectType: "annotationMetadata.Annotation"
               };

               kalturaAPICaller.startMultirequest();
               kalturaAPICaller.call(params);
               kalturaAPICaller.call(params2);
               return kalturaAPICaller.execMultirequest();
           }
       };
    });

    angular.module('kAPI').service('kAPIdocumentService', function (kalturaAPICaller, kAPIbaseEntryService) {
        return {
            addFromUploadedFile : function(context)
            {
                if (!context || !_.every(['entryId','uploadTokenId','fileName'], _.partial(_.has,context)))
                {
                    return Promise.reject({message : 'missing required arguments'});
                }

                //get current entry
                return kAPIbaseEntryService.get(context.entryId).then(
                    function (result)
                    {
                        var coEditors = result['entitledUsersEdit'] + "," + result['userId'];
                        var coPublishers = result['entitledUsersPublish'] + "," + result['userId'];
                        var pdfExtentions = '.pdf';
                        var conversionProfileId = context.fileName.substr(-pdfExtentions.length) === pdfExtentions ?
                            configuration.presentationConversionPdfProfileId : configuration.presentationConversionProfileId;

                        //add new document entry
                        return kalturaAPICaller.call({
                            service: "document_documents",
                            action: "addfromuploadedfile",
                            uploadTokenId : context.uploadTokenId,
                            "documentEntry:parentEntryId" : context.entryId,
                            "documentEntry:entitledUsersEdit" : coEditors,
                            "documentEntry:entitledUsersPublish" : coPublishers,
                            "documentEntry:objectType":"KalturaDocumentEntry",
                            "documentEntry:description" : context.fileName,
                            "documentEntry:type" : 10,
                            "documentEntry:conversionProfileId" : conversionProfileId,
                            "documentEntry:documentType" : 13
                        });
                    
                    },
                    function (err)
                    {
                        return Promise.reject(err);
                    }
                );
            },
            getDocumentEntryById: function (id) {

                return kalturaAPICaller.call({
                    service: "document_documents",
                    action: "get",
                    entryId: id
                    //This should be loaded from enums.,"statusln":"Hello world PHP."
                });
            }
        };
    });

