    angular.module('kAPI').service('kAPIliveAnalyticsServiceMock', function (kalturaAPICaller, kAPISystemService) {
        
        var mockTime=false;


        if (mockTime) {

            var firstTime = new Date();
            var pace = 1;
            var offset=1000;

             kAPISystemService.getServerTime = function () {
                    var now = new Date();

                    return offset+ firstTime.getTime() + (now.getTime() - firstTime.getTime()) * pace;
                };
        }



        var historyInMinutes=40*1; //2 hours
        var futureInMinutes=60*10; // 10 hours
        var interval=10*1000;

        var graphOffsetInTime=kAPISystemService.getServerTime()-historyInMinutes*60*1000;//convert into seconds

        var currentTimeInSeconds=graphOffsetInTime;
        var endTimeInSeconds=kAPISystemService.getServerTime()+futureInMinutes*60*1000;

        var generatedGraph=[];
        var index=0;

        while (currentTimeInSeconds<endTimeInSeconds) {
            var value=Math.max(0,Math.floor(index*10+1000*Math.sin(index/100*3.14*2)));
            var bufferTime=Math.random()*100;
            generatedGraph.push( { time: currentTimeInSeconds, audience:value, peakAudience:value, avgBitrate:(value+7), bufferTime:bufferTime });
            currentTimeInSeconds+=interval;
            index++;
        }

        var currentGraphIndex=function() {
            var index=Math.floor((kAPISystemService.getServerTime() -graphOffsetInTime)/interval); //10 seconds interval
            return Math.max(0,Math.min(generatedGraph.length-1,index));
        };

        return {
            get: function (id, action, reportType, fromTime, toTime, liveString) {

                var currentIndex=currentGraphIndex();

                if (action==="getreport") {

                    if (reportType==="ENTRY_TOTAL") {
                        var result = {
                            objects: [generatedGraph[currentIndex]]
                        };
                        return result;
                    }
                }
                if (action==="getevents") {

                    var data = "";
                    var current=Math.floor(kAPISystemService.getServerTime()/1000);
                    for (var i=0;i<=currentIndex;i++) {
                        var dataTime=Math.floor(generatedGraph[i].time/1000);
                        if (dataTime>=current+fromTime &&
                            dataTime<=current+toTime) {
                            data += dataTime + "," + generatedGraph[i].audience + ";";
                        }
                    }
                    return  [ { data : data }];
                }
                return null;
            }
        };
    });

    angular.module('kAPI').service('kAPISystemService', function (kalturaAPICaller) {

        var serverTimeDiff = 0;

        // when using jsonp we do not get http headers since the request is actually done by the browser
        // so this is a simple http
        function getTimeFromServer()
        {
            kalturaAPICaller.call({ service: "system", action: "getTime"}).then(
                function(data){
                    serverTimeDiff = new Date().getTime() - data*1000;
                    klog.debug('got ' + data + ' from server. Setting serverTimeDiff to '+ serverTimeDiff);
                });
        }
        var timer = setInterval(function(){getTimeFromServer();}, 10*1000);

        return {
            get: function (id) {
                return kalturaAPICaller.call({
                    service: "uiConf",
                    action: "get",
                    "id": id
                });
            },
            getServerTime : function(){
                return (new Date().getTime()) - serverTimeDiff;
            }
        };
    });

