'use strict';

kUI.directive('kwpGraph', function () {
    return {
        restrict: 'E',
        transclude: true,
        replace: true,
        scope: {
            graphData: '=',
            tooltips: '=',
            totalPoints: '='
        },
        link: function (scope, elem) {
            scope.data = {
                sliderVal : 0
            };

            var data = [],
                t = new Date(),
                maxGraphValue = 5,
                initData = [[],[]],
                lastThumbWidth = -1;

            if (scope.tooltips) {
                $("<div id='kwp-graph-tooltip'></div>").css({
                    position: "absolute",
                    display: "none",
                    border: "1px solid #fdd",
                    padding: "2px",
                    "background-color": "#fee",
                    opacity: 0.80
                }).appendTo("body");

                $("#placeholder").bind("plothover", function (event, pos, item) {

                    if (item) {
                        // show tooltips only for positive values
                        if (item.datapoint[1] >= 0) {
                            var time = new Date(item.datapoint[0]);
                            var count = item.datapoint[1];

                            $("#kwp-graph-tooltip").html(time.toLocaleTimeString() + ": " + count)
                                .css({top: item.pageY + 5, left: item.pageX - 70})
                                .fadeIn(200);
                        }
                    } else {
                        $("#kwp-graph-tooltip").hide();
                    }
                });
            }

            scope.$watch('graphData',function(){
                var updateSliderVal = false;
                // if the slider is in the max, and we get new data we want to move it to the new max.
                // in case there are less than totalPoints in the graph we set scope.data.sliderVal to zero
                if (parseInt(scope.data.sliderVal) === scope.getSliderMax()){
                    updateSliderVal = true;
                }
                data = [];
                maxGraphValue = 5;
                scope.graphData.forEach(function(point){
                    t.setTime(point.x * 1000);
                    data.push( [t.getTime(), point.y] );
                    if (point.y > maxGraphValue){
                        maxGraphValue = point.y;
                    }
                });

                $(elem).find("#kwpGraphSlider").attr('max', scope.getSliderMax());
                if (updateSliderVal) {
                    scope.data.sliderVal = scope.getSliderMax();
                }
                update(false);
            }, true);

            var plot = $.plot("#placeholder",initData, {
                series: {
                        shadowSize: 2,
                        color:"#76b55d",
                        lines :{
                            lineWidth : 4
                        }
                },
                yaxis: {
                    min: 0,
                        max: maxGraphValue,
                        color:"#403f40",
                        tickDecimals:0
                },
                xaxis: {
                    //this is the mode for display time
                    mode: "time",
                    timeformat: "%H:%M",
                    timezone: "browser",
                    color:"#141414"
                    },
                    grid: {
                        backgroundColor: "#323032",
                        borderWidth : 0,
                        hoverable:true
                }
            });

            $(elem).resize(function(){
                plot.resize();
                plot.setupGrid();
                plot.draw();
                update(true);
            });

            $(elem).find(".flot-text").css({color: "#999999",'font-size': "x-small"});

            scope.getSliderMax = function() {
                return Math.max(0, data.length - scope.totalPoints);
            };

            var update = function(force) {
                var selectorText = "#kwpGraphSlider::-webkit-slider-thumb";

                // What is -4? the width we get it of the entire element, but we want the thumb only,
                // so we remove a magic number for some spacing between the thumb and the slider
                var newThumbWidth = Math.min(
                    Math.round((scope.totalPoints/data.length) * $(kwpGraphSlider).width()),
                    $(kwpGraphSlider).width()-4);

                if (newThumbWidth !== lastThumbWidth){

                    for (var i=0; i < document.styleSheets.length; i++) {
                       if (document.styleSheets[i].title === "kwpGraphCSS"){
                           for (var j =0; j < document.styleSheets[i].cssRules.length; j++) {
                               if (document.styleSheets[i].cssRules[j].selectorText === selectorText) {
                                   document.styleSheets[i].removeRule(j);
                                   break;
                               }
                           }
                           document.styleSheets[i].addRule(selectorText, "width: " + newThumbWidth + "px !important");
                           break;
                       }
                    }
                    lastThumbWidth = newThumbWidth;
                }

                if ((scope.getSliderMax() === parseInt(scope.data.sliderVal)) || force) {
                    var newdata = [];

                    //30 minutes
                    newdata = [];
                    for (var k = parseInt(scope.data.sliderVal); k < parseInt(scope.data.sliderVal) + scope.totalPoints; k++) {
                        newdata.push(data[k]);
                    }

                    //and how you plot it again
                    plot.getOptions().yaxes[0].max = maxGraphValue;
                    plot.setData([newdata]);
                    plot.setupGrid();
                    plot.draw();
                }
            };

            scope.goToNowCommand = CreateCommand("", "kicon-target", function (){
                scope.data.sliderVal = scope.getSliderMax();
                update(true);
            });

            scope.showGoToNowBtn = function() {
                return (parseInt(scope.data.sliderVal) !== scope.getSliderMax());
            };

            scope.showSlider = function() {
                return (scope.graphData.length > scope.totalPoints);
            };

            update(true);

            scope.onSlider = function() {
                update(true);
            };
        },
        templateUrl: "../common/kUI/directives/partials/kwpGraphTemplate.html"
    };
});