kngLayout.addPanelDirective("kSmartPanelAligment", function() {

    return {
        scope: {
            slidesAspectRatio : '@',
            videoApectRatio : '@'

        },

        link: function ($scope) {

            var scope = $scope;
            scope.$watch('slidesAspectRatio', function (val) {
                recalcLayout();
            });
            scope.$watch('videoApectRatio', function (val) {
                recalcLayout();
            });

            var minVideoWidth=30; //10%

            var slideHeight = 0 ;
            var slideWidth = 0;
            var videoWidth = 0;
            var videoHeight = 0;
            //case 1
            var spaceY = 3; //3%
            var spaceX = 3;

            elementWatcher.onresize($scope.elem[0], function () {
                recalcLayout();
            });



            var calcSizes=function() {
                //klog.info("pane:" + clientWidth + " x " + clientHeight);


                //equations:
                // 3*spaceX + slideWidth + videoWidth  = 100;
                // 2*spaceY + slideHeight   = 100;
                // videoWidth>minVideoWidth
                //  slideWidth/slideHeight=SAR;
                //  videoWidth/videoHeight=VAR;

                var clientWidth = scope.elem[0].clientWidth;
                var clientHeight = scope.elem[0].clientHeight;


                var SAR = (scope.slidesAspectRatio) * (clientHeight / clientWidth);
                var VAR = (scope.videoApectRatio) * (clientHeight / clientWidth);


                spaceY = 0;
                spaceX = 1;

                //from the first equation above we can calculate the Z param
                var   Z = (minVideoWidth-100+3 * spaceX)/SAR + 100 -2 * spaceY;

                slideHeight = 100 - 2 * spaceY - Math.max(0,Z);
                slideWidth = slideHeight * SAR;
                videoWidth = 100 - slideWidth - 3 * spaceX;
                videoHeight = videoWidth / VAR;

                if (videoHeight > slideHeight) {
                    //readjust
                    spaceX = (100 - slideWidth - slideHeight * VAR) / 3;
                    videoWidth = 100 - slideWidth - 3 * spaceX;
                    videoHeight = videoWidth / VAR;
                }


            }

            function setPanelSize(panel,left,top,width,height) {
                panel.elem.css({
                    'visibility': 'visible',
                    opacity: 1,
                    'top': top + '%',
                    'left': left + '%',
                    'width': width + '%',
                    'height': height + '%'
                });
            }

            var recalcLayout = function () {

                var panels=scope.panels;

                if (panels.length === 0) {
                    return;
                }

                panels.sort(function(a, b){
                   return a.order - b.order;
                });

                if (panels.length > 1) {
                    calcSizes();
                    setPanelSize(panels[0],spaceX,(50-slideHeight/2),slideWidth,slideHeight);
                    setPanelSize(panels[1],slideWidth + 2 * spaceX,(50-slideHeight/2),videoWidth,slideHeight);
                }
                else {
                    setPanelSize(panels[0],0,0,100,100);
                }


            };


            return {
                recalcLayout: recalcLayout,
                childAdded: function (panel) {
                    /*
                    panel.elem.css( {
                        "-webkit-transition": "top 0.3s, left 0.3s, width 0.3s, height 0.3s",
                        "transition-timing-function": "ease"
                    });*/
                    scope.$watch( function() {
                         return panel.visible;
                    },function(a,b) {
                        recalcLayout();
                    });
                },
                childRemoved: function(panel) {
                    /*
                    panel.elem.css( {
                        "-webkit-transition": "top 0s, left 0s, width 0s, height 0s",
                        "transition-timing-function": "ease"
                    });*/
                }
            }
        }
    }
});