/* globals _ */
/* globals PromisesQueue */
/* globals Promise */

(function () {
    "use strict";
    function Provider(){

        function Service(kProducerActions, kalturaAPI, $q, $http, Polls_MetadataProfileSystemName,
                         Polls_ResponseProfileSystemName, POLL_STATE, POLL_STATE_STATUS, POLL_DATA_STATUS,
                         $rootScope, kaPlayerViewControlsService, PLAYER_VIEW_MODE, PLAYER_VIEW_STATE,pollEventNotificationTemplates,kEntryManagementService)
        {
            var self = this;
            var pollingHandler = null;
            var currentPollId = null;
            var currentPollStatus = null;
            var selectedPollAsset = null;
            /**
             * holds the poll id created by current producer.
             * indicates if to open poll wizard for newly fetched polls
             */
            var locallyCreatedPollId = null;
            var getMetadataProfilePromise = null;
            var metadataProfileId = 0;
            var lastPollResults = null;

            function init() {
                kProducerActions.subscribeStateChange(function() {
                    var selectPollState = kProducerActions.getState('actions.select-poll-state');
                    var podcastingType = kProducerActions.getState('podcasting.type');

                    if (podcastingType === "poll"){
                        var pollChanged = currentPollId !== selectPollState.pollId;
                        var pollStatusChanged = currentPollStatus !== selectPollState.status;

                        if (pollChanged || pollStatusChanged){
                            switch(selectPollState.status) {
                                case POLL_STATE_STATUS.IN_PROGRESS:
                                    startPollingVotes(selectPollState.pollId);
                                    break;
                                case POLL_STATE_STATUS.FINISHED:
                                    stopPollingVotes(selectPollState.ksHash);
                                    currentPollId = null;
                                    currentPollStatus = null;
                                    lastPollResults = null;
                                    break;
                            }
                        }

                        currentPollId = selectPollState.pollId;
                        currentPollStatus = selectPollState.status;
                    }
                });
            }

            /**
             * Add a new poll data
             * @param pollData an object of poll data. e.g:
             * {
             *      text: {
             *              state: 'active',
             *              question: 'is this a Q ?',
             *              answers: {
             *                  1: 'this is answer number 1',
             *                  2: this is answer number 2,
             *                  ...
             *              }
             *            }
             *
             * @returns {promise} the poll actionId (cue point id) is succeed or an error message if failed
             */
            function addPollData(pollData)
            {
                if (!pollData || !_.isObject(pollData)) {
                    return $q.reject('pollData must be of type object');
                }
                if (!pollData.text || !_.isObject(pollData.text)) {
                    return $q.reject('pollData object should contain a text property which holds an object');
                }
                if (!pollData.text.state) {
                    pollData.text.state = POLL_DATA_STATUS.ACTIVE;
                }

                return kProducerActions.createInstanceOrientedAction('poll-data', pollData);
            }

            function createPollObject()
            {
                return kalturaAPI.pollsService.addPollObject();
            }

            /**
             * Update poll results
             * @param pollResults an object of poll data. e.g:
             *              {
             *              totalVoters: 'is this a Q ?',
             *              answers: {
             *                  1: 10,
             *                  2: 20,
             *                  ...
             *             }
             *
             * @returns {promise} the poll actionId (cue point id) is succeed or an error message if failed
             */
            function updatePollResults(pollId, pollResults)
            {
                if (!pollResults || !_.isObject(pollResults)) {
                    return $q.reject('pollResults must be of type object');
                }

                if (pollResults.totalVoters === undefined || pollResults.answers[1] === undefined){
                    klog.error("pollResults is not in the correct format" + JSON.stringify(pollResults));
                }

                // we need to enrich the poll results with the poll id so we will be able to later fetch the right one when showing the poll
                var actionContent = _.extend({pollId : pollId},pollResults);
                kProducerActions.setStateOrientedAction('poll-results', actionContent);
            }

            /**
             * Update an existing poll data
             * @param pollData
             * @param pollId
             * @returns {promise} the poll actionId (cue point id) is succeed or an error message if failed
             */
            function updatePollData(pollData, pollId)
            {
                if (!pollData || !_.isObject(pollData)) {
                    return $q.reject('pollData must be of type object');
                }
                //at least one content should be provied for the update process
                if (!pollData.text || !_.isObject(pollData.text)) {
                    return $q.reject('pollData object should contain at least one property:' +
                                                                            '"text" or "data" which holds an object');
                }
                if (!pollId) {
                    return $q.reject('pollId is required');
                }

                return kProducerActions.updateInstanceOrientedAction(pollData, pollId);
            }

            /**
             * creates new poll state
             * e.g: {
             *         pollId: '1_32fff3',
             *         state:  'draft'
             *      }
             * @param pollState
             * @returns {promise}
             */
            function updateSelectedPollState(pollState)
            {
                if (!pollState || !_.isObject(pollState)) {
                    return $q.reject('pollState must be of type object');
                }
                if (!pollState.pollId || !pollState.status) {
                    return $q.reject('pollState object should contain a pollId and a state properties');
                }

                return kProducerActions.setStateOrientedAction('select-poll-state',pollState);
            }

            /**
             * get last poll state for given poll id
             * @param pollId
             * @returns {{pollId: *, showAnswers: boolean, showResults: (number|string), showTotals: (number|string), status: string}}
             */
            function getPollState(pollId)
            {
                //start with default poll state
                var selectedPollState =
                {
                    pollId : pollId,
                    showAnswers : false,
                    showResults : POLL_STATE.DISABLED,
                    showTotals : POLL_STATE.DISABLED,
                    status : POLL_STATE_STATUS.PRELIMINARY
                };
                var stateHistory = kProducerActions.getStateActionHistory('select-poll-state');
                if(!_.isEmpty(stateHistory)) {
                    //iterating from end of array to return the most updated state
                    _.forEachRight(stateHistory, function (pollStateObject) {
                        if(pollStateObject) {
                            try{
                                if(pollStateObject.pollId === pollId) {
                                    selectedPollState = pollStateObject;
                                    return false;
                                }
                            }catch (ex) {
                                //do nothing...
                            }
                        }
                    });
                }
                return selectedPollState;
            }

            /**
             * get last poll result object for given id
             * @param pollId
             * @returns {{pollId: *, answers: {}, totalVoters: number}}
             */
            function getPollResults(pollId)
            {
                //start with default poll result
                var selectedPollResults = null;
                var resultHistory = kProducerActions.getStateActionHistory('poll-results');
                if(!_.isEmpty(resultHistory)) {
                    //iterating from end of array to return the most updated result
                    _.forEachRight(resultHistory, function (pollResultObject) {
                        if(pollResultObject) {
                            try{
                                if(pollResultObject.pollId === pollId) {
                                    selectedPollResults = pollResultObject;
                                    return false;
                                }
                            }catch (ex) {
                                //do nothing...
                            }
                        }
                    });
                }
                return selectedPollResults;
            }

            /**
             * return active polls data object
             * @returns {*}
             */
            function getActivePollsDataAsArray()
            {
                var pollsArray = [];

                var pollsObjects =  kProducerActions.getInstanceActionsContent('poll-data');
                if(!_.isEmpty(pollsObjects)) {
                    _.each(pollsObjects, function (actionContent, key) {
                        if(actionContent.text.state === 'active') {
                            // returning relevant poll information such as id, type and questions/answers
                            pollsArray.push(_.extend({},actionContent,{id : key, type : 'poll'}));
                        }
                    });
                }
                return pollsArray;
            }

            // run multi-request on server to get aggregated data
            function getVotesFromServer() {

                // get number of answers
                if (kProducerActions.getInstanceActionsContent('poll-data')[currentPollId] === undefined) {
                    klog.warn("poll-data does not contain data for poll " + currentPollId + ' Not fetching data from server');
                    return;
                }

                if (kProducerActions.getInstanceActionsContent('poll-data')[currentPollId].text === undefined){
                    klog.warn("poll-data does not contain text object for poll " + currentPollId + ' Not fetching data from server');
                    return;
                }

                if (kProducerActions.getInstanceActionsContent('poll-data')[currentPollId].text.answers === undefined){
                    klog.warn("poll-data does not contain text.answers data for poll " + currentPollId + ' Not fetching data from server');
                    return;
                }

                var pollData = kProducerActions.getInstanceActionsContent('poll-data')[currentPollId];
                var answers = pollData.text.answers;
                var answerIds = [];
                _.each(answers, function (ansText, ansKey) {
                    answerIds.push(ansKey);
                });

                kalturaAPI.pollsService.getVotes(currentPollId, answerIds).then(function (res) {

                    var pollResults =
                        {
                            pollId: currentPollId,
                            answers: res.answerCounters,
                            totalVoters: res.numVoters
                        };

                        // Get last results by pollId
                        var stateHistory = kProducerActions.getStateActionHistory('poll-results');
                        stateHistory = _.chain(stateHistory).filter(function (content) {
                            return content.pollId === currentPollId;
                        }).last().value();

                        if (!angular.equals(lastPollResults, pollResults)) {
                            lastPollResults = pollResults;
                            $rootScope.$broadcast('poll-results-updated', lastPollResults);
                        }

                        // @todo - implement logic after we get the requierments
                        // only upate results with the server if there was an over 5% change
                        if (shouldUpdatePollResults(stateHistory, pollResults)) {

                            updatePollResults(currentPollId, pollResults);
                        }
                    }
                );
            }

            /**
             * check if to update poll results state.
             * @param stateHistory current poll results object know to application
             * @param newPollResults new poll results calculated from server
             * @returns {boolean} if to update current poll results
             */
            function shouldUpdatePollResults(stateHistory, pollResults)
            {
                var update = false;

                if(pollResults.totalVoters === 0) {
                    update = false;
                } else if(!stateHistory && pollResults) {
                    update = true;
                } else if (stateHistory.totalVoters * 1.05 < pollResults.totalVoters ||
                    stateHistory.totalVoters > pollResults.totalVoters) {
                    //checking threshold and negative change of total voters (in case of user answer changes)
                    update = true;
                } else if (stateHistory.totalVoters === pollResults.totalVoters) {
                    //in case there is no change in total voters count, check if no answers were changed by users
                    update = !angular.equals(stateHistory.answers, pollResults.answers);
                }

                return update;
            }

            function getActivePollResults() {
                return lastPollResults;
            }

            function startPollingVotes(pollId){

                lastPollResults = null;

                if (getMetadataProfilePromise === null){
                    getMetadataProfilePromise = kalturaAPI.metadataProfileService.list(Polls_MetadataProfileSystemName);
                }

                getMetadataProfilePromise.then(function(res){
                    metadataProfileId = res.objects[0].id;
                    currentPollId = pollId;
                    pollingHandler = setInterval(getVotesFromServer, 2000);
                }, function(res){
                    klog.warn("failed to get " + Polls_MetadataProfileSystemName + " with error " + JSON.stringify(res));
                    getMetadataProfilePromise = null;
                });
            }

            function stopPollingVotes(ksHash){
                if(pollingHandler !== null) {
                    clearInterval(pollingHandler);
                    pollingHandler = null;
                    //update poll results only if current producer initiate the 'stop poll' call
                    kalturaAPI.login().then(
                        function (ks)
                        {
                            if (getKSHash(ks) === ksHash) {
                                updatePollResults(currentPollId, lastPollResults);
                            }
                        }
                    );
                }
            }

            function changeSelectedPoll(pollAsset)
            {
                selectedPollAsset = pollAsset;
                var pollState = getPollState(pollAsset.id);
                //make sure player view mode is video only before updating selected poll
                updatePlayerViewMode();
                kProducerActions.setStateOrientedAction('select-poll-state', pollState);
            }

            function setSelectedPoll(pollAsset)
            {
                selectedPollAsset = pollAsset;
            }

            function getLocallyCreatedPollId()
            {
                return locallyCreatedPollId;
            }

            function setLocallyCreatedPollId(pollId)
            {
                locallyCreatedPollId = pollId;
            }

            /**
             * a complete poll is a poll with a Question and the first two answers.
             * also, a complete and valid poll have only consecutive answers
             * (if answers num. 1,2,4 are set - the poll is invalid)
             * @return boolean if the poll is complete
             */
            function isPollComplete (pollAsset)
            {
                var complete = false;
                var pollText = !_.isEmpty(pollAsset) ? pollAsset.text : {};
                if (pollText && _.isObject(pollText)) {
                    if (!_.isEmpty(pollText.question)) {
                        if (!_.isEmpty(pollText.answers)) {
                            //currently the poll has a question
                            complete = true;
                            //to check for min. two answers
                            var answerCount = 0;
                            //to check that all answers are consecutive
                            var sequenceStopped = false;
                            _.each(pollText.answers, function (answer) {
                                if (!_.isEmpty(answer)) {
                                    //we have valid answer after a blank one
                                    if (sequenceStopped) {
                                        complete = false;
                                    } else {
                                        answerCount++;
                                    }
                                } else {
                                    sequenceStopped = true;
                                }
                            });
                            //a poll is complete if the answers sequence didn't break and there are at least 2 answers
                            complete = complete && answerCount >= 2;
                        }
                    }
                }
                return complete;
            }

            function _debugDeleteResponseProfile() {
                return kalturaAPI.responseProfileService.list().then(function (res) {
                    var promies = [];
                    res.objects.forEach(function (item) {
                        if (item.systemName.indexOf(Polls_ResponseProfileSystemName) > -1 ) {
                            promies.push(kalturaAPI.responseProfileService.delete(item.id));
                        }
                    });
                    return Promise.all(promies);
                });
            }

            function _debugInstallResponseProfile(){
                self._debugDeleteResponseProfile().then(function () {
                    kalturaAPI.responseProfileService.addPollsResponseProfile(Polls_ResponseProfileSystemName, Polls_ResponseProfileSystemName).then(function (r) {
                        alert("response profile was created " + JSON.stringify(r));
                    });
                });
            }

            function _debugInstallMetadataProfile(){

                $http.get('../common/kPolls/resources/pollVoteCustomMetadataProfile.xsd').then(function (xsd) {
                    kalturaAPI.metadataProfileService.list(Polls_MetadataProfileSystemName).then (function (res) {

                        if (res.objects.length === 0) {

                            return kalturaAPI.metadataProfileService.add(Polls_MetadataProfileSystemName, xsd.data, "annotationMetadata.Annotation").then(function (res) {
                                alert(JSON.stringify(res));
                            });
                        } else {
                            var customMetadataProfile = res.objects[0];
                            customMetadataProfile.xsd = xsd.data;

                            return kalturaAPI.metadataProfileService.update(customMetadataProfile.id, customMetadataProfile.xsd).then(function (res) {
                                alert(JSON.stringify(res));
                            }, function(res){
                                alert('failed to install profile with error: ' + JSON.stringify(res));
                            });
                        }
                    });
                });
            }

            function updatePlayerViewMode()
            {
                //make sure player view mode is video only
                //not checking for kaPlayerViewControlsService.getCurrentViewId() !== PLAYER_VIEW_MODE.VIDEO_ONLY
                //since when webcast event doesn't have slides, player view mode is video only (unlocked)
                kaPlayerViewControlsService.changeViewById(PLAYER_VIEW_MODE.VIDEO_ONLY, PLAYER_VIEW_STATE.LOCKED);
            }

            kEntryManagementService.addPushServerNotificationsToClearOnArchive(pollEventNotificationTemplates);
            kEntryManagementService.addArchiveCustomAction( function() {
                klog.info("Archiving polls");

                var polls= kProducerActions.getInstanceActionsContent('poll-data');
                return _.map(polls,function(pollData, pollId) {
                    var answerIds = _.keys(pollData.text.answers);

                    return kalturaAPI.pollsService.resetVotes(pollId,answerIds).then(function (res) {
                        klog.info("Reset all votes of pollId " + pollId );
                        return $q.resolve();
                    });
                });
            });

            self.updatePollResults = updatePollResults; // @todo - remove this, it's public till we kill the simulator
            self.addPollData = addPollData;
            self.createPollObject = createPollObject;
            self.updatePollData = updatePollData;
            self.updatePollState = updateSelectedPollState;
            self.getPollState = getPollState;
            self.getPollResults = getPollResults;
            self.changeSelectedPoll = changeSelectedPoll;
            self.setSelectedPoll = setSelectedPoll;
            self.getLocallyCreatedPollId = getLocallyCreatedPollId;
            self.setLocallyCreatedPollId = setLocallyCreatedPollId;
            self.isPollComplete = isPollComplete;
            self.getActivePollsDataAsArray = getActivePollsDataAsArray;
            self._debugInstallMetadataProfile = _debugInstallMetadataProfile;
            self._debugInstallResponseProfile = _debugInstallResponseProfile;
            self._debugDeleteResponseProfile = _debugDeleteResponseProfile;

            ////@todo - remove sample code below
            //$rootScope.$on('poll-results-updated', function (e, res) {
            //    alert(JSON.stringify(res));
            //});
            init();
        }

        this.$get = function ($injector) {

            return $injector.instantiate(Service);
        };
    }

    angular.module('kPolls').provider('kPollsService', Provider);

})();