(function () {
    "use strict";

    function Directive() {

        function Controller()
        {

        }

        function link(scope, element, attrs, ctrls)
        {
            element.disableSelection();

            attrs.$observe('kSortable', function(sortableOn) {
                if (sortableOn === 'true'){
                    element.sortable();
                }
                else if (element.hasClass('ui-sortable')) {
                    element.sortable('destroy');
                }
            });
        }

        return {
            restrict: 'A',
            controller: Controller,
            require: ['kSortable'],
            link: link
        };
    }

    angular.module('k-assets-timeline').directive('kSortable', Directive);
}());