/* globals _ /
 / globals klog /
 / globals PromisesQueue /
 / globals Promise */

(function (){
    "use strict";

    function Directive(kPollsService, $rootScope)
    {

        function Controller($scope)
        {
            $scope.addPoll = function ()
            {
                    $scope.addPollEnabled = false;
                    $scope.isLoading = true;

                    //creating an empty poll
                    kPollsService.addPollData({text: {}}).then(
                        function (poll) {
                            //update that current producer created a new poll (need to open the 'add new poll' wizard)
                            kPollsService.setLocallyCreatedPollId(poll.id);
                            $rootScope.$broadcast("pollAdded", {prevIndex: $scope.prevIndex, poll: poll});
                        },
                        function (err) {
                            $scope.isLoading = false;
                            $scope.addPollEnabled = true;
                            klog.error(err);
                        }
                    );
            };

            $rootScope.$on("pollWizardOpened", function () {
                $scope.isLoading = false;
            });

            $rootScope.$on("pollWizardClosed", function () {
                $scope.addPollEnabled = true;
            });
        }

        function Link(scope, element, attrs, ctrls)
        {
            scope.addPollEnabled = true;
            scope.isLoading = false;
        }

        return {
            restrict: 'E',
            templateUrl: '../common/kPolls/directives/k-poll-add.html',
            scope: {'prevIndex':'='},
            link: Link,
            controller: Controller,
            controllerAs: 'vm'
        };

    }


    angular.module('kPolls').directive('kPollAdd', Directive);


})();
