kwpApp.controller('onlineController', ['$scope', "onlineStatusService", "gettextCatalog", 'kCef',function ($scope, onlineStatusService, gettextCatalog,kCef) {

  //  $scope.template = "./partials/onlineStatus.html";
    $scope.onlineAPI = onlineStatusService.onlineAPI();
    $scope.canClose=function() {
        return kCef.available;
    }
    $scope.exitCommand = CreateCommand(gettextCatalog.getString("Close application"), "", function () {
        window.close();
    });
}]);


