/* globals kserviceInfo */
/* globals Promise */

(function() {
    'use strict';



    angular.module('kAPI').service('kalturaService',  function( $injector, $http, kalturaAPI, gettextCatalog, $interval, $timeout, $q) {
        var sessionSrv;

        function delay(fn,t) {
            return new Promise( function(s,f) {
                $timeout( function() {
                    fn().then(s).catch(f);
                },t);
            });
        }



        var oneMinute=60*1000;
        var check_ks_expiry_in_progress=false;
        function check_ks_expiry() {
            klog.debug("check_ks_expiry");
            kalturaAPI.login().then(function () {
                if (kserviceInfo.ks_expiry &&
                    (new Date().getTime() > kserviceInfo.ks_expiry.getTime() - 4 * 60 * oneMinute)) { //check 4 hours min.

                    klog.error("KS has expeired!! ({0} vs {1})  check_ks_expiry_in_progress={2}", new Date(),
                        kserviceInfo.ks_expiry, check_ks_expiry_in_progress);

                    if (!check_ks_expiry_in_progress) {
                        check_ks_expiry_in_progress = true;

                        var kdialogService = $injector.get('kdialog');
                        kdialogService.alert(gettextCatalog.getString("Application session has expired<br>Please re-launch"), false).then(function () {
                            check_ks_expiry_in_progress = false;
                        }, function () {
                            check_ks_expiry_in_progress = false;
                        });
                    }
                }
            });
        }

        //return MMMDDYYYY date from unix timestamp
        function getDateFromTimestamp(timestamp)
        {
            var a = new Date(timestamp * 1000);
            var months = ['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sep','Oct','Nov','Dec'];
            var year = a.getFullYear();
            var month = months[a.getMonth()];
            var date = a.getDate();
            var time = month + ' '+date + ' '+year;
            time=a.toDateString();
            return time;
        }

        check_ks_expiry();
        $interval(check_ks_expiry,10*oneMinute);//check every 10 min.

        return {

            isLive: function (mediaEntryId) {
                return kalturaAPI.login().then( function () {
                    return  kalturaAPI.liveStreamService.isLive(mediaEntryId);
                },function(fail) {
                    return Promise.reject(fail);
                });
            },

            getLiveEntryInfo: function(entryId) {

                return kalturaAPI.login().then( function () {

                    var liveEntryInfo = kalturaAPI.baseEntryService.get(entryId);

                    return liveEntryInfo;
                },function(fail) {
                    return Promise.reject(fail);
                });
            },


            updateLiveEntryInfo: function(entryId, parametersObj) {
                return kalturaAPI.login().then( function () {

                   return  kalturaAPI.liveStreamService.update(entryId, parametersObj);

                }, function(fail) {
                    return Promise.reject(fail);
                });
            },

            getDocumentSlides : function(docEntryId) {
                klog.info("kalturaService.getDocumentSlides(): getDocumentSlides docEntryId=" + docEntryId);

                if (docEntryId) {
                    if (docEntryId==="MOCKSLIDES") {
                        return Promise.resolve({slidesUrl: configuration.mockSlides, document:{ description: "mock" }});
                    }
                    return kalturaAPI.login().then (function(result) {
                        var url=null;
                        kalturaAPI.startMultirequest();
                        return kalturaAPI.execMultirequest([
                            kalturaAPI.documentService.getDocumentEntryById(docEntryId),
                            kalturaAPI.flavorAssetService.list(docEntryId,"images"),
                            kalturaAPI.flavorAssetService.list(docEntryId,"source"),
                            kalturaAPI.flavorAssetService.getUrl("{2:result:objects:0:id}",'metadata.xml',true)
                                .then( function(metadataUrl) {
                                    url = metadataUrl;
                            })]).then(function(results){
                                klog.info("base url for "+docEntryId+ " "+url);
                                return Promise.resolve({ slidesUrl:url, document:results[0], fileExt: results[2].objects[0].fileExt});
                        });

                    });
                }else
                {
                    return Promise.reject();
                }
            },

            deleteRecordedEntry : function(mediaEntryId) {
                return kalturaAPI.liveStreamService.update(mediaEntryId, { recordedEntryId: null, redirectEntryId: null });
            },

            clearPushServerNotifications:function(entryId,pushServerNotificationsToClear) {
                var promises=[];
                var params =  {
                    entryId: entryId
                };
                _.each(pushServerNotificationsToClear,function(templateName) {
                    promises.push(kalturaAPI.eventNotificationTemplate.sendCommand("CLEAR_QUEUE",templateName,params).catch(function(err) {
                        klog.info("Exception while calling  eventNotificationTemplate:CLEAR_QUEUE "+JSON.stringify(err));
                        return $q.resolve();
                    }));
                });
                return $q.all(promises);

            },

            getServerTime: function(){

                return kalturaAPI.getServerTime();
            },

            getAverageRates : function(entryID, start, end){
                return kalturaAPI.login().then(function() {
                    return kalturaAPI.liveAnalyticsService.get(entryID, "getreport", "ENTRY_TOTAL", start, end, 1);
                });
            },

            getLiveEntryTimeline : function(entryID, start, end){
                return kalturaAPI.login().then(function() {
                    return kalturaAPI.liveAnalyticsService.get(entryID, "getevents", "ENTRY_TIME_LINE", start, end);
                });
            },

            updateVODEntryAfterArchive : function (vodEntryId,liveEntry){
                if(vodEntryId) {
                    return kalturaAPI.login().then(function() {
                        return kalturaAPI.baseEntryService.get(vodEntryId).then(function (vodEntry) {
                            var vodCreationTime = vodEntry.createdAt;
                            var vodCreateDate = getDateFromTimestamp(vodCreationTime);
                            var updateParams = {
                                "baseEntry:name" : vodEntry.name + ' ' + vodCreateDate,
                                "baseEntry:description" : liveEntry.description,
                                "baseEntry:tags" : liveEntry.tags,
                                "baseEntry:displayInSearch" : kalturaAPI.baseEntryService.ENTRY_DISPLAY_IN_SEARCH_TYPE.PARTNER_ONLY
                            };
                            return kalturaAPI.baseEntryService.update(vodEntryId, updateParams);
                        }).catch(function (reason) {
                            return Promise.reject(reason);
                        });
                    });
                } else {
                    return Promise.resolve('No VOD entry is linked to this live entry');
                }
            }
        };
    });
})();


