//@todo - testing code - remove
(function () {
    "use strict";

    function Directive() {
        function Controller(kPollsService,$scope,$timeout, $q, kProducerActions, kalturaAPI, Polls_MetadataProfileSystemName) {

            var self = this;

            function createEmptyPollText() {
                return {
                    question: '',
                    answers: {
                        1: '',
                        2: '',
                        3: '',
                        4: '',
                        5: ''
                    }
                };
            }


            function refreshPollsList()
            {
                var polls = kProducerActions.getInstanceActionsContent('poll-data');

                var newPollsList = [];
                var index = 0;
                _.each(polls, function(poll, id)
                {
                    index++;
                    var text = poll.text || createEmptyPollText();

                    newPollsList.push({id : id, text : text,  name : index,  tooltip : text.question || '{missing}' } );
                });

                self.polls = newPollsList;
                syncCreateContent();
            }

            function syncCreateContent()
            {
                var poll = self.create.selectedPollId ? _.find(self.polls, {id : self.create.selectedPollId}) : null;

                if (poll)
                {
                    _.extend(self.create,
                        {  text : _.cloneDeep(poll.text)});
                }else
                {
                    _.extend(self.create,
                        {text : createEmptyPollText()});
                }

            }


            function refreshSelectPollStatus()
            {
                var selectPollState = kProducerActions.getState('actions.select-poll-state');
                var podcastingType = kProducerActions.getState('podcasting.type');

                if (podcastingType === 'poll' && selectPollState)
                {
                    self.currentyShownPoll = {id : selectPollState.pollId, status : selectPollState.status, showAnswers : selectPollState.showAnswers,  allowVoting : selectPollState.allowVoting, showResults : selectPollState.showResults, showTotals : selectPollState.showTotals};
                }else
                {
                    self.currentyShownPoll = null;
                }

            }
            function init()
            {
                kProducerActions.isReady().then(function()
                {
                    kProducerActions.subscribeInstanceChange(refreshPollsList);

                    kProducerActions.subscribeStateChange(function() {
                        refreshSelectPollStatus();
                    });

                    refreshSelectPollStatus();
                    refreshPollsList();

                });

                $scope.$watch('vm.create.selectedPollId',function()
                {
                    syncCreateContent();
                })


                syncCreateContent();

            }
            function createPoll()
            {
                self.create.message = 'submitting...';
                var promise = null;

                var documentEntryId = kProducerActions.getState('actions.select-deck-document.documentEntryId');

                var content = { text : self.create.text};
                if (self.create.selectedPollId)
                {
                    promise = kPollsService.updatePollData(content, self.create.selectedPollId);
                }else {
                    promise = kPollsService.addPollData(content);
                }

                promise.then(function(result)
                {
                    self.create.message = 'done';

                    $timeout(function()
                    {
                        if (self.create.message === 'done')
                        {
                            self.create.message = '';
                        }
                    },2000);
                    if(!self.create.selectedPollId){
                        self.create.selectedPollId = result.id;
                    }
                },function(reason)
                {
                    self.create.message = reason;
                });
            }

            function voteAsPlayer()
            {
                //alert(self.pollResults.selectedPollId + ' ' + self.voteAsPlayerData.vote);
                kalturaAPI.metadataProfileService.list(Polls_MetadataProfileSystemName).then(function(res){
                    kalturaAPI.pollsService.voteAsPlayer(configuration.MediaEntryId, self.pollResults.selectedPollId, res.objects[0].id, self.voteAsPlayerData.vote, "myUserId");
                });
            }

            function updateResults()
            {
                if (self.pollResults.selectedPollId )
                {
                    self.pollResults.message = 'submitting...';

                    kPollsService.updatePollResults(self.pollResults.selectedPollId, self.pollResults.text);

                    self.pollResults.message = 'done';

                    $timeout(function () {
                        if (self.pollResults.message === 'done') {
                            self.pollResults.message = '';
                        }
                    }, 2000);
                }else
                {
                    self.pollResults.message = 'aborted. please select poll...';
                }
            }


            function updateStatus() {
                if (self.pollStatus.selectedPollId && self.pollStatus.state)
                {
                    self.pollStatus.message = 'submitting...';

                    kPollsService.updatePollState({
                        'pollId': self.pollStatus.selectedPollId,
                        'status': self.pollStatus.status,
                        showAnswers : self.pollStatus.showAnswers,
                        showResults: self.pollStatus.showResults,
                        showTotals: self.pollStatus.showTotals
                    });


                    self.pollStatus.message = 'done';

                    $timeout(function () {
                        if (self.pollStatus.message === 'done') {
                            self.pollStatus.message = '';
                        }
                    }, 2000);
                }else
                {
                    self.pollStatus.message = 'aborted. please select poll and state...';
                }

            }

            self.updateResults = updateResults;
            self.currentyShownPoll = null;
            self.createPoll = createPoll;
            self.updateStatus = updateStatus;
            self.voteAsPlayer = voteAsPlayer;
            self.create = {selectedPollId : null, message : '', text : null};
            self.pollStatus = {
                message : '',
                selectedPollId : '',
                state : 'show',
                availableResults : [{id : 'disabled', name: 'disabled'},{id : 'percentages', name: 'Percentages'}],
                showResults : 'disabled',
                availableTotals : [{id : 'disabled', name: 'disabled'},{id : 'numbers', name: 'Numbers'}],
                status : 'preliminary',
                showTotals : 'disabled',
                showAnswers : false,
                allowVoting : false};
            self.pollResults = {message : '', selectedPollId : '', text : {
                totalVoters: '',
                answers: {
                    1: '',
                    2: '',
                    3: '',
                    4: '',
                    5: ''
                }}};
            self.polls = [];
            self.availableStatus = [{id : 'preliminary', name: 'Preliminary'},{id : 'inProgress', name : 'In Progress'}, ,{id : 'finished', name : 'Finished'}];
            self.voteAsPlayerData = {vote: 1};

            init();
        }

        function Link(scope, element, attrs, ctrls) {

        }

        return {
            restrict: 'A',
            scope: {},
            templateUrl: '../common/kPolls/directives/k-polls-simulator.html',
            controller: Controller,
            link: Link,
            controllerAs: 'vm',
            bindToController: {
            }
        };
    }

    angular.module('kPolls').directive('kPollsSimulator', Directive);

})();