'use strict';

kUI.directive('kuiCopyToClipboard',function ($compile,$interval,$filter, $timeout) {

    var COPIED_ICON_TIMEOUT_DELAY = 5000;

    return {
        restrict : 'E',
        transclude : true,
        replace:true,
        scope: {
            tooltipLocation: '@'
        },
        templateUrl: "../common/kUI/directives/partials/kuiCopyToClipboard.html",
        link: function (scope, $element, attrs) {
            scope.text = attrs.text;
            scope.onCopyClick = onCopyClick;
            scope.isCopied = false;
            scope.iconTimeoutDelayPromise = null;

            function onCopyClick() {
                copyToClipboard();
                scope.isCopied = true;

                scope.iconTimeoutDelayPromise = $timeout(function () {
                    scope.isCopied = false;
                    scope.iconTimeoutDelayPromise = null;
                }, COPIED_ICON_TIMEOUT_DELAY);
            }

            function copyToClipboard() {
                var el = document.createElement('textarea');
                el.value = scope.text;
                el.setAttribute('readonly', '');
                el.style.position = 'absolute';
                el.style.opacity = 0;
                el.style.left = '-9999px';
                document.body.appendChild(el);
                el.select();
                document.execCommand('copy');
                document.body.removeChild(el);
            }

            scope.$on('$destroy', function() {
                if (scope.iconTimeoutDelayPromise) {
                    $timeout.cancel(scope.iconTimeoutDelayPromise);
                }
            });
        }
    };
});