/* globals _ */

'use strict'
kwpApp.factory('localHardwareService', ['$rootScope', 'slidesService', 'kalturaService','asyncLiveEntryInfoService','$injector','kdialog','kAssetsTimelineService','kCef', function($rootScope, slidesService, kalturaService, asyncLiveEntryInfoService, $injector, kdialog, kAssetsTimelineService, kCef){

    var _displays = [];
    var _currentFullScreenDisplay;
    var _fullScreenWindow;

    var getCurrentFullScreenDisplay = function(){
        return _currentFullScreenDisplay;
    };

    var getDisplays = function(){
        return _displays;
    };

    var getFullScreenWindow = function(){
        return _fullScreenWindow;
    };

    var closeFullScreenWindow = function(){

        _fullScreenWindow = undefined;
        setCurrentFullScreenDisplay(-1);

        kCef.sendIPC("closeFullScreenWindow");
    };

    var openFullScreenWindow = function(assetWindowUrl)
    {
        if(!_fullScreenWindow){

            kCef.sendIPC("openExternalWindow",{display: _currentFullScreenDisplay, url: assetWindowUrl}).then( function(win) {
                _fullScreenWindow=win;
            });

        }
        else{
            closeFullScreenWindow();
        }
    };

    if (kCef.available) {
        kCef.on('displayDevices', function (result) {
            _displays = result;

	        var tmpCurrentDisplay = undefined;
	        var currentDisplayExists = false;

            klog.info("dispalyDevices: " + JSON.stringify(_displays));

            if (_currentFullScreenDisplay) {
                _displays.forEach(function (d) {
                    if (d.id === _currentFullScreenDisplay.id) {
                        currentDisplayExists = true;
                        tmpCurrentDisplay = d;
                    }
                });
                if (!currentDisplayExists || (tmpCurrentDisplay && tmpCurrentDisplay.isAppWindow)) {
                    closeFullScreenWindow();
                    setCurrentFullScreenDisplay(-1);
                }
            }

        });
    }

    var refreshDisplayDevices = function() {
        if (kCef.available) {
            kCef.sendIPC('listDisplayDevices');
        }
    };



    var setCurrentFullScreenDisplay = function(index){
        if (index >= 0){
            _currentFullScreenDisplay = _displays[index];
        }
        else{
            _currentFullScreenDisplay = undefined;
        }
    };

    var GetSelectedDisplayID = function(){
        return _currentFullScreenDisplay.id;
    };

    refreshDisplayDevices();

    return {
        refreshDisplayDevices: refreshDisplayDevices,
        getDisplays : getDisplays,
        setCurrentFullScreenDisplay: setCurrentFullScreenDisplay,
        GetSelectedDisplayID: GetSelectedDisplayID,
        getCurrentFullScreenDisplay: getCurrentFullScreenDisplay,
        getFullScreenWindow: getFullScreenWindow,
        closeFullScreenWindow: closeFullScreenWindow,
        openFullScreenWindow: openFullScreenWindow
    };
}]);
