'use strict';
kQna.factory('metaDataGenerator', function () {

    var xsdSchemeOrder = ['Assignees','State','ThreadId','Type','ThreadCreatorId'];

    function _xmlParser(xmlText) {

        var xml = $.parseXML(xmlText);

        var $xml = $( xml ).find('metadata').children();

        var obj={};

        $.each( $xml, function(inx, node){
            if (node.nodeType===1) {
                obj[node.nodeName] = node.textContent;
            }
        });

        return _merge({},obj);
    }

    function _createMetadataXmlFromObject(obj) {

        obj = _merge({},obj);

        var xml="<metadata>";

        var convertProp = function(propertyName,propertyValue) {
            xml += "<" + propertyName + ">" + propertyValue + "</" + propertyName + ">";
        };

        for(var propertyName in obj) {

            if (obj.hasOwnProperty(propertyName)){

                if (obj[propertyName] instanceof Array) {

                    obj[propertyName].forEach(function (value) {
                        convertProp(propertyName,value);
                    });

                } else {
                    convertProp(propertyName, obj[propertyName]);
                }
            }


        }
        xml+="</metadata>";
        return xml;
    }

    function _merge(metaDataObject,dataObjectToAppend){

        var property = null;

        var returnedObject = {};

        for (var i = 0;i < xsdSchemeOrder.length; i++) {

            property = xsdSchemeOrder[i];

            if(metaDataObject.hasOwnProperty(property)){

                if(dataObjectToAppend.hasOwnProperty(property)){
                    returnedObject[property] = dataObjectToAppend[property];
                }
                else{
                    returnedObject[property] = metaDataObject[property];
                }

            }else{
                if(dataObjectToAppend.hasOwnProperty(property)){
                    returnedObject[property] = dataObjectToAppend[property];
                }
            }
        }
        return returnedObject;
    }

    return {

        merge : _merge,

        createObj : function(metadataUnorderedObject){

            return _merge({},metadataUnorderedObject);

        },

        createXmlFromObj : _createMetadataXmlFromObject,

        createObjFromXml : _xmlParser

    };

});
