kQna.directive('kwpQnaScreenAnswer',['gettextCatalog','$anchorScroll','$location','$timeout', function (gettextCatalog,$anchorScroll,$location,$timeout) {

    var controller = ['$scope','qnaService','kalturaService', function ($scope,qnaService,kalturaService) {

        $scope.replyToMessage = function(qnaEntry,message) {

            qnaService.makeReply(qnaEntry,message);

        };

    }];

    return {
        restrict : 'E',
        //transclude : true,
        replace :true,
        controller: controller,

        link: function (scope, elem, attrs) {

            scope.replyText = "";

            //TODO:Seems wasteful need to check.
            scope.scrollTo=function(entry) {

                $timeout(function () {
                    $anchorScroll(entry.getId());
                },10);
            };

        },
        templateUrl: "../common/kQna/views/kwpQnaScreenAnswer.html"
    };
}]);


