'use strict';


/*
 * Incoming screen directive.Represent the live feed screen where producer receives all incoming data from users.
 * Also this module holds connection to the model of the qnaService.
 */
kQna.directive('kwpQnaScreenSettings',['qnaService','gettextCatalog',function (qnaService,gettextCatalog) {

    return {
        restrict : 'E',
        //transclude : true,
        replace : false,

        link: function (scope, elem, attrs) {

            scope.enableQna = function(){

                scope.qnaModuleState.qnaEnabled = true;

                qnaService.enableQna();
            };

            scope.disableQna = function(){

                scope.qnaModuleState.qnaEnabled = false;

                scope.qnaModuleState.announcementOnly = false;

                qnaService.disableQna();

            };

            scope.disableAnnouncementsOnly = function(){

                scope.qnaModuleState.announcementOnly = false;

                qnaService.disableAnnouncementsOnly();

            };

            scope.enableAnnouncementsOnly = function(){

                if (scope.qnaModuleState.qnaEnabled){

                    scope.qnaModuleState.announcementOnly = true;

                    qnaService.enableAnnouncementsOnly();

                }
            };
            // close function on QNA moderator mode
            scope.closeQnaSettings = function(){
                // go back to the current selected QNA view
	            this.selectSubset(this.selectedSubset);
            };

        },
        templateUrl: "../common/kQna/views/kwpQnaScreenSettings.html"
    };
}]);


