/* global _ */
/* global Promise */

'use strict';

webcastInfoModule.controller('webcastInfoController', function (kalturaAPI,$sce, $scope, kCef) {
    this.template = "../common/webcastInfo/webcastInfoView.html";

    var _this=this;
    _this.streamingInfo=[];
    _this.presenters = [];

    kalturaAPI.login().then(function() {

        kalturaAPI.startMultirequest();
            if (configuration.eventsmetadataprofileid) {
                if (configuration.eventsmetadataprofileid) {
                    kalturaAPI.metadataService.list(configuration.MediaEntryId, configuration.eventsmetadataprofileid).then(function (res) {
                        if (res && res.objects && res.objects.length > 0) {
                            var xml = res.objects[0].xml;
                            var xmlDoc = $.parseXML(xml);
                            initPresenters(xmlDoc);
                        }
                    });
                }
            }
            kalturaAPI.baseEntryService.get(configuration.MediaEntryId).then(function(res) {
                _this.title=res.name;
                    _this.appHostUrl = configuration.appHostUrl;
                _this.description=$sce.trustAsHtml(res.description);
                _this.streamingInfo.push({ name: "Primary Url", value:  res.primaryBroadcastingUrl});
                _this.streamingInfo.push({ name: "Secondary Url", value:  res.secondaryBroadcastingUrl});
                _this.streamingInfo.push({ name: "streamName", value:  res.streamName.replace("_%i","_1")});
            });
        kalturaAPI.execMultirequest()
    });

    function initPresenters(metadataXml)
    {
        var presenters = extractPresenters(metadataXml);
        var promises = [];
        if(presenters) {
            _.forEach(presenters, function (presenter, id) {
                if (presenter.image) {
                    promises.push(kalturaAPI.thumbAssetService.getUrl(presenter.image));
                }
                else{
                    promises.push(Promise.resolve(null));
                }
            });

            Promise.all(promises).then(function(results){
                _.forEach(results, function(res, index){
                    presenters[index].bio = $sce.trustAsHtml(presenters[index].bio);
                    presenters[index].image_src = res;
                    _this.presenters.push(presenters[index]);
                });
            });
        }
    }

    function extractPresenters(metadataXml)
    {
        var presenters = [];
        if(metadataXml && metadataXml.getElementsByTagName('Presenter')) {
            var presentersXml = metadataXml.getElementsByTagName('Presenter');
            _.forEach(presentersXml, function (presenter, id) {
                var name = presenter.getElementsByTagName('PresenterName')[0].innerHTML;
                var title = presenter.getElementsByTagName('PresenterTitle')[0].innerHTML;
                var bio = presenter.getElementsByTagName('PresenterBio')[0].innerHTML;
                var image = presenter.getElementsByTagName('PresenterImage')[0].innerHTML;
                presenters.push({
                    'name' : name,
                    'title' : title,
                    'bio': bio,
                    'image' : image,
                    'image_src': ''
                });
            });
        }
        return presenters;
    }

    $scope.openExternalLink = function()  {
        kCef.sendIPC('open-external-link',  configuration.appHostUrl + '/media/' + configuration.MediaEntryId);
    };

});