/* globals _ */
/* globals klog */
/* globals PromisesQueue */
/* globals Promise */

(function (){
    "use strict";

    function Directive(kPollsService, kProducerActions, $uibModal, $rootScope, $window, $timeout,
                       gettextCatalog, POLL_DATA_STATUS, POLL_STATE_STATUS)
    {
        var FULL_DIALOG_HEIGHT = 425;
        var DIALOG_LEFT_OFFSET = 40;
        var DIALOG_TOP_OFFSET = 20;


        function Controller(kPollsService, $modalInstance, $scope, $rootScope)
        {
            $scope.isLoading = false;

            $scope.remainingCharsCount =
            {
                question : "110",
                answer : "60"
            };

            $scope.getWizardTitle = function ()
            {
                if(_.isEmpty($scope.poll.question)) {
                    return gettextCatalog.getString('Add a New Poll Question');
                }else if(!$scope.isPollValid()) {
                    return gettextCatalog.getString('Add Answers');
                }else {
                    return gettextCatalog.getString('Edit Poll');
                }
            };
            
            $scope.getWizardSubTitle = function ()
            {
                if(_.isEmpty($scope.poll.question)) {
                    return '';
                }else if(!$scope.isPollValid()) {
                    return gettextCatalog.getString('(up to 5)');
                } else {
                    return '';
                }
            };

            $scope.savePoll = function () {

                $scope.isLoading = true;

                var newPollData = {
                  text: {
                      state : $scope.poll.state,
                      question : $scope.poll.question,
                      answers : $scope.poll.answers
                  }
                };

                updatePollData(newPollData);
            };

            /**
             * closing the modal and saving only if poll is valid or not broadcasted
             */
            $scope.minimizeAndSave = function ()
            {
                if($scope.isPollValid() || !$scope.isPollBeingBroadcast()) {
                    $scope.savePoll();
                } else {
                    //close wizard without saving
                    $scope.poll.question = $scope.initialPollData.question;
                    _.each($scope.initialPollData.answers, function (answer, key) {
                        $scope.poll.answers[key] = answer;
                    });
                    closeModal();
                }
            };

            $scope.deletePoll = function ()
            {
                var updatedPollData = {
                    text: {
                        state : POLL_DATA_STATUS.INACTIVE,
                        question : $scope.poll.question,
                        answers : $scope.poll.answers
                    }
                };

                updatePollData(updatedPollData);
            };

            $scope.isPollAnswersEmpty = function ()
            {
                var empty = true;
                if(!_.isEmpty($scope.poll.answers)) {
                    _.each($scope.poll.answers, function (answer) {
                       if(!_.isEmpty(answer)) {
                           empty = false;
                       }
                    });
                }
                return empty;
            };

            $scope.isPollValid = function ()
            {
                var complete = false;
                if(!_.isEmpty($scope.poll.question)) {
                    if(!_.isEmpty($scope.poll.answers)) {
                        //currently the poll has a question
                        complete = true;
                        //to check for min. two answers
                        var answerCount = 0;
                        //to check that all answers are consecutive
                        var sequenceStopped = false;
                        _.each($scope.poll.answers, function (answer) {
                            if(!_.isEmpty(answer)) {
                                //we have valid answer after a blank one
                                if(sequenceStopped) {
                                    complete = false;
                                } else {
                                    answerCount++;
                                }
                            } else {
                                sequenceStopped = true;
                            }
                        });
                        //a poll is complete if the answers sequence didn't break and there are at least 2 answers
                        complete = complete && answerCount >= 2;
                    }
                }
                return complete;
            };

            $scope.removeAnswer = function (answerId)
            {
                var newAnswerObject = {};
                _.each($scope.poll.answers, function (answer, id) {
                    //no need to add the requested answer to remove
                    var answerKey = parseInt(id);
                    if(answerKey !== answerId) {
                        if(id < answerId) {
                            newAnswerObject[answerKey] = answer;
                        }else {
                            newAnswerObject[answerKey - 1] = answer;
                        }
                    }
                });

                $scope.poll.answers = newAnswerObject;
            };

            $scope.$on('modal.closing', function(event, reason, closed)
            {
                if ($scope.isLoading) {
                    event.preventDefault();
                }
            });

            $scope.hideInputRemoveIcon = function ($event)
            {
                var element = $($event.currentTarget).parent();
                var removeIcon = element.find('.remove-poll-answer');
                var charCount = element.find('.poll-char-count');
                if(removeIcon && charCount) {
                    removeIcon.css({'visibility' : 'hidden'});
                    charCount.css({'visibility' : 'visible'});
                }
            };

            $scope.resetInputStyle = function ($event)
            {
                var element = $($event.currentTarget).parent();
                var removeIcon = element.find('.remove-poll-answer');
                var charCount = element.find('.poll-char-count');
                if(removeIcon && charCount) {
                    removeIcon.attr('style','');
                    charCount.attr('style','');
                }
            };

            /**
             * Private functions
             */

            //update poll data only if in preliminary state
            function updatePollData (newPollData)
            {
                if($scope.isEditable) {

                    kPollsService.updatePollData(newPollData, $scope.poll.id).then(
                        function (poll)
                        {
                            closeModal();
                        },
                        function (err)
                        {
                            //todo deal with update poll error
                            $scope.isLoading = false;
                            klog.error(err);
                        });
                } else {
                    //close wizard without saving
                    closeModal();
                }
            }
            
            function closeModal()
            {
                $scope.isLoading = false;
                $rootScope.$broadcast("pollWizardClosed");
                $modalInstance.close();
            }

            
        }
        
        function Link(scope, element, attrs, ctrls)
        {
            scope.isEditable = false;
            scope.isDeletable = false;

            scope.$watch(function(){return scope.visible;}, function(value)
            {
                if(value === true){
                    $timeout(function () {
                        scope.open();
                    });
                }
            });

            scope.isPollBeingBroadcast = function ()
            {
                var isBroadcast = false;
                //checking if current poll is being broadcast
                var podcastingType = kProducerActions.getState('podcasting.type');
                if(podcastingType === 'poll') {
                    var selectedPollState = kProducerActions.getState('actions.select-poll-state');
                    if(selectedPollState && selectedPollState['pollId'] === scope.poll.id) {
                        return true;
                    }
                }

                return isBroadcast;
            };

            scope.open = function()
            {
                $rootScope.$broadcast("pollWizardOpened");

                scope.initialPollData = _.cloneDeep(scope.poll);
                
                var modalInstance = $uibModal.open({
                    scope: scope,
                    controller: Controller,
                    templateUrl: '../common/kPolls/directives/k-poll-wizard-modal.html',
                    backdrop: 'static',
                    windowTopClass: 'poll-wizard-modal',
                    keyboard  : false
                });

                updateInitialPollState();

                // listen to poll state changes from server and act accordingly
                kProducerActions.subscribeStateChange(function()
                    {
                        updatePollState();
                    }
                );

                function updateInitialPollState()
                {
                    var pollState = kPollsService.getPollState(scope.poll.id);
                    if(pollState) {
                        scope.isEditable = pollState.status === POLL_STATE_STATUS.PRELIMINARY;
                        scope.isDeletable = scope.isEditable && !scope.isPollBeingBroadcast();
                    }
                }

                function updatePollState()
                {
                    var pollState = kProducerActions.getState('actions.select-poll-state');
                    if(pollState && pollState['pollId'] ===  scope.poll.id) {
                        scope.isEditable = pollState.status === POLL_STATE_STATUS.PRELIMINARY;
                        scope.isDeletable = scope.isEditable && !scope.isPollBeingBroadcast();
                    }
                }

                modalInstance.rendered.then(function ()
                {
                    onResize();

                    $timeout(function () {
                        onResize();
                    }, 200);

                    angular.element($window).on('resize', onResize);

                    $('.modal-dialog').resize(
                        function ()
                        {
                            onResize();
                        }
                    );

                    scope.$on('$destroy', cleanUp);

                });

                function onResize()
                {
                    $timeout(function () {
                        //DOM has finished rendering
                        var targetPosition = $('.centered-event').offset();
                        if(targetPosition) {
                            var left = targetPosition.left - $(document).scrollLeft() - DIALOG_LEFT_OFFSET;
                            var top = 0;
                            //if there is enough height for a full poll dialog
                            if(targetPosition.top > FULL_DIALOG_HEIGHT) {
                                top = targetPosition.top - $(document).scrollTop() - ($('.modal-dialog').height() + DIALOG_TOP_OFFSET);
                            }
                            if(left < 30 || top < 30) {
                                onResize();
                            } else {
                                $('.modal-dialog').css({top: top, left: left, transition:'all 0.5s linear'});
                            }
                        }
                        else{
                            klog.warn("failed to get centered-event - poll wizard will not show in the correct place");
                        }
                    });
                }

                modalInstance.result.then(function(){
                    //Get triggers when modal is closed
                    $rootScope.$broadcast("pollWizardClosed");
                }, function(){
                    //gets triggers when modal is dismissed.
                    $rootScope.$broadcast("pollWizardClosed");

                });

                function cleanUp()
                {
                    angular.element($window).off('resize', onResize);
                    $('.modal-dialog').off('resize', onResize);
                }
            };
        }

        return {
            restrict: 'E',
            scope: {visible:'=', poll:'='},
            link: Link,
            controllerAs: 'vm',
            transclude: true,
            replace: true,
            backdrop : 'static'
        };
        
    }

    angular.module('kPolls').directive('kPollWizardModal', Directive);


})();