kQna.service('qnaSuspendFetching',function () {
    var value=false;
    var pending=0;
    this.suspend = function() {
        //console.warn("suspend",pending);
        pending++;
    };
    this.resume = function() {
        //console.warn("resume ",pending);
        if (pending>0) {
            pending--;
        }
    };
    this.isSuspended=function() {
        return pending>0;
    };
});
