kwpApp.controller('sessionController', function ($scope, $rootScope, captureService,  sessionService, gettextCatalog, shortcutService, asyncLiveEntryInfoService, kdialog, $interval, rtcService) {

    $scope.duration = (configuration.todate.getTime()-configuration.fromdate.getTime());

    var minimalProgress = 1;

    $scope.template = "./partials/sessionView.html";

    $scope.captureService = captureService;

    $scope.sessionService = sessionService;

    $scope.elapsedTime = 0;

    $scope.showEntryManagment = configuration.showEntryManagment;

    $scope.remainingTime = $scope.duration;

    $scope.shortcutService = shortcutService;

    $scope.elapsedPercentage = 0;

    $scope.progressValue = 0;

    $scope.max = 100;

    var updatingExplictLive=false;

    var goLiveActive = false;

    $scope.getLiveStatusText = function() {
        if (captureService.isExplicitLive()) {
            if (captureService.isBroadcasting()) {
                return captureService.getViewMode() === 0 ? 'PREVIEW' : 'LIVE';
            }
            return 'OFFLINE';
        }
        else {
            return captureService.isBroadcasting() ? 'LIVE' : 'OFFLINE';
        }
    };

    $scope.isViewModeAll = function() {
        if (captureService.isBroadcasting()) {
            return captureService.isExplicitLive() && (captureService.getViewMode() === 1);
        }
        return false;
    };

    $scope.onGoLiveClick = function() {
        goLiveActive = true;
        updatingExplictLive=true;
        return sessionService.goLive().finally(function() {
            updatingExplictLive=false;
        });
    };

    $scope.onEndLiveClick = function() {
        kdialog.yesNoDialog({
            yesMessage: gettextCatalog.getString('End Live'),
            noMessage: gettextCatalog.getString('Cancel'),
            title: gettextCatalog.getString('Hold your horses!'),
            message: gettextCatalog.getString('Are you sure you want to end the live broadcast? this action can not be undone')
        })
            .then(function() {
                goLiveActive = false;
                updatingExplictLive=true;
                return sessionService.endLive().finally(function() {
                    if (rtcService.getIsStreamBroadcasting)
                    {
                        if (rtcService.isMicrophoneMuted)
                        {
                            rtcService.toggleMicrophoneMute();
                        }
                        rtcService.stopRtcStream(true);
                    }
                    updatingExplictLive=false;
                });
            });
    };

    $scope.isExplicitLiveUpdateInProgress = function() {
        return asyncLiveEntryInfoService.isUpdatingLiveEntry() && updatingExplictLive;
    }

    $interval(function() {
        var now = new Date().getTime();
        $scope.elapsedTime = now - configuration.fromdate.getTime();
        $scope.remainingTime = configuration.todate.getTime() - now;
        var progressValue = Math.min(100 * $scope.elapsedTime / ($scope.duration), $scope.max);

        if ($scope.remainingTime <= 0) {
            $scope.remainingTime = 0;
        }

        if ($scope.elapsedTime <= 0) {
            $scope.elapsedTime = 0;
            $scope.remainingTime = $scope.duration;
        }

        // show indicator if we are in the session time. If we show the indicator it will have a minimal size of 1%
        // before we start the progress will show no line, and the time will show a negative value (time to start)
        $scope.progressValue = progressValue <= minimalProgress && progressValue > 0 ? minimalProgress : progressValue;
    }, 1000);

    $rootScope.$on('rtcStreamUpdated', function(event, status, userDecided) {
        if (status && goLiveActive)
        {
            $scope.onGoLiveClick();
        }
        if (!status && !userDecided)
        {
            goLiveActive = false;
        }
    });
});
