(function() {
    'use strict';

    angular.module('kUI').factory('kdialog',  function ($uibModal,$rootScope) {
        var preDialogCallback = null,
            postDialogCallback = null,
            dialogsOpened=0;

        var modalOpen = function (params) {

            var modalInstance = null;

            var promise = new Promise(function (success, dismiss) {
                if (preDialogCallback && dialogsOpened === 0) {
                    preDialogCallback();
                }

                var dialogClosed = function () {
                    dialogsOpened--;
                    if (postDialogCallback && dialogsOpened === 0) {
                        postDialogCallback();
                    }
                };

                dialogsOpened++;

                modalInstance = $uibModal.open(params);

                modalInstance.result.then(function (data) {

                    dialogClosed();

                    if (success) {
                        success(data);
                    }

                }, function () {

                    dialogClosed();

                    if (dismiss) {
                        dismiss();
                    }
                });
            });

            promise.modalInstance = modalInstance;
            return promise;
        };


        return {

            alert: function(message,closeApp) {
                var stack = new Error().stack;

                klog.error("Alert "+message+" "+stack);
                return modalOpen({
                    templateUrl: '../common/kUI/services/dialogs/alert.html',
                    backdrop : 'static',
                    controller: 'alertDialogController',
                    resolve: {
                        config:  function () {
                            return {
                                message: message,
                                closeApp: closeApp
                            };
                        }
                    }});
            },


            attention: function(titleText, messageText, closeButtonText) {
                var modelDialog=modalOpen({
                    backdrop : 'static',
                    templateUrl: '../common/kUI/services/dialogs/attention.html',
                    controller: 'attentionDialogController',
                    resolve: {
                        config:  function () {
                            return {
                                titleText: titleText,
                                messageText: messageText,
                                closeButtonText: closeButtonText
                            };
                        }
                    }});
                // return a tuple of instance & promise so it won't be possible to .then it
                // this is not a correct fix, but I don't want to refactor the all thing
                return {
                    instance : modelDialog.modalInstance,
                    promise : modelDialog
                };
            },

            infoDialog: function(configuration){

                return modalOpen({
                    templateUrl: '../common/kUI/services/dialogs/info.html',
                    controller: 'infoController',
                    resolve: {
                        config:  function () {
                            return configuration;
                        }
                    }}).then(function(res){

                    klog.info("Modal finished.");

                },function(err){

                    klog.error("Error with closing the modal.");

                });
            },

            yesNoDialog: function (config) {

                return modalOpen({
                        templateUrl: '../common/kUI/services/dialogs/dialogBase.html',
                        controller: 'dialogBaseController',
                        resolve: {
                            config:  function () {
                                return config;
                            }
                        }});
            },

            screenSelectDialog: function (config) {

                return modalOpen({
                    templateUrl: '../common/kUI/services/dialogs/screenSelect.html',
                    controller: 'dialogBaseController',
                    resolve: {
                        config:  function () {
                            return config;
                        }
                    }});
            },

            disappearingDialog: function(configuration){

                return modalOpen({
                    templateUrl: '../common/kUI/services/dialogs/disappear.html',
                    controller: 'disappearController',
                    resolve: {
                        config:  function () {
                            return configuration;
                        }
                    }}).then(function(res){

                    klog.info("Modal finished.");

                },function(err){

                    klog.error("Error with closing the modal.");

                });
            },

            templatedDialog: function (config) {

                return modalOpen(config);
            },

            dismiss: function (context, status) {
                // backward compatibility - if we get an object with modalInstance on it - pass it to close,
                // otherwise - call close on it
                if (context && context.modalInstance) {
                    context.modalInstance.close();
                }else
                {
                    // close resolve the promise with success, while dismiss with failure
                    if (status){
                        context.close();
                    }
                    else {
                        context.dismiss();
                    }
                }
            },

            setPreDialogCallback : function(f) {
                preDialogCallback = f;
            },

            setPostDialogCallback : function(f) {
                postDialogCallback = f;
            }
        };
    });

    angular.module('kUI').controller('dialogBaseController', function ($scope, $modalInstance, config, gettextCatalog, $sce) {

        $scope.title = config.title ||  whiteLabelingConfiguration.appName;
        $scope.message = $sce.trustAsHtml(config.message);
        if (config.inputs)
        {
            $scope.inputs = config.inputs;
            $scope.screenPage = 0;
            $scope.maxPage = Math.ceil(config.inputs.length / 8) - 1;
            $scope.selectedIndex = -1;
        }
        var yesMessage=config.yesMessage ||  gettextCatalog.getString("Yes");
        var noMessage=config.noMessage ||  gettextCatalog.getString("No");


        $scope.yesCommand = CreateCommand(yesMessage, "", function () {

            $modalInstance.close();
        });

        $scope.noCommand = CreateCommand(noMessage, "", function () {

            $modalInstance.dismiss('cancel');
        });

        $scope.screenSelectCommand = CreateCommand(yesMessage, "", function () {
            var retVal = null;
            if ($scope.selectedIndex != -1)
            {
                retVal = $scope.inputs[$scope.selectedIndex];
            }
            $modalInstance.close(retVal);
        });

        $scope.nextScreens = CreateCommand("", "", function () {
            $scope.screenPage++;
        });

        $scope.prevScreens = CreateCommand("", "", function () {
            $scope.screenPage--;
        });

        $scope.range = function(min, max) {
            var input = [];
            for (var i = min; i <= max; i += 1) {
                input.push(i);
            }
            return input;
        };

        $scope.setSelectedIndex = function(selectedIndex)
        {
            $scope.selectedIndex = selectedIndex;
        }

    });

    angular.module('kUI').controller('infoController', function ($scope, $modalInstance, config, gettextCatalog, $sce, upgradeService,kCef) {

        $scope.template = config.templateUrl;

        $scope.title = config.title;

        $scope.openURL = function(url){
            kCef.openExternal(url);
        };

        $scope.upgradeService = upgradeService;

        $scope.closeCommand = CreateCommand("Close", "", function () {

            $modalInstance.close();
        });
    });

    angular.module('kUI').controller('disappearController', function ($scope, $modalInstance, config, gettextCatalog, $sce) {

        $scope.template = config.templateUrl;

        $scope.title = config.title;

        setTimeout(function(){
            $scope.opacity = 0;
        },1500);

        setTimeout(function(){
            $modalInstance.close();
        },4500);

    });


    angular.module('kUI').controller('alertDialogController', function ($scope, $modalInstance, config, gettextCatalog,$sce) {

        $scope.message = $sce.trustAsHtml(config.message);

        if (config.closeApp) {

            $scope.closeCommand = CreateCommand(gettextCatalog.getString("Close Application"), "", function () {

                $modalInstance.close();
            });
        } else {
            $scope.closeCommand = CreateCommand(gettextCatalog.getString("OK"), "", function () {
                $modalInstance.close();
            });
        }
    });

    angular.module('kUI').controller('attentionDialogController', function ($scope, $modalInstance, config, gettextCatalog,$sce) {

        $scope.messageText = $sce.trustAsHtml(config.messageText);
        $scope.titleText = $sce.trustAsHtml(config.titleText);

        $scope.closeCommand = CreateCommand(config.closeButtonText, "", function () {
            $modalInstance.close();
        });
    });
})();