"use strict";

kwpApp.controller('fullScreenController', function($scope, localHardwareService, gettextCatalog, kAssetsTimelineService, ASSET_TYPE, kPollContainer) {

    var FULL_SCREEN_HTML = "FullScreenWindow.html";
    var ORIGIN_HTML = "index.html";

    $scope.template = "./partials/fullScreenView.html";

    $scope.$watch(
        function()
        {
            return kAssetsTimelineService.currentTimelineAsset;
        },
        function()
        {
            if (localHardwareService.getFullScreenWindow()) {
                var assetType = kAssetsTimelineService.currentTimelineAsset ?
                    kAssetsTimelineService.currentTimelineAsset.type : '';
                updateFullScreenWindow(assetType);
            }
    });

    $scope.clicked = function(val) {
        if (val === 'None'){
            if (localHardwareService.getFullScreenWindow()) {
                localHardwareService.closeFullScreenWindow();
            }
        }
        // if currentDisplay is undefined or it's a different display
        else if (!localHardwareService.getCurrentFullScreenDisplay() || val.id !== localHardwareService.getCurrentFullScreenDisplay().id){
            if (localHardwareService.getFullScreenWindow()) {
                localHardwareService.closeFullScreenWindow();
            }
            localHardwareService.setCurrentFullScreenDisplay(val.index);
            var assetType = kAssetsTimelineService.currentTimelineAsset ?
                kAssetsTimelineService.currentTimelineAsset.type : '';
            openFullScreenWindow(assetType);
        }
    };

    $scope.getMonitorNamesAndIDs = function() {
        return localHardwareService.getDisplays();
    };

    $scope.selectedDisplayID = function(){
        localHardwareService.GetSelectedDisplayID();
    };

    $scope.icon = function(monitorID){
        if (monitorID === 'None' && !localHardwareService.getCurrentFullScreenDisplay()){
            return "kicon-yes SelectedListOption";
        }
        else if (localHardwareService.getCurrentFullScreenDisplay() && monitorID === localHardwareService.getCurrentFullScreenDisplay().id) {
            return "kicon-yes SelectedListOption";
        }
        return "iconSizeIndent";
    };

    $scope.getDropDownListIconColorClass = function() {

        if (localHardwareService.getCurrentFullScreenDisplay()) {
            return "CircleIndicator";
        }
        else{
            return "";
        }
    };

    $scope.disableSelectionDropDown = function(){
        return (!localHardwareService.getDisplays() || localHardwareService.getDisplays().length < 2);
    };

    $scope.onToggle = function() {
        var $btn = $(fullScreenControllerID);
        var $menu = $(DisplaySelectionList);
        var margin = ($btn.outerWidth() - $menu.outerWidth()) / 2 + 'px';
        $menu.css('margin-left', margin);

        var selectorText = "#DisplaySelectionList:after";

        var triangleSizeInPX = 6;

        var posTriangle = (parseInt($menu.innerWidth()) - (2*parseInt(triangleSizeInPX))) / 2 + "px";

        for (var i=0; i < document.styleSheets.length; i++) {
            if (document.styleSheets[i].href && document.styleSheets[i].href.indexOf("screenSelection.css") >= 0){
                for (var j =0; j < document.styleSheets[i].cssRules.length; j++) {
                    if (document.styleSheets[i].cssRules[j].selectorText === selectorText) {
                        document.styleSheets[i].removeRule(j);
                        break;
                    }
                }
                document.styleSheets[i].addRule(selectorText, "border-width: 0 " + triangleSizeInPX + "px " + triangleSizeInPX + "px " +triangleSizeInPX + "px");
                document.styleSheets[i].addRule(selectorText, "position: absolute");
                document.styleSheets[i].addRule(selectorText, "bottom: 100%");
                document.styleSheets[i].addRule(selectorText, "margin-left: " + posTriangle);
                document.styleSheets[i].addRule(selectorText, "border-style: solid");
                document.styleSheets[i].addRule(selectorText, "border-color: #222222 transparent");
                document.styleSheets[i].addRule(selectorText, "content: ''");
                document.styleSheets[i].addRule(selectorText, "width: 0");
                document.styleSheets[i].addRule(selectorText, "height: 0");

                break;
            }
        }
    };

    function updateFullScreenWindow(assetType)
    {
        if(assetType === ASSET_TYPE.SLIDE) {
            updateSlideFullScreenWindow();
        }else if(assetType === ASSET_TYPE.POLL) {
            updatePollFullScreenWindow();
        }else{
            //default screen - opening slide window with no image
            updateSlideFullScreenWindow();
        }
    }

    function openFullScreenWindow(assetType)
    {
        if(assetType === ASSET_TYPE.SLIDE) {
            openSlideFullScreenUrl();
        }else if(assetType === ASSET_TYPE.POLL) {
            openPollFullScreenUrl();
        }else{
            //default screen - opening slide window with no image
            openSlideFullScreenUrl();
        }
    }

    function updateSlideFullScreenWindow()
    {
        kAssetsTimelineService.getCurrentAssetUrl().then(function(result)
        {
            return result.url;
        },function()
        {
            // the is only here for safety since we know that we have current slide key so we should be able to extract url
            return '';
        }).then(function(result)
        {
            ipcRenderer.send('slideUpdate', {type:'slide', data: result});
        });
    }

    function updatePollFullScreenWindow()
    {
        var newPollState = JSON.stringify(kPollContainer.getUpdatedPollContainer());
        ipcRenderer.send('pollUpdate', {type: 'poll', data: newPollState});
    }

    function openSlideFullScreenUrl()
    {
        kAssetsTimelineService.getCurrentAssetUrl().then(
            function(slideUrl)
            {
                var windowUrl = location.protocol +
                    location.pathname.replace(ORIGIN_HTML, FULL_SCREEN_HTML) +
                    "?StartingImage=" + slideUrl.url;

                return windowUrl;
            },
            function(){
                var windowUrl = location.protocol +
                    location.pathname.replace(ORIGIN_HTML, FULL_SCREEN_HTML) +
                    "?StartingImage=" + '';

                return windowUrl;
            }
        ).then(function (windowUrl) {
            localHardwareService.openFullScreenWindow(windowUrl);
        });
    }

    function openPollFullScreenUrl()
    {
        var pollInitialState = encodeURIComponent(JSON.stringify(kPollContainer.getUpdatedPollContainer()));
        var windowUrl = location.protocol +
            location.pathname.replace(ORIGIN_HTML, FULL_SCREEN_HTML) +
            '?initialPollState=' + pollInitialState;
        localHardwareService.openFullScreenWindow(windowUrl);
    }
    
    // register to size changes to reposition the drop down window
    var slideNavigatorElem = document.getElementById("slideNavigator");
    elementWatcher.onresize(slideNavigatorElem, function(){$scope.onToggle();});

});