
kngLayout.addPanelDirective("kFillPanel", function() {

    return {
        scope: {
            collapsedWidth: '@'
        },
        link: function ($scope) {

            var scope = $scope;


            var recalcLayout = function () {

                scope.panels=scope.panels.sort(function(a,b) {
                    return a.order>b.order;
                });

	            var panels=scope.panels.filter ( function(panel) {
                    return panel.isVisible();
                });

                var collapsed = panels.filter(function (panel) {
                    return panel.isCollapsed();
                });
                var expanded = panels.filter(function (panel) {
                    return !panel.isCollapsed();

                });

                var expMargin=0; //pixels between expanded panels

                var clientWidth = scope.elem[0].clientWidth;
                var clientHeight = scope.elem[0].clientHeight;

                var collapsedSize = parseInt(scope.collapsedWidth);

                if (collapsedSize === undefined || isNaN(collapsedSize)) {

                    collapsedSize = 30;

                }

                if (panels.length===1) {

                    scope.panels.forEach( function(panel) {
                        if (!panel.isVisible()) {
                            panel.elem.css('display', 'none');
                        } else {
                            panel.elem.css ( {
                                'top' : '0px',
                                'bottom': clientHeight + 'px',
                                'left' : "0px",
                                'width' :   clientWidth+'px'});
                        }
                    });
                    return;
                }

                var right = clientWidth;
                //hide all collapsed
                for (var i = 0; i < collapsed.length; i++) {

                    var width=collapsed[i].getSizeValue(clientWidth);
                    collapsed[i].elem.css ( {
                        'top' : '0px',
                        'bottom': clientHeight + 'px',
                        'left' : (right-collapsedSize) + "px",
                        'width' :   width+'px'});

                    right-=collapsedSize;
                }


                var rightPercent=100*right/clientWidth;
                var marginPercent=100*expMargin/clientWidth;

                if (collapsed.length>0) {
                    rightPercent -= marginPercent;
                }

                if (expanded.length > 0) {
                    for (  i = expanded.length-1; i >=0; i--) {
                        var panelWidth=rightPercent;

                        if (expanded[i].size) {
                            var dx= expanded[i].getSizeValue(clientWidth);
                            panelWidth=100*dx/clientWidth;//move to %, and override
                        }

                        //adjust
                        if (expanded[i].minSize) {
                            var minPanelWidth= expanded[i].getMinSizeValue(clientWidth);
                            minPanelWidth=100*minPanelWidth/clientWidth;//move to %

                            panelWidth=Math.max(panelWidth,minPanelWidth);
                        }


                        expanded[i].elem.css ( {
                            'top' : '0%',
                            'bottom':  '100%',
                            'left' : rightPercent-panelWidth + "%",
                            'width' : panelWidth + "%"});

                        rightPercent-=panelWidth+marginPercent;
                    }
                }
            };
            return {
                recalcLayout: recalcLayout,
                childAdded: function (panel) {
                    panel.elem.addClass( "fillPanelAnimation" );
                }
            };
        }
    };
});