/* globals Promise */

(function () {
    "use strict";

    function Directive() {
        function Controller(kEntryManagementService, gettextCatalog) {

            var self = this;

            self.resetEntry = kEntryManagementService.resetEntry;
            self.canEnableResetEnterBtn = kEntryManagementService.canEnableResetEnterBtn;
            self.archiveToolTip = kEntryManagementService.getArchiveDisabledTooltip;
        }

        function Link(scope, element, attrs, ctrls) {
            var ctrl = ctrls[0];
        }

        return {
            restrict: 'A',
            scope: {},
            require: ['kEntryManagementWidget'],
            templateUrl: '../common/kEntryManagement/directives/kEntryManagementWidget.html',
            controller: Controller,
            controllerAs: 'vm',
            bindToController: {},
            link: Link
        };
    }

    angular.module('kEntryManagementModule').directive('kEntryManagementWidget', Directive);

})();
