'use strict';

kUI.directive('kwpButton', function () {
    return {
        restrict: 'E',
        transclude: true,
        replace: true, //TODO: deprecated should br removed.

        scope: {
            command: '=',
            showText: '=?',
            id: '@',
            clickInterval: '=',
            tooltipPlacement: '@'
        },
        link: function (scope, elem) {
            if (scope.showText === undefined) {
                scope.showText = false;
            }
            if (scope.tooltipPlacement === undefined) {
                scope.tooltipPlacement = "bottom";
            }

            if (!scope.showText) {
                var height = elem.css("height");
                elem.css("width", height);
            }

            scope.iconid = function () {


                var runner = elem;
                while (!runner[0].id) {
                    runner = $(runner).parent();

                    if (!runner) {
                        return "icon";
                    }
                }
                return runner[0].id + "-icon";
            }

            scope.mousedown = function () {
                if (scope.clickInterval) {
                    scope.command.do();


                    var interval = scope.clickInterval,
                        current = interval;
                    scope.timer = setInterval(function () {
                        current -= 10;
                        if (current < 0) {
                            scope.command.do();
                            interval -= 50;
                            current = Math.max(10, interval);
                        }
                    }, 10);
                }
            };
            scope.mouseup = function () {
                if (scope.clickInterval) {
                    clearInterval(scope.timer);
                }
            };
            scope.click = function () {
                if (!scope.clickInterval) {
                    scope.command.do();
                }
            };
        },
        templateUrl: "../common/kUI/directives/partials/kwpButtonTemplate.html"
    };
});

