(function() {
    'use strict';

    function registerAdapter(kProducerActionsProvider, cuePointsServiceProvider) {
        kProducerActionsProvider.registerAction('entry-reset-mode', 'codeCuePoint.Code', {transmitMode : 'always'});
        cuePointsServiceProvider.addTagToArchiveExcludeList('entry-reset-mode');
        cuePointsServiceProvider.addTagToArchiveExcludeList('webcast:internal-state');
    }

    angular.module('kEntryManagementModule',[]);

    angular.module('kEntryManagementModule').config(registerAdapter);
})();
