'use strict';

function PromisesQueue(filterFn) {
    this._lastRequest=Promise.resolve();
    this._delayTimeBetweenSameActionTypeMS = 1000;
    this._actionNameAndIdMap={};
    this._lastExecutionTimePerAction={};
    this._id=0;
    this._disableSameAction=true;
    this._filterFn=filterFn;
    this._pendingActions = 0;
}

PromisesQueue.prototype.getNumberOfPendingActions = function()
{
    return (this._pendingActions);
};

PromisesQueue.prototype.add=function(func,actionName) {

    var _this=this;
    var id=++this._id;
 
    //klog.debug("action {0} id={1}",actionName,id);

    function onActionDone(result,statusMessage)
    {
        _this._pendingActions--;
        statusMessage = statusMessage || '';
        klog.debug('PromisesQueue.onActionDone(): action "{0}" done with status {1}, number of remaining actions in queue = {2}', actionName, statusMessage,_this._pendingActions);

        return result;
    }

    function handleActionRegistered(result)
    {
        _this._actionNameAndIdMap[actionName]=id;
        _this._pendingActions++;
        klog.debug('PromisesQueue.onActionDone(): new action "{0}" pending invokation, number of actions in queue = {1}', actionName, _this._pendingActions);
        return result;
    }

    var processNext=function() {
        //klog.debug("addPromise invoke {0}",actionName);

        if (_this._disableSameAction && actionName &&
            _this._actionNameAndIdMap[actionName] &&
            _this._actionNameAndIdMap[actionName] !== id) {

            klog.debug("PromisesQueue.onActionDone(): Request #{0} was cancelled, since there is a newer one ({1})", id, _this._actionNameAndIdMap[actionName]);
            return onActionDone(Promise.reject({reason : 'redundant'}), 'cancelled, there is a newer request for that type');
        }

        //if this action was done before
        if (_this._lastExecutionTimePerAction[actionName]) {
            if (_this._lastExecutionTimePerAction[actionName] + _this._delayTimeBetweenSameActionTypeMS >= new Date().getTime()) {
                var timeToWait = _this._delayTimeBetweenSameActionTypeMS - (new Date().getTime() - _this._lastExecutionTimePerAction[actionName]);

                klog.debug("PromisesQueue.onActionDone(): action of type {0} and id {1} was done less than {2} ms ago, waiting {3} ms", actionName, id, _this._delayTimeBetweenSameActionTypeMS, timeToWait);

                var delay = function(ms) {
                    return new Promise(function (resolve, reject) {
                        setTimeout(resolve, ms);
                    });
                };

                return delay(timeToWait+1).then(function(){ return processNext(); });
            }
        }

        delete _this._actionNameAndIdMap[actionName];

        _this._lastExecutionTimePerAction[actionName] = new Date().getTime();

        if (_this._filterFn) {
            if (_this._filterFn(_this._actionNameAndIdMap, actionName)) {
                return onActionDone(Promise.reject({reason : 'ignored'}),'filtered out');
            }
        }

        // call the function
        try {
            var ret = func();
            // if someone made a mistake and passed a non-Promise function we want to protect oneself
            if (ret instanceof Promise){
                return ret.then(function(result)
                {
                    return onActionDone(result,'action resolved');
                },function(result)
                {
                    return onActionDone(Promise.reject(result),'action rejected');
                });
            }

            return onActionDone(Promise.resolve(),'action invoked');
        }
        catch(e){
            return onActionDone(Promise.reject(e),'action failed');
        }

    };

    handleActionRegistered();

    this._lastRequest=this._lastRequest.then(function() {
        return processNext();
    }, function(reason) {
        klog.error(reason);
        return processNext();
    });

    return  this._lastRequest;

};
