"use strict";

kQna.factory('QnaThreadsManager', function(metaDataGenerator,kalturaAPI,QnaThread,THREAD_STATE, $rootScope) {


    function QnaThreadsManager(){
        this.threads = [];
        this.onAirThreads = [];
        this.threadsDictionary = {};
        this.displayEntries = [];
        this._counters = {};
        this._counters[THREAD_STATE.NONE] =     { total: 0 };
        this._counters[THREAD_STATE.ANSWERED] = { total: 0 };
        this._counters[THREAD_STATE.FOLLOWUP] = { total: 0 };
        this._counters[THREAD_STATE.DONE] =     { total: 0 };
        this._counters[THREAD_STATE.PENDING] =  { total: 0, newMessages: 0 };
        this._counters[THREAD_STATE.ANNOUNCED]= { total: 0 };
        this._counters[THREAD_STATE.FORWARDED]= { total: 0 };
        this._counters[THREAD_STATE.CLEAR] =    { total: 0 };
    }


    QnaThreadsManager.prototype.updateCounters = function(from,to, metadata, isFirstFetch){

        if(from !== null){
            this._counters[from].total -= 1;

            if (this._counters[from].total === 0 && from === THREAD_STATE.PENDING) {
                this._counters[THREAD_STATE.PENDING].newMessages = 0;
            }
        }
        if(to !== null && !_isMarkedAsDeleted(metadata)){
            this._counters[to].total += 1;

            // Update New-Messages counter
            if (!isFirstFetch && to === THREAD_STATE.PENDING) {
                this._counters[THREAD_STATE.PENDING].newMessages++;
                $rootScope.$emit('NewIncomingMessage');
            }
        }

        //klog.warn("Q&A: Moved from {0} to {1} = {2}",from,to,$.map(this._counters,function(a,b) { return b+"="+a.total;}).join(";"));
    };

    function _isMarkedAsDeleted(metadata) {
        return metadata && metadata.State === THREAD_STATE.DELETED;
    }

    /*
     * This function parses the data received from server and updates the live data data structure respectively.
     * Assumption:data is a list QnaThreads.
     */
    QnaThreadsManager.prototype.appendQnaData = function(newItems, isFirstFetch){

        //klog.debug("Q&A: got new items from server ({0}) ",newItems.length);

        for(var i = 0;i < newItems.length;i++) {

            var qnaEntry = newItems[i];

            var type = qnaEntry.getType();

            if(qnaEntry.getType() === "AnswerOnAir"){

                var updated=false;
                for(var j=0;j<this.onAirThreads.length;j++) {
                    if (this.onAirThreads[j].getId()===qnaEntry.getId()) {
                        this.onAirThreads[j]=qnaEntry;
                        updated=true;
                        break;
                    }
                }
                if (!updated) {
                    this.onAirThreads.push(qnaEntry);
                }
                continue;
            }

            var qnaEntryThreadId = qnaEntry.getThreadId();

            var qnaThread = this.threadsDictionary[qnaEntryThreadId];

            var fromThreadState = null;

            var targetThreadState = null;

            //This is a new entry which does not belong to any thread, therefore we create a new thread.
            if (qnaThread === undefined)
            {

                qnaThread = new QnaThread(qnaEntryThreadId);

                this.threads.push(qnaThread);

                this.displayEntries.push(qnaEntry);

                this.threadsDictionary[qnaEntryThreadId] = qnaThread;

            }
            //We add to an existing thread, we first verify if this thread location (priority) should be changed.
            else
            {
                fromThreadState = qnaThread.getState();

                var oldEntry=qnaThread.getEntry(qnaEntry.getId());
                if (oldEntry) {
                    //update existing one if state has changed and update time is newer
                    if (oldEntry.getUpdateTime()<=qnaEntry.getUpdateTime() &&
                        oldEntry.getState()!==qnaEntry.getState()) {
                        klog.info("Q&A: Updated stated from server for {0} ",qnaEntry.toString());
                        oldEntry.cuePoint.metadata.State = qnaEntry.getState();
                        oldEntry.getThread().updateStateWithoutUpdate(qnaEntry.getState());
                        this.updateCounters(fromThreadState, qnaEntry.getState(), qnaThread.getCuePointMetaData() ,isFirstFetch);
                    }
                    continue;
                }


                //This is the case where we need to set a new priority to this thread and move it be the first.This happens when the entry is
                //an announcement or if the entry is a Question/Response from user.
                if (qnaEntry.isQuestion() )
                {
                    let foundNonQuestion = false;
                    for (let i = 0; i < qnaThread.entries.length; i++)
                    {
                        if (!qnaThread.entries[i].isQuestion())
                        {
                            foundNonQuestion = true;
                        }
                    }
                    if (!foundNonQuestion)
                    {
                        var ThreadIndex = this.threads.indexOf(qnaThread);
                        this.threads.splice(ThreadIndex, 1);
                        var entryIndex = this.displayEntries.indexOf(qnaThread.entries[0]);
                        var tmpEntry = this.displayEntries.splice(entryIndex, 1);
                        this.threads.push(qnaThread);
                        // this.threads.unshift(qnaThread);
                        this.displayEntries.push(qnaThread.entries[0]);
                        // this.displayEntries.unshift(qnaThread.entries[0]);
                    }
                    if (foundNonQuestion)
                    {
                        //We update a previous thread AND we need to remove it from its position and advance it to be the first.

                        var ThreadIndex = this.threads.indexOf(qnaThread);

                        var entryIndex = this.displayEntries.indexOf(qnaThread.getLatestReceivedEntry());

                        klog.info("Q&A: Replaced entry in displayEntries entryIndex {0} to ThreadIndex {1}", entryIndex, ThreadIndex);
                        this.threads.splice(ThreadIndex, 1);
                        this.displayEntries.splice(entryIndex, 1);
                        this.threads.push(qnaThread);
                        this.displayEntries.push(qnaEntry);
                    }
                }
            }
            klog.info("Q&A: Added {0} to thread {1}",qnaEntry.toString(),qnaThread.toString());
            //We add the entry to the thread.
            qnaThread.appendEntry(qnaEntry);

            targetThreadState = qnaThread.getState();

            this.updateCounters(fromThreadState,targetThreadState, qnaThread.getCuePointMetaData(), isFirstFetch);

            if(qnaEntry.isAnnouncement()){

                this._counters[THREAD_STATE.ANNOUNCED].total += 1;
            }
        }

    };

    QnaThreadsManager.prototype.getThreadList = function(){

        return this.threads;

    };

    QnaThreadsManager.prototype.getCounters = function(){

        return this._counters;

    };

    QnaThreadsManager.prototype.clearNewMessagesCounter = function() {
        this.counters && this.counters[THREAD_STATE.PENDING] && (this.counters[THREAD_STATE.PENDING].newMessages = 0);
    };

    QnaThreadsManager.prototype.setThreadAsDone = function(qnaThread){

        var prevState = qnaThread.getState();

        qnaThread.done();

        this.updateCounters(prevState,THREAD_STATE.DONE);

    };

    QnaThreadsManager.prototype.setThreadAsDoneByThreadId = function(qnaThreadID){

        var qnaThread = this.threadsDictionary[qnaThreadID];

        if(qnaThread){
            this.setThreadAsDone(qnaThread);
        }
        else{

            klog.error("Error could not set thread as done since thread does not exist.");

        }
    };


    QnaThreadsManager.prototype.setThreadAsFollowed = function(qnaThread){

        var prevState = qnaThread.getState();

        qnaThread.followUp();

        this.updateCounters(prevState,THREAD_STATE.FOLLOWUP);

    };

    QnaThreadsManager.prototype.setThreadAsForwarded = function(qnaThread,assignedTo){

        var prevState = qnaThread.getState();

        qnaThread.forward(assignedTo);

        this.updateCounters(prevState,THREAD_STATE.FORWARDED);

    };

    QnaThreadsManager.prototype.setThreadAsCleared = function(qnaThread){

        var prevState = qnaThread.getState();

        qnaThread.clear();

        this.updateCounters(prevState,THREAD_STATE.CLEAR);
    };

    QnaThreadsManager.prototype.deleteAnnouncementThread = function(qnaThread){
        var prevState = qnaThread.getState();
        qnaThread.delete();
        this.updateCounters(prevState, null);
    };

    QnaThreadsManager.prototype.activeOnAirAnswer = function(id){

        return this.onAirThreads.filter(function(answer) {
            return answer.getEndTime()===undefined;
        });
    };

    return QnaThreadsManager;
});
