kwpApp.controller("audioPreviewController", function ($scope, $q, captureService) {
	$scope.template = "./partials/audioPreviewView.html";
	$scope.showPreview = false;

	var deferred = $q.defer();
	var ready = deferred.promise;
	//$scope.$watch('preview', function () { alert("hh"); }, true);
	$scope.onpreview = function showPreviewChanged() {
		$scope.preview = !$scope.preview;
		//alert("AA" + $scope.showPreview);
		try {
			if ($scope.preview) {
				ready.then(function (previewDevice) {
					/// alert(JSON.stringify(previewDevice));
					captureService.startPreviewDevice(previewDevice, "audioPreview");
				});
			}
			else {
				ready.then(function (previewDevice) {
					captureService.stopPreviewDevice(previewDevice);
				});
			}
		}
		catch (a) {
			alert(a);
		}
	};

	var previewDevice = null;
	$(document).ready(function () {
		captureService.enumDevice.then(function (devices) {
			for (i = 0; i < devices.length; i++) {
				var element = devices[i];

				if (element.DeviceType == "Audio") {

					previewDevice = devices[i];
					deferred.resolve(previewDevice);
					$scope.showPreview = true;
					$scope.onpreview();
					break;
				}
			}
		});

	});
});