
'use strict'

kQna.filter('lastCreated',['gettextCatalog',function(gettextCatalog){
    // In the return function, we must pass in a single parameter which will be the data we will work on.
    // We have the ability to support multiple other parameters that can be passed into the filter optionally
    var minutesText = " "+gettextCatalog.getString("mins");
    var nowText = gettextCatalog.getString("Now");
    var multipleHoursText = gettextCatalog.getString("hrs");
    var singleHourText = gettextCatalog.getString("hr");

    return function(lastUpdateTime,createdAt) {

        lastUpdateTime/=1000;


        var hours = parseInt( lastUpdateTime / 3600 );
        var minutes = parseInt( lastUpdateTime / 60 ) % 60;
        var seconds = lastUpdateTime % 60;

        if (hours === 0) {
            //Between 1 to 2 minutes
            if (minutes <= 1) {

                return "Now";

            }

            return minutes + " " + minutesText;
        }
        //Assumption: >= than 1 hour ago.
        if(hours < 24){

            return hours + ((hours === 1) ? " " + singleHourText : " " + multipleHoursText);

        }
        else{

            return createdAt.toLocaleDateString();
        }
    };
}]);