angular.module('myApp', ['cfp.hotkeys'])
    .config(function(hotkeysProvider) {
        hotkeysProvider.includeCheatSheet = true;
    });

angular.module('kAPI').constant("kserviceinfo", kserviceInfo);
angular.module('kUpgradeModule').constant("kUpgradeServiceInfo", kUpgradeServiceInfo);

var kwpApp = angular.module('kwpApp', ['gettext', 'ngAnimate', 'ui.bootstrap', 'cfp.hotkeys', 'kngLayout', 'kAPI',
                                        'kUpgradeModule', 'kQna','kUI','kCef','webcastInfoModule','monitoringModule', 'k-deck-slides',
                                        'kaPlayerViewControl', 'kEntryManagementModule','kProducerActions', 'kPolls', 'linky',
                                        'k-assets-timeline', 'k-main-asset','kRTC']);
kwpApp.constant("kUpgradeServiceInfo",kUpgradeServiceInfo);


// Set the language to use the translations in.
// Without this code, or with 'en' we default to English.
// the strings that can be passed passed to setCurrentLanguage are all the valid LCIDs
// The ones we support are in po/translations.js
angular.module('kwpApp').run(function (gettextCatalog) {
    gettextCatalog.setCurrentLanguage('en');
});

kwpApp.config(function(){
    Number.prototype.pad = function (size) {
        var s = String(this);
        if (typeof (size) !== "number") { size = 2; }

        while (s.length < size) { s = "0" + s; }
        return s;
    }
});

kwpApp.config(['$compileProvider', function($compileProvider) {
    $compileProvider.aHrefSanitizationWhitelist(/^\s*(https?|file|client):/);
    $compileProvider.imgSrcSanitizationWhitelist(/^\s*(https?|file|client|data):/);
}]);

kwpApp.config(function($sceDelegateProvider, $logProvider){
    $logProvider.debugEnabled(true);
    $sceDelegateProvider.resourceUrlWhitelist([
        'self',
        "http://cdnapi.kaltura.com/**"
    ]);
});

kwpApp.config(function($tooltipProvider) {

    //This is currently a temporary fix for the tooltip.
    $tooltipProvider.options({ animation : false, appendToBody: true,popupDelay:1000 });

});



/* We add the HTTP interceptor to our application. */
kwpApp.config(['$httpProvider', function($httpProvider) {
    $httpProvider.interceptors.push('onlineStatusService');
}]);

kwpApp.run(function (upgradeService, captureService, kdialog, $rootScope) {
    $rootScope.safeApply = function safeApply(operation, $scope) {

        var phase = this.$root.$$phase;
        if (phase !== '$apply' && phase !== '$digest') {
            this.$apply(operation);
            return;
        }

        if (operation && typeof operation === 'function')
            operation();
    };

    upgradeService.checkForUpdates();
});

// prevent horizontal scroll, which causes undesired UI states
$(function() {
    var $body = $(document);
    $body.bind('scroll', function() {
        // "Disable" the horizontal scroll.
        if ($body.scrollLeft() !== 0) {
            $body.scrollLeft(0);
        }
    });
});

var oldMouseStart = $.ui.draggable.prototype._mouseStart;
$.ui.draggable.prototype._mouseStart = function (event, overrideHandle, noActivation) {
    this._trigger("beforeStart", event, this._uiHash());
    oldMouseStart.apply(this, [event, overrideHandle, noActivation]);
};
