/* globals _ */
/* globals PromisesQueue */
/* globals Promise */

(function () {
    "use strict";

        function Service(kProducerActions, kalturaService, kaPlayerViewControlsService,
                         slidesService, $q, $rootScope, kPollsService, POLL_STATE_STATUS,
                        ASSET_TYPE, POLL_STATE, PLAYER_VIEW_STATE, PLAYER_VIEW_MODE)
        {
            var self = this;

            var isReadyPromise = null;

            var slideEntryCompositeKey = ['slideName','documentEntryId'];

            var timelineAssets = [];

            var currentTimelineAsset = null;

            function load()
            {

                var deferred = $q.defer();

                kProducerActions.isReady().then(function()
                {
                    //initial sync
                    syncProducerStateActions();

                    // listen to state changes from server and act accordingly
                    kProducerActions.subscribeStateChange(function() {
                        syncProducerStateActions();

                        // if there was a change in the timeline-assets-state - broadcast it
                        var timelineState = kProducerActions.getState('actions.timeline-assets-state');
                        var currentTimeLineState = getTimeLineAssetState();
                        if (!angular.equals(currentTimeLineState, timelineState)) {
                            var broadcastOptions = {};
                            if(timelineState && timelineState.docId !== currentTimeLineState.docId) {
                                broadcastOptions.recalcLayoutOptions = {
                                    'elementSizes': 'regular',
                                    'renderAllSlides': true
                                };
                            }
                            updateTimelineAssets(broadcastOptions);
                        }
                    });

                    //listen to instance changes from server

                    kProducerActions.subscribeInstanceChange(function () {
                    // TODO [es] filter by type of instance
                        updateTimelineAssets();
                        klog.info('kAssetsTimelineService.load(): broadcasting "AssetsTimelineReady"');
                    });

                    //get every presentation changes
                    $rootScope.$on("PresentationChanged", function() {
                        //update local assets array
                        updateTimelineAssets();
                        //sync actions state
                        syncProducerStateActions();
                    });

                    //load slides
                    slidesService.load().then(
                        function (res) {
                            if(res && res.state) {
                                //support fist load with poll and with no slides
                                if (res.state === 'no_change') {
                                    updateTimelineAssets();
                                    syncProducerStateActions();
                                }
                            }
                            deferred.resolve({});
                        },
                        function (err) {
                            deferred.reject(err);
                        }
                    );

                    $rootScope.$on('pollAdded',
                        function(event, data) {
                            if(data && data.poll) {
                                var prevIndex = (data.prevIndex !== undefined) ? data.prevIndex : -1;
                                addAsset(prevIndex, data.poll, ASSET_TYPE.POLL);
                            }
                        }
                    );

                });
                //return after all preparations ended
                isReadyPromise =  deferred.promise;

            }

            function syncProducerStateActions ()
            {
                //active asset update
                var selectedAsset = null;
                switch(kProducerActions.getState('podcasting.type')) {
                    case '':
                        currentTimelineAsset = null;
                        break;
                    case 'thumb':
                        selectedAsset = kProducerActions.getState('actions.select-a-thumb');
                        if(selectedAsset) {
                            selectedAsset.type = 'slide';
                            if(!areAssetsDataEquals(currentTimelineAsset, selectedAsset)) {
                                var existingAsset = findAssetInCurrentAssetsTimeline(selectedAsset);
                                if(existingAsset) {
                                    currentTimelineAsset = existingAsset;
                                } else {
                                    currentTimelineAsset = _.clone(selectedAsset);
                                    currentTimelineAsset.detached = true;
                                }
                            }
                        } else {
                            currentTimelineAsset = null;
                        }
                        break;
                    case 'poll':
                        selectedAsset = kProducerActions.getState('actions.select-poll-state');
                        if(selectedAsset) {
                            selectedAsset.type = 'poll';
                            var matchingAsset = findAssetInCurrentAssetsTimeline(selectedAsset);

                            if(!areAssetsDataEquals(currentTimelineAsset, matchingAsset)) {
                                currentTimelineAsset = _.clone(matchingAsset);
                                kPollsService.setSelectedPoll(currentTimelineAsset);
                            }
                        } else {
                            currentTimelineAsset = null;
                        }
                        break;
                    default :
                        break;
                }
            }

            function updateTimelineAssets(broadcastOptions)
            {
                // TODO - check if an actual change occurred
                //init timeline assets
                timelineAssets = [];
                //update timeline assets
                if(checkTimelineAssetsStateRelevancy()) {
                    //order timeline assets according to timelineAssetsState
                    syncTimelineAssetsWithState();
                } else {
                    //order timeline assets:
                    //polls first (ordered by their position in timelineAssetsState) and then slides
                    orderTimelineAssetsWithoutSate();
                }
                $rootScope.$broadcast("AssetsTimelineChanged", broadcastOptions);

            }

            /**
             * checks if timelineAssets state exists and if current docId equals to state docId
             * @returns {boolean}
             */
            function checkTimelineAssetsStateRelevancy()
            {
                var currentDocId = kProducerActions.getState('actions.select-deck-document.documentEntryId');
                var timelineState = kProducerActions.getState('actions.timeline-assets-state');
                if(timelineState && timelineState.docId) {
                    return currentDocId === timelineState.docId;
                } else {
                    return false;
                }
            }

            /**
             * sync current timeline assets array order with timeline state order
             */
            function syncTimelineAssetsWithState()
            {
                var slides = slidesService.getPresentationSlides();
                var polls = kPollsService.getActivePollsDataAsArray();

                var timelineState = getTimelineStateAsArray();
                _.each(timelineState, function (assetId) {

                    var slideAsset = _.find(slides, function (slide) {
                       return slide.slideName === assetId;
                    });
                    if(slideAsset) {
                        timelineAssets = timelineAssets.concat(slideAsset);
                    } else {
                        var pollAssetIndex = _.findIndex(polls, function (poll) {
                            return poll.id === assetId;
                        });
                        if(pollAssetIndex > -1) {
                            var pollAsset = polls.splice(pollAssetIndex, 1);
                            timelineAssets = timelineAssets.concat(pollAsset);
                        }
                    }
                });
                //if exists, adding unordered polls to the timeline head
                if(!_.isEmpty(polls)) {
                    Array.prototype.unshift.apply(timelineAssets, polls);
                }
            }

            /**
             * order current timeline assets array (not using timeline state order - just for polls if exists)
             */
            function orderTimelineAssetsWithoutSate()
            {
                var slides = slidesService.getPresentationSlides();
                var polls = kPollsService.getActivePollsDataAsArray();
                var reorderedPolls = [];

                var timelineState = getTimelineStateAsArray();
                //reorder polls if needed
                if(timelineState) {
                    //reorder polls according to relative order in timeline state
                    _.each(timelineState, function (assetId) {
                        var pollAssetIndex = _.findIndex(polls, function (poll) {
                            return poll.id === assetId;
                        });
                        if(pollAssetIndex > -1) {
                            var pollAsset = polls.splice(pollAssetIndex, 1);
                            reorderedPolls = reorderedPolls.concat(pollAsset);
                        }
                    });
                    //if exists, adding unordered polls to the timeline head
                    if(!_.isEmpty(polls)) {
                        reorderedPolls = polls.concat(reorderedPolls);
                    }
                } else {
                    reorderedPolls = polls;
                }
                //add slides ti timeline assets array
                timelineAssets = reorderedPolls.concat(slides);
            }

            /**
             * Gets the url of current asset even when it is not asset of current deck/presentation
             * @returns {*} a deferred response with the url of currently shown poll
             */
            function getCurrentAssetUrl()
            {
                var deferred = $q.defer();

                if (currentTimelineAsset) {
                    if(currentTimelineAsset.type === ASSET_TYPE.SLIDE) {
                        getCurrentSlideUrl(deferred);
                    }
                }else {
                    // there isn't an active asset - reject the request
                    deferred.reject({});
                }

                return deferred.promise;
            }

            function getTimelineStateAsArray()
            {
                var timelineState = kProducerActions.getState('actions.timeline-assets-state');
                if(timelineState && timelineState.state) {
                    //get the state string
                    timelineState = timelineState.state;
                    //convert to array
                    return timelineState.split(',');
                }

                return null;
            }
            
            function getCurrentSlideUrl(deferred)
            {
                // the producer is currently showing a slide (has active slide either part of current deck or of previous one)
                if (currentTimelineAsset.src) {
                    // the active slide is part of current deck - just return the current slide url.
                    // if it wasn't the current slide object will be without this property (and others)
                    deferred.resolve({url: currentTimelineAsset.src});
                }else if (currentTimelineAsset.documentEntryId && currentTimelineAsset.slideName) {
                    //this is not a slide from this current slide deck, need to fetch its' url from server
                    kalturaService.getDocumentSlides(currentTimelineAsset.documentEntryId).then(function (result) {
                        var url = result.slidesUrl;
                        var baseUrl = url.substring(0, url.lastIndexOf('/') + 1);
                        deferred.resolve({url : baseUrl + currentTimelineAsset.slideName});
                    }, function (error) {
                        deferred.reject({});
                    });
                } else {
                    // we have no way to determine the url of current slide - reject the request
                    deferred.reject({});
                }
            }

            /**
             * Adding pollAsset after specific asset if provided
             * @param prevAssetIndex
             * @param assetData
             * @param type
             */
            function addAsset(prevAssetIndex, assetData, type)
            {
                if (assetData) {
                    if(type === ASSET_TYPE.POLL) {
                        //update current timeline assets array with new poll
                        var tempPoll = createAsset(
                            {
                                id: assetData.id,
                                state: POLL_STATE.ACTIVE,
                                answers: {},
                                question: {},
                                type : ASSET_TYPE.POLL
                            });

                        timelineAssets.splice(prevAssetIndex + 1, 0, tempPoll);
                        updateTimeLineAssetsState({ updateServer: true });
                    }
                }
            }

            function changeAssetToPosition(oldPosition, newPosition) {
                var tmp = timelineAssets[oldPosition];
                timelineAssets.splice(oldPosition, 1);
                timelineAssets.splice(newPosition, 0, tmp);
            }

            function updateTimeLineAssetsState(options) {

                if (options && options.updateServer) {
                    // get the last state & current state and do stuff only if we had a change
                    var oldTimeLineState = kProducerActions.getState('actions.timeline-assets-state');
                    var currentTimeLineState = getTimeLineAssetState();
                    if (!angular.equals(currentTimeLineState, oldTimeLineState)) {
                        kProducerActions.setStateOrientedAction(
                            'timeline-assets-state',
                            currentTimeLineState
                        );
                        $rootScope.$broadcast("AssetsTimelineChanged");
                    }
                }
                else {
                    $rootScope.$broadcast("AssetsTimelineChanged", {
                        'recalcLayoutOptions': {
                            'elementSizes': 'same',
                            'renderAllSlides': true
                        }
                    });
                }
            }

            function getTimeLineAssetState() {
                //updating timeline state with the new timeline assets order
                var currentDocInfo = slidesService.getActiveDocumentInfo();
                var currentDocId = currentDocInfo ? currentDocInfo.id : '';
                return {docId : currentDocId , state : extractStateFromTimelineAssets()};
            }
            
            function extractStateFromTimelineAssets()
            {
                var timelineState = [];
                _.each(timelineAssets, function (asset) {
                   var assetId = asset.id ? asset.id : asset.slideName;
                    timelineState.push(assetId);
                });
                return timelineState.join();
            }

            function areAssetsDataEquals(assetA, assetB)
            {
                if (!assetA && !assetB) {
                    return true;
                }
                if (!assetA || !assetB) {
                    return false;
                }
                if(assetA.type !== assetB.type) {
                    return false;
                }
                if(assetA.type === ASSET_TYPE.SLIDE) {
                    return areSlidesDataEquals(assetA, assetB);
                }
                if(assetA.type === ASSET_TYPE.POLL) {
                    return arePollsDataEquals(assetA, assetB);
                }
            }

            function areSlidesDataEquals(slideA,slideB)
            {
                var result = true;
                _.each(slideEntryCompositeKey,function(propertyName) {
                    if (slideA[propertyName] !== slideB[propertyName]) {
                        result = false;
                    }
                });
                return result;
            }

            function arePollsDataEquals(pollA, pollB)
            {
                return angular.equals(pollA, pollB);
            }

            function findAssetInCurrentAssetsTimeline(assetState)
            {
                if(assetState.type === ASSET_TYPE.SLIDE) {
                    var existingSlide = _.find(timelineAssets,{documentEntryId : assetState.documentEntryId, slideName : assetState.slideName});
                    if(existingSlide) {
                        return existingSlide;
                    }
                } else if (assetState.type === ASSET_TYPE.POLL) {
                    var existingPoll = _.find(timelineAssets,{id : assetState.pollId});
                    if(existingPoll) {
                        return existingPoll;
                    }
                }
                return null;
            }

            function changeAsset(asset)
            {
                if (!asset) {
                    klog.info('kAssetsTimelineService.changeAsset(): requested to remove current asset.');

                    if (!currentTimelineAsset) {
                        klog.info('kAssetsTimelineService.changeAsset(): not showing any asset at the moment');
                        return;
                    }

                    currentTimelineAsset = null;

                    kProducerActions.setStateOrientedAction('remove-selected-thumb',{});
                    kaPlayerViewControlsService.changeViewById(PLAYER_VIEW_MODE.VIDEO_ONLY, PLAYER_VIEW_STATE.LOCKED);
                }else {
                    klog.info('kAssetsTimelineService.changeAsset(): replacing current asset with "{0}"', JSON.stringify(asset));

                    if (areAssetsDataEquals(currentTimelineAsset, asset)) {
                        klog.info('kAssetsTimelineService.changeAsset(): the requested asset is equal to active asset. ignore request');
                        return;
                    }

                    if(asset.type === ASSET_TYPE.SLIDE) {
                        slidesService.changeSelectedSlide(asset);
                    } else if (asset.type === ASSET_TYPE.POLL) {
                        kPollsService.changeSelectedPoll(asset);
                    }

                    currentTimelineAsset = createAsset(asset);
                }
            }

            /**
             * Creates Asset per type
             * @param asset
             * @returns {*}
             */
            function createAsset(asset)
            {
                if (asset.type === ASSET_TYPE.SLIDE) {
                    return {
                        documentEntryId: asset.documentEntryId,
                        src: asset.src,
                        index: asset.index,
                        title: asset.title,
                        notes: asset.notes,
                        content: asset.content,
                        id: asset.id,
                        slideName: asset.slideName,
                        type: 'slide'
                    };
                } else if (asset.type === ASSET_TYPE.POLL) {
                    return {
                        data: asset.data || {},
                        text: asset.text || {},
                        id: asset.id || '',
                        type: 'poll'
                    };
                }
            }

            /**
             * get prev asset only if not out of array bounds
             * if out of bound return current
             * @returns {*}
             */
            function gotoPrevAsset()
            {
                var asset = getPrevAsset(currentTimelineAsset, false);
                if(!asset) {
                    return currentTimelineAsset;
                }
                return asset;
            }

            /**
             * get next asset only if not out of array bounds
             * if out of bound return current
             * @returns {*}
             */
            function gotoNextAsset()
            {
                var asset = getNextAsset(currentTimelineAsset, false, false);
                if(!asset) {
                    return currentTimelineAsset;
                }
                return asset;
            }

            /**
             * get next asset
             * @param asset current asset
             * @param isAdded is a new asset added - won't need to verify broadcasting validity
             * @param broadcast if asset should be broadcasted
             * @returns {*}
             */
            function getNextAsset (asset, isAdded, broadcast)
            {
                if (asset && timelineAssets.length > 0) {
                    var assetIndex = getAssetIndexInTimelineArray(asset);
                    return getNextValidAsset(assetIndex + 1, isAdded, broadcast);
                }
                else {
                    if (timelineAssets.length > 0) {
                        //get first valid asset
                        return getNextValidAsset(0, isAdded, broadcast);
                    }else {
                        return null;
                    }
                }
            }

            /**
             * get prev asset
             * @param asset
             * @param broadcast is this asset should be broadcasted
             * @returns {*}
             */
            function getPrevAsset (asset, broadcast)
            {
                if (asset && timelineAssets.length > 0) {
                    var assetIndex = getAssetIndexInTimelineArray(asset);
                    return getPrevValidAsset(assetIndex - 1, broadcast);
                }
                else {
                    if (timelineAssets.length > 0) {
                        //get first valid asset
                        return getNextValidAsset(0, false, broadcast);
                    }else {
                        return null;
                    }
                }
            }

            /**
             * get first prev valid asset
             * @param startingIndex
             * @param broadcast if asset should be broadcasted
             * @returns {*}
             */
            function getPrevValidAsset(startingIndex, broadcast)
            {
                while (startingIndex >= 0) {
                    if(!broadcast || canBroadcastAsset(timelineAssets[startingIndex])) {
                        return timelineAssets[startingIndex];
                    }
                    startingIndex--;
                }
                //no prev valid asset was found
                return null;
            }

            /**
             * get next valid asset
             * @param startingIndex
             * @param isAdded is a new asset added - won't need to verify broadcasting validity
             * @param broadcast if asset shuold be broadcasted
             * @returns {*}
             */
            function getNextValidAsset(startingIndex, isAdded, broadcast)
            {
                while (startingIndex < timelineAssets.length) {
                    if(!broadcast || isAdded || canBroadcastAsset(timelineAssets[startingIndex])) {
                        return timelineAssets[startingIndex];
                    }
                    startingIndex++;
                }
                //no valid asset was found
                return null;
            }

            /**
             * return an asset object by a given ID
             * @param assetId
             * @returns {*}
             */
            function getAssetById(assetId)
            {
                return _.find(timelineAssets, function (asset) {
                   return asset.type === 'poll' ?
                            asset.id === assetId : asset.slideName === assetId;
                });
            }

            function getAssetByIndex(index)
            {
                return timelineAssets[index];
            }

            function debugFlipToNextAsset ()
            {
                try {
                    currentTimelineAsset = gotoNextAsset();
                }
                catch(e)
                {

                }
            }

            function getAssetIndexInTimelineArray(asset)
            {
                if(asset) {
                    return _.findIndex(timelineAssets, function (obj) {
                        if(obj.type === asset.type) {
                            if(obj.type === 'poll') {
                                return obj.id === asset.id;
                            } else {
                                return obj.slideName === asset.slideName;
                            }
                        }else {
                            return false;
                        }
                    });
                } else {
                    return -1;
                }

            }

            function isReady()
            {
                return isReadyPromise;
            }

            /**
             * returns if current displayed asset is locking timeline from replacing it
             * @returns {boolean}
             */
            function isTimelineLocked()
            {
                var locked = false;
                if(currentTimelineAsset && currentTimelineAsset.type === 'poll') {
                    var pollState = kProducerActions.getState('actions.select-poll-state');
                    locked = pollState && pollState.status === POLL_STATE_STATUS.IN_PROGRESS;
                }
                return locked;
            }

            /**
             * return true if a given asset is valid for broadcast
             * @param asset
             * @returns {boolean|*}
             */
            function canBroadcastAsset(asset)
            {
                var broadcast = true;
                if(asset && asset.type === ASSET_TYPE.POLL) {
                    broadcast = kPollsService.isPollComplete(asset);
                }
                return broadcast;
            }

            Object.defineProperty(Service.prototype, "currentTimelineAsset", {
                get: function () {
                    return currentTimelineAsset;
                },
                set: function (asset) {
                    changeAsset(asset);
                }
            });

            Object.defineProperty(Service.prototype, "timelineAssets", {
                get: function () {
                    return timelineAssets;
                }
            });


            /**
             * load service
             * Usage will be valid only after isReady
             */
            load();
            
            /**
             * public functions
             */

            self.getCurrentAssetUrl = getCurrentAssetUrl;
            self.changeAsset = changeAsset;
            self.getNextAsset = getNextAsset;
            self.getPrevAsset = getPrevAsset;
            self.gotoNextAsset = gotoNextAsset;
            self.gotoPrevAsset = gotoPrevAsset;
            self.getAssetById = getAssetById;
            self.getAssetIndexInTimelineArray = getAssetIndexInTimelineArray;
            self.isTimelineLocked = isTimelineLocked;
            self.canBroadcastAsset = canBroadcastAsset;
            self.debugFlipToNextAsset = debugFlipToNextAsset;
            self.changeAssetToPosition = changeAssetToPosition;
            self.getAssetByIndex = getAssetByIndex;
            self.updateTimeLineAssetsState = updateTimeLineAssetsState;

            self.isReady = isReady;

        }


    angular.module('k-assets-timeline').service('kAssetsTimelineService', Service);

})();