(function() {
    'use strict';

    angular.module('kAPI').factory('sessionService', ['$rootScope','kalturaService','asyncLiveEntryInfoService','$injector','kdialog','kCef', function sessionService($rootScope, kalturaService, asyncLiveEntryInfoService, $injector, kdialog,kCef){

        var streamProfiles = [buildEncodingConfig([900, 640, 480])];
        var title = null;
        var rtmpURL = null;
        var totalDurationBeforeCurrentSegment;
        var RECORD_STATUS =  {DISABLED:0, APPENDED:1, PER_SESSION:2};
        var recordStatus = RECORD_STATUS.APPENDED;
        var recordedEntryId;

        var OnEntryInfoChangeFunc = function(res){

            klog.debug(res);

            title = res.name;
            rtmpURL = res.primaryBroadcastingUrl + "/" + res.streamName;
            recordStatus = res.recordStatus;
            recordedEntryId = res.recordedEntryId;

            if (!configuration.streamProfiles) {
                streamProfiles = [];

                res.bitrates.forEach(function (config) {

                    streamProfiles.push(buildEncodingConfig(config));

                });
            } else {

                streamProfiles=configuration.streamProfiles;
            }

            totalDurationBeforeCurrentSegment = res.lastElapsedRecordingTime;

            klog.info("Profiles = " + JSON.stringify(streamProfiles));
        };




        function refreshEntryInfoPromise() {

            return kalturaService.getLiveEntryInfo(configuration.MediaEntryId).then(function (res) {

                OnEntryInfoChangeFunc(res);

            }, function (error) {
                if (error.message==='Invalid KS "EXPIRED", Error "-1,INVALID_STR"') {
                    error.message="Session has expired, please refresh your browser and relaunch app";
                }
                kdialog.alert(error.message,true).then( function () {
                   window.close();
                });
            });
        }

        var sessionInfoPromise = refreshEntryInfoPromise();

        var readyPromise = Promise.all([sessionInfoPromise]).then(function(){
            $rootScope.$broadcast('sessionloaded');
            klog.info('raising sessionloaded event');

            asyncLiveEntryInfoService.registerOnEntryInfoChange(OnEntryInfoChangeFunc);

        }, function(){
            klog.error('sessionInfoPromise failed');
        });

        function buildEncodingConfig(config) {
            return {
                bitrate: config[0],
                width: config[1],
                height: config[2]
            };
        }


        var loadedEvents=[];
        function isApplicationReady(ev) {
            klog.info("isApplicationReady with " + ev.name);
            loadedEvents[ev.name]=true;
            if (loadedEvents.dialogpoped || ( loadedEvents.sessionloaded && loadedEvents.timelineloaded && loadedEvents.mainSlideLoaded && loadedEvents.produceractionsloaded)) {
                try {
                    if (kCef.available) {
                        kCef.sendIPC("loaded");
                    }
                }
                catch(e) {
                    klog.error(e);
                }
            }
        }
        function goLive() {
            return asyncLiveEntryInfoService.updateLiveEntryInfo({ viewMode: 1, recordingStatus: 2 },true);
        }

        function endLive() {
            return asyncLiveEntryInfoService.updateLiveEntryInfo({ viewMode: 0, recordingStatus: 0 },true);
        }

        $rootScope.$on('sessionloaded', isApplicationReady);
        $rootScope.$on('timelineloaded', isApplicationReady);
        $rootScope.$on('mainSlideLoaded', isApplicationReady);
        $rootScope.$on('dialogpoped', isApplicationReady);
        $rootScope.$on('produceractionsloaded', isApplicationReady);

        return {
            RECORD_STATUS: RECORD_STATUS,
            ready: readyPromise,
            getTitle: function() {
                return title;
            },
            getStreamProfiles: function() {
                return streamProfiles;
            },
            getUrl: function(){
               return rtmpURL;
            },
            getTotalDurationBeforeCurrentSegment: function() {
                return totalDurationBeforeCurrentSegment;
            },
            getRecordStatus: function(){
                return recordStatus;
            },
            getRecordedEntryId: function(){
                return recordedEntryId;
            },
            goLive: function() {
                return goLive();
            },
            endLive: function() {
                return endLive();
            }
        };
    }]);
})();