'use strict';

kQna.directive('kwpQnaScreenAnswerOnAirSelect',['gettextCatalog', function (gettextCatalog) {

    return {
        restrict : 'E',

        link: function (scope, elem, attrs) {

            scope.currentHighlightedIndex = 0;

            scope.answerOnAirOnEntryClick = function(index){

                scope.currentHighlightedIndex = index;

            };

            scope.selectEntry = function(){

                scope.setCurrentSelectedEntry(scope.currentSelectedThread.entries[scope.currentHighlightedIndex]);

                scope.setQnaState(scope.QNA_STATES.ANSWER_ON_AIR_RECORD);

            };
            scope.closeWindow = CreateCommand("", "kicon-close2", function () {

                scope.setQnaState(scope.QNA_STATES.ANSWER);

            },gettextCatalog.getString("Close"));

        },
        templateUrl: "../common/kQna/views/kwpQnaScreenAnswerOnAirSelect.html"
    };
}]);

