
kngLayout.addPanelDirective("kSplitPanel", function() {

    return {
        scope: {
            orientation: '@',
            showHandle : '='

        },
        link: function ($scope) {

            var scope = $scope;
            var drag = false;
            var lastPanelSize = null;
            var vertical = scope.orientation === 'vertical';
            var classes = (vertical) ? "split-handler-vertical split-handler-resize" : "split-handler-horizontal split-handler-resize";
            if (scope.showHandle) {
                classes+=(!vertical)  ? " split-handler-horizontal-handle" : " split-handler-vertical-handle";
            }
            var handler = angular.element('<div class="'+classes+' split-handler-resize"></div>');

            var pane1, pane2;

            var lastPos = null;
            var lastPosBeforeDlbClick=null;

            var changeSplitPoint = function (pos) {



                var bounds = scope.elem[0].getBoundingClientRect();
                //klog.debug("vertical=" + vertical + ";" + "pos="+pos);




                lastPos = pos;
                //     $(handler[0]).addClass("split-handler-resize");


                var pane1Min,pane2Min;

                if (!vertical) {

                    var height = bounds.bottom - bounds.top;

                    lastPanelSize=height;

                    pane1Min = pane1.getMinSizeValue(height) || 100;
                    pane2Min = pane2.getMinSizeValue(height) || 100;

                    var releventPos = pos;

                    if (pos < pane1Min) return;

                    if (height - pos < pane2Min) {
                        releventPos = height-pane2Min;
                    }

                    var e1h = releventPos * 100 / height;
                    var e2h = (height - releventPos) * 100 / height;
                    if (pane2.isVisible()) {
                        handler.css('top', e1h + '%');
                        pane1.elem.css('height', e1h + '%');
                        pane2.elem.css({
                            'top': e1h + '%',
                            'height': (e2h) + '%',
                            'right': '0%',
                            'width': '100%'
                        });
                    } else {
                        pane1.elem.css('height', '100%');
                        pane2.elem.css('display', 'none');
                        handler.css('display', 'none');

                    }

                } else {

                    var width = bounds.right - bounds.left;

                    pane1Min = pane1.getMinSizeValue(width) || 100;
                    pane2Min = pane2.getMinSizeValue(width) || 100;

                    if (!lastPosBeforeDlbClick && pos < pane1Min) return;
                    if (!lastPosBeforeDlbClick && width - pos < pane2Min) return;

                    var e1w = pos * 100 / width;
                    var e2w = (width - pos) * 100 / width;
                    if (lastPosBeforeDlbClick) {
                        e2w = (width - lastPosBeforeDlbClick) * 100 / width;
                    }
                    var hw = (pos - 2) * 100 / width;

                    handler.css('left', hw + '%');
                    pane1.elem.css('width', e1w + '%');
                    pane2.elem.css({
                        'left': e1w + '%',
                        'width': (e2w) + '%',
                        'top': '0%',
                        'height': '100%'
                    });
                }
            }

            var initHandler = function () {
                //    if (scope.resizeSplitter==undefined ||
                //      scope.resizeSplitter==true) {
                scope.elem.bind('mousemove', function (ev) {
                    if (!drag) return;

                    var bounds = scope.elem[0].getBoundingClientRect();

                    if (!vertical) {
                        //  klog.debug("mousemove vertical with", ev.clientY - bounds.top);
                        changeSplitPoint(ev.clientY - bounds.top);
                    }
                    else {
                        //// klog.debug("mousemove horizontal with", ev.clientX - bounds.left);
                        changeSplitPoint(ev.clientX - bounds.left);
                    }

                    scope.recalcLayout();

                });

                handler.bind('mousedown', function (ev) {
                    ev.preventDefault();
                    drag = true;
                });
                handler.bind('dblclick',function(ev) {

                    var bounds = scope.elem[0].getBoundingClientRect();

                    [pane1.elem,pane2.elem,handler].forEach(function (elem) {
                        elem.addClass( "fillPanelAnimation" );
                        elem.bind('transitionend',function(e) {
                            elem.removeClass( "fillPanelAnimation" );

                        });
                    });
                    if (lastPosBeforeDlbClick) {
                        changeSplitPoint(lastPosBeforeDlbClick);
                        lastPosBeforeDlbClick=null;
                    } else {
                        lastPosBeforeDlbClick=lastPos;
                        changeSplitPoint(bounds.right-45);
                    }
                });

                angular.element(document).bind('mouseup', function (ev) {
                    drag = false;
                });
                //  }
            };

            var initialize = function () {
                if (pane1 && pane2) {
                    return;
                }

                if (scope.panels.length === 2 && scope.elem) {
                    pane1 = scope.panels[0];
                    pane2 = scope.panels[1];
                    pane1.elem.after(handler);

                    initHandler();

                    var splitPoint=pane1.size;
                    if (splitPoint === undefined)  {
                        if (pane2.size) {
                            if (!vertical) {
                                splitPoint = pane2.getSizeValue(scope.elem[0].clientHeight);
                            } else {
                                splitPoint = pane2.getSizeValue(scope.elem[0].clientWidth);

                            }
                        } else {
                            splitPoint=300;
                        }
                    }
                    if (!vertical) {
                        changeSplitPoint(scope.elem[0].clientHeight - splitPoint);
                    }else {
                        changeSplitPoint(scope.elem[0].clientWidth- splitPoint);

                    }
                }
            };

            initialize();

            return {
                childAdded: function (panel) {
                    initialize();
                  //  panel.elem.addClass( "fillPanelAnimation" );

                },
                resize:function() {
                    if (lastPos !== null) {
                        var bounds = scope.elem[0].getBoundingClientRect();
                        if (!vertical) {
                            var newHeight = bounds.bottom - bounds.top;
                            lastPos = newHeight - (lastPanelSize - lastPos);
                        } else {
                            var newWidth = bounds.right - bounds.left;
                            lastPos = newWidth - (lastPanelSize - lastPos);

                        }
                    }
                },
                recalcLayout: function () {
                    initialize();
                    if (pane1 && pane2 &&
                        lastPos !== null) {
                        changeSplitPoint(lastPos);
                    }

                }
            };
        }
    };
});

