kngLayout.addPanelDirective("kUniformPanel", function() {
    return {
        scope: {
            orientation: '@'
        },
        link: function ($scope) {

            var scope = $scope;

            return {
                childAdded: function (panel) {

                    //klog.debug("a");
                },
                recalcLayout: function () {
                    var clientWidth = $scope.elem[0].clientWidth;
                    var clientHeight = $scope.elem[0].clientHeight;

                    var runner = 0;
                    var dx = 100 / $scope.panels.length;

                    for (var i = 0; i < $scope.panels.length; i++) {
                        var child = $scope.panels[i];

                        if ($scope.orientation === 'vertical') {
                            $(child.elem).css(
                                {
                                    'position': 'absolute',
                                    'top': runner + '%',
                                    'left': '0px',
                                    'width': '100%',
                                    'height': dx + '%'
                                });
                        }
                        else {
                            $(child.elem).css(
                                {
                                    'position': 'absolute',
                                    'left': runner + '%',
                                    'top': '0px',
                                    'height': '100%',
                                    'width': dx + '%'
                                });

                        }
                        runner += dx;
                    }

                }
            };
        }
    };
});
