"use strict";

var Animate=function() {

/*
    Math.easeInQuad = function (t, b, c, d) {
        return -c/2 * (Math.cos(Math.PI*t/d) - 1) + b;

    };
*/
    Math.easeInQuad = function (t, b, c, d) {
        return b+t*c;

    };

    var animateInterval=null;
    var t=0;
    var from=undefined;
    var to=undefined;
    var lastValue=undefined;

    var interpolateFunc= Math.easeInQuad;

    Animate.prototype.transition=function(A,B,func,duration,steps) {

        function step() {

            lastValue=interpolateFunc(t,from,to-from,1);
            func(lastValue);

            t+=1/steps;

            if (t>=1)
                t=1;

            if (t>=1){
                clearInterval(animateInterval);
                animateInterval=null;
            }
        }

        t=0;

        if (animateInterval) {
            from=lastValue;
            to=B;
            step();
        }
        else {
            from=A;
            to=B;
            animateInterval = setInterval(step, duration / steps);
        }
    }
}