'use strict';

kQna.directive('kwpQnaMenuAnswer',['gettextCatalog', function (gettextCatalog) {

    var controller = ['$scope','qnaService', function ($scope,qnaService) {


    }];


    return {
        restrict : 'E',
        transclude : true,
        replace : true,

        link: function (scope, elem, attrs) {

            scope.closeAnswerWindow = CreateCommand("", "kicon-close2", function () {

                scope.setQnaState(scope.QNA_STATES.INCOMING);

            },gettextCatalog.getString("Close"));


        },
        templateUrl: "../common/kQna/views/kwpQnaMenuAnswer.html"
    };
}]);