kwpApp.factory('onlineStatusService', ['$q', '$injector','$rootScope','kCef', function ($q, $injector,$rootScope, kCef) {
    /* The service has 3 Possible states.The service itself runs in the background and offers 3 isState_X functions to know the state.:
    RUNNING: (Initial state) status running: Module is running without disturbing any requests and maybe just filtering,classifying or stamping requests.
    RECOVERING: Module is trying to recover connection.
    CONNECTION_TERMINATED_FATAL: The connection is dead and cannot be recovered.Application Should terminate.
     */

    /*This represents the recovery process object.Only the recovery process can/should change the state of the service */
    var RecoveryProcess = (function () {

        /* HACK HACKJ REMOVE */
        var oldtheAppFilter;



        var states = {ONLINE:0 , RECOVERING:1 , CONNECTION_TERMINATED_FATAL:2};

        var currentState = {state : states.ONLINE};

        var MAX_RECOVERY_ATTEMPTS = configuration.onlineRecoveryAttempts;

        var RECOVERY_WAIT_TIME_MILLISECONDS = configuration.onlineRecoveryIntervalInMS;

        var recoverThreadHandler = null;

        var currentRecoverAttempt = 1;

        var httpBuffer = (function () {

            var bufferFailed = [];

            var bufferBlocked = [];

            /** Service initialized later because of circular dependency problem. */
            /** Important: Do NOT try to initialize that as argument to service.  */
            var $http;

            /** This function Just returns the config of the request so the request can proceed as usual.*/
            function startHttpRequest(config, deffered) {

                deffered.resolve(config);

            }

            function retryHttpRequest(config, deferred) {
                function successCallback(response) {
                    deferred.resolve(response);
                }
                function errorCallback(response) {
                    deferred.reject(response);
                }
                $http = $http || $injector.get('$http');
                $http(config).then(successCallback, errorCallback);
            }

            return {
                /**
                 * Appends HTTP request configuration object with deferred response attached to buffer.
                 */
                appendFailed: function(config, deferred) {
                    bufferFailed.push({
                        config: config,
                        deferred: deferred
                    });
                },
                appendBlocked: function(config, deferred) {
                    bufferBlocked.push({
                        config: config,
                        deferred: deferred
                    });
                },
                /**
                 * Abandon or reject (if reason provided) all the buffered requests.
                 */
                rejectAll: function (reason) {
                    if (reason) {
                        for ( var i = 0; i < bufferFailed.length; i++) {

                            bufferFailed[i].deferred.reject(reason);

                        }
                        for ( i = 0; i < bufferBlocked.length; i++){

                            bufferBlocked[i].deferred.reject(bufferBlocked[i].config);

                        }
                    }
                    bufferFailed = [];
                    bufferBlocked = [];
                },
                /**
                 * Retries all the buffered requests clears the buffer.
                 */
                retryAll: function(updater) {

                    updater = typeof updater !== 'undefined' ? updater : function(arg){return arg;};

                    for(var i = 0; i < bufferFailed.length; i++){

                        retryHttpRequest(updater(bufferFailed[i].config), bufferFailed[i].deferred);
                    }

                    for (i = 0; i < bufferBlocked.length; i++) {
                        startHttpRequest(updater(bufferBlocked[i].config), bufferBlocked[i].deferred);
                    }

                    //clear buffers.
                    bufferFailed = [];
                    bufferBlocked = [];
                }
            }})();


        //This runs every RECOVERY_WAIT_TIME_MILLISECONDS milliseconds to try and recover the network.
        function _recoveryProcess(){

            currentRecoverAttempt += 1;
            if(currentRecoverAttempt > MAX_RECOVERY_ATTEMPTS){
                //We could not recover from the disconnection.
                klog.error("Recovery stops.the network connection/connection to our server died.we will notify the user and close.");
                _endRecoveryProcessFATAL();
                return;
            }
            klog.warn("Recovering network.Attempt "+currentRecoverAttempt+" out of "+MAX_RECOVERY_ATTEMPTS);
            $rootScope.$broadcast("NetworkEvent",{"event":"recovering","action":"recovering","attempts":MAX_RECOVERY_ATTEMPTS,"interval":RECOVERY_WAIT_TIME_MILLISECONDS,"current_attempt":currentRecoverAttempt});
            //We need to try to send all the requests which are in the queue.Since we are in RECOVERY state we know we have at least one request in the queue.
            httpBuffer.retryAll();
            //Buffers should be empty at this point in time.We should wait for a signal to know that requests passed so we can change our state to running.
        }



        function _endRecoveryProcess(){

            klog.info("Ending recovery process");
            currentRecoverAttempt = 1;
            clearInterval(recoverThreadHandler);

        }

        /* We call this when the network connection was not resolved and the application could not recover.Calling this makes all pending requests to return as failed. */
        function _endRecoveryProcessFATAL(){

            _endRecoveryProcess();
            klog.error("Ending recovery process FATAL.Rejecting all requests.");


            currentState.state = states.CONNECTION_TERMINATED_FATAL;

            showDialog();

            httpBuffer.rejectAll(new Error("FATAL ONLINE STATUS"));

        }

        var kdialogService;
        var dialog=null;

        function showDialog() {
            kdialogService = kdialogService || $injector.get('kdialog');

            if (dialog) {
                return;
            }
            if (kCef.available) {
                kCef.sendIPC("loaded");
            }
            dialog=kdialogService.templatedDialog({
                templateUrl: './partials/onlineStatus.html',
                controller: 'onlineController',
                keyboard: false,
                backdrop: 'static',
                resolve: {
                    config:  function () {
                        return null;
                    }
                }});
        }

        function hideDialog() {
            if (dialog) {
                kdialogService = kdialogService || $injector.get('kdialog');
                kdialogService.dismiss(dialog);
                dialog = null;
            }
        }

        function _startRecoveryProcess() {

            recoverThreadHandler = setInterval(_recoveryProcess, RECOVERY_WAIT_TIME_MILLISECONDS);

            $rootScope.$broadcast("NetworkEvent",{"event":"disconnect","action":"recovering","attempts":MAX_RECOVERY_ATTEMPTS,"interval":RECOVERY_WAIT_TIME_MILLISECONDS});

        }

        function _notifyRequestSuccess(){

            if(currentState.state === states.ONLINE){

                return;
            }

            if(currentState.state === states.RECOVERING){

                klog.info("Seems like network is back,Ending recovery process");


                currentState.state  = states.ONLINE;

                hideDialog();

                $rootScope.$broadcast("NetworkEvent",{"event":"reconnect","action":"none","attempts":MAX_RECOVERY_ATTEMPTS,"interval":RECOVERY_WAIT_TIME_MILLISECONDS});

                _endRecoveryProcess();

                //We now need to flush any requests that were added to the queue while we were recovering.If any would fail we will move again to Recovering state.
                httpBuffer.retryAll();
            }
        }

        /* Add a request that was blocked due to some reason and should be retried when Recovery decides that the network is in a valid state.The Recovery process should resolve the request with the
        config parameter and NOT to try the http request by himself.
        param[in] config - The request object.
        param[in] deffered - The promise object that was returned due to blocking and should be resolved when possible.
         */
        function _addBlockedRequest(config,deffered){

            if(currentState.state  === states.ONLINE){
                klog.error("Error blocking requests while module is running correctly.");
                alert("Internal Error.Not allowed to block requests while running correctly.");
                return;
            }
            httpBuffer.appendBlocked(config,deffered);
        }

        /*
        Notify the recover process that a request failed.The recover process should change its state respectively or just add the failure to current requests which are recovering.
        param[in] config - The failed request.
        param[in] deffered - The promise object that was returned to the caller which made the request.This promise should be resolved AFTER retrying the request using http.
         */
        function _notifyRequestFail(config,deffered){

            httpBuffer.appendFailed(config,deffered);

            if(currentState.state  === states.ONLINE){

                klog.warn("problem with the network.Seems like there is a problem with the network, we will try to recover. config="+JSON.stringify(config));


                currentState.state  = states.RECOVERING;

                showDialog();

                _startRecoveryProcess();

            }

        }

        function _isStateRunning(){
            return (currentState.state  === states.ONLINE);

        }

        function _isStateRecovering(){
            return (currentState.state  === states.RECOVERING);
        }

        function _getRecoverMessage(){
            if(currentState.state  === states.RECOVERING){
                return "Connection to server lost. Attempting to reconnect.\n" +
                    "Attempt "+currentRecoverAttempt+" of "+MAX_RECOVERY_ATTEMPTS+"\n.Please wait.";
            }
            if(currentState.state  === states.CONNECTION_TERMINATED_FATAL){
                return "Connection to server lost. Unable to reconnect.\nPlease check your internet connectivity, try restarting the application and if the problem persists contact support";
            }
            return "";
        }

        function _isStateFatal() {
            return (currentState.state === states.CONNECTION_TERMINATED_FATAL);


            //return currentState.state  === states.CONNECTION_TERMINATED_FATAL;

        }
        function _getData(){

            return currentState ;

        }

        function _getCurrentAttempt(){
            return currentRecoverAttempt;
        }
        function _getRecoveryInterval(){
            return RECOVERY_WAIT_TIME_MILLISECONDS;
        }

        function _getMaxAttempts(){
            return MAX_RECOVERY_ATTEMPTS;
        }

        return {
            /* Notify the module that a request completed successfully (some request).*/
            notifyRequestSuccess : _notifyRequestSuccess,

            /* Notify that a specific request failed.the module will try to recover it.*/
            notifyRequestFail : _notifyRequestFail,

            /* Add an http request which was blocked due to recover state */
            addBlockedRequest : _addBlockedRequest,

            isStateRunning : _isStateRunning,

            isStateRecovering : _isStateRecovering,

            isStateFatal : _isStateFatal,

            getRecoverMessage : _getRecoverMessage,

            getCurrentAttempt : _getCurrentAttempt,

            getRecoveryInterval : _getRecoveryInterval,

            getData : _getData,

            getMaxAttempts : _getMaxAttempts

        };

    })();

    var $templateCache = $templateCache || $injector.get('$templateCache');

    var shouldIgnore=function (config) {
        
        if (config.url.indexOf("api_v3/index.php")===-1) {
            return true;
        }

        return false;
    };

    return {
        /* The service is currently in recovering state.This function might be binded to a controller scope which will display the status.*/
        onlineAPI : function(){

          return {

              isOnline : RecoveryProcess.isStateRunning,
              isRecovering : RecoveryProcess.isStateRecovering,
              isFatal : RecoveryProcess.isStateFatal,
              getRecoveryAttempt : RecoveryProcess.getCurrentAttempt,
              getRecoveryInterval : RecoveryProcess.getRecoveryInterval,
              getMaxAttempts : RecoveryProcess.getMaxAttempts

          };

        },
        request: function(config) {

            if (shouldIgnore(config)) {
                return config;
            }
            //In case we are in Fatal state, we just reject the request.
            if(RecoveryProcess.isStateFatal()){
                klog.error("FATAL:New requests are not allowed using http services,app should die.REJECTING");
                return $q.reject(new Error("FATAL ONLINE STATUS"));
            }

            //We time stamp our requests when they leave for future private use.
            if(config.requestTimestamp === undefined){

                config.requestTimestamp = new Date().getTime();

            }

            //We are fine,HTTP connections are OK ant no failed requests in queue.we just return the original request object.
            if(RecoveryProcess.isStateRunning()){

                return config;
            }

            //We are currently in recovery state.we had a failure so we push this request to the block buffer http queue.In case the
            //request is a already blocked request we let it pass to execution.
            if(RecoveryProcess.isStateRecovering()){

                //A previously blocked request, so we just return it so it could be executed.
                if(config.blocked === true){

                    return config;

                }
                config.blocked = true;

                var deffered = $q.defer();

                //We are currently recovering.this will be processed once we are fine.
                RecoveryProcess.addBlockedRequest(config,deffered);

                return deffered.promise;
            }

            klog.error("Error undefined state for http interceptor.");

            alert("Internal Error state undefined.");

            return null;

        },

        response: function(response) {

            if (shouldIgnore(response.config)) {
                return response;
            }

            response.config.responseTimestamp = new Date().getTime();

            RecoveryProcess.notifyRequestSuccess();

            return response;
        },

        responseError: function(response) {

            if (shouldIgnore(response.config)) {
                return response;
            }

            var deffered = $q.defer();

            if(configuration.stopOnNetworkErrors)

                RecoveryProcess.notifyRequestFail(response.config, deffered);

            klog.error("Some error occurred on network.we are rejecting an HTTP request.error code is:"+response.status+".This error code is being treated asdisconnectionn until specifically defined.");

            return deffered.promise;

            //alert("Error in JavaScript http request."+response.status);
            //switch(response.status) {
            //    //This what we will get on network disconnect
            //    case NETWORK_STATUS.NOT_FOUND:
            //        //We return a promise object here.Recovery Process should resolve it.
            //        var deffered = $q.defer();
            //
            //        RecoveryProcess.notifyRequestFail(response.config,deffered);
            //
            //        return deffered.promise;
            //
            //    case NETWORK_STATUS.INTERNAL_SERVER_ERROR:
            //        return $q.reject(response);
            //        //TODO:Implement the rest of the cases.maybe we should retry all requests for some amount of time.
            //        break;
            //
            //    default:
            //        break;
            //
            //}
            ////klog.error("Some error occurred on network.we are rejecting an HTTP request.error code is:"+response.status+".This error code is not being handled yet.");
            //return $q.reject(response);
        }
    };

}]);

