'use strict';

kQna.directive('kwpQnaRelativeTime',function ($compile,$interval,$filter) {



    var updaters=[];

    setInterval( function () {
        updaters.forEach(function(update) {
            try {
                update();
            }catch (e) {
                klog.warn("Exception while updating {0} {1}", e.message, e.stack);
            }
        });
    },30000);//60 seconds


    return {
        restrict : 'E',
        transclude : true,
        replace:true,
        scope:{
            onTimeChange :'&'
        },
        template: "<span class='qnaThreadTime' tooltip-placement='left' uib-tooltip='{{::time|date:\"medium\"}}' ></span>",
        link: function (scope, $element, attrs) {

            //TODO: need to work with date object
            scope.time=new Date(attrs.time*1000);

            //scope.onTimeChange(function(newEntry){
            //
            //    scope.time = newEntry.getTime() * 1000;
            //
            //    update();
            //
            //});

            function update() {

                scope.currentTime = new Date();

                if (scope.currentTime - scope.time < 120 * 1000) {
                    if (!$element.hasClass("now")) {
                        $element.addClass("now");
                        $element.removeClass("notNow");
                    }
                } else {
                    if (!$element.hasClass("notNow")) {
                        $element.removeClass("now");
                        $element.addClass("notNow");
                    }
                }
                var time=$filter("lastCreated")(scope.currentTime-scope.time ,scope.time);
                $element.text(time);
            }

            update();

            updaters.push(update);


            scope.$on('$destroy', function(){
                var index=updaters.indexOf(update);
                if (index>=0) {
                    updaters.splice(index, 1);
                }
            });

        }
    };
});