/* globals _ */
/* globals PromisesQueue */
/* globals Promise */

(function() {
    'use strict';

    function registerAdapter(kProducerActionsProvider) {
        var handler = {
            checkContentRelevancy : function(state,actionContent,api)
            {
                var result;
                if (_.get(state,'podcasting.type') === '')
                {
                    var lastSelectAThumbActionCuePoint = api.getLastTransmittedAction('select-a-thumb');
                    var lastRemoveSelectedThumbCuePoint = api.getLastTransmittedAction('remove-selected-thumb');
                    var lastSelectPollState = api.getLastTransmittedAction('select-poll-state');

                    if (lastRemoveSelectedThumbCuePoint)
                    {
                        if ((lastSelectAThumbActionCuePoint &&
                            lastSelectAThumbActionCuePoint.createdAt > lastRemoveSelectedThumbCuePoint.createdAt) ||
                            (lastSelectPollState && lastSelectPollState.createdAt > lastRemoveSelectedThumbCuePoint.createdAt))
                        {
                            result = true;
                        } else {
                            result = api.isNewContent('remove-selected-thumb', actionContent);
                        }
                    }else
                    {
                        result = true;
                    }
                }
                return result;
            },
            updateState : function(state,content)
            {
                var modified = false;

                if (_.get(state,'podcasting.type') !== '') {
                    _.set(state,'podcasting.type','');
                    modified = true;
                }

                return modified;
            }
        };

        kProducerActionsProvider.registerAction('remove-selected-thumb','codeCuePoint.Code', handler);
    }

    angular.module('k-deck-slides').config(registerAdapter);

})();