/* globals _ */
/* globals PromisesQueue */
/* globals Promise */

(function() {
    'use strict';

    var documentBaseUrlMapping={};

    documentBaseUrlMapping["MOCKSLIDES"]=configuration.mockSlides.substring(0, configuration.mockSlides.lastIndexOf("/")+1);

    function registerActionHandlerFactory(kProducerActionsProvider) {

        function ActionHandlerFactory()
        {
            function validateData(actionData)
            {
                var errorMessage = '';

                if(actionData)
                {
                    if(actionData.dataOrientation && actionData.dataOrientation !== 'state'){
                        errorMessage += "property 'dataOrientation' value is invalid (acceptable value is 'state' only)\n";
                    }
                    if (actionData.transmitMode)
                    {
                        if (['always','on-air-only'].indexOf(actionData.transmitMode) === -1)
                        {
                            errorMessage += "property 'transmitMode' value '" + actionData.transmitMode + "' is invalid (acceptable values are '{empty}', 'always','on-air-only'";
                        }
                    }
                }


                if (errorMessage)
                {
                    klog.error(errorMessage);
                    return false;
                }else
                {
                    return true;
                }
            }

            function create(actionData)
            {
                if (!validateData(actionData))
                {
                    throw new Error("action data is invalid. please review logs and modify");
                }

                return _.extend({
                    transmitMode : 'on-air-only',
                    emptyContentMode : 'fix-automatically',
                    dataOrientation: 'state',
                    checkContentRelevancy : function(state,actionContent,api) {
                        var lastActionCuePointContent = api.getLastTransmittedContent();
                        return (!lastActionCuePointContent || !angular.equals(lastActionCuePointContent, actionContent));
                    },
                    extractContentFromCuePoint: function (cuePoint) {
                        return cuePoint && cuePoint.partnerData ? (_.isString(cuePoint.partnerData) ? JSON.parse(cuePoint.partnerData) : cuePoint.partnerData) : null;
                    },
                    transmitState: function (content, kalturaAPI, captureService) {

                        var _this=this;
                        //limit thumb content length (avoiding url max length) - thumb description is for search purpose only
                        if(content.content) {
                            content.content = (content.content).substring(0, 600);
                        }

                        var baseUrl=documentBaseUrlMapping[content.documentEntryId];
                        var baseUrlPromise;

                        if (!baseUrl) {
                            baseUrlPromise = kalturaAPI.flavorAssetService.list(content.documentEntryId, "images").then( function(result){

                                //we want the base url, so the space here is important (otherwise it will omit the file_name in the url
                                return kalturaAPI.flavorAssetService.getUrl(result.objects[0].id, " ",true).then( function(result) {

                                    return documentBaseUrlMapping[content.documentEntryId] = result.trim();

                                });
                            });
                        } else {
                            baseUrlPromise=Promise.resolve();
                        }

                        var tags=_this.name;
                        if (captureService.isPreviewMode()) {
                            tags+=", "+kProducerActionsProvider.__PREVIEW_CUEPOINT_TAG__;
                        }

                        return baseUrlPromise.then( function() {
                            kalturaAPI.startMultirequest();
                            kalturaAPI.cuePointService.add(configuration.MediaEntryId, content.title, content.content, content, tags);//1
                            kalturaAPI.thumbAssetService.add(configuration.MediaEntryId, "{1:result:id}");//2
                            kalturaAPI.thumbAssetService.setContent("{2:result:id}", documentBaseUrlMapping[content.documentEntryId]+content.slideName);//3
                            if (captureService.isPreviewMode()) {
                                kalturaAPI.cuePointService.updateStatus("{1:result:id}", 3);//mark as handled
                            }
                            return kalturaAPI.execMultirequest().then(function (res) {
                                return Promise.resolve(res[0]);
                            }, function () {
                                return Promise.reject();
                            });
                        });
                    }

                }, actionData,{
                    cuePointType: 'thumbCuePoint.Thumb'
                });
            }

            this.create = create;

        }

        kProducerActionsProvider.registerActionHandlerFactory('codeCuePoint.Thumb',new ActionHandlerFactory());

        kProducerActionsProvider.__PREVIEW_CUEPOINT_TAG__ = "__PREVIEW_CUEPOINT_TAG__";
    }

    angular.module('kProducerActions').config(registerActionHandlerFactory);

})();