'use strict';

/*
 * Main qna directive.keeps the global shared state of the module which is accessible by sub screens.
 * Also this module holds connection to the model of the qnaService.
 */
kQna.directive('kwpQna', ['qnaService', function (qnaService) {

    var controller = ['$scope','qnaService','captureService', function ($scope,qnaService,captureService) {


        $scope.qnaThreadsData = {

            model :   qnaService.getLiveData()

        };


        $scope.setIndicator=function(val) {

            $scope.$parent.indicator=val;
        };

        $scope.captureService = captureService;

    }];

    return {
        restrict : 'E',

        transclude : true,

        replace : true,

        controller: controller,

        link: function (scope, elem, attrs) {

            scope.qnaModuleState = qnaService.getQnaModuleState;

            scope.QNA_STATES = {INCOMING:"INCOMING",ANSWER:"ANSWER",ANSWER_ON_AIR_SELECT:"ANSWER_ON_AIR_SELECT",ANSWER_ON_AIR_RECORD:"ANSWER_ON_AIR_RECORD",SETTINGS:'SETTINGS'};

            scope.state = {menuOpen:false};

            //Initialized state of this module is the incoming state.
            scope.currentQnaState = scope.QNA_STATES.INCOMING;

            //Shared state of a selected qna thread in case it is selected.
            scope.currentSelectedThread = null;

            //Shared state of a selected qna entry in case it is selected.
            scope.currentSelectedEntry = null;

            scope.visibleInstructions = {answerOnAir:true};

            //Shared function that allows a sub screen to switch to a different screen.
            scope.setQnaState = function(qnaState){

                scope.currentQnaState = qnaState;
            };

            scope.setCurrentSelectedEntry = function(entry){

                scope.currentSelectedEntry = entry;

            };

            //Function to set the current selected thread of the module.
            scope.setCurrentReplyThread = function(thread){

                scope.currentSelectedThread = thread;

            };

            //Function to set the current selected entry of the module.
            scope.setCurrentAnswerEntry = function(entry){

                scope.currentSelectedEntry = entry;

            };

        },
        templateUrl: "../common/kQna/views/kwpQna.html"
    };
}]);
