'use strict';

var kCefModule=angular.module('kCef',[]);


kCefModule.factory("kCef",function($rootScope) {
	var counter = 0
	var requests=new Map();
	var events=new Map();

    function on(eventName,callback) {
	    var eventObj=events.get(eventName);
        if (!eventObj) {
            eventObj= {
                cb: []
            }
            events.set(eventName,eventObj);

	        function _toConsumableArray(arr) {
	            if (Array.isArray(arr)) {
	                for (var i = 0, arr2 = new Array(arr.length); i < arr.length; i++) {
	                    arr2[i] = arr[i]; } return arr2;
	            } else {
	                return Array.from(arr);
	            }
	        }

	        ipcRenderer.on("ipc-promise-common-repeat-event_" + eventName, function (event, params) {
		        eventObj.cb.forEach(function (fn) {
			        return fn.apply(undefined, _toConsumableArray(params.data));
		        });
	        });
            var params = {
                data: null,
                eventName: eventName
            }
            ipcRenderer.send("ipc-promise-common-repeat-event",params);
        }
        eventObj.cb.push(callback);
    }
    function sendIPC(eventName) {
	    for (var _len = arguments.length, data = Array(_len > 1 ? _len - 1 : 0), _key = 1; _key < _len; _key++) {
		    data[_key - 1] = arguments[_key];
	    }
        if (counter===0) {
            ipcRenderer.on("ipc-promise-common-event-response", function(event, resParams) {
	            var callParams = requests.get(resParams.id);
                if (callParams) {
                    if (resParams.isResolved) {
                        callParams.resolve(resParams.data);
                    }
                    else {
                        callParams.reject(resParams.data);
                    }
                }
            });

        }

        return new Promise(function (resolve, reject) {
	        var id = eventName + new Date().getTime() + (++counter);

	        var params = {
                data: data,
                eventName: eventName,
                id: id,
                isResolved: false,
                resolve: resolve,
                reject: reject
            };

            requests.set(id,params);

            ipcRenderer.send("ipc-promise-common-event", params);
        });
    }

    window.addEventListener('keydown', function(e){
        if (e.altKey && e.ctrlKey && e.code === "KeyD") {
            configuration.debuggingMode = !configuration.debuggingMode;
	        var scope = angular.element($('#theApp')).scope();
            scope.$apply();
            $rootScope.$emit('debuggingModeChange');
            sendIPC("debuggingMode",configuration.debuggingMode);
        }
    }, false);

    return {
        available: window.electron ? true: false,
        reload: function() {
            if (this.available) {
                sendIPC("reload");
            }
            else {
                location.reload();
            }
        },
        sendIPC: sendIPC,
        openShell : function openShell(path) {
		      var electron = require('electron'),
			    shell = electron.shell;
		      shell.openItem(path);
        },
        openExternal:function(path) {
		      var electron = require('electron'),
			    shell = electron.shell;
		      shell.openExternal(path);
        },
        on: on
    };
});
