/* globals _ */
/* globals angular */
/* globals PromisesQueue */
/* globals Promise */

(function () {
    'use strict';

    function registerAdapter(kProducerActionsProvider, cuePointsServiceProvider) {
        var handler = {
            transmitMode : 'always'
        };

        kProducerActionsProvider.registerAction('select-deck-document', 'codeCuePoint.Code', handler);
        cuePointsServiceProvider.addTagToArchiveExcludeList('select-deck-document');
    }

    angular.module('k-deck-slides').config(registerAdapter);

})();