(function () {
    "use strict";

    function Directive($timeout) {

        function Controller()
        {

        }

        function link(scope, element, attrs, ctrls)
        {
            var ctrl = ctrls[0];
            var assetsTimeLine = ctrls[1];
            var $element = $(element);
            var $parent = $element.parent();


            $element.droppable({
                tolerance: "intersect",
                over: function( event, ui ){
                    var draggableIndex = $(ui.draggable).index();
                    var droppableIndex = $(event.target).index();
                    assetsTimeLine.moveElement(draggableIndex, droppableIndex);
                    // Do local update, we don't want any update to go to the server, just on stop
                    assetsTimeLine.updateAssetsState( { updateServer: false } );
                }
            });
        }

        return {
            restrict: 'A',
            controller: Controller,
            require: ['kDroppableAsset','^assetsTimeline'],
            link: link
        };
    }

    angular.module('k-assets-timeline').directive('kDroppableAsset', Directive);
}());