kUI.directive('kaDetachedDropdown', function ($rootScope, $document) {


    return {
        restrict: 'A',

        link: function (scope, elem, attrs) {

            var dropdownId = attrs.kaDetachedDropdown;

            function isVisible()
            {
                // check if visible
                return elem.hasClass('dropdown-visible');
            }

            function hideWidget()
            {
                $rootScope.$broadcast('ka-dropdown-is-open-mode',{id : dropdownId, isOpen:false});

                // hide widget
                elem.fadeTo(400,0,function()
                {
                    // remove class used to manage opacity while dropdown is visible
                    elem.removeClass('dropdown-visible');
                    elem.css('display','none');
                });

                // un-register to document click to auto-close if needed
                $document.unbind('click', documentClickBind);
            }

            function showWidget()
            {
                $rootScope.$broadcast('ka-dropdown-is-open-mode',{id : dropdownId, isOpen:true});

                // show widget
                elem.fadeTo(400,0.9, function () {

                    // add class used to manage opacity while dropdown is visible and remove explicitly defined opacity
                    elem.addClass('dropdown-visible').css({'opacity':'', display : 'block'});

                    // register to document click to auto-close if needed
                    $document.unbind('click', documentClickBind);
                    $document.bind('click', documentClickBind);
                });
            }

            function documentClickBind(event) {
                // check if user clicked outside of widget
                var dpContainsTarget = elem.find(event.target).length > 0;
                if (isVisible() && !(dpContainsTarget)) {
                    // widget is visible and user clicked ouside - hide widget
                    hideWidget();
                }
            }

            scope.$on("ka-dropdown-toggle-request",function(e,args) {
                // handle only the relevant detached dropdown
                if (attrs.kaDetachedDropdown === args.id)
                {
                    if (isVisible())
                    {
                        hideWidget();
                    }else
                    {
                        showWidget();
                    }
                }
            });

            scope.$on('$destroy',function()
            {
                // make sure you unbind from the 'click' to prevent memory leak.
                $document.unbind('click',documentClickBind);
            });

            // hide the element before it shown.
            elem.css({opacity : 0,display : 'none'});
        }


    }

});
