(function(){
    'use strict';

    angular.module('kwpApp').controller('DebugController',function (asyncLiveEntryInfoService, kProducerActions, $scope, kalturaService, slidesService, kalturaAPI, captureService, $injector, kdialog, $interval, $timeout, cuePointsService, kPollsService, kAssetsTimelineService, kPollsExportService,kCef , rtcService) {

        var _keys = {
            stateSlidesEntryId : "slidesEntryId"
        };


        $scope.template = "./partials/debugView.html";

        $scope._captureService = captureService;
        $scope._cuePointService = cuePointsService;
        $scope._rtcService = rtcService;
        $scope.deletingSlides=false;
        $scope.mediaEntryId = configuration.MediaEntryId;
        $scope.configuration = configuration;
        $scope.app_version=kUpgradeServiceInfo.app_version;
        $scope.documentEntryId= '';

        function init()
        {
            kProducerActions.isReady().then(function()
            {
                $scope.producerState = kProducerActions.getState(true);

                kProducerActions.subscribeStateChange(function()
                {
                    $scope.producerState = kProducerActions.getState(true);
                });
            });

            setInterval(function(){
                $scope.serverTime = kalturaAPI.getServerTime();
                $scope.entryInfo = asyncLiveEntryInfoService.getCurrentEntryInfo();
            }, 1000);


            cuePointsService.on('cuepoint-added',function(cuePoint)
            {
                $scope.cuePoints.push(cuePoint);
            });

            $scope.cuePoints = cuePointsService.getAllCuepoints();
        }
        $scope.countWatchers=function() {
            var WatcherCounter=$injector.get("WatcherCounter");
            alert(WatcherCounter.count());

        };
        $scope.setDigestCounter = function () {
            var WatcherCounter=$injector.get("DigestCounter");

            WatcherCounter.setCounter();
        };
        $scope.resetDigestCount = function () {
           // var WatcherCounter=$injector.get("DigestCounter");
           // WatcherCounter.resetDigestCount();

        };
        $scope.openAppPath = function () {
            if (configuration.app_path.endsWith(".app")) {
                kCef.openShell(configuration.app_path + "/Contents/");
            }
            kCef.openShell(configuration.app_path);
        };
        $scope.openAppDataPath = function () {
            kCef.openShell(configuration.app_data_path);
        };
        $scope.openLogFolder = function () {
            kCef.openShell(configuration.app_data_path+"/logs");
        };
        $scope.isLive = function () {
            kalturaService.isLive($scope.mediaEntryId).then(function (res) {
                alert(res);
            });
        };

        //$scope.deleteSlides=function(){
        //    $scope.deletingSlides=true;
        //    cuePointsService.deleteCuePoints($scope.mediaEntryId, ['thumb']).then (function () {
        //        klog.warn("slides deleted!!");
        //        $scope.deletingSlides=false;
        //    });
        //};
        //
        //$scope.deleteQna=function(){
        //    $scope.deletingSlides=true;
        //
        //    $scope._cuePointService.deleteCuePoints($scope.mediaEntryId, ['qna']).then(function () {
        //        klog.warn("qna data deleted!!");
        //        $scope.deletingSlides=false;
        //    });
        //};

        $scope.deletePlayerViewControl=function(){
            //$scope.deletingSlides=true;
            //
            //$scope._cuePointService.deleteCuePoints($scope.mediaEntryId, ['player-view-mode']).then(function () {
            //    klog.warn("qna data deleted!!");
            //    $scope.deletingSlides=false;
            //});
        };

        $scope.installPollsMetadataprofile = function(){
            kPollsService._debugInstallMetadataProfile();
        };

        $scope.installPollsResponseProfile = function(){
            kPollsService._debugInstallResponseProfile();
        };

        $scope.deleteRecordedEntry=function(){

            kalturaService.deleteRecordedEntry($scope.mediaEntryId).then(function(){
                klog.warn("deleted recorded entry");
            });
        };

        $scope.hasUnHandledCuePoints = function()
        {
            alert($scope._cuePointService.hasUnHandledCuePoints());
        };

        $scope.cuePoints = [];
        $scope.lastCuePointAddedTime=null;
        $scope.autoAddCuePoints={
            status: false,
            intervalDuration:10,
            interval: null};
        $scope.isBroadcasting=function() {
            return  captureService.isBroadcasting();
        };
        $scope.autoAddCuePointsChanged=function() {
            if ($scope.autoAddCuePoints.status) {

                var alignedTime=0;
                $scope.autoAddCuePoints.interval=$interval(function () {

                    if (captureService.isBroadcasting()) {
                        var lastAlignedTime = alignedTime;
                        alignedTime = Math.floor($scope.serverTime / ($scope.autoAddCuePoints.intervalDuration*1000));

                        if (alignedTime !== lastAlignedTime) {
                            kAssetsTimelineService.debugFlipToNextAsset();
                        }
                    } else {
                        $interval.cancel($scope.autoAddCuePoints.interval);
                        $scope.autoAddCuePoints.status=false;
                    }
                }, 100);

            } else {
                try {
                    $interval.cancel($scope.autoAddCuePoints.interval);
                }
                catch (e) {
                }
                $scope.autoAddCuePoints.interval=null;
            }

        };
        $scope.listCuePoints = function () {
            kalturaAPI.cuePointService.list(configuration.MediaEntryId).then(function (res) {
                var r = JSON.stringify(res.objects,null,"\t");
                alert(r);
                klog.warn(r);
            });
        };
        $scope.viewMediaEntry=function(){

            kalturaService.getLiveEntryInfo(configuration.MediaEntryId).then( function (res) {
                klog.debug(res);
                alert(""+JSON.stringify(res,null,"\t"));
            }, function (res) {
                alert(JSON.stringify(res));
            });
        };

        $scope.getCuePointsStyle = function(cuePoint)
        {
            if (cuePoint.tags === 'webcast:internal-state')
            {
                return 'producerStateCuePoint';
            }else if (cuePoint.cuePointType === 'thumbCuePoint.Thumb')
            {
                return 'thumbCuePoint';
            }else {
                return 'codeCuePoint';
            }
        };

        $scope.getCuePointsType = function(cuePoint)
        {
            if (cuePoint.tags === 'webcast:internal-state')
            {
                return 'producer state';
            }else if (cuePoint.cuePointType === 'thumbCuePoint.Thumb')
            {
                return 'thumb';
            }else {
                return 'code';
            }
        };

        $scope.deleteAllCuePoints=function() {


            kalturaAPI.cuePointService.list({mediaEntryId:configuration.MediaEntryId,
                types:"codeCuePoint.Code,thumbCuePoint.Thumb,annotation.Annotation"}).then(function (res) {

                PromiseMapBatch(res.objects, function deleteCuePoint(item) {
                    kalturaAPI.cuePointService.delete(item.id);
                },30);


          });
        }

        $scope.addMockSlides=function() {
            klog.info("add mock presentation document to ");
            kProducerActions.setStateOrientedAction('select-deck-document',{documentEntryId : "MOCKSLIDES" });
        }

        $scope.onDlbClickCuePoint=function(cuePoint) {

            if (!cuePoint)
            {
                return;
            }

            if (cuePoint.cuePointType=='thumbCuePoint.Thumb') {
                if (!cuePoint.assetId) {
                    alert("No asset id!");
                    return;
                }
                var json="";
                var thumbAssetUrl="";
                kalturaAPI.startMultirequest();
                    kalturaAPI.cuePointService.get(cuePoint.id).then(function(res) {

                        json = JSON.stringify(res, undefined, 2);
                    });
                    kalturaAPI.thumbAssetService.getUrl(cuePoint.assetId).then(function (res) {
                        thumbAssetUrl=res;
                    });
                kalturaAPI.execMultirequest().then(function() {

                    kdialog.templatedDialog({
                        template: '<img src="' + thumbAssetUrl + '" width="300" height="125" /></img><br><pre contenteditable="true" style="max-height: 250px; overflow: auto; font-size: 9px">' + json + '</pre>'
                    }).then(function (a) {

                    });
                });
            } else {
                var json = JSON.stringify(cuePoint, undefined, 2);

                kdialog.templatedDialog({
                    template: '<pre contenteditable="true" style="max-height: 250px; overflow: auto; font-size: 9px">' + json + '</pre>'
                }).then(function (a) {

                });
            }
        };

        var openEntryPlayerPage=function(entryId,name) {
            var uiConfId=kserviceInfo.playerUIConf;
            var url=kserviceInfo.serverAddress+"/index.php/extwidget/preview/partner_id/"+kserviceInfo.partnerId+"/uiconf_id/"+uiConfId+
                "/entry_id/"+entryId+"/embed/auto?";


            kCef.openURL(url,name);
        };

        $scope.openPlayerPage=function(live) {


            var entryId=$scope.mediaEntryId;

            if (!live) {
                kalturaService.getLiveEntryInfo(entryId).then( function (res) {
                    openEntryPlayerPage(res.recordedEntryId,entryId+" VOID Entry");
                }, function (res) {
                    alert(JSON.stringify(res));
                });
            } else {
                openEntryPlayerPage(entryId,entryId+" Live Entry");

            }
        };

        $scope.exportPolls=function(){
            kPollsExportService.exportPollsDataToFile();
        };

        init();
    });
})();