kngLayout.addPanelDirective("kTabPanel", function () {
    return {
        scope: {
            showHeader: '='
        },
        templateUrl: "../common/kngLayout/directives/partials/kTabPanelTemplate.html",
        link: function ($scope) {
            var scope = $scope;
            var visibleTabPage=null;

            scope.isRtcPanel = configuration.participantsPanel;

            scope.isTabSelected=function(panel) {
                return !scope.isCollapsed() && visibleTabPage===panel;
            };
            scope.clickOnTabHeader=function() {
               scope.toggleCollapse();
            };
            scope.onTabPageSelected=function(panel,e) {
                if (visibleTabPage!==panel) {
                    visibleTabPage = panel;
                    recalcLayout();

                    if (scope.isCollapsed()) {
                        scope.toggleCollapse();
                    }
                }
                else {
                    //close the tab is clicking on same icon
                    scope.toggleCollapse();
                }
                if (e) {
                    e.stopPropagation();
                }
            };

            var recalcLayout = function () {
              //  scope.title=scope.panels[0].ktitle;
            //    scope.icon=scope.panels[0].icon;
                var sortedPanels = null; // sorted list of panels to manipulate
                if (visibleTabPage) {
                    // sort the panels where the one to be shown in the last one
                    var indexOfVisiblePage = scope.panels.indexOf(visibleTabPage);
                    sortedPanels = scope.panels.slice(0);
                    sortedPanels.splice(indexOfVisiblePage,1); // remove the visible panel from array
                    sortedPanels.push(visibleTabPage); // push the visible panel to the end of the array
                }else
                {
                    sortedPanels = scope.panels; // no active panel exists - use the list as is
                }


                sortedPanels.forEach(function(panel) {
                    panel.elem.css({
                        'top': '0px',
                        'bottom': '0px',
                        'left':  "0px",
                        'right': '0px'
                    });

                    // use the show/hide to modify the visibility of the element immediately (to prevent overlapping of the panel being hidden and the one that we are going to show
                    if (visibleTabPage===panel)
                    {
                        panel.elem.show();
                    }else
                    {
                        panel.elem.hide();
                    }

                });

            };
            return {
                recalcLayout: recalcLayout,
                childAdded: function (panel) {
                    if (visibleTabPage===null) {
                        visibleTabPage = panel;
                    }
                }
            };
        }
    };
});

