/* globals _ */
/* globals PromisesQueue */
/* globals Promise */

(function () {
    "use strict";

    function Service(kProducerActions,$rootScope, captureService, PLAYER_VIEW_STATE) {
        var self = this;

        var currentViewModeId = null;
        var currentViewModeState = null;
        var RecordingStateChangedUnregister = null;

        var supportedViews = {
            'sbs-parent-in-right': { svg : 'player-view-sbs-2'},
            'sbs-parent-in-left': {svg : 'player-view-sbs'},
            'pip-parent-in-small': { svg : 'player-view-pip-2'},
            'pip-parent-in-large': { svg : 'player-view-pip'},
            'parent-only': {svg : 'player-view-single'},
            'no-parent': {svg : 'player-view-single-2'}
        };

        /**
         * check if producer app is updated with the most relevant player view mode
         */
        function onProducerStateChanged() {

            var newViewMode = kProducerActions.getState('actions.change-view-mode');

            if (newViewMode && newViewMode.playerViewModeId)
            {
                var state = newViewMode.viewModeLockState ? newViewMode.viewModeLockState : PLAYER_VIEW_STATE.UNLOCKED;
                changeViewById(newViewMode.playerViewModeId, state);
            }

        }



        function getCurrentViewId() {
            return currentViewModeId;
        }

        function getCurrentViewState()
        {
            return currentViewModeState;
        }

        /**
         * changes the player view mode
         * @param viewId player view mode id
         * @param viewState indicates if the player view mode is locked or not
         */
        function changeViewById(viewId, viewState) {
            if ( (viewId && viewId !== currentViewModeId && _.has(supportedViews,viewId)) ||
                    (viewState && viewState !== currentViewModeState) ) {
                currentViewModeId = viewId;
                currentViewModeState = viewState ? viewState : PLAYER_VIEW_STATE.UNLOCKED;
                updateServer();
                notifyViewModeIdChange();
            }
        }


        function notifyViewModeIdChange()
        {
            $rootScope.$broadcast('kaPlayerViewControlsService.onPlayerViewModeIdChanged',{playerViewModeId:  currentViewModeId});
        }

        function onRecordingStateChanged() {
            if (captureService.isBroadcasting()) {
                klog.info('boradcasting is on, check if need to force default view');

                var playerViewActionContent = kProducerActions.getState('actions.change-view-mode',null);
                var poscastingType = kProducerActions.getState('podcasting.type',null);

                if (!playerViewActionContent && !poscastingType)
                {
                    klog.info('Started broadcasting without slides, make sure control view is changed to video only');
                    changeViewById(configuration.defaultViewWhenBroadcastingWithoutSlides, PLAYER_VIEW_STATE.LOCKED);
                }

                if (RecordingStateChangedUnregister)
                {
                    klog.info('unregister monitoring changes in recording state');
                    RecordingStateChangedUnregister();
                    RecordingStateChangedUnregister = null;
                }
            }
        }



        function init() {
            // make sure we fetched all cue points currently exists on the server
            kProducerActions.isReady().then(function()
            {
                if (captureService.isBroadcasting()) {
                    onRecordingStateChanged();
                }else {
                    klog.info('Currently not broadcasting, monitor broadcasting mode.');
                    RecordingStateChangedUnregister = $rootScope.$on('RecordingStateChanged', onRecordingStateChanged);
                }

                // listen to changes from server and act accordingly
                kProducerActions.subscribeStateChange(function() {
                    
                    onProducerStateChanged();
                });

                // run sync from server once to sync producer state (if server doesn't contain any relevant cuepoint)
                onProducerStateChanged();
            });
        }

        /**
         * Updates the server with a modification of the current player view mode
         */
        function updateServer() {
            // don't update server if no view was selected
            if (currentViewModeId) {
                klog.info('update server with state change. "playerViewModeId" = "{0}" and "viewModeLockState" = {1}',
                    currentViewModeId, currentViewModeState);
                kProducerActions.setStateOrientedAction('change-view-mode',
                    {
                        playerViewModeId: currentViewModeId,
                        viewModeLockState : currentViewModeState
                    });
            }
        }


        /**
         * Returns the svg name of current view
         */
        function getCurrentViewSVGName() {
            var currentViewId = getCurrentViewId();

            if (currentViewId && supportedViews[currentViewId])
            {
                return supportedViews[currentViewId].svg;
            }else
            {
                return '';
            }
        }

        self.getCurrentViewSVGName = getCurrentViewSVGName;
        self.getCurrentViewId = getCurrentViewId;
        self.getCurrentViewState = getCurrentViewState;
        self.changeViewById = changeViewById;

        init();
    }

    angular.module('kaPlayerViewControl').service('kaPlayerViewControlsService', Service);

})();