"use strict";

kQna.factory('QnaThread', ['metaDataGenerator','kalturaAPI','THREAD_STATE',function(metaDataGenerator,kalturaAPI,THREAD_STATE) {

    //TODO:This should come from a constant of a profile module or as a parameter to remove dependency.
    var myUserId = kserviceInfo.userId;

    function CreateQnaThreadStateMachine() {

        return StateMachine.create({

            initial: THREAD_STATE.NONE,

            events: [
                {
                    name: 'clear',
                    from: [THREAD_STATE.PENDING,THREAD_STATE.CLEAR,THREAD_STATE.NONE],
                    to: THREAD_STATE.CLEAR
                },
                {
                    name: 'delete',
                    from: [THREAD_STATE.PENDING,THREAD_STATE.NONE],
                    to: THREAD_STATE.DELETED
                },
                {
                    name: 'pending',
                    from: [THREAD_STATE.PENDING, THREAD_STATE.ANSWERED, THREAD_STATE.CLEAR, THREAD_STATE.FOLLOWUP, THREAD_STATE.DONE, THREAD_STATE.NONE],
                    to: THREAD_STATE.PENDING
                },
                {
                    name: 'answer',
                    from: [THREAD_STATE.PENDING, THREAD_STATE.ANSWERED, THREAD_STATE.FOLLOWUP, THREAD_STATE.DONE],
                    to: THREAD_STATE.ANSWERED
                },
                {
                    name: 'done',
                    from: [THREAD_STATE.ANSWERED, THREAD_STATE.FOLLOWUP, THREAD_STATE.DONE, THREAD_STATE.PENDING,THREAD_STATE.NONE],
                    to: THREAD_STATE.DONE
                },
                {
                    name: 'follow',
                    from: [THREAD_STATE.ANSWERED, THREAD_STATE.PENDING, THREAD_STATE.DONE, THREAD_STATE.FOLLOWUP,THREAD_STATE.NONE],
                    to: THREAD_STATE.FOLLOWUP
                },
            ]
        });
    }

    function QnaThread(threadId) {

        this.threadID = threadId;

        this.threadStateID = threadId;

        this.fsm = CreateQnaThreadStateMachine();

        this.entries = [];

        this.lastRecivedUpdateTime = null;

        this.lastRecievedEntry = null;

    }

    //We mark a thread state using its last entry.This allows to track thread history while maintaining the last
    //updated state in the last thread.
    QnaThread.prototype._markThread = function(updateObj){

        var entries = this.entries;

        var lastQnaEntry = entries[entries.length - 1];
        lastQnaEntry.setState(updateObj);
    };
    QnaThread.prototype.updateStateWithoutUpdate=function() {
        switch(this.entries.last().getState()) {
            case THREAD_STATE.DONE:
                this.fsm.done();
                break;
            case THREAD_STATE.PENDING:
                this.fsm.pending();
                break;
            case THREAD_STATE.FOLLOWUP:
                this.fsm.follow();
                break;
            case THREAD_STATE.ANSWERED:
                this.fsm.answer();
                break;
            case THREAD_STATE.CLEAR:
                this.fsm.clear();
                break;
        }
    };

    QnaThread.prototype.appendEntry = function(qnaEntry) {

        this.entries.push(qnaEntry);

        //Message from user which is NOT me.
        if(qnaEntry.isQuestion() ) {

            this.lastRecivedUpdateTime = qnaEntry.getTime();

            this.lastRecievedEntry = qnaEntry;
        }

        qnaEntry.setThread(this);


        //Change thread state if last message contains a done or follow up signature.
        if(qnaEntry.getState() === THREAD_STATE.DONE){

            this.fsm.done();
            return;
        }
        if(qnaEntry.getState() === THREAD_STATE.FOLLOWUP){

            this.fsm.follow();
            return;
        }

        if(qnaEntry.getState() === THREAD_STATE.CLEAR){

            this.fsm.clear();
            return;

        }

        //Message from user which is NOT me.
        if(qnaEntry.isQuestion() ){

            if(qnaEntry.getState() !== THREAD_STATE.ANSWERED) {
                this.fsm.pending();
            } else {
                this.fsm.answer();
            }

        //Message made by ME.
        }else{

            //This thread is not an announcement therefore this entry is an answer.
            if(this.entries.length > 1){

                this.fsm.answer();

            }

            //This is an announcement made by me.
            else{

                this.fsm.pending();

            }
        }
    };


    QnaThread.prototype.getEntry= function(id){

        for (var i=0;i<this.entries.length;i++) {
            if (this.entries[i].getId()===id) {
                return this.entries[i];
            }
        }
        return null;
    };

    QnaThread.prototype.getEntries = function(){

        return this.entries;
    };

    QnaThread.prototype.clear = function() {

        this.fsm.clear();

        this._markThread({State:THREAD_STATE.CLEAR});

    };

    QnaThread.prototype.delete = function() {

        this.fsm.delete();

        this._markThread({State:THREAD_STATE.DELETED});

    };

    QnaThread.prototype.done = function() {

        this.fsm.done();

        this._markThread({State:THREAD_STATE.DONE});

    };

    QnaThread.prototype.forward = function(assignedTo) {

        this.fsm.forward();

        this._markThread({State:THREAD_STATE.FORWARDED,Assignees:assignedTo});

    };

    QnaThread.prototype.followUp = function() {

        this.fsm.follow();

        this._markThread({State:THREAD_STATE.FOLLOWUP});

    };

    QnaThread.prototype.getThreadID = function() {

        return this.threadID;

    };

    QnaThread.prototype.getThreadStateID = function(){
        return this.threadID + this.entries.length.toString() + this.fsm.current;
    };

    QnaThread.prototype.getLatestReceivedEntry = function() {

        return this.lastRecievedEntry;

    };

    QnaThread.prototype.getLastEntry = function() {

        if(this.entries.length > 0) {

            return this.entries[this.entries.length - 1];

        }

        return null;

    };

    QnaThread.prototype.getFirstQuestion = function() {

        if(this.entries.length > 0) {

            return this.entries[0];

        }

        return null;

    };


    QnaThread.prototype.getCreationTime = function() {

        if(this.entries.length > 0) {

            return this.entries[0].getTime();

        }

        return null;

    };

    QnaThread.prototype.getThreadTime = function() {

        if(this.lastRecivedUpdateTime === null){

            if(this.entries.length > 0) {

                return this.entries[this.entries.length - 1].getTime();

            }
            return 0;

        }

        return this.lastRecivedUpdateTime;

    };

    QnaThread.prototype.isAnnouncement = function() {

        return this.entries.length === 1 && this.entries[0].isAnnouncement();

    };

    QnaThread.prototype.isFollowUp = function() {

        return this.fsm.current === THREAD_STATE.FOLLOWUP;

    };

    QnaThread.prototype.isAnswered = function() {

        return this.fsm.current === THREAD_STATE.ANSWERED;

    };

    QnaThread.prototype.isDone = function() {

        return this.fsm.current === THREAD_STATE.DONE;

    };

    QnaThread.prototype.isPending = function() {
        var metadata = this.getCuePointMetaData();
        var isDeletedAnnouncement = metadata && metadata.State === THREAD_STATE.DELETED && metadata.Type === "Announcement";

        return this.fsm.current === THREAD_STATE.PENDING && !isDeletedAnnouncement;
    };

    QnaThread.prototype.getState = function(){

        return this.fsm.current;

    };

    QnaThread.prototype.isForwarded = function(){

        return this.fsm.current === THREAD_STATE.FORWARDED;

    };

    QnaThread.prototype.toString = function(){

        return this.getThreadID();

    };

    QnaThread.prototype.getCuePointMetaData = function() {
        var entry =  this.entries.length ? this.entries[0]: null;
        return entry ? entry.cuePoint.metadata : null;
    };

    return QnaThread;
}]);