'use strict';

kwpApp.factory('captureService', function(kalturaService,sessionService,asyncLiveEntryInfoService,$rootScope){

    // derived functions (classes) should override this and put the needed promises if any.
    var externalBroadcasting = false;

    //Represent the start time of the current live session.A session can be composed from several live sessions and several offline sessions.
    var currentSessionStartTime = null;

    //represent the sum of all duration from previous live segmants in the curnt session besides the one currentlly running.(should be 0 in the first iteration).
    var totalLiveDurationPassed = 0;

    // for debug only, till we get a working server - server sends unix time (seconds from 1/1/1970 00:00 UTC)
    //sessionStartTime = (new Date().getTime() / 1000);

    var explicitLive = 0;
    var entryViewMode = 0;

    var rtcBroadcasting = null;

    $rootScope.$on('rtcStreamUpdated', function(event, status) {
        rtcBroadcasting = status;
    });

    var onLiveEntryChange = function(res) {

        var previousPreviewMode=isPreviewMode();
        var previousexternalBroadcasting=externalBroadcasting;

        var liveStatus = parseInt(res.liveStatus);
        if (isNaN(liveStatus)){
            klog.warn("got liveStatus: {0} which is not a number", liveStatus);
            liveStatus = 0; //LiveEntryStatus::STOPPED
        }

        explicitLive = (res.explicitLive === true) || (res.explicitLive === 1);
        entryViewMode = parseInt(res.viewMode);

        var currentBroadcastStartTime = parseInt(parseFloat(res.currentBroadcastStartTime) * 1000);

        if (liveStatus === 1 || liveStatus === 2 || liveStatus === 3){
            //This is a BUG fix.
            externalBroadcasting = true;
            // make the session service refreshSessionInfo - some refactoring here might be needed...
            if (parseInt(currentSessionStartTime) !== parseInt(currentBroadcastStartTime)) {

                currentSessionStartTime = currentBroadcastStartTime;

            }
        }
        else {

            externalBroadcasting = false;
            currentSessionStartTime = null;
            rtcBroadcasting = null;

        }

        totalLiveDurationPassed = res.lastElapsedRecordingTime;


        if (previousexternalBroadcasting!==externalBroadcasting) {
            $rootScope.$broadcast("RecordingStateChanged", externalBroadcasting,isPreviewMode());
        }

        if (isPreviewMode()!==previousPreviewMode) {

            $rootScope.$broadcast("PreviewModeChanged", isPreviewMode());
        }
    };

    asyncLiveEntryInfoService.registerOnEntryInfoChange(onLiveEntryChange);


    function startRecording() {

        return new Promise(function (success, failure) {

            klog.info("Called startRecording on captureServiceExternal.We are slaved to the server and returning according to current state.");

            if (externalBroadcasting === true) {
                success();
            } else {
                failure();
            }

        });
    }

    function stopRecording() {

        return new Promise(function (success, failure) {

            klog.info("Called stopRecording on captureServiceExternal.We are slaved to the server and returning according to current state.");

            if (externalBroadcasting === true) {
                success();
            } else {
                failure();
            }

        });
    }

    function getPosition() {

        return Promise.resolve(Math.round(kalturaService.getServerTime()/1000));
    }

    function getViewMode() {
        return entryViewMode;
    }

    function isExplicitLive() {
        return explicitLive;
    }

    function isBroadcasting() {
        return (rtcBroadcasting===null)? externalBroadcasting : rtcBroadcasting;
    }

    function debugSetSessionStartTimeToNow(){
        currentSessionStartTime = (new Date().getTime() / 1000);
    }

    function debugSetSessionStartTimeToUndefined(){
        currentSessionStartTime = undefined;
    }

    function _startPreviewVideo(){
        return new Promise(function(resolve,fail){
            resolve(true);
        });
    }

    function _stopPreviewVideo(){
        return new Promise(function(resolve,fail){
            resolve(true);
        });
    }

    function isPreviewMode() {
        return isBroadcasting() && explicitLive && entryViewMode===0;
    }

    function isLiveMode() {
        return isBroadcasting() && explicitLive && entryViewMode===1;
    }
    var readyPromise = new Promise(function(resolve,reject){
        resolve(true);
    });

    this.debugSetSessionStartTimeToNow = debugSetSessionStartTimeToNow;
    this.debugSetSessionStartTimeToUndefined = debugSetSessionStartTimeToUndefined;
    this.getPosition = getPosition;
    this.startRecording = startRecording;
    this.stopRecording = stopRecording;
    this.startPreviewVideo = _startPreviewVideo;
    this.stopPreviewVideo = _stopPreviewVideo;
    this.isBroadcasting = isBroadcasting;
    this.isExplicitLive = isExplicitLive;
    this.getViewMode = getViewMode;
    this.isPreviewMode = isPreviewMode;
    this.isLiveMode = isLiveMode;
    this.ready= readyPromise;
    //this.serverGettingSignal = serverGettingSignal;
    this.sessionStartTime = function() {return currentSessionStartTime;};
    return this;
});
