kwpApp.factory('shortcutService', ['$injector','$rootScope','gettextCatalog','hotkeys', function ($injector,$rootScope,gettextCatalog,hotkeys) {

    var tooltipCreator = function(text,command){

        var key=hotkeys.get(command);

        return text + (key ? ' ('+key.format()+')' : '');
    };

    var keyInfo = function(keyDescription,tooltipText,keyMap) {

        if (navigator.platform.startsWith("Mac")) {
            keyMap=keyMap.replace("ctrl", "command");

        }


        return {
            keyMap : keyMap,
            tooltipText : tooltipText,
            description : keyDescription
        };

    }

    var keyInfoDictionary = {
        "WEBCAST_KEYPRESS_SESSION_START":      keyInfo(gettextCatalog.getString("Start presentation"),gettextCatalog.getString("Start presentation"),"ctrl+s"),
        "WEBCAST_KEYPRESS_SESSION_STOP":       keyInfo(gettextCatalog.getString("Pause presentation"),gettextCatalog.getString("Pause presentation"),"ctrl+p"),
        "WEBCAST_KEYPRESS_SLIDE_FORWARD_SPACE":     keyInfo(gettextCatalog.getString("Move to next presentation slide"),gettextCatalog.getString("Next slide"),"space"),
        "WEBCAST_KEYPRESS_SLIDE_FORWARD_ENTER":     keyInfo(gettextCatalog.getString("Move to next presentation slide"),gettextCatalog.getString("Next slide"),"enter"),
        "WEBCAST_KEYPRESS_SLIDE_FORWARD_RIGHT":     keyInfo(gettextCatalog.getString("Move to next presentation slide"),gettextCatalog.getString("Next slide"),"right"),
        "WEBCAST_KEYPRESS_SLIDE_FORWARD_DOWN":      keyInfo(gettextCatalog.getString("Move to next presentation slide"),gettextCatalog.getString("Next slide"),"down"),
        "WEBCAST_KEYPRESS_SLIDE_FORWARD_PAGE_DOWN": keyInfo(gettextCatalog.getString("Move to next presentation slide"),gettextCatalog.getString("Next slide"),"pagedown"),
        "WEBCAST_KEYPRESS_SLIDE_BACKWARDS_SPACE":   keyInfo(gettextCatalog.getString("Move to previews presentation slide"),gettextCatalog.getString("Previous slide"),"backspace"),
        "WEBCAST_KEYPRESS_SLIDE_BACKWARDS_LEFT":    keyInfo(gettextCatalog.getString("Move to previews presentation slide"),gettextCatalog.getString("Previous slide"),"left"),
        "WEBCAST_KEYPRESS_SLIDE_BACKWARDS_UP":      keyInfo(gettextCatalog.getString("Move to previews presentation slide"),gettextCatalog.getString("Previous slide"),"up"),
        "WEBCAST_KEYPRESS_SLIDE_BACKWARDS_PAGE_UP": keyInfo(gettextCatalog.getString("Move to previews presentation slide"),gettextCatalog.getString("Previous slide"),"pageup"),
        "WEBCAST_KEYPRESS_TIMELINE_FORWARD":   keyInfo(gettextCatalog.getString("Preview one slide right on the timeline"),gettextCatalog.getString("Scroll right"),"alt+right"),
        "WEBCAST_KEYPRESS_TIMELINE_BACKWARDS": keyInfo(gettextCatalog.getString("Preview one slide left on the timeline"),gettextCatalog.getString("Scroll left"),"alt+left"),
        "WEBCAST_KEYPRESS_TOGGLE_AUDIENCE":    keyInfo(gettextCatalog.getString("Open/Close Live analytics tab (toggle)"),gettextCatalog.getString("Toggle live analytics"),"alt+a"),
        "WEBCAST_KEYPRESS_TOGGLE_RTC":    keyInfo(gettextCatalog.getString("Open/Close Self Serve tab (toggle)"),gettextCatalog.getString("Toggle self serve"),"alt+r"),
        "WEBCAST_KEYPRESS_TOGGLE_MUTE":    keyInfo(gettextCatalog.getString("Mute/Unmute Microphone"),gettextCatalog.getString("Toggle mute microphone"),"alt+m"),
        "WEBCAST_KEYPRESS_TOGGLE_Q&A":    keyInfo(gettextCatalog.getString("Open/Close Q&A tab (toggle)"),gettextCatalog.getString("Toggle Q&A"),"alt+q"),
        "WEBCAST_KEYPRESS_TOGGLE_NOTES":       keyInfo(gettextCatalog.getString("Open/Close Notes tab (toggle)"),gettextCatalog.getString("Toggle notes"),"alt+n"),
        "WEBCAST_KEYPRESS_TOGGLE_TIMELINE_CONTENT_SETTINGS":       keyInfo(gettextCatalog.getString("Open/Close timeline content settings tab (toggle)"),gettextCatalog.getString("Toggle timeline content settings"),"alt+l"),
        "WEBCAST_KEYPRESS_TIMELINE_GO_TO_CURRENT" : keyInfo(gettextCatalog.getString("Take the timeline to the next slide"),gettextCatalog.getString("Sync timeline and presentation"),"ctrl+g"),
        "WEBCAST_KEYPRESS_TIMELINE_SET_FOCUSED_AS_CURRENT" : keyInfo(gettextCatalog.getString("Change displayed slide - to the one being previewed on the timeline"),gettextCatalog.getString("Present selected slide on timeline"),"alt+up")
    };

    var _keyCodes = (function(){
        var keys = Object.keys(keyInfoDictionary);

        var keycodesDictionary = {};
        var i =0;

        for(i=0;i<keys.length;i++){
            keycodesDictionary[keys[i]]= keys[i];
        }
        return keycodesDictionary;

    })();

    function _getKeyTooltip(KeyCode,withoutprefix){

        var keyInfo=keyInfoDictionary[KeyCode];

        var tooltipText=keyInfo.tooltipText;

        var tooltipTextFinal = tooltipCreator(withoutprefix ? "" : tooltipText ,keyInfo.keyMap);

        return tooltipTextFinal;

    }

    function _bindKey(keyCode,scope,func){
        // QNA-moderator mode - disable all shortcuts so that the moderator will not interfere with main-presenter flows
        if(scope.qnaModeratorMode){
            return;
        }
        hotkeys.bindTo(scope)
            .add({
                combo: keyInfoDictionary[keyCode].keyMap,
                description: keyInfoDictionary[keyCode].description,
                callback: func
            });
    }

    function _unbindKey(keyCode){
        hotkeys.del(keyInfoDictionary[keyCode].keyMap);
    }

    function bindCommand($scope,command,shortcut) {
        _bindKey(shortcut,$scope,command.do);
        command.tooltip=_getKeyTooltip(shortcut);
    }


    return {
        getKeyToolTip : _getKeyTooltip,
        bindKey :     _bindKey,
        unbindKey :    _unbindKey,
        bindCommand: bindCommand,
        keyCodes : _keyCodes
    };
}]);

