"use strict";


kwpApp.directive('kImageContainer', ['$parse', function ($parse) {
    return {
        restrict: 'A',
        scope : {
        },
        controller : function()
        {
            var images = [];

            this.notifyResize = function()
            {
                _.each(images,function(image)
                {
                   image.onResize();
                });
            };

            this.unregister = function(image)
            {
                if (image) {
                    var imageIndex = images.indexOf(image);

                    if (imageIndex !== -1)
                    {
                        images.splice(imageIndex,1);
                    }
                }
            };

            this.register = function(image)
            {
                if (image && image.onResize) {
                    images.push(image);
                }
            };
        },
        link: function (scope, parentElement, attrs,ctrl) {
            // listen to resize of parent element
            $(parentElement).resize(ctrl.notifyResize);

            scope.$on('$destroy', function () {
                $(parentElement).removeResize(ctrl.notifyResize);
            })
        }
    };
}]);