kngLayout.addPanelDirective=function (name,factory)  {

    this.directive(name, function (shortcutService) {


        //scope for all panels
        var generalScope = {
            id: '@id',
            collapsed: '@',
            ktitle: '@',
            size: '@',
            minSize: '@',
            order:'@',
            visible:'='

        };
        var templateInfo={};

        //if factory present, then create the directiveInfo from it
        if (factory) {
            templateInfo = factory();
        }

        templateInfo.name=name;
        angular.extend(generalScope, templateInfo.scope);

        var template="<div class='k-panel'  ng-transclude></div>";
        var templateUrl=templateInfo.templateUrl ;

        if (templateInfo.templateUrl) {
            template = undefined;
        }

        return {
            restrict: 'E',
            require: [name],  //find kPanel on parents (without current element)
            replace: true,
            transclude: true,
            scope: generalScope,
            template: template,
            templateUrl: templateUrl,
            link: function (scope, element, attrs, required) {

               // klog.warn("Added {0} {1} {2}",name,scope.collapsed,scope.showHeader)
                scope.elem = element;


                if(attrs.keycodecollapse) {

                    shortcutService.bindKey(attrs.keycodecollapse,scope,scope.toggleCollapse);

                }

                scope.getTitleToolTip=function() {
                    if (attrs.keycodecollapse) {
                        return scope.ktitle+" "+shortcutService.getKeyToolTip(attrs.keycodecollapse,true);
                    }
                    return scope.ktitle;
                };
                scope.translateToPixels=function(value,scale) {
                    if (!value) {
                        return undefined;
                    }

                    var endsWith = function(suffix) {
                        return value.indexOf(suffix, this.length - suffix.length) !== -1;
                    };

                    if (endsWith("px")) {
                        return parseInt(value);
                    }
                    return scale* parseFloat(value) / 100.0;
                };

                scope.getSizeValue=function(scale) {
                    return scope.translateToPixels(scope.size,scale);
                };

                scope.getMinSizeValue=function(scale) {
                    return scope.translateToPixels(scope.minSize,scale);
                };

                var kPanelParent=null;

                //find the first panel "derived" controller
                 var runner=scope.$parent;
                 while (runner) {
                     if (runner.panelcontroller) {
                     kPanelParent=runner.panelcontroller;
                     break;
                     }
                    runner=runner.$parent;
                 }

                var $this=required[0];
                scope.kPanelParent=kPanelParent;
                if (scope.kPanelParent) {
                    scope.kPanelParent.addChildPanel(scope);
                } else {
                    //in this case, we need to register to the onsize of the window, since we are a top most panel
                    $( window ).resize(function() {
                        scope.resize();
                    });
                }

                if (templateInfo && templateInfo.link) {
                    $this.setTemplateBehaviour(templateInfo.link);
                }
                scope.recalcLayout();
                scope.$on('$destroy',function() {
                    if (scope.kPanelParent) {
                        scope.kPanelParent.removeChildPanel(scope);
                    }
                });

            },
            controller: function ($scope) {

                $scope.panels = [];
                $scope.qnaModeratorMode = configuration.qnaModeratorMode;
                $scope.panelcontroller = this;

                // klog.warn($scope.id+" = "+$scope.collapsed);

                $scope.toggleCollapse = function () {
                    if (templateInfo.toggleCollapse) {
                        templateInfo.toggleCollapse($scope);
                        return;
                    }
                    //Quick and dirty.
                    $scope.collapsed = !$scope.isCollapsed();
                    $scope.kPanelParent.collapseChanged();
                };

                this.getScope=function() {
                    return $scope;
                };

                $scope.isCollapsed=function() {
                    return  $scope.collapsed && ($scope.collapsed===true ||  $scope.collapsed.toLowerCase() === "true");
                };

                $scope.isVisible=function() {
                    return  $scope.visible;

                }

                this.toggleCollapse = function () {
                    $scope.toggleCollapse();
                };

                this.setTemplateBehaviour = function (link) {
                    try {

                        $scope.templateBehaviour = link($scope);

                        if ($scope.templateBehaviour &&
                            $scope.templateBehaviour.childAdded) {

                            $scope.panels.forEach(function (panel) {
                                $scope.templateBehaviour.childAdded(panel);
                            });
                        }

                    }
                    catch (e) {
                        alert(e);
                    }
                };

                $scope.resize =function() {
                    if ($scope.elem === undefined) {
                        return;
                    }
                    if ($scope.templateBehaviour &&
                        $scope.templateBehaviour.resize) {
                        $scope.templateBehaviour.resize();
                    }
                    $scope.recalcLayout();
                };

                $scope.recalcLayout = function () {
                    //not created yet
                    if ($scope.elem === undefined) {
                        return;
                    }
                    if ($scope.templateBehaviour &&
                        $scope.templateBehaviour.recalcLayout) {
                        $scope.templateBehaviour.recalcLayout();
                    }
                    else {
                        var clientWidth = $scope.elem[0].clientWidth;
                        var clientHeight = $scope.elem[0].clientHeight;

                        var dx = (100) / $scope.panels.length;
                        for (var i = 0; i < $scope.panels.length; i++) {
                            var child = $scope.panels[i];
                            child.elem[0].style.position = 'absolute';
                            child.elem[0].style.top = '0px';
                            child.elem[0].style.left = '0px';
                            child.elem[0].style.width = '100%';
                            child.elem[0].style.height = '100%';

                            child.recalcLayout();
                        }

                    }
                    $scope.panels.forEach(function(child) {
                        child.recalcLayout();
                    });


                };

                this.addChildPanel = function (panel) {
                    $scope.panels.push(panel);
                    if ($scope.templateBehaviour &&
                        $scope.templateBehaviour.childAdded) {
                        $scope.templateBehaviour.childAdded(panel);
                    }

                    $scope.recalcLayout();
                };
                this.removeChildPanel = function (panel) {
                    var index=$scope.panels.indexOf(panel);
                    if (index<0) {
                        return;
                    }
                    $scope.panels.splice(index, 1 );

                    if ($scope.templateBehaviour &&
                        $scope.templateBehaviour.childRemoved) {
                        $scope.templateBehaviour.childRemoved(panel);
                    }

                    $scope.recalcLayout();
                };



                this.collapseChanged = function (panel) {
                    $scope.recalcLayout();
                };

                this.getSizeValue = $scope.getSizeValue;
                this.getMinSizeValue=$scope.getMinSizeValue;
            }
        };
    });
};
kngLayout.addPanelDirective("kpanel");
