/* globals Promise */

(function() {
    'use strict';

    function SlidesService(kalturaService, $http, kdialog, $rootScope, kProducerActions, $q,
                           kaPlayerViewControlsService, PLAYER_VIEW_MODE, PLAYER_VIEW_STATE)
    {
        var self = this;

        var activeDocumentEntryId = '';
        var loadPromise = null;
        var presentationSlides = [];
        var activeDocumentFileName = '';
        var slidesSize = null;
        var selectedSlideAsset = null;


        function backwardCompatibilityForSlidesFromKMS()
        {
            // IMPORTANT: this function must be called after kProducerAction is ready
            var currentSelectDeckDocument = kProducerActions.getState('actions.select-deck-document',null);

            if (configuration.documentEntryId && !currentSelectDeckDocument)
            {
                klog.info("loading default presentation provided by KMS with document id " + configuration.documentEntryId);
                replaceSlides(configuration.documentEntryId);
            }
        }


        // register to cuepoint changes only after cuepoint service is ready
        kProducerActions.isReady().then(function() {

            backwardCompatibilityForSlidesFromKMS();

            syncProducerActions();

            // listen to changes from server and act accordingly
            kProducerActions.subscribeStateChange(function() {
                syncProducerActions();
            });
        });

        function syncProducerActions()
        {
            //docEntry update
            var deckDocumentId = kProducerActions.getState('actions.select-deck-document.documentEntryId','');

            if ( deckDocumentId !== activeDocumentEntryId) {
                reload();
            }
        }

        function reload()
        {
            if (loadPromise) {
                // make sure we are not currently running and then re-load document
                loadPromise.finally(function() {
                    loadPromise = null; // make sure load will run from fresh
                    load();
                });
            }else {
                load();
            }
        }


        /**
         * Loads slides into service (if provided a relevant document to load)
         * @returns {null|Promise|*|{options, files}}
         */
        function load()
        {
            if (loadPromise) {
                return loadPromise;
            }

            var deferred = $q.defer();
            loadPromise = deferred.promise;

            var onPresentationSlidesLoaded = function (slides)
            {
                // update presentation slides and current slide parameters
                presentationSlides = slides || [];

                if (slides && slides.length > 0) {
                    var img = new Image();
                    img.src = slides[0].src;
                    img.onload = function (e) {

                        // get slides dimensions
                        slidesSize = {
                            'width': e.srcElement.naturalWidth,
                            'height': e.srcElement.naturalHeight
                        };
                        $rootScope.$broadcast('slidesSizeSet',slidesSize);


                        deferred.resolve(slides);
                    };
                }
                else {
                    deferred.resolve(slides);
                }
            };

            var loadSlidesFromMetadataXml = function (documentEntryId, result, isPdf) {
                var slides = [];
                var itemsArr = $(result.data).find(isPdf ? 'item' : 'slide');
                for (var i = 0, len = itemsArr.length; i < len; i++) {

                    var children = itemsArr[i].children;
                    var slideURL = null;
                    var content = "";
                    var notes = "";
                    var id = i;
                    var slideName = '';
                    var title = "slide #" + (i + 1);
                    for (var j = 0, len2 = children.length; j < len2; j++) {
                        if (children[j].localName === "imagefile" || children[j].localName === "name") {
                            slideURL = baseUrl + children[j].textContent;
                            id = slideName = children[j].textContent;
                        }
                        if (children[j].localName === "text") {
                            content = children[j].innerHTML;
                        }
                        if (children[j].localName === "notes") {
                            notes = children[j].innerHTML;
                            notes = notes.replace(/(?:\r\n|\r|\n)/g, '<br />');
                        }
                        if (children[j].localName === "title") {
                            title = children[j].innerHTML;
                        }
                    }

                    var index = i;

                    // Adding document entry id to make sure we can later compare for uniquness
                    slides.push({
                        documentEntryId: documentEntryId,
                        src: slideURL,
                        index: index,
                        title: title,
                        notes: notes,
                        content: content,
                        id: id,
                        slideName: slideName,
                        type: 'slide'
                    });
                    // }
                }
                return Promise.resolve(slides);
            };
            var baseUrl = 'slides/';

            kProducerActions.isReady().then(function()
            {
                var requiredDocumentId = kProducerActions.getState('actions.select-deck-document.documentEntryId','');

                if (activeDocumentEntryId === requiredDocumentId) {
                    deferred.resolve({state: 'no_change'});
                }else {
                    // raise presentationChanged event after a successful change
                    loadPromise.then(function () {
                        klog.info('slidesService.load(): broadcasting "PresentationChanged"');
                        $rootScope.$broadcast("PresentationChanged");
                    });

                    var documentEntryId = activeDocumentEntryId  = requiredDocumentId;

                    if (requiredDocumentId) {
                        // reset internal parameters
                        presentationSlides = [];
                        activeDocumentFileName = '';

                        // scenario: has presentation document to load
                        klog.info("slidesService.load(): loading document with entry id '{0}' from server", documentEntryId);
                        kalturaService.getDocumentSlides(documentEntryId).then(function (result) {
                            activeDocumentFileName = result.document ? result.document.description : '';
                            var url = result.slidesUrl;
                            baseUrl = url.substring(0, url.lastIndexOf('/') + 1);
                            var isPdf = result.fileExt.toLowerCase() === 'pdf';

                            if (isPdf) {
                                url = url.replace('metadata.xml', 'imagesList.xml');
                            }

	                        var relocateUrl = !isPdf ? "/relocate/metadata.xml" : "/relocate/imagesList.xml";

                            if (!url.endsWith(relocateUrl)) {
                                //this is to avoid bug in backend where download action doesn't have CORS header on rediret
                                url += relocateUrl;
                            }

                            return $http.get(url).then(function (result) {
                                return loadSlidesFromMetadataXml(documentEntryId, result, isPdf);
                            });
                        }).catch(function(error) {
                            var message = (error ? error.message : '');
                            kdialog.alert(message, true).then(function () {
                                window.close();
                            });
                        }).then(onPresentationSlidesLoaded);
                    }
                    else {
                        // scenario: no active presentation document.
                        onPresentationSlidesLoaded();
                    }
                }
            });
            return loadPromise;

        }

        function removeSlides()
        {
            return replaceSlides(null);
        }

        function replaceSlides (newSlidesEntryId)
        {
            newSlidesEntryId = newSlidesEntryId || ''; // make sure new slides entry id is not null (to remove slides we use empty string

            klog.info('SlidesService.replaceSlides(): updating presentation document to id {0}', newSlidesEntryId);
            kProducerActions.setStateOrientedAction('select-deck-document',{documentEntryId : newSlidesEntryId });
            return $q.resolve();
        }

        function getPresentationSlides()
        {
            return presentationSlides;
        }

        function getSlideSize()
        {
            return slidesSize;
        }

        function getSlidesAspectRatio()
        {
            return slidesSize ? (slidesSize.width / slidesSize.height) : (16.0 / 9.0);
        }
        
        function getActiveDocumentInfo()
        {
            if (activeDocumentEntryId) {
                return {id : activeDocumentEntryId,  name : activeDocumentFileName, slides : presentationSlides};

            }else {
                return null;
            }
        }

        function changeSelectedSlide(slideAsset)
        {
            selectedSlideAsset = slideAsset;
            kProducerActions.setStateOrientedAction('select-a-thumb',
                {
                    slideName: slideAsset.slideName,
                    documentEntryId: slideAsset.documentEntryId,
                    title: slideAsset.title,
                    content: slideAsset.content,
                    type: 'slide'
                });
            //after sending the slide change update player view mode if needed
            updatePlayerViewMode();
        }
        
        function updatePlayerViewMode()
        {
            //if player view mode is video only - change to default slide view mode
            if(kaPlayerViewControlsService.getCurrentViewId() === PLAYER_VIEW_MODE.VIDEO_ONLY) {
                kaPlayerViewControlsService.changeViewById(PLAYER_VIEW_MODE.PIP_VIDEO_LARGE, PLAYER_VIEW_STATE.UNLOCKED);
            }
        }

        self.getPresentationSlides = getPresentationSlides;
        self.getSlidesSize = getSlideSize;
        self.getSlidesAspectRatio = getSlidesAspectRatio;
        self.getActiveDocumentInfo = getActiveDocumentInfo;
        self.replaceSlides = replaceSlides;
        self.removeSlides = removeSlides;
        self.changeSelectedSlide = changeSelectedSlide;

        self.load = load;

    }

    angular.module('k-deck-slides').service('slidesService', SlidesService);

})();

