kwpApp.controller('modelController', function($scope,$rootScope,$document,captureService,sessionService,kAssetsTimelineService, kProducerActions) {

    $scope.captureService = captureService;
    $scope.sessionService = sessionService;

    $rootScope.$on('slidesSizeSet', function(scope,size) {
        $scope.slidesAspectRatio = size.width/size.height;
    });

    var unregisterStreamUpdated = $rootScope.$on('rtcStreamUpdated', function(event, status, userDecided, aspectRatio, peerName)  {
        calcShouldShowPresentationPanel();
        $scope.showVideo = status;
        if (aspectRatio) {
            $scope.videoApectRatio = aspectRatio;
        }
        if (!peerName)
            peerName = false;
        $scope.peerName = peerName;
    });

    $rootScope.$on('PresentationChanged',function (){
        calcShouldShowPresentationPanel();
    });

    $scope.$on('$destroy', unregisterStreamUpdated);

    $scope.showVideo=false;
    $scope.peerName=false;
    $scope.videoApectRatio=16.0/9.0;
    $scope.slidesAspectRatio=16.0/9.0;
    $scope.refreshVideoLayout = captureService.refreshVideoLayout;

    $scope.configuration = configuration;
    $scope.shouldShowPresntationPanel=undefined;
    $scope.debugExpanded = false;
    $scope.qnaEnabled = configuration.QnAEnabled;
    $scope.qnaModeratorMode = configuration.qnaModeratorMode;
    $scope.showSlides = configuration.showSlides;
	$scope.enabledAnalytics = configuration.enabledAnalytics;
	$scope.liveModerator = configuration.liveModerator;
	$scope.participantsPanel = configuration.participantsPanel;

    var presentationLoaded = function () {
        calcShouldShowPresentationPanel();
    };

    kAssetsTimelineService.isReady().then(function()
    {
        presentationLoaded();
        $scope.$on("AssetsTimelineChanged",presentationLoaded);
    });

    var calcShouldShowPresentationPanel = function()
    {
        var selectDeckDocument = kProducerActions.getState('actions.select-a-thumb','');
        $scope.shouldShowPresntationPanel=$scope.showSlides && (kAssetsTimelineService.timelineAssets.length>0 || selectDeckDocument !== '');
    };

   function checkIsSupportedBrowser() {
       // Firefox 1.0+
       var isFirefox = typeof InstallTrigger !== 'undefined';

       // Safari 3.0+ "[object HTMLElementConstructor]"
       var isSafari = /constructor/i.test(window.HTMLElement) || (function (p) { return p.toString() === "[object SafariRemoteNotification]"; })(!window['safari'] || (typeof safari !== 'undefined' && safari.pushNotification));

       // Chrome 1+
       var isChrome = !!window.chrome && (/Chrome/.test(navigator.userAgent) && /Google Inc/.test(navigator.vendor));

       // Edge (based on chromium) detection
       var isEdgeChromium = isChrome && (navigator.userAgent.indexOf("Edg") != -1);

       var isElectron = navigator.userAgent.toLowerCase().indexOf(' electron/') > -1;

       return isElectron || isFirefox || isSafari || isChrome || isEdgeChromium;
   }

    $scope.supportedBrowser = checkIsSupportedBrowser();
});


