(function () {
    "use strict";

    function Directive($timeout) {

        function link(scope, element, attrs, ctrls)
        {
            var regex = new RegExp("^[^\n\r]*$");

            var $element = $(element);

            element.on('keypress', function(e) {
                var char = String.fromCharCode(e.which);
                var text = angular.element(e.srcElement).val();
                if(!regex.test(char) || !regex.test(text)) {
                    event.preventDefault();
                }
            });


            $timeout(function ()
            {
                $element.height(0).height($element[0].scrollHeight);
            },200);

            element
                .on( 'change keyup keydown paste cut',
                    function (){
                        $element.height(0).height($element[0].scrollHeight);
                    });
        }

        return {
            restrict: 'A',
            link: link
        };

    }

    kUI.directive('kTextarea', Directive);

}());
