/*jshint bitwise: false*/

(function() {
    'use strict';

    function Service(cuePointsService, kalturaAPI, gettextCatalog, kdialog, kalturaService, captureService, sessionService, kProducerActions, $q, kCef, asyncLiveEntryInfoService) {
        var self = this;
        var openedDialog = null;
        var lastResetEntryData = null;
        var pushServerNotificationToClearOnArchive=[];
        var archiveCustomActions = [];
        var isAutoArchiveEnabled = false;

        function needToForceArchive() {
            return cuePointsService.hasUnHandledCuePoints() && sessionService.getRecordStatus() !== sessionService.RECORD_STATUS.DISABLED;
        }

        function resetEntry(){
            sessionService.ready.then(function() {
                kdialog.yesNoDialog({
                    yesMessage: needToForceArchive() ? gettextCatalog.getString("Yes, force archive") : gettextCatalog.getString("Yes, Continue"),
                    noMessage: gettextCatalog.getString("No, Go Back"),
                    title: gettextCatalog.getString("Archive Entry"),
                    message: getAreYouSureMessageText()
                }).then(function () {
                    kalturaAPI.login().then(function (ks) {
                        kProducerActions.setStateOrientedAction('entry-reset-mode', { action: 'start', ksHash: getKSHash(ks) });
                    });
                });
            });
        }

        function getAreYouSureMessageText(){
            var message = gettextCatalog.getString("Archiving an entry will remove all entry data recorded so far, including slide cue points and Q&A.");
            if (sessionService.getRecordedEntryId() !== undefined && sessionService.getRecordedEntryId() !== "") {
                message += "<br>" +gettextCatalog.getString("The VOD recording will be available under entry ID: ") + sessionService.getRecordedEntryId();
            }
            if (needToForceArchive()) {
                message += "<br><br><p style='color:red; font-size: 20px'>" +gettextCatalog.getString("Webcast recording is still being processed.<br/>Forcing archive may result in missing slides on the recorded webcast. ") + "</p>";
                message += "<br><br>The operation is permanent and cannot be undone.<br>Are you sure you want to force archive?";
            }
            else {
                message += "<br><br>The operation is permanent and cannot be undone.<br>Are you sure you want to continue?";
            }
            return message;
        }

        function addPushServerNotificationsToClearOnArchive(templates_name) {

            pushServerNotificationToClearOnArchive = pushServerNotificationToClearOnArchive.concat(templates_name);
        }

        function addArchiveCustomAction(fn) {
            archiveCustomActions = archiveCustomActions.concat(fn);
        }

        function resetEntryInternal(mediaEntryId) {
            var promises = [];
            promises.push($q.when(cuePointsService.handleResetEntryDeleteCuePoint(mediaEntryId)));
            promises.push(kProducerActions.retransmitInstanceOrientedActions());
            promises.push($q.when(kalturaService.updateVODEntryAfterArchive(sessionService.getRecordedEntryId(),asyncLiveEntryInfoService.getCurrentEntryInfo())));
            promises.push($q.when(kalturaService.deleteRecordedEntry(mediaEntryId)));
            promises.push($q.when(kalturaService.clearPushServerNotifications(mediaEntryId,pushServerNotificationToClearOnArchive)));
            _.each(archiveCustomActions, function(fn) {
                promises.push(fn());
            });

            return $q.all(promises);
        }

        function onEntryResetNotStart(message){
            if(openedDialog){
                new Promise(function (success, dismiss) {
                    kdialog.dismiss(openedDialog.instance, false);
                    success();
                }).then(function(){
                    kdialog.infoDialog({
                        title:message
                    }).then(function(){
                        kCef.reload();
                    });
                });
            }
        }

        function canEnableResetEnterBtn() {
            return !captureService.isBroadcasting() && !isAutoArchiveEnabled;
        }

        function onProducerStateChanged() {

            var resetEntryData = kProducerActions.getState('actions.entry-reset-mode');

            if (resetEntryData && !angular.equals(lastResetEntryData,resetEntryData))
            {
                lastResetEntryData = resetEntryData;
                entryReset();
            }
        }

        function entryReset(){

            kalturaAPI.login().then(function (ks) {
                if (lastResetEntryData.action === 'start'){
                    //pendingEntryResetCuePointsIds[entryResetCuePoint.id] = 1;
                    openedDialog = kdialog.attention(gettextCatalog.getString("Archive in progress..."), gettextCatalog.getString("This action may take some time,<br>please be patient:)"), gettextCatalog.getString("Abort"));
                    openedDialog.promise.then( function () {
                        kProducerActions.setStateOrientedAction('entry-reset-mode', { action: 'abort', ksHash: getKSHash(ks) });

                    });
                    if (lastResetEntryData.ksHash === getKSHash(ks)){
                        resetEntryInternal(configuration.MediaEntryId).then(function () {
                            kProducerActions.setStateOrientedAction('entry-reset-mode', { action: 'end', ksHash: getKSHash(ks) });
                        }, function (err) {
                            kProducerActions.setStateOrientedAction('entry-reset-mode', { action: 'failed', ksHash: getKSHash(ks) });
                            klog.error('failed to reset entry ' + configuration.MediaEntryId+ " " +JSON.stringify(err));
                        });
                    }
                }
                else if (lastResetEntryData.action === 'abort') {
                    onEntryResetNotStart(gettextCatalog.getString("Archive operation Aborted!"));
                }
                else if (lastResetEntryData.action === 'failed') {
                    onEntryResetNotStart(gettextCatalog.getString("Archive operation Failed!"));
                }
                else if (lastResetEntryData.action === 'end') {
                    onEntryResetNotStart(gettextCatalog.getString("Archived Successfully!"));
                } else {
                    klog.warn('got entry-reset with unknown action code ' + lastResetEntryData.action);
                }
            });
        }

        function init() {
            // make sure we fetched all cue points currently exists on the server
            kProducerActions.isReady().then(function() {

                // listen to changes from server and act accordingly
                kProducerActions.subscribeStateChange(function() {

                    onProducerStateChanged();
                });

                // run sync from server once to sync producer state (if server doesn't contain any relevant cuepoint)
                onProducerStateChanged();
            });

            asyncLiveEntryInfoService.registerOnEntryInfoChange(OnEntryInfoChangeFunc);
        }

        function OnEntryInfoChangeFunc() {
            var entry = asyncLiveEntryInfoService.getCurrentEntryInfo();
            isAutoArchiveEnabled = entry.recordingOptions.shouldAutoArchive;
        }

        function getArchiveDisabledTooltip() {
            return isAutoArchiveEnabled ? gettextCatalog.getString("Archiving has been automatically enabled") : gettextCatalog.getString("Archive");
        }

        self.getArchiveDisabledTooltip = getArchiveDisabledTooltip;
        self.canEnableResetEnterBtn = canEnableResetEnterBtn;
        self.resetEntry = resetEntry;
        self.addPushServerNotificationsToClearOnArchive=addPushServerNotificationsToClearOnArchive;
        self.addArchiveCustomAction = addArchiveCustomAction;
        init();
    }

    angular.module('kEntryManagementModule').service('kEntryManagementService', Service);

})();
