var EventManager = (function(){

//    EVENTS = {AF : }

    var clients = [];
    var recived = false;
    var _register_for_event = function(eventID,handle){
        clients.push(handle);
    }
    var _register_for_single_event = function(handle){
        //clients.push(handle);
    }
    var _un_register_for_event = function(handle){
        //clients.push(handle);
    }

    var _RegisterForAllEvents = function(callback){
        clients.push(callback);
    }

    var _report_event = function(eventID,data_json_string){
        //alert("MessageFromEventHandler = "+eventID+" data "+ data_json_string);
        klog.info("New event received on event manager.number of client to be informed:"+clients.length);
        for(var i=0;i < clients.length;i++){
            clients[i](eventID,data_json_string);
        }
    }


    return {
        RegisterForEvent : _register_for_event,
        RegisterForEventOnce : _register_for_single_event,
        UnregisterForEvent : _un_register_for_event,
        ReportEvent : _report_event,
        RegisterForAllEvents : _RegisterForAllEvents
    }


})();